import {getRequest, postFromData} from './config'

function getCodepic (link) {
  return getRequest(link, {
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function PostLoginData (link, user, password) {
  return postFromData(link, {username: user, password: password}).then((res) => {
    return Promise.resolve(res.data)
  })
}

function postRegistData (link, username, password, nick, captchacode) {
  let data = {
    username: username,
    password: password,
    nickname: nick,
    captchacode: captchacode
  }
  return postFromData(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 退出
function logout (link) {
  return getRequest(link).then((res) => {
    return Promise.resolve(res.data)
  })
}

const api = {
  PostLoginData,
  postRegistData,
  getCodepic,
  logout
}

export default api



