import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/components/index/index'
import Rank from '@/components/rank/index'
import AboutUs from '@/components/about-us/index'
import StatusPage from '@/components/statusPage/index'
import Login from '@/components/login/index'
import Library from '@/components/library/index'
import QuestionBank from '@/components/questionBank/index'
import QuestionDetail from '@/components/question-detail/index'
import questionSolve from '@/components/question-detail/questionSolve'
import competition from '@/components/competition/index'
import test from '@/components/competition/test'
import competitionRank from '@/components/competition/competitionRank'
import PersonalCenter from '@/components/personal-center/index'
import Quiz from '@/components/quiz/index'
import quizAdd from '@/components/quiz/quizAdd'
import quizPreview from '@/components/quiz/quizPreview'
import quizStart from '@/components/quiz/quizStart'
import quizCheck from '@/components/quiz/quizCheck'
import quizDetail from '@/components/quiz/quizDetail'
import addTest from '@/base/personal/addTest'
import Forums from '@/components/forums/index'
import postSearch from '@/components/forums/postSearch'
import postPublish from '@/components/forums/postPublish'
import postDetail from '@/components/forums/postDetail'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/rank',
      name: 'rank',
      component: Rank
    },
    {
      path: '/about',
      name: 'about',
      component: AboutUs
    },
    {
      path: '/status',
      name: 'status',
      component: StatusPage
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/library',
      component: Library,
      children: [
        {
          path: '/',
          name: 'questionBank',
          component: QuestionBank
        },
        {
          path: 'questionDetail',
          name: 'questionDetail',
          component: QuestionDetail
        }
      ]
    },
    {
      path: '/questionSolve',
      name: 'questionSolve',
      component: questionSolve
    },
    {
      path: '/competition',
      name: 'competition',
      component: competition
    },
    {
      path: '/test',
      name: 'test',
      component: test
    },
    {
      path: '/competitionRank',
      name: 'competitionRank',
      component: competitionRank
    },
    {
      path: '/personal-center/:isShow',
      name: 'PersonalCenter',
      component: PersonalCenter
    },
    {
      path: '/addTest',
      name: 'addTest',
      component: addTest
    },
    {
      path: '/forums',
      name: 'forums',
      component: Forums,
      children: [{
        path: '/postSearch/:type/:page?',
        name: 'postSearch',
        component: postSearch
      },
      {
        path: '/postPublish',
        name: 'postPublish',
        component: postPublish
      },
      {
        path: '/postDetail/:id',
        name: 'postDetail',
        component: postDetail
      }
      ]
    },
    {
      path: '/quiz',
      name: 'quiz',
      component: Quiz,
      children: [{
        path: '/quizAdd/:courseId/:id?',
        name: 'quizAdd',
        component: quizAdd
      }, {
        path: '/quizPreview/:id/:uid?',
        name: 'quizPreview',
        component: quizPreview
      }, {
        path: '/quizStart/:id',
        name: 'quizStart',
        component: quizStart
      }, {
        path: '/quizCheck/:id',
        name: 'quizCheck',
        component: quizCheck
      }, {
        path: '/quizDetail/:id',
        name: 'quizDetail',
        component: quizDetail
      }
      ]
    }
  ]
})

// 路由全局守卫
router.beforeEach((to, from, next) => {
  var storage = window.sessionStorage
  var userData = storage.getItem('userInfo')
  var isStorage = userData !== 'undefined' && userData !== null && userData !== undefined
  if (isStorage) {
    // 用户名规范检测
    if (to.fullPath === '/index' || to.fullPath === '/') {
      next()
      return
    }
    var user = JSON.parse(userData)
    var userNick = user.nick
    // 判断是否是竞赛小组名，是的话允许20长度
    var reg = userNick.indexOf('GPNU_') === 0
      ? /^[-\w\u4e00-\u9fa5]{2,20}$/ : /^[-\w\u4e00-\u9fa5]{2,14}$/
    if (!reg.test(userNick)) {
      // 如果没有改掉不规范用户名，只能让用户待在首页，主要防止用户自己输入路由
      next('/index')
      return
    }
  } else {
    // 需要登录的界面判断检测, 个人中心没登录回到登录页
    if ([
      'personal-center',
      'quizDetail',
      'quizAdd',
      'quizPreview',
      'quizStart',
      'quizCheck'].indexOf(to.fullPath.split('/')[1]) >= 0) {
      next('/login?redirect=' + to.fullPath)
      return
    }
  }
  next()
})

export default router



