<template>
  <div>
    <div class="rank">
      <div class="rank-search">
        <el-row :gutter="100">
          <el-col :span="rankRanges.length !== 0?2:4">
            <el-dropdown trigger="click" @command="rankTypeChange">
              <el-button size="small">
                &nbsp;&nbsp;&nbsp;&nbsp;{{rankTypeActive}}&nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-if="rankType !== rankTypeActive" v-for="(rankType, index) in rankTypes" :key="index" :command="rankType">&nbsp;&nbsp;&nbsp;{{rankType}}&nbsp;&nbsp;&nbsp;&nbsp;</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>
          <el-col :span="2" v-if="rankRanges.length !== 0">
            <el-dropdown trigger="click" @command="rankRangeChange">
              <el-button size="small">
                &nbsp;&nbsp;&nbsp;&nbsp;{{rankRangeActive}}&nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-if="rankRange !== rankRangeActive" v-for="(rankRange, index) in rankRanges" :key="index" :command="rankRange">&nbsp;&nbsp;&nbsp;{{rankRange}}&nbsp;&nbsp;&nbsp;&nbsp;</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>
          <el-col :span="7" :offset="13">
            <el-input size="small" placeholder="用户昵称/姓名搜索" v-model="searchInput" class="input-with-select" @keyup.enter.native="getSearchList(1,20)">
              <!--<el-button slot="append" icon="el-icon-search" @click="searchRank"></el-button>-->
              <el-button slot="append" icon="el-icon-search" @click="getSearchList(1,20)"></el-button>
            </el-input>
          </el-col>
        </el-row>
      </div>
      <el-table
        :data="tableData"
        stripe
        v-loading="tableLoading"
        style="width: 100%">
        <el-table-column
          label="名次"
          width="120px"
          align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.ranking == 1" style="color: rgb(248, 208, 28);"><i class="icon-diyi"></i></span>
            <span v-else-if="scope.row.ranking == 2" style="color: rgb(190, 197, 204);"><i class="icon-dier"></i></span>
            <span v-else-if="scope.row.ranking == 3" style="color: rgb(208, 153, 106);"><i class="icon-disan"></i></span>
            <span v-else>{{scope.row.ranking}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="昵称"
          width="250px"
          align="center">
          <template slot-scope="scope">
            <span class="rankUserName" style="cursor: pointer" @click="getinfo(scope.row)">
              <img class="userImage" :src="scope.row.userImage">
              {{scope.row.nick}}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          property="signature"
          show-overflow-tooltip
          label="签名"
          width="550px"
          align="center">
        </el-table-column>
        <el-table-column
          property="pass"
          label="解决">
        </el-table-column>
        <el-table-column
          property="submit"
          label="提交">
        </el-table-column>
        <el-table-column
          label="通过率">
          <template slot-scope="scope">
            <span :class="scope.row.passRate >= 80 ? 'green' : scope.row.passRate >= 40 ? 'blue' : 'red'">{{(scope.row.passRate)}}%</span>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        title="学生信息"
        :visible.sync="dialogVisible"
        width="60%">
        <Information :information="infoData"></Information>
        <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
              </span>
      </el-dialog>
      <div class="public-pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="Page"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="20"
          background
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import Information from '@/base/infomation/infomation'
import api from '@/api/rank'

export default {
  components: {
    Information
  },
  data () {
    return {
      info: {},
      dialogVisible: false,
      total: 0,
      Page: 1,
      Pagesize: 20,
      rankTypeActive: '总榜',
      rankTypes: ['总榜', '月榜', '周榜', '日榜'],
      rankTypesOfClick: [],
      rankRangeActive: '全范围',
      rankRanges: [],
      currentPage: 1,
      ajaxTableData: [],
      tableData: [],
      searchInput: '',
      infoData: '',
      rankType: 'all',
      rankRange: null,
      tableLoading: false,
      defaultImage: require('../../../static/image/acmlogo.jpg')
    }
  },
  methods: {
    handleSizeChange (val) {
      this.Pagesize = val
      this.getSearchList(this.Page, val, null, this.rankType, this.rankRange)
    },
    handleCurrentChange (val) {
      this.Page = val
      this.getSearchList(val, this.Pagesize, null, this.rankType, this.rankRange)
    },
    rankTypeChange (command) {
      if (this.Page !== 1) {
        this.Page = 1
      }
      var type
      if (command === '周榜') {
        type = 'week'
      } else if (command === '月榜') {
        type = 'month'
      } else if (command === '日榜') {
        type = 'day'
      } else {
        type = 'all'
      }
      this.rankType = type
      this.getSearchList(1, 20, this.searchInput, this.rankType, this.rankRange)
      this.rankTypeActive = command
    },
    rankRangeChange (command) {
      if (this.Page !== 1) {
        this.Page = 1
      }
      var rangeId
      if (command === '班级榜') {
        rangeId = this.info.code.slice(0, this.info.code.length - 2)
      } else if (command === '年级榜') {
        this.$message({
          message: '暂无此筛选范围，敬请期待',
          type: 'warning',
          duration: 1000,
          showClose: true
        })
        rangeId = null
        return
      } else if (command === '学校榜') {
        this.$message({
          message: '暂无此筛选范围，敬请期待',
          type: 'warning',
          showClose: true,
          duration: 1000
        })
        rangeId = null
        return
      } else {
        rangeId = null
      }
      this.rankRange = rangeId
      this.getSearchList(1, 20, this.searchInput, this.rankType, this.rankRange)
      this.rankRangeActive = command
    },
    filterTableDateList (list) {
      const _this = this
      let tableData = []
      for (let i = 0; i < list.length; i++) {
        let data = {}
        data.submit = list[i].submit
        data.pass = list[i].solved
        data.uid = list[i].uid
        data.nick = list[i].nick
        data.signature = list[i].signature
        data.passRate = (list[i].solved / list[i].submit * 100).toFixed(2)
        data.userImage = list[i].avatarUrl ? list[i].avatarUrl : _this.defaultImage
        tableData.push(data)
      }
      return tableData
    },
    obtainData () {
      api.getRankList('/ranks').then(function (response) {
      }).catch(function (response) {
        // console.log(response)
      })
    },
    getAllList (page = 1, pagesize = 20) {
      const _this = this
      api.getAllRankList('/rank_all', page, pagesize).then(function (res) {
        _this.total = res.total
        _this.ajaxTableData = res.data
        _this.tableData = _this.filterTableDateList(_this.ajaxTableData)
        for (let i = 0; i < res.data.length; i++) {
          _this.tableData[i].ranking = pagesize * (page - 1) + i + 1
        }
      })
    },
    getSearchList (page = 1, pagesize = 20, searchInput = this.searchInput, type = null, rangeId) {
      const _this = this
      _this.tableLoading = true
      api.getSomeoneRank('/rank_all', page, pagesize, searchInput, type, rangeId).then(function (res) {
        _this.total = res.total
        _this.ajaxTableData = res.data
        _this.tableData = _this.filterTableDateList(_this.ajaxTableData)
        for (let i = 0; i < res.data.length; i++) {
          _this.tableData[i].ranking = pagesize * (page - 1) + i + 1
        }
        _this.tableLoading = false
      })
    },
    getinfo (data) {
      this.infoData = null
      this.dialogVisible = true
      this.infoData = data.uid
    },
    getListInfo () {
      const _this = this
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json !== 'undefined' && json !== null && json !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
        api.getInfoList('/users/detail', uid).then(function (res) {
          _this.info = res.data.user_info
          _this.limitRangeChoose()
        })
      }
    },
    limitRangeChoose () {
      if (this.info.identity === '2') {
        this.rankRanges = ['全范围', '学校榜']
      } else if (this.info.identity === '1') {
        this.rankRanges = ['全范围', '班级榜', '年级榜', '学校榜']
      } else {
        this.rankRanges = []
      }
    }
  },
  created () {
    /*  this.getRankList()
     this.obtainData()  */
    //    this.getAllList()
    this.getListInfo()
    this.rankType = this.$route.query.rankType
    if (this.rankType !== undefined) {
      this.getSearchList(1, 20, null, this.rankType, null)
    } else {
      this.getSearchList(1, 20, null, null, null)
    }
    this.rankTypeActive = (this.rankType == null) ? '总榜' : (this.rankType === 'all') ? '总榜' : (this.rankType === 'month') ? '月榜' : (this.rankType === 'week') ? '周榜' : '日榜'
  }
}
</script>

<style scoped>
  .rank {
    width: 1170px;
    margin: 0 auto;
    padding-top: 20px;
  }
  .rank-search {
    /*width: 200px;*/
    margin-bottom: 20px;
  }
  .public-pagination {
    padding: 20px 0;
  }
  .public-pagination /deep/ .el-pagination .el-pager .active{
    background-color: #f48118 !important;
  }
  .public-pagination /deep/ .el-pagination .el-pagination__total{
    color: inherit;
  }
  .public-pagination /deep/ .el-pagination .el-pagination__jump{
    color: inherit;
  }
  .rank .green {
    color: #8ecc45;
  }
  .rank .blue {
    color: #6495ed;
  }
  .rank .red {
    color: #ff6c3f;
  }
  .icon-diyi:before {
    font-size: 26px;
  }
  .icon-dier:before {
    font-size: 24px;
  }
  .icon-disan:before {
    font-size: 22px;
  }
  .rankUserName{
    display: block;
    padding-left: 80px;
    width: 100%;
    text-align: left;
    font-size: 14px;
    color: #333333;
    transition: all .1s;
    cursor: pointer;
  }
  .rankUserName:hover{
    color: #f48118;
    font-weight: 600;
  }
  .userImage{
    width: 29px;
    height: 29px;
    border-radius: 50%;
    position: absolute;
    left: 45px;
    top: 8px;
    border: 1px solid #67c23a52;
    box-shadow: 1px 2px 21px #6495ed5c;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -ms-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
  }
  .rankUserName:hover .userImage {
    box-shadow: 1px 2px 10px #ff6c3f;
    width: 31px;
    height: 31px;
    left: 44px;
    top: 7px;
  }
</style>



