<template>
  <div class="CallFeedback">
    <div class="feedback-head">
          <span>反馈意见</span>
          <button class="edit" @click="openEditBox">发布</button>
    </div>
    <div class="feedback-content">
      <el-table
        :data="showFeedbackListPartData"
        border
        stripe
        header-row-class-name="head-class"
        style="width: 100%">
        <el-table-column
          type="expand">
          <template slot-scope="props">
            <p style="line-height: 2em"><span class="content">反馈内容&nbsp;|&nbsp;</span>{{props.row.content}}</p>
          </template>
        </el-table-column>
        <el-table-column
          type="index"
          label="序号"
          align="center"
          width="60"
          :index="indexMethod">
        </el-table-column>
        <el-table-column
          prop="title"
          label="反馈标题"
          align="center">
        </el-table-column>
        <el-table-column
          align="center"
          label="反馈类型"
          width="120">
          <template slot-scope="scope">
            <span>{{getType(scope.row.type)}}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="审核情况"
          width="120">
          <template slot-scope="scope">
            <span v-if="scope.row.is_solved === 0" style="color: #6495ed; font-weight: 600;">待审核</span>
            <span v-if="scope.row.is_solved === 1" style="color: #8ecc45; font-weight: 600;">已通过</span>
            <span v-if="scope.row.is_solved === 2" style="color: #ff6c3f; font-weight: 600;">未通过</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="public-pagination">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-size="pageSize"
        background
        layout="total, prev, pager, next, jumper"
        :total="showFeedbackListData.length">
      </el-pagination>
    </div>
    <el-dialog
      title="反馈意见"
      :visible.sync="feedbackDialog"
      width="60%"
    >
      <el-form ref="form" :rules="feedbackRule" :model="feedbackForm" label-width="80px">
        <el-form-item label="标题" prop="title">
          <el-row>
            <el-col :span="24">
              <el-input v-model="feedbackForm.title" placeholder="请输入标题"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="反馈类型">
          <el-row>
            <el-col :span="24">
              <el-select v-model="feedbackForm.type" placeholder="请选择反馈类型">
                <el-option
                  v-for="item in feedbackType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="反馈内容" prop="content">
          <el-row>
            <el-col :span="24">
              <el-input
                type="textarea"
                :rows="10"
                placeholder="请输入内容"
                v-model="feedbackForm.content"
                resize="none">
              </el-input>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="feedbackDialog = false">取 消</el-button>
        <el-button type="primary" @click="sendFeedback">提交我的建议</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from '@/api/index'
import apiGetlist from '@/api/personCenter'

export default {
  name: 'CallFeedback',
  data () {
    return {
      feedbackDialog: false,
      feedbackType: [
        {
          label: '用户体验',
          value: 1
        },
        {
          label: '用户界面',
          value: 2
        },
        {
          label: '题库',
          value: 3
        },
        {
          label: '后台效率',
          value: 4
        },
        {
          label: '发现BUG',
          value: 5
        },
        {
          label: '新功能',
          value: 6
        },
        {
          label: '其他',
          value: 7
        }
      ],
      feedbackForm: {
        // uid: -1,
        title: '',
        content: '',
        type: 1
      },
      feedbackRule: {
        title: [
          {
            required: true,
            message: '标题不能为空',
            trigger: 'blur'
          }
        ],
        content: [
          {
            required: true,
            message: '内容不能为空',
            trigger: 'blur'
          }
        ]
      },
      showFeedbackListData: [],
      // 分页展示的信息
      showFeedbackListPartData: [],
      page: 1,
      pageSize: 10
    }
  },
  methods: {
    openEditBox () {
      this.feedbackForm = {
        // uid: -1,
        title: '',
        content: '',
        type: 1
      }
      this.feedbackDialog = !this.feedbackDialog
    },
    indexMethod (index) {
      return index - (this.pageSize - 1) + (this.page * this.pageSize)
    },
    getType (typeValue) {
      for (let i = 0; i < this.feedbackType.length; i++) {
        if (typeValue === this.feedbackType[i].value) {
          return this.feedbackType[i].label
        }
      }
    },
    sendFeedback () {
      const _this = this
      _this.$refs.form.validate(function (valid) {
        if (valid) {
          // _this.feedbackForm.uid = _this.User.uid
          api.postFeedback('/feedbacks', _this.feedbackForm).then(function (res) {
            if (res.success === 'true') {
              _this.$message({
                message: '反馈成功，我们会及时处理您的建议',
                type: 'success'
              })
              _this.getFeedbackList()
              _this.feedbackDialog = false
            } else {
              _this.$message.error('反馈上传失败，请稍候再试')
              _this.feedbackDialog = false
            }
          })
        } else {
          return false
        }
      })
    },
    getFeedbackList () {
      const _this = this
      apiGetlist.getUserFeedback('/feedbacks/mine', {}).then(function (res) {
        _this.showFeedbackListData = res.data.feedback_list
        _this.handleCurrentChange(1)
      })
    },
    handleCurrentChange (val) {
      this.page = val
      this.showFeedbackListPartData = null
      this.showFeedbackListPartData = this.showFeedbackListData.slice((this.page - 1) * this.pageSize, this.page * this.pageSize)
    }
  },
  created () {
    this.getFeedbackList()
  }
}
</script>

<style scoped>
  .CallFeedback{
    min-height: 700px;
    background-color: white;
    padding: 20px 20px;
  }
.feedback-head{
  padding-bottom: 5px;
  border-bottom: 1px solid gainsboro;
  line-height: 40px;
  font-size: 20px;
}
.edit{
  border: none;
  padding: 10px 40px;
  background: #f48118;
  border-radius: 10px;
  color: white;
  float: right;
  cursor: pointer;
}
.edit:hover{
  background-color: #d48118;
}
.feedback-content{
  margin: 20px 0;
}
.content {
  font-size: 15px;
  font-weight: 600;
}
</style>



