<template>
<div class="preview-item">
  <div class="preview-item_No">{{No}}</div>
  <div class="preview-item_main">
    <!--填空题-->
    <div v-if="type === 1">
      <p class="preview-item_type1_description" v-html="selfData.description"></p>
    </div>
    <!--选择题-->
    <div v-if="type === 2">
      <p class="preview-item_type2_description" v-html="selfData.description"></p>
      <ul class="preview-item_type2_option">
        <li
          v-for="(eng, engIdx) in orderEngList" :key="engIdx"
          :class="{
            'answerSpan': isShowAnswer && selfData.answer.indexOf(eng)>=0,
            'option_defaultItem': answer && !isShowAnswer,
            'option_chooseCorrect': answer && !isShowAnswer && answer.indexOf(eng)>= 0 && selfData.answer.indexOf(eng)>= 0,
            'option_chooseWrong': answer && !isShowAnswer && (answer.indexOf(eng)>= 0 && selfData.answer.indexOf(eng)< 0),
            'option_noChooseCorrect': answer && !isShowAnswer && (answer.indexOf(eng)< 0 && selfData.answer.indexOf(eng)>= 0)}">
          <b>{{eng}}:</b>&nbsp;&nbsp;<span v-html="selfData['option'+eng]"></span>
        </li>
      </ul>
    </div>
    <!--编程题-->
    <div v-if="type === 3">
      <div class="preview-item_type3_title">{{selfData.title}}</div>
      <div class="preview-item_type3_memoryAndTime">
        <i class="icon-project_space"></i> 空间限制：{{selfData.memory_limit}}
        &nbsp;&nbsp;&nbsp;
        <i class="icon-shijian"></i> 时间限制：{{selfData.time_limit}}
      </div>
      <div class="preview-item_type3_part">
        <h5>题目描述</h5>
        <div class="preview-item_type3_content" v-html="selfData.description"></div>
      </div>
      <div class="preview-item_type3_part">
        <h5>输入描述</h5>
        <div class="preview-item_type3_content" v-html="selfData.input"></div>
      </div>
      <div class="preview-item_type3_part">
        <h5>输出描述</h5>
        <div class="preview-item_type3_content" v-html="selfData.output"></div>
      </div>
      <div class="preview-item_type3_part">
        <h5>样本输入</h5>
        <div class="preview-item_type3_content" v-html="selfData.sample_input.replace(/\n/g, '<br>')"></div>
      </div>
      <div class="preview-item_type3_part">
        <h5>样本输出</h5>
        <div class="preview-item_type3_content" v-html="selfData.sample_output.replace(/\n/g, '<br>')"></div>
      </div>
      <div class="preview-item_answerDiv" v-if="answer && !isShowAnswer">
        <h5 class="preview-item_answerDiv_title">我的作答</h5>
        <textarea :value="answer" class="preview-item_answerDiv_content" readonly="readonly"></textarea>
      </div>
    </div>
    <!--问答题-->
    <div v-if="type === 4">
      <h3 class="preview-item_type4_title"><p v-html="selfData.description"></p></h3>
      <div class="preview-item_type4_answer" v-if="isShowAnswer">
        <span class="answerSpan">答：</span>
        <span v-html="!selfData.answer[0] ? '略' : selfData.answer[0]"></span>
      </div>
      <div class="preview-item_answerDiv" v-if="answer && !isShowAnswer">
        <h5 class="preview-item_answerDiv_title">我的作答</h5>
        <textarea :value="answer" class="preview-item_answerDiv_content" readonly="readonly"></textarea>
      </div>
    </div>
    <!--判断题-->
    <div v-if="type === 5">
      <div class="preview-item_type5">
        <p v-html="selfData.description"></p>
        <span>（&nbsp;&nbsp;
          <template v-if="answer">
            <template v-if="isShowAnswer">
              <span v-if="answer !== selfData.answer" :class="{'wrongAnswer': answer !== selfData.answer}">{{answer}}</span>
              <span class="answerSpan">&nbsp;{{selfData.answer}}</span></template>
            <template v-else><span>{{answer}}</span></template>
          </template>
          <template v-else>
            <template v-if="isShowAnswer"><span class="answerSpan">{{selfData.answer}}</span></template>
            <template v-else><span>&nbsp;&nbsp;</span></template>
          </template>
        &nbsp;&nbsp;）</span>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'preview-item',
  props: {
    No: {
      type: Number,
      default: 1,
      request: true
    },
    type: {
      type: Number,
      default: 0,
      request: true
    },
    detail: {
      type: Object,
      default: function () {
        return {}
      },
      request: true
    },
    answer: {},
    isShowAnswer: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      selfData: {},
      orderEngList: 'ABCD'
    }
  },
  methods: {
    // 实现格式化
    formatCode (str, isHtml = true) {
      return str
    },
    mapData () {
      const TYPE = this.type.toString()
      const _this = this
      var clearHTML = function (str) {
        return str
      }
      var mapWays = {
        '1': function () {
          var temp = clearHTML(_this.detail.description)
          var i = 0
          while (temp.indexOf('{}') >= 0) {
            if (_this.isShowAnswer) {
              temp = temp.replace('{}', `<span class="answerSpan">${_this.detail.answer[i++]}</span>`)
            } else {
              if (_this.answer) {
                temp = temp.replace('{}', `<span> ${_this.answer[i++] || `&nbsp;`} </span>`)
              } else {
                temp = temp.replace('{}', `<span> &nbsp; </span>`)
              }
            }
          }
          _this.selfData.description = _this.formatCode(temp)
        },
        '2': function () {
          _this.selfData.description = _this.formatCode(clearHTML(_this.detail.description))
          _this.selfData.optionA = _this.detail.optionA.replace(/<\/?.+?\/?>/g, '')
          _this.selfData.optionB = _this.detail.optionB.replace(/<\/?.+?\/?>/g, '')
          _this.selfData.optionC = _this.detail.optionC.replace(/<\/?.+?\/?>/g, '')
          _this.selfData.optionD = _this.detail.optionD.replace(/<\/?.+?\/?>/g, '')
        },
        '3': function () {
          _this.selfData.description = _this.formatCode(clearHTML(_this.detail.description))
          _this.selfData.input = _this.formatCode(clearHTML(_this.detail.input))
          _this.selfData.sample_input = _this.formatCode(clearHTML(_this.detail.sample_input))
          _this.selfData.sample_output = _this.formatCode(clearHTML(_this.detail.sample_output))
        },
        '4': function () {
          _this.selfData.description = _this.formatCode(clearHTML(_this.detail.description))
        }
      }
      if (mapWays[TYPE]) {
        mapWays[TYPE]()
      }
    }
  },
  created () {
    // 拷贝对象
    this.selfData = JSON.parse(JSON.stringify(this.detail))
    this.mapData()
  },
  watch: {
    isShowAnswer () {
      this.mapData()
    }
  }
}
</script>

<style scoped>
  .preview-item {
    display: flex;
    justify-content: space-around;
    margin: 20px 0;
  }
  .preview-item_No {
    font-size: 16px;
    font-family: SimHei;
    font-weight: 600;
    line-height: 60px;
    color: #333;
    flex: 0 0 auto;
    text-align: center;
    width: 50px;
  }
  >>> .answerSpan {
    font-weight: 600;
    color: #e03232;
  }
  .preview-item_main {
    width: 800px;
    line-height: 1.8em;
    min-height: 60px;
    font-size: 13px;
    flex: 1 0 auto;
    border-radius: 15px;
    background-color: rgba(242, 242, 242, 1);
    padding: 15px 30px;
  }
  /*填空题空项目样式*/
  .preview-item_type1_description >>> span {
    text-align: center;
    min-width: 80px;
    padding-left: 10px;
    padding-right: 10px;
    display: inline-block;
    border-bottom: 1px solid #333;
  }
  /*选择题样式*/
  .preview-item_type2_option span {
    line-height: 3em;
  }
  .preview-item_type2_option .option_defaultItem {
    padding-left: 10px;
    border-radius: 1.5em;
    border: 1px solid rgba(255, 255, 255, 0);
    background-color: #fff;
    margin: 6px 0;
  }
  .preview-item_type2_option .option_chooseCorrect {
    border: 1px solid rgb(142, 204, 69);
  }
  .preview-item_type2_option .option_chooseWrong {
    border: 1px solid rgb(255, 0, 0);
  }
  .preview-item_type2_option .option_noChooseCorrect {
    border: 1px solid rgb(255, 0, 0);
    border-right: 1px solid rgb(142, 204, 69);
    border-bottom: 1px solid rgb(142, 204, 69);
  }
  /*编程题样式*/
  .preview-item_type3_title {
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    margin: 10px;
  }
  .preview-item_type3_memoryAndTime {
    text-align: center;
  }
  .preview-item_type3_part {
    background-color: rgba(228, 228, 228, 1);
    border-radius: 5px;
    padding: 5px 10px;
    margin: 10px 0;
  }
  .preview-item_type3_part h5 {
    font-size: 13px;
    font-weight: 600;
  }
  .preview-item_type3_content {
    padding-left: 1em;
  }
  >>> .preview-item_type3_content img{
    max-width: 100% !important;
  }
  /*问答题样式*/
  .preview-item_type4_title {
    font-size: 13px;
    font-weight: 300;
  }
  .preview-item_type4_answer {
    margin-top: 5px;
  }
  /*判断题样式*/
  .preview-item_type5 {
    display: flex;
    justify-content: space-between;
  }
  .preview-item_type5:first-child {
    flex: 1 0 auto;
  }
  .preview-item_type5>first-child {
    min-width: 58px;
    white-space: nowrap;
  }
  .preview-item_type5 .wrongAnswer {
    text-decoration: line-through;
  }
  /*回答展示框*/
  .preview-item_answerDiv {
    border-radius: 5px;
    padding: 5px;
    margin-top: 10px;
    background-color: rgba(228, 228, 228, 1);;
  }
  .preview-item_answerDiv_title {
    font-size: 14px;
    text-align: center;
    line-height: 2;
  }
  .preview-item_answerDiv_content {
    width: 100%;
    border-radius: 5px;
    min-height: 100px;
    padding: 10px 10px;
  }
</style>



