<!--suppress ALL -->
<template>
  <transition :name="openAnimation ? 'fade' : ''">
    <div class="quiz-input" :style="getBaseStyle">
      <span class="quiz-input_label" v-if="label" @click="$refs.input.focus()">{{label}}&nbsp;:</span>
      <div class="quiz-input_content" :style="getContentStyle">
        <input ref="input"
               :type="getInputType"
               class="quiz-input_default"
               :value="inputValue"
               :placeholder="tip"
               :disabled="disabled"
               :style="getDefaultInputStyle"
               @input="handleInput"
               @blur="handleBlur"
               @focus="handleFocus"
               @keydown.enter="handleBlur"
               @keydown.up="handleKeyUp"
               @keydown.down="handleKeyDown">
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'quiz-input',
  props: {
    label: {
      type: String
    },
    type: {
      type: String,
      default: 'text'
    },
    floatLen: {
      type: Number,
      default: 0
    },
    range: { // type为number存在，为数值范围
      type: Array
    },
    tip: {
      type: String,
      default: ''
    },
    pre: {
      type: String
    },
    next: {
      type: String
    },
    value: {},
    fontSize: {
      type: Number,
      default: 16
    },
    lineHeight: {
      type: String
    },
    fontColor: {
      type: String,
      default: '#333'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    isCenter: {
      type: Boolean,
      default: true
    },
    hideUnderline: {
      type: Boolean,
      default: false
    },
    contentWidth: {
      type: String,
      default: '100%'
    },
    openAnimation: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    getInputType () {
      return this.type === 'number' ? 'text' : this.type
    },
    getBaseStyle () {
      return {
        fontSize: this.fontSize + 'px',
        lineHeight: (this.fontSize + 10) + 'px'
      }
    },
    getContentStyle () {
      return {
        width: this.contentWidth,
        fontSize: this.fontSize + 'px',
        height: this.lineHeight ? (this.lineHeight * this.fontSize) + 'px' : (this.fontSize + 10) + 'px',
        cursor: this.disabled ? 'no-drop' : 'normal',
        textAlign: this.isCenter ? 'center' : 'normal',
        borderBottom: this.hideUnderline ? 'none' : '1px solid #636363',
        marginLeft: this.hideUnderline ? '0px' : '15px'
      }
    },
    getDefaultInputStyle () {
      return {
        fontSize: this.fontSize + 'px',
        color: this.fontColor,
        lineHeight: this.lineHeight ? this.lineHeight : (this.fontSize + 10) + 'px',
        textAlign: this.isCenter ? 'center' : 'normal'
      }
    }
  },
  data () {
    return {
      oldPre: this.pre || '',
      oldNext: this.next || '',
      inputValue: this.value
    }
  },
  methods: {
    isSimple (str) {
      return !str || str.length === 0 || str === ''
    },
    // 实现input的双向绑定，v-model
    handleInput (e) {
      // console.log(e)
      var oldValue = this.inputValue
      this.inputValue = this.limitInput(this.type, e, e.data, e.target.value, oldValue)
      // 通知父元素修改props
      this.$emit('input', this.limitOutput(this.inputValue))
    },
    // 处理数值增减
    handleKeyUp (e) {
      var oldValue = this.inputValue
      if (this.type !== 'number' || oldValue === '') return
      this.inputValue = this.limitInput(this.type, e, 'UP', e.target.value, oldValue)
      // 通知父元素修改props
      this.$emit('input', this.limitOutput(this.inputValue))
    },
    handleKeyDown (e) {
      var oldValue = this.inputValue
      if (this.type !== 'number' || oldValue === '') return
      this.inputValue = this.limitInput(this.type, e, 'DOWN', e.target.value, oldValue)
      // 通知父元素修改props
      this.$emit('input', this.limitOutput(this.inputValue))
    },
    // 数据添加前后缀
    addDecorate (str, pre, next) {
      if (str.length === 0) {
        return ''
      }
      if (!this.isSimple(pre)) {
        // var preLength = pre.length
        str = str.indexOf(pre) !== 0 ? pre + str : str
      }
      if (!this.isSimple(next)) {
        var nextLength = next.length
        str = str.indexOf(next) < 0 || (str.indexOf(next) >= 0 && str.indexOf(next) + nextLength === str.length) ? str + next : ''
      }
      return str
    },
    clearDecorate (str, pre, next) {
      if (!this.isSimple(pre)) {
        var preLength = pre.length
        str = str.indexOf(pre) === 0 ? str.slice(preLength) : str
      }
      if (!this.isSimple(next)) {
        var nextLength = next.length
        str = str.length >= nextLength && str.indexOf(next) === str.length - nextLength ? str.slice(0, -nextLength) : str
      }
      return str
    },
    handleBlur (e) {
      this.inputValue = this.addDecorate(this.limitOutput(e.target.value), this.pre, this.next)
    },
    handleFocus (e) {
      this.inputValue = this.clearDecorate(e.target.value, this.pre, this.next)
    },
    limitOutput (valueStr) {
      if (this.type !== 'number') return valueStr
      var val = (+valueStr)
      if (!this.range) return val.toString()
      if (this.range.length > 0) {
        // 限制范围
        if (val < this.range[0]) return this.range[0].toString()
      }
      if (this.range.length > 1) {
        if (val > this.range[1]) return this.range[1].toString()
      }
      return val.toString()
    },
    limitInput (type, e, input, newValue, oldValue) {
      const _this = this
      var limitInputFuns = {
        'number': function () {
          if (input == null) return newValue.replace(/^\.+/, '')
          // 上键++
          if (input === 'UP') return parseFloat(oldValue) + 1
          // 下键--
          if (input === 'DOWN') return parseFloat(oldValue) - 1
          // 限制输入内容为数字和小数点
          var reg = new RegExp(`[0-9${_this.floatLen > 0 ? '.' : ''}]`)
          if (!reg.test(input)) return oldValue
          // 限制小数点位置
          if (input === '.') {
            if (oldValue === '' || oldValue.indexOf('.') > 0) return oldValue
          } else {
            if (oldValue.indexOf('.') > 0 && oldValue.indexOf('.') === oldValue.length - _this.floatLen - 1) return oldValue
          }
          return newValue
        }
      }
      if (limitInputFuns[type]) {
        e.target.value = limitInputFuns[type]()
      }
      return e.target.value
    }
  },
  created () {
    this.oldPre = this.pre || ''
    this.oldNext = this.next || ''
    var temp = this.inputValue == null ? '' : this.inputValue
    this.inputValue = this.addDecorate(temp.toString(), this.pre, this.next)
  },
  watch: {
    pre () {
      this.inputValue = this.clearDecorate(this.inputValue.toString(), this.oldPre, this.next)
      this.inputValue = this.addDecorate(this.inputValue.toString(), this.pre, this.next)
      this.oldPre = this.pre
    },
    next () {
      this.inputValue = this.clearDecorate(this.inputValue.toString(), this.pre, this.oldNext)
      this.inputValue = this.addDecorate(this.inputValue.toString(), this.pre, this.next)
      this.oldNext = this.next
    }
  }
}
</script>

<style scoped>
  .fade-enter-active, .fade-leave-active {
    transition: all .5s;
  }

  .fade-enter, .fade-leave-to {
    transform: translateY(-100%);
    opacity: 0;
  }

  .quiz-input {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .quiz-input_label {
    color: #636363;
    flex: 1 0 auto;
  }

  .quiz-input:hover .quiz-input_label {
    color: #272822;
  }

  .quiz-input_content {
    /*height: 20px;*/
  }

  .quiz-input_default {
    width: 100%;
    background-color: transparent;
  }

  input[disabled], /*兼容IE8*/
  input:disabled {
    border: none;
    color: #ACA899;
    opacity: 1;
    cursor: no-drop;
  }
</style>



