<!--考试答题页-->
<template>
    <div class="quizStart" ref="mainContainer">
      <div class="quizStart_header" :style="getHeaderStyle">
        <h3 class="quizStart_header_title">{{quizInfo.title}}</h3>
        <div class="quizStart_header_rightFlame">
          <span class="quizStart_rightFlame_status"><i v-show="isTiping" class="quizStart_icon-tip icon-tixing"></i>{{getTimeStatus}}</span>
          <quiz-timer
            v-if="isShowTimer"
            class="quizStart_rightFlame_time"
            :initTime="quizInfo.start_time"
            :duration="quizInfo.duration"
            :advance="quizInfo.advance"
            :isBeginTimer="isBeginTimer"
            @notBeginTimerHandler="quizInfo.status = 0"
            @enterTimerHandler="quizInfo.status = 1"
            @endTimerHander="endTimerHandler"
            @tipsTimerHander="tipsTimerHandler"></quiz-timer>
          <span class="quizStart_rightFlame_btn" v-if="step === 2 && isTiping" @click="advanceSubmit()">提前交卷</span>
        </div>
      </div>
      <div class="quizStart_container" :style="getContainerStyle">
        <!--处于考前须知页面-->
        <template v-if="step === 1">
          <div class="quizStart_step1">
            <quiz-panel :padding="getMainPanelPadding">
              <div class="quizStart_step1_container" :style="getSameHeight">
                <h4>考前须知</h4>
                <div class="quizStart_step1_content">
                  <p v-for="(item, index) in quizStartTips" :key="index">{{index+1}}.&nbsp;{{item}}</p>
                </div>
                <div :class="{'quizStart_step1_btns': true, 'quizStart_step1_btns_canClick': isCanClick}">
                  <span v-if="quizInfo.status === 0">等待开考</span>
                  <span v-if="quizInfo.status === 1" @click="changeCurStep(2)">进入考试</span>
                  <span v-if="quizInfo.status === 2">正在批阅</span>
                  <span v-if="quizInfo.status === 3" @click="toQuizList()">查看成绩</span>
                </div>
              </div>
            </quiz-panel>
          </div>
        </template>
        <!--处于考试答题页面-->
        <template v-else-if="step === 2">
          <div class="quizStart_step2">
            <div class="quizStart_step2_menu">
              <quiz-panel title="答题进度" :padding="getMainPanelPadding">
                <div class="quizStart_step2_menu_container" :style="getSameHeight">
                  <div v-for="(item, index) in quizInfo.detail" :key="item.type" class="quizStart_step2_menuItem">
                    <h5 class="label">
                      {{orderList[index]}}、{{problemType[item.type]}}&nbsp;(共&nbsp;{{item.problem_info.length}}&nbsp;题)
                    </h5>
                    <div class="problemBtns">
                      <span
                        :class="{
                          'isAnswered': ifAnswer(problem.student_answer),
                          'isAnswer_beMarked': ifAnswer(problem.student_answer) && problem.isMark,
                          'noAnswer_beMarked': !ifAnswer(problem.student_answer) && problem.isMark,
                         }"
                        v-for="(problem, problemNo) in item.problem_info"
                        :key="problem.problem_id"
                        @click="toDetail(item.type, problemNo+1)">{{problemNo+1}}</span>
                    </div>
                  </div>
                </div>
              </quiz-panel>
            </div>
            <div class="quizStart_step2_detail">
              <quiz-panel title="题目详情" :padding="getMainPanelPadding">
                <div class="quizStart_step2_detail_container" :style="getSameHeight">
                  <div v-for="(problem, problemNo) in quizInfo.detail[curType-1].problem_info" :key="problem.problem_id">
                    <template v-if="curProblemNo === problemNo+1">
                      <h5 class="label">
                        {{problemType[curType]}}&nbsp;
                        {{curProblemNo}}&nbsp;
                        (&nbsp;{{quizInfo.detail[curType-1].value}}&nbsp;分)
                      </h5>
                      <div class="problemDetail">
                          <!--填空题-->
                          <div v-if="curType === 1" class="problemDetail_type1">
                            <div class="problemDetail_title" onselectstart="return false">
                              <span v-for="(word, idx) in problem.description.split('{}')" :key="idx">
                                <span>{{word}}</span>
                                <span v-if="idx !== problem.description.split('{}').length-1">
                                  <quiz-input
                                    v-model="quizInfo.detail[curType-1].problem_info[curProblemNo-1].student_answer[idx]"
                                    lineHeight="2"
                                    :hideUnderline="true"
                                    :openAnimation="false"></quiz-input>
                                </span>
                              </span>
                            </div>
                          </div>
                          <!--选择题-->
                          <div v-else-if="curType === 2" class="problemDetail_type2">
                            <div class="problemDetail_title" onselectstart="return false">
                              <span>(&nbsp;{{problem.isMulti ? '多选题': '单选题'}}&nbsp;)</span>
                              <span v-for="(word, idx) in problem.description.split('{}')" :key="idx">
                                <span v-html="word"></span>
                                <span v-if="idx !== problem.description.split('{}').length-1">________</span>
                              </span>
                            </div>
                            <ul class="chooseList">
                              <li
                                :class="problem.student_answer.indexOf('A')>=0 ? 'beChoosed':''"
                                @click="doAnswer('A')">
                                <b>A:</b>&nbsp;&nbsp;{{problem.optionA}}</li>
                              <li
                                :class="problem.student_answer.indexOf('B')>=0 ? 'beChoosed':''"
                                @click="doAnswer('B')">
                                <b>B:</b>&nbsp;&nbsp;{{problem.optionB}}</li>
                              <li
                                :class="problem.student_answer.indexOf('C')>=0 ? 'beChoosed':''"
                                @click="doAnswer('C')">
                                <b>C:</b>&nbsp;&nbsp;{{problem.optionC}}</li>
                              <li
                                :class="problem.student_answer.indexOf('D')>=0 ? 'beChoosed':''"
                                @click="doAnswer('D')">
                                <b>D:</b>&nbsp;&nbsp;{{problem.optionD}}</li>
                            </ul>
                          </div>
                          <!--编程题-->
                          <div v-else-if="curType === 3"  class="problemDetail_type3">
                            <!--<span>{{problem.problem_id}}</span>-->
                            <h5 class="type3_title">{{problem.title}}</h5>
                            <div class="type3_limit" onselectstart="return false;">
                              <span><i class="icon-project_space"></i>&nbsp;空间限制：{{problem.memory_limit}}</span>
                              <span><i class="icon-shijian"></i>&nbsp;时间限制：{{problem.time_limit}}</span>
                              <span class="type3_btn_submit" v-if="isOpenAnswer" @click="sumbitCode(problem.problem_id)">提交</span>
                              <span :class="{'type3_btn': true, 'type3_open_btn': isOpenAnswer}" @click="isOpenAnswer =! isOpenAnswer">
                                <template v-if="isOpenAnswer">返回</template>
                                <template v-if="!isOpenAnswer">答题</template>
                              </span>
                            </div>
                            <div class="type3_content">
                              <span>题目描述</span>
                              <p v-html="problem.description"></p>
                            </div>
                            <div class="type3_content">
                              <span>输入描述</span>
                              <p v-html="problem.input"></p>
                            </div>
                            <div class="type3_content">
                              <span>输出描述</span>
                              <p v-html="problem.output"></p>
                            </div>
                            <div class="type3_content">
                              <span>样本输入</span>
                              <p v-html="problem.sample_input.replace(/\n/g, '<br>')"></p>
                            </div>
                            <div class="type3_content">
                              <span>样本输出</span>
                              <p v-html="problem.sample_output.replace(/\n/g, '<br>')"></p>
                            </div>
                            <div :class="{'type3_Answer': true, 'type3_Answer_open': isOpenAnswer}">
                              <div class="type3_Answer_header">
                                <span>答题代码</span>
                              </div>
                              <textarea v-model="quizInfo.detail[curType-1].problem_info[curProblemNo-1].student_answer"></textarea>
                              <span v-if="isSubmitting" class="type3_Answer_result" :style="{'color': getResultStyle(0)}">正在判题中...</span>
                              <span
                                v-if="!isSubmitting && quizInfo.detail[curType-1].problem_info[curProblemNo-1].result !== -1"
                                class="type3_Answer_result"
                                :style="{'color': getResultStyle(quizInfo.detail[curType-1].problem_info[curProblemNo-1].result)}">
                                {{status[quizInfo.detail[curType-1].problem_info[curProblemNo-1].result]}}</span>
                            </div>
                          </div>
                          <!--问答题-->
                          <div v-else-if="curType === 4" class="problemDetail_type4">
                            <div class="problemDetail_title" onselectstart="return false">
                              <span v-html="problem.description"></span>
                            </div>
                            <div class="problemDetail_input">
                              <span
                                v-show="!ifAnswer (quizInfo.detail[curType-1].problem_info[curProblemNo-1].student_answer)"
                                class="doubleClickToInput"
                                onselectstart="return false;"
                                @dblclick="quizInfo.detail[curType-1].problem_info[curProblemNo-1].student_answer = '答：'">双击此处输入您的答案</span>
                              <textarea v-model="quizInfo.detail[curType-1].problem_info[curProblemNo-1].student_answer"></textarea>
                            </div>
                          </div>
                          <!--判断题-->
                          <div v-else class="problemDetail_type5">
                            <div class="problemDetail_title" onselectstart="return false">
                              <span v-html="problem.description"></span>
                            </div>
                            <ul class="chooseList">
                              <li
                                :class="problem.student_answer === 'T' ? 'beChoosed':''"
                                @click="doAnswer('T')">
                                <b>正确</b></li>
                              <li
                                :class="problem.student_answer === 'F'? 'beChoosed':''"
                                @click="doAnswer('F')">
                                <b>错误</b></li>
                            </ul>
                          </div>
                      </div>
                    </template>
                  </div>
                </div>
              </quiz-panel>
            </div>
          </div>
        </template>
        <!--处于考试结束后提醒页面-->
        <template v-else>
          <div class="quizStart_step3">
            <quiz-panel :padding="getMainPanelPadding">
              <div class="quizStart_step3_container" :style="getSameHeight">
                <h4>考后提醒</h4>
                <div class="quizStart_step3_content">
                  <p v-for="(item, index) in quizEndTips" :key="index">{{index+1}}.&nbsp;{{item}}</p>
                </div>
                <div class="quizStart_step3_btns">
                  <span>提交成功</span>
                </div>
              </div>
            </quiz-panel>
          </div>
        </template>
        <div style="text-align: right">
          <div style="position: absolute; display: inline-block">
            <div class="quizStart_rightBtns">
              <quiz-btn
                v-if="step !== 2"
                icon="icon-shuaxin2"
                size="20px"
                radius="default"
                width="45px"
                height="45px"
                @click.native="getQuizData()"></quiz-btn>
              <template v-if="step === 2">
                <quiz-btn
                  icon="content-考前<br>须知"
                  size="12px"
                  radius="default"
                  theme="FORBID"
                  width="45px"
                  height="45px"
                  @click.native="changeCurStep(1)"></quiz-btn>
                <quiz-btn
                  icon="icon-shangyiti"
                  size="20px"
                  radius="default"
                  width="45px"
                  height="45px"
                  @click.native="preProblem()"></quiz-btn>
                <quiz-btn
                  icon="icon-xiayiti"
                  size="20px"
                  radius="default"
                  width="45px"
                  height="45px"
                  @click.native="nextProblem()"></quiz-btn>
                <quiz-btn
                  icon="icon-biaoji1"
                  size="20px"
                  radius="default"
                  :theme="isCurMark ? 'CHOOSE' : 'FORBID'"
                  width="45px"
                  height="45px"
                  @click.native="markProblem()"></quiz-btn>
                <quiz-btn
                  icon="icon-baocun"
                  size="20px"
                  radius="default"
                  theme="SURE"
                  width="45px"
                  height="45px"
                  @click.native="debounce(saveAnswerData, 500)"></quiz-btn>
              </template>
              <quiz-btn
                v-if="step === 3"
                icon="icon-fanhui2"
                size="20px"
                radius="default"
                theme="BACK"
                width="45px"
                height="45px"></quiz-btn>
            </div>
          </div>
        </div>
        <span class="quizStart_saveTip" v-if="step === 2 && isSaving">保存答案中</span>
      </div>
      <div class="quizStart_examEnd"
           v-if="step > 1 && isEnd"
           v-loading="isEnd"
           :element-loading-text="'正在保存考试数据并在 '+endTimer+' 秒后返回个人中心页'"
           element-loading-spinner="el-icon-loading"
           element-loading-background="rgba(0, 0, 0, 0.8)"></div>
    </div>
</template>

<script>
import quizPanel from '@/base/quiz/quiz-panel'
import quizBtn from '@/base/quiz/quiz-btn'
import quizInput from '@/base/quiz/quiz-input'
import quizTimer from '@/base/quiz/quiz-timer'
import api from '@/api/quiz'
import {mapMutations} from 'vuex'
export default {
  name: 'quizStart',
  components: {
    quizPanel,
    quizBtn,
    quizInput,
    quizTimer
  },
  data () {
    return {
      isFull: false,
      isEnd: false,
      isSubmitting: false,
      isTiping: false,
      isSaving: false,
      isShowTimer: false,
      isBeginTimer: false,
      isOpenAnswer: false,
      isMarking: false, // 标记是否处于标记时刻, 主要用于不让用户频繁更改状态
      step: 1, // 1-考试须知，答题前第一个页面，2-考试答题中页面， 3-考试结束后页面
      quizId: -1,
      quizInfo: {},
      quizStartTips: null,
      quizEndTips: null,
      orderList: '一二三四五',
      problemType: {
        '1': '填空题',
        '2': '选择题',
        '3': '编程题',
        '4': '问答题',
        '5': '判断题'
      },
      curType: 1, // 所在详细题目类型
      curProblemNo: 1, // 所在详细题目序号
      endTimer: 5, // 用于返回
      timeout: null, // 用于防抖
      saveProblemList: [],
      status: [
        'Waiting...',
        'Waiting...',
        'Compiling...',
        'Running',
        'Accept',
        'Presenting Error',
        'Wrong Answer',
        'Time Limit Exceed',
        'Memory Limit Exceed',
        'Output Limit Exceed',
        'Runtime Error',
        'Compile Error'
      ],
      screenType: '' // 记录何种方法监听全屏事件
    }
  },
  computed: {
    getHeaderStyle () {
      return {
        top: this.isFull ? '0px' : '60px'
      }
    },
    getContainerStyle () {
      return {
        paddingTop: this.isFull ? '0px' : '50px'
      }
    },
    getSameHeight () {
      return {
        minHeight: this.isFull ? 'calc(100vh - 170px)' : 'calc(100vh - 220px)'
      }
    },
    isCurMark () {
      return this.quizInfo.detail[this.curType - 1].problem_info[this.curProblemNo - 1].isMark
    },
    isCanClick () {
      return this.quizInfo.status === 1 || this.quizInfo.status === 3
    },
    getTimeStatus () {
      var statusList = {
        0: '离考试开始还剩：',
        1: '离考试结束还剩：',
        2: '考试已结束，等待教师批阅。',
        3: '考试已结束，成绩已公布。'
      }
      return this.quizInfo.start_time !== '-1'
        ? statusList[this.quizInfo.status]
        : '等待教师手动开启'
    },
    getMainPanelPadding () {
      return '10px 10px'
    }
  },
  methods: {
    ...mapMutations([
      'TOGGLE_HEADER'
    ]),
    changeCurStep (newStep) {
      if (newStep === 2) {
        this.isOpenStudentList = false
        this.operScreen(/* isFull= */true)
      } else {
        this.operScreen(/* isFull= */false)
      }
      this.step = newStep
    },
    operScreen (isToFull) {
      if (this.isFull === isToFull) return
      if (isToFull) {
        // console.log('开启全屏')
        var docElm = document.documentElement
        // W3C
        if (docElm.requestFullscreen) docElm.requestFullscreen()
        // FireFox
        else if (docElm.mozRequestFullScreen) docElm.mozRequestFullScreen()
        // Chrome等
        else if (docElm.webkitRequestFullScreen) docElm.webkitRequestFullScreen()
        // IE11
        else if (docElm.msRequestFullscreen) docElm.msRequestFullscreen()
      } else {
        // console.log('取消全屏')
        if (document.exitFullscreen) document.exitFullscreen()
        else if (document.mozCancelFullScreen) document.mozCancelFullScreen()
        else if (document.webkitCancelFullScreen) document.webkitCancelFullScreen()
        else if (document.msExitFullscreen) document.msExitFullscreen()
      }
    },
    getResultStyle (result) {
      return (result === 0 || result === 1)
        ? '#8ecc45' : result === 2
          ? '#888' : result === 3
            ? '#888' : result === 4
              ? '#8ecc45' : result === 5
                ? '#f577d0' : result === 6
                  ? '#d43039' : result === 7
                    ? '#6495ed' : result === 8
                      ? '#6495ed' : result === 9
                        ? '#284cad' : result === 10
                          ? '#ff6c3f' : '#843f10'
    },
    toQuizList () {
      this.$router.push({name: 'PersonalCenter', params: {isShow: 4}})
      this.operScreen(/* isFull= */false)
    },
    advanceSubmit () {
      this.$confirm('提前交卷后不可再进入考试，是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '我觉得需要再检查一会',
        type: 'warning'
      }).then(() => {
        this.endTimerHandler(true)
      })
    },
    endTimerHandler () {
      this.saveAnswerData(/* isEnd = */true)
    },
    tipsTimerHandler (val) {
      // 提醒时间到执行的方法
      this.isTiping = true
    },
    preProblem () {
      if (this.curProblemNo <= 1) {
        for (let temp = this.curType - 1; temp - 1 >= 0; temp--) {
          if (this.quizInfo.detail[temp - 1].problem_info.length !== 0) {
            this.curType = temp
            this.curProblemNo = this.quizInfo.detail[this.curType - 1].problem_info.length
            return
          }
        }
      } else {
        this.curProblemNo = this.curProblemNo - 1
      }
    },
    nextProblem () {
      var obj = this.quizInfo.detail[this.curType - 1].problem_info
      if (this.curProblemNo >= obj.length) {
        for (let temp = this.curType + 1; temp - 1 < this.quizInfo.detail.length; temp++) {
          if (this.quizInfo.detail[temp - 1].problem_info.length !== 0) {
            this.curType = temp
            this.curProblemNo = 1
            return
          }
        }
      } else {
        this.curProblemNo = this.curProblemNo + 1
      }
    },
    markProblem () {
      if (this.isMarking) return
      const _this = this
      var obj = this.quizInfo.detail[this.curType - 1].problem_info[this.curProblemNo - 1]
      this.isMarking = true
      var time = null
      if (obj.isMark) {
        api.cancelMarkProblem('/quiz/ismark', {
          quiz_id: this.quizId,
          problem_id: obj.problem_id,
          item_id: this.curProblemNo
        }).then(res => {
          if (res.success === 'true') {
            // 调用接口保存标记信息
            obj.isMark = false
            time = setTimeout(function () {
              _this.isMarking = false
              clearTimeout(time)
            }, 100)
          }
        })
      } else {
        api.markProblem('/quiz/ismark', {
          quiz_id: this.quizId,
          problem_id: obj.problem_id,
          item_id: this.curProblemNo
        }).then(res => {
          if (res.success === 'true') {
            // 调用接口保存标记信息
            obj.isMark = true
            time = setTimeout(function () {
              _this.isMarking = false
              clearTimeout(time)
            }, 100)
          }
        })
      }
    },
    doAnswer (answer) {
      const _this = this
      var obj = _this.quizInfo.detail[this.curType - 1].problem_info[this.curProblemNo - 1]
      var idx = obj.student_answer.indexOf(answer)
      var answerWay = {
        '2': function () {
          if (idx >= 0) {
            obj.student_answer.splice(idx, 1)
          } else {
            if (obj.isMulti) {
              obj.student_answer.push(answer)
            } else {
              obj.student_answer.pop()
              obj.student_answer.push(answer)
            }
          }
        },
        '5': function () {
          obj.student_answer = obj.student_answer === answer ? '' : answer
        }
      }
      answerWay[this.curType]()
    },
    toDetail (type, no) {
      this.curType = type
      this.curProblemNo = no
    },
    ifAnswer (answer) {
      return typeof (answer) === 'object'
        ? (answer instanceof Array)
          ? answer.length !== 0 && answer.some(i => i.replace(/\s+/g, '')) : true
        : answer.replace(/^ +&/g, '')
    },
    getQuizTips () {
      this.quizStartTips = [
        `考试开始后，计时不停止，考试过程中异常退出答题界面，可在时间结束前重新进入继续作答。`,
        `考试过程中如出现断电、断网等突发情况导致窗口关闭，网络中断前的答题内容会自动保存，但不排除部分丢失的情况。`,
        `考试期间请勿交流考试相关内容，一经发现，取消该次考试成绩。`,
        `考试期间请勿浏览其他页面网页，所有非考试操作将视为作弊行为，一经发现，取消该次考试成绩。`,
        `结束后请各位考生务必等待系统提示“考试数据成功上传”后才关闭浏览器，否则将视为异常离考状态，考试数据将不做效。`,
        `结束后，考生需要等待教师评阅，在成绩公布后如需要查看成绩等信息可常到个人中心下的考试列表中点击“考试详情”查看。`,
        `再次提醒，考试结束后系统会自动保存并提交，但小几率会出现网络不良的情况，因此建议各位考生一定要在等待系统提示“考试数据成功上传”后方才离开考场。如若长时间没有看到该提示，可以手动点击保存答题数据按钮。`,
        `${this.quizInfo.advance === 0 ? `本次考试不允许提前交卷。` : `本次考试允许在考试结束前${this.quizInfo.advance}分钟内提前交卷。`}`
      ]
      this.quizEndTips = [
        `考试结束后，系统自动保存并提交。`,
        `考试结束并等待结果公布后，考生可在考试列表页中找到并进入该考试的数据详情页，其中包含本次考试的成绩排行榜以及相关的考试信息。`,
        `考试结束并等待结果公布后，考生可查看自己的答题结果，结果页中包含本次考试的正确答案（不包含编程题），同时考生可以在结果页中查看自己的错题并添加进自己的错题集中。不仅如此，更可以在结果页下与其他同学一同讨论解疑。`
      ]
    },
    getQuizId () {
      this.quizId = this.$route.params.id
    },
    getStudentAnswer () {
      api.getStudentAnswer('/quiz/detail', this.quizId).then(res => {
        var answerList = res.data.detail
        answerList.forEach(i => {
          var elem = this.quizInfo.detail[i.type - 1].problem_info.find(elem => elem.problem_id === i.problem_info.problem_id)
          if (elem) {
            elem.student_answer = i.problem_info.student_answer
            elem.isMark = i.problem_info.isMark === 1
          }
        })
      })
    },
    getQuizData () {
      this.isShowTimer = false
      this.isBeginTimer = false
      api.getQuizInfo('/quiz/preview', this.quizId, false).then(res => {
        // 调整时间
        res.data.start_time = res.data.start_time.replace('T', ' ')
        res.data.detail = res.data.detail.map(i => {
          i.problem_info = i.problem_info.map(j => {
            if (i.type === 1) j.description = j.description.replace(/<input.+?\/>/g, '{}')
            if (i.type === 2) {
              j.description = j.description.replace(/<input.+?\/>/g, '{}')
              j.optionA = j.optionA.replace(/<\/?.+?\/?>/g, '')
              j.optionB = j.optionB.replace(/<\/?.+?\/?>/g, '')
              j.optionC = j.optionC.replace(/<\/?.+?\/?>/g, '')
              j.optionD = j.optionD.replace(/<\/?.+?\/?>/g, '')
            }
            if (i.type === 3) {
              j.result = -1
            }
            j.student_answer = []
            j.isMark = false
            return j
          })
          return i
        })
        this.quizInfo = res.data
        // 计时器开始
        this.isShowTimer = true
        this.isBeginTimer = true // 计时器开始
        // 相关数据初始化
        this.getQuizInitProblemType()
        this.getQuizTips()
        // 获取学生答题数据
        this.getStudentAnswer()
      })
    },
    getQuizInitProblemType () {
      for (let i = 0; i <= 4; i++) {
        if (this.quizInfo.detail[i].problem_info.length !== 0) {
          this.curType = this.quizInfo.detail[i].type
          return
        }
      }
    },
    // 保存答案前的防抖和题目记录
    debounce (fn, wait) {
      this.isSaving = true
      // 先将题目数据存起来
      var problem = this.quizInfo.detail[this.curType - 1].problem_info[this.curProblemNo - 1]
      this.saveProblemList = this.timeout === null ? [] : this.saveProblemList.filter(i => i.problem_id !== problem.problem_id)
      var answerList = []
      if (this.curType === 1) {
        for (let i = 0; i < problem.student_answer.length; i++) {
          let answer = problem.student_answer[i]
          answerList.push(answer == null ? '' : i.toString())
        }
        if (problem.student_answer.length === 0) answerList.push('')
      }
      this.saveProblemList.push({
        problem_id: problem.problem_id,
        item_id: this.curProblemNo,
        type: this.curType, // 题目类型
        answer: this.curType === 1 ? answerList : problem.student_answer
      })
      if (this.timeout !== null) clearTimeout(this.timeout)
      this.timeout = setTimeout(fn, wait)
    },
    saveAnswerData (isEnd = false) {
      api.saveStudentAnswer('/quiz/detail', {
        quiz_id: this.quizId,
        answer_list: this.saveProblemList // 有可能更新一道题，有可能更新多道题
      }, isEnd ? 1 : 0).then(res => {
        if (res.success === 'false') {
          this.$message({
            message: '答案数据自动保存失败，请稍后手动保存！',
            type: 'warning',
            showClose: true,
            duration: 1500
          })
          return
        }
        if (isEnd) {
          this.isEnd = isEnd
          this.isTiping = false
          this.isShowTimer = false
          this.quizInfo.status = 2
          this.changeCurStep(3)
          var timer = setInterval(() => {
            if (this.endTimer === 0) {
              this.toQuizList()
              clearInterval(timer)
            }
            this.endTimer--
          }, 1000)
        }
      }).finally(() => {
        this.isSaving = false
        this.timeout = null
      })
    },
    sumbitCode (id) {
      var problem = this.quizInfo.detail[this.curType - 1].problem_info[this.curProblemNo - 1]
      // 如果代码为空或者在编译中或者在30秒内
      if (problem.student_answer === '' || problem.student_answer == null || problem.student_answer.length === 0) {
        this.$message.warning('答案不能为空！')
        return
      }
      if (this.isSubmitting) {
        this.$message.warning('仍在判题中，勿重复操作！')
        return
      }
      var obj = {
        problem_id: id,
        protype: 3,
        source: problem.student_answer,
        language: this.quizInfo.language
      }
      this.isSubmitting = true
      api.postAnswer('/problems/submit', obj).then(() => {
        api.getQuestionResult('/problems/submit', id).then((res) => {
          problem.result = res.data.result
          this.isSubmitting = false
        })
      })
    },
    srceenChangeHandler () {
      this.isFull = document.fullscreen
      this.TOGGLE_HEADER(!document.fullscreen)
    }
  },
  mounted () {
    // 监听全屏事件
    var docElm = document.documentElement
    if (docElm.requestFullscreen) this.screenType = 'fullscreenchange'
    else if (docElm.mozRequestFullScreen) this.screenType = 'mozfullscreenchange'
    else if (docElm.webkitRequestFullScreen) this.screenType = 'webkitfullscreenchange'
    else if (docElm.msRequestFullscreen) this.screenType = 'msfullscreenchange'
    if (this.screenType) document.addEventListener(this.screenType, this.srceenChangeHandler)
  },
  created () {
    this.getQuizId()
    this.getQuizData()
  },
  watch: {
    // 监听到对象数据变化并调用函数保存用户答案
    quizInfo: {
      deep: true,
      handler: function () {
        if (this.isMarking || this.step !== 2) return
        // 保存用户答案数据
        this.debounce(this.saveAnswerData, 1000)
      }
    }
  },
  destroyed () {
    // 清除全屏事件
    if (this.screenType) document.removeEventListener(this.screenType, this.srceenChangeHandler)
  }
}
</script>

