<template>
    <div class="submitData">
      <div class="Title">
        <span>{{HisSubObj.problem_name}}</span>
      </div>
      <div class="show-mid">
        <div class="showData">
          <span class="HisTitle">{{(dataType === 1) ? '历史提交记录' : '历史提交记录排行榜'}}</span>
          <!-- <span class="selfHis" @click="showSelfHis">查看我的提交历史</span> 如何需要 注意修改count！ -->
          <span class="subCount">提交总次数：{{count}}</span>
          <div class="showHisSubList" v-loading="loading">
            <div v-for="(hisSub,index) in showList" :key="index" class="showHisSub">
              <span class="status" v-if="hisSub.result=== 1">W</span>
              <span class="status" v-if="hisSub.result=== 2">C</span>
              <span class="status" v-if="hisSub.result=== 3">R</span>
              <span class="status" v-if="hisSub.result=== 4"  style="color: #67C23A;transition: all 0.4s">AC</span>
              <span class="status" v-if="hisSub.result=== 5"  style="color:#F56C6C;transition: all 0.4s">PE</span>
              <span class="status" v-if="hisSub.result=== 6"  style="color: #F56C6C;transition: all 0.4s">WA</span>
              <span class="status" v-if="hisSub.result=== 7"  style="color: #E6A23C;transition: all 0.4s">TLE</span>
              <span class="status" v-if="hisSub.result=== 8"  style="color: #E6A23C;transition: all 0.4s">MLE</span>
              <span class="status" v-if="hisSub.result=== 9"  style="color: #E6A23C;transition: all 0.4s">OLE</span>
              <span class="status" v-if="hisSub.result=== 10" style="color:#F56C6C;transition: all 0.4s">RE</span>
              <span class="status" v-if="hisSub.result=== 11" style="color:#F56C6C;transition: all 0.4s">CE</span>
             <span class="stuName">{{hisSub.student_name}}</span>
             <span class="subTime">提交时间：{{(hisSub.submit_time).replace('T', ' ')}}</span>
             <span class="runTime">{{hisSub.run_time}}</span>
             <span class="Memory">{{hisSub.memory}}</span>
            </div>
          </div>
        </div>

        <div class="choose-and-status">
          <div class="choose">
            <span class="chooseTitle">筛选条件</span>
            <div class="select-area">
              <i class="icon-sanjiaoxing-down"
               style="position: absolute;
                       bottom: 9px;
                       left: 15px;
                       width: 16px;
                       height: 16px;
                       z-index: 1;
                       color: #f48118;"></i>
              <el-select v-model="statusType"
                       @change="checkTypeChange"
                       placeholder="所有状态">
               <el-option
                 v-for="type in statusTypes"
                 :key="type.value"
                 :label="type.label"
                 :value="type.value">
               </el-option>
              </el-select>
            </div>
            <div class="select-area">
              <i class="icon-sanjiaoxing-down"
               style="position: absolute;
                       bottom: 9px;
                       left: 15px;
                       width: 16px;
                       height: 16px;
                       z-index: 1;
                       color: #f48118;">
              </i>
               <el-select v-model="languageType"
                       @change="checkTypeChange"
                       placeholder="所有语言">
                  <el-option
                    v-for="item in languageTypes"
                    :key="item.value"
                    :label="item.label"
                     :value="item.value">
                  </el-option>
               </el-select>
            </div><!--<span class="select" @click="selectType">筛选</span> -->
         </div>
           <div class="statusEcharts">
             <span class="statusEchartsTitle">状态统计数据</span>
             <div id="Status" class="echart_status "></div>
           </div>
        </div>
      </div>
      <div class="hisSubListPage">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="Page"
          :page-sizes="[5, 10, 20, 50]"
          :page-size="20"
          background
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="HisSubObj.count">
        </el-pagination>
      </div>
    </div>
</template>

<script>
import echarts from 'echarts'
import api from '@/api/questionDetail'

export default {
  name: 'submitDate',
  props: {
    problemId: {
      type: Number
    },
    dataType: {
      // 1-提交历史；2-提交排行
      type: Number,
      default: 1
    }
  },
  data: function () {
    return {
      loading: true,
      HisSubObj: {},
      Page: 1,
      count: '',
      showList: [],
      hisSub: {},
      languageType: '',
      statusType: '',
      pageSize: 20,
      isShowSlef: false,
      CN: '',
      CPlusN: '',
      JavaN: '',
      Python3N: '',
      statusTypes: [{value: 4, label: 'Accept'},
        {value: 1, label: 'Waiting'},
        {value: 2, label: 'Compiling'},
        {value: 3, label: 'Running'},
        {value: 5, label: 'Presenting Error'},
        {value: 6, label: 'Wrong Answer'},
        {value: 7, label: 'Time Limit Exceed'},
        {value: 8, label: 'memory Limit Exceed'},
        {value: 9, label: 'Output Limit Exceed'},
        {value: 10, label: 'Runtime Error'},
        {value: 11, label: 'Compile Error'},
        {label: '所有状态'}],
      languageTypes: [{value: 1, label: 'C++'},
        {value: 0, label: 'C'},
        {value: 6, label: 'Python3'},
        {value: 3, label: 'Java'},
        {label: '所有语言'}]
    }
  },
  methods: {
    checkTypeChange () {
      this.getStatusHistory(this.problemId)
    },
    getStatusHistory (problemId) {
      const _this = this
      // console.log(problemId)
      this.loading = true
      if (_this.isShowSlef === false) {
        api.getStatusHistoryList('/states/history', {
          problem_id: problemId,
          status: _this.statusType,
          language: _this.languageType,
          page: _this.Page,
          pagesize: this.pageSize
        }).then(function (res) {
          // console.log(_this.languageType)
          // console.log(res.data)
          _this.HisSubObj = res.data
          _this.showList = _this.HisSubObj.history_list
          _this.count = _this.HisSubObj.count
          // console.log(_this.HisSubObj.languageObj.C)
          _this.CN = _this.HisSubObj.languageObj.C
          _this.CPlusN = _this.HisSubObj.languageObj.C++
          _this.JavaN = _this.HisSubObj.languageObj.Java
          _this.Python3N = _this.HisSubObj.languageObj.Python3
          _this.updateEcharts()
          _this.loading = false
        })
      } else {
        api.getStatusHistoryList('/states/history', {
          problem_id: _this.problemId,
          status: this.statusType,
          language: this.languageType,
          page: this.Page,
          pagesize: this.pageSize
        }).then(function (res) {
          // console.log(res.data)
          _this.HisSubObj = res.data
          _this.showList = _this.HisSubObj.my_history
          _this.count = _this.HisSubObj.my_count
          // console.log(_this.count)
          _this.CN = _this.HisSubObj.languageObj.C
          _this.CPlusN = _this.HisSubObj.languageObj.C++
          _this.JavaN = _this.HisSubObj.languageObj.Java
          _this.Python3N = _this.HisSubObj.languageObj.Python3
          _this.updateEcharts()
          _this.loading = false
        })
      }
    },
    getStatusRank (problemId) {
      const _this = this
      // console.log(problemId)
      this.loading = true
      if (_this.isShowSlef === false) {
        api.getStatusRankList('/states/history', {
          problem_id: problemId,
          status: _this.statusType,
          language: _this.languageType,
          page: _this.Page,
          pagesize: this.pageSize,
          rank: true
        }).then(function (res) {
          // console.log(_this.languageType)
          // console.log(res.data)
          _this.HisSubObj = res.data
          _this.showList = _this.HisSubObj.history_list
          _this.count = _this.HisSubObj.count
          // console.log(_this.HisSubObj.languageObj.C)
          _this.CN = _this.HisSubObj.languageObj.C
          _this.CPlusN = _this.HisSubObj.languageObj.C++
          _this.JavaN = _this.HisSubObj.languageObj.Java
          _this.Python3N = _this.HisSubObj.languageObj.Python3
          _this.updateEcharts()
          _this.loading = false
        })
      } else {
        api.getStatusRankList('/states/history', {
          problem_id: _this.problemId,
          status: this.statusType,
          language: this.languageType,
          page: this.Page,
          pagesize: this.pageSize,
          rank: true
        }).then(function (res) {
          // console.log(res.data)
          _this.HisSubObj = res.data
          _this.showList = _this.HisSubObj.my_history
          _this.count = _this.HisSubObj.my_count
          // console.log(_this.count)
          _this.CN = _this.HisSubObj.languageObj.C
          _this.CPlusN = _this.HisSubObj.languageObj.C++
          _this.JavaN = _this.HisSubObj.languageObj.Java
          _this.Python3N = _this.HisSubObj.languageObj.Python3
          _this.updateEcharts()
          _this.loading = false
        })
      }
    },
    handleSizeChange (val) {
      this.pageSize = val
      if (this.dataType === 1) {
        this.getStatusHistory(this.problemId)
      } else {
        this.getStatusRank(this.problemId)
      }
    },
    handleCurrentChange (val) {
      this.Page = val
      if (this.dataType === 1) {
        this.getStatusHistory(this.problemId)
      } else {
        this.getStatusRank(this.problemId)
      }
    },
    selectType () {
      // console.log(this.languageTypes[this.languageType - 1].label)
      if (this.dataType === 1) {
        this.getStatusHistory(this.problemId)
      } else {
        this.getStatusRank(this.problemId)
      }
    },
    showSelfHis () {
      this.isShowSlef = true
      if (this.dataType === 1) {
        this.getStatusHistory(this.problemId)
      } else {
        this.getStatusRank(this.problemId)
      }
    },
    drawStatus (id) {
      this.echart_status = echarts.init(document.getElementById(id))
      this.echart_status.setOption({
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'right',
          data: ['Accept', 'Waiting', 'Compiling', 'Running', 'Presenting Error', 'Wrong Answer', 'Time Limit Exceed', 'memory Limit Exceed', 'Output Limit Exceed', 'Runtime Error', 'Compile Error']
        },
        series: [
          {
            name: '编程语言',
            type: 'pie',
            selectedMode: 'single',
            radius: [0, '30%'],

            label: {
              normal: {
                position: 'inner'
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
            ]
          },
          {
            name: '提交状态',
            type: 'pie',
            radius: ['40%', '55%'],
            data: [
            ]
          }
        ]
      })
    },
    updateEcharts () {
      const _this = this
      this.echart_status.setOption({
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'right',
          data: ['Accept', 'Waiting', 'Compiling', 'Running', 'Presenting Error', 'Wrong Answer', 'Time Limit Exceed', 'memory Limit Exceed', 'Output Limit Exceed', 'Runtime Error', 'Compile Error']
        },
        series: [
          {
            name: '编程语言',
            type: 'pie',
            selectedMode: 'single',
            radius: [0, '30%'],

            label: {
              normal: {
                position: 'inner'
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {value: _this.HisSubObj.languageObj.C, name: 'C'},
              {value: _this.HisSubObj.languageObj['C++'], name: 'C++'},
              {value: _this.JavaN, name: 'Java'},
              {value: _this.Python3N, name: 'Python3'}
            ]
          },
          {
            name: '提交状态',
            type: 'pie',
            radius: ['40%', '55%'],
            data: [
              {value: _this.HisSubObj.statusObj.Accept, name: 'Accept'},
              {value: _this.HisSubObj.statusObj.Waiting, name: 'Waiting'},
              {value: _this.HisSubObj.statusObj.Compiling, name: 'Compiling'},
              {value: _this.HisSubObj.statusObj.Running, name: 'Running'},
              {value: _this.HisSubObj.statusObj.Presenting_Error, name: 'Presenting Error'},
              {value: _this.HisSubObj.statusObj.Wrong_Answer, name: 'Wrong Answer'},
              {value: _this.HisSubObj.statusObj.Time_Limit_Exceed, name: 'Time Limit Exceed'},
              {value: _this.HisSubObj.statusObj.Memory_Limit_Exceed, name: 'Memory Limit Exceed'},
              {value: _this.HisSubObj.statusObj.Output_Limit_Exceed, name: 'Output Limit Exceed'},
              {value: _this.HisSubObj.statusObj.Runtime_Error, name: 'Runtime Error'},
              {value: _this.HisSubObj.statusObj.Compile_Error, name: 'Compile Error'}
            ]
          }
        ]
      })
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      this.drawStatus('Status')
    })
  },
  created () {
    if (this.dataType === 1) {
      this.getStatusHistory(this.problemId)
    } else {
      this.getStatusRank(this.problemId)
    }
  },
  watch: {
    dataType () {
      this.languageType = ''
      this.statusType = ''
      if (this.dataType === 1) {
        this.getStatusHistory(this.problemId)
      } else {
        this.getStatusRank(this.problemId)
      }
    }
  }
}
</script>

<style scoped>
  .submitData{
    max-width: 1600px;
    margin: 0 auto;
    position: relative;
  }
  .Title {
    color: #111;
    font-size: 30px;
    font-weight: 600;
    line-height: 3em;
    text-overflow: ellipsis;
    text-align: center;
  }
  .show-mid{
    width: 100%;
    padding: 4% 3% 3% 4%;
    display: flex;
    justify-content: center;
  }
  .showData{
    background-color: #f7f7f7;
    border-radius: 10px;
    width: 54%;
    height: 462px;
    padding-top: 19px;
    border: 1px solid rgba(230, 230, 230, 0.3);
    box-shadow: -2px 5px 10px rgba(133, 193, 38, 0.2);
  }
  .showData .HisTitle{
    font-size: 20px;
    font-weight: bolder;
    color: #353535;
    padding-left: 19px;
  }
  .showData .subCount{
     margin-left: 20px;
     font-size: 10px;
     color: #6b6969;
     display: inline-block;
     margin-bottom: 12px;
   }
  .showData .selfHis{
    margin-left: 20px;
    font-size: 15px;
    color: #6b6969;
    display: inline-block;
    margin-bottom: 12px;
  }
  .showData .selfHis:hover{
    color: #ea8313bf;
    cursor: pointer;
  }
  .showData .showHisSubList{
    overflow-y: auto;
    overflow-x: hidden;
    height: 407px;
    background-color: #fff;
  }
  .showHisSubList .showHisSub{
    height: 74px;
    width:100%;
    border: 0px solid rgba(230, 230, 230, 1);
    border-bottom-width: 1px;
    padding-top: 18px;
    line-height: 23px;
    position: relative;
  }
  .showHisSubList .showHisSub .status{
    border-radius: 50%;
    margin-left: 20px;
    text-align: center;
    -webkit-box-shadow: 1px 2px 21px #67c23a59;
    box-shadow: 1px 2px 21px #67c23a59;
    position: absolute;
    height: 40px;
    width: 40px;
    padding: 8px;
  }
  .showHisSubList .showHisSub .stuName{
    margin-left: 76px;
    font-size: 18px;
    font-weight: 500;
    position: absolute;
  }
  .showHisSubList .showHisSub .subTime{
    position: absolute;
    margin-top: 29px;
    margin-left: 75px;
    color: #a7a3a3;
  }
  .showHisSubList .showHisSub .runTime{
    position: absolute;
    margin-left: 80%;
    margin-top: 3px;
    color:#9ddc7c;
  }
  .showHisSubList .showHisSub .Memory{
    position: absolute;
    margin-top: 23px;
    margin-left: 80%;
    color:#5b983a;
  }
  .hisSubListPage{
    padding: 20px 0;
  }
  hisSubListPage /deep/ .el-pagination .el-pager .active{
    background-color: #f48118 !important;
  }
  .hisSubListPage /deep/ .el-pagination .el-pagination__total{
    color: inherit;
  }
  .hisSubListPage /deep/ .el-pagination .el-pagination__jump{
    color: inherit;
  }
  .choose-and-status{
    width: 560px;
    margin-left: 2em;
  }
  .choose{
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    height: 110px;
    border: 1px solid rgba(230, 230, 230, 0.3);
    box-shadow: -2px 5px 10px rgba(133, 193, 38, 0.2);
  }
  .statusEcharts .statusEchartsTitle,
  .choose .chooseTitle{
    display: block;
    font-size: 15px;
    color: #353535;
    padding-top: 12px;
    padding-left: 19px;
  }
  .choose .select-area{
    display: inline-block;
    position: relative;
    margin-top: 15px;
    margin-bottom: 10px;
    margin-left: 5px;
    width: 48%;
  }
  .choose .select-area /deep/  .el-input__inner {
    border: none;
    border-bottom: 1px solid #f48118;
    border-radius: 0;
    padding-left: 31px;
    padding-top: 11px;
  }
  .choose .select-area /deep/ .el-input__suffix {
    display: none;
  }
  .el-select-dropdown__list .selected {
    color: #f48118;
  }
  .choose .select-area /deep/ .el-select>.el-input{
    display: block;
    margin-left: 12px;
    margin-right: 12px;
  }
  .statusEcharts{
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    width: 560px;
    height: 328px;
    border: 1px solid rgba(230, 230, 230, 0.3);
    box-shadow: -2px 5px 10px rgba(133, 193, 38, 0.2);
    margin-top: 20px;
    position: relative;
  }
  .echart_status{
    position: relative;
    width: 107%;
    height: 100%;
    left: -7%;
  }
</style>



