    removeItem: function (editor) {
      // 自定义菜单配置,去除哪几个菜单项
      var AllItems = [
        {chinese: '标题', english: 'head'},
        {chinese: '粗体', english: 'bold'},
        {chinese: '字号', english: 'fontSize'},
        {chinese: '字体', english: 'fontName'},
        {chinese: '斜体', english: 'italic'},
        {chinese: '下划线', english: 'underline'},
        {chinese: '删除线', english: 'strikeThrough'},
        {chinese: '文字颜色', english: 'foreColor'},
        {chinese: '背景颜色', english: 'backColor'},
        {chinese: '链接', english: 'link'},
        {chinese: '列表', english: 'list'},
        {chinese: '对齐', english: 'justify'},
        {chinese: '引用', english: 'quote'},
        {chinese: '表情', english: 'emoticon'},
        {chinese: '图片', english: 'image'},
        {chinese: '表格', english: 'table'},
        {chinese: '视频', english: 'video'},
        {chinese: '代码', english: 'code'},
        {chinese: '撤销', english: 'undo'},
        {chinese: '重复', english: 'redo'}
      ]

      var loc = -1// 记录下标索引

      var haveItems = [
        'head', // 标题
        'bold', // 粗体
        'fontSize', // 字号
        'fontName', // 字体
        'italic', // 斜体
        'underline', // 下划线
        'strikeThrough', // 删除线
        'foreColor', // 文字颜色
        'backColor', // 背景颜色
        'link', // 插入链接
        'list', // 列表
        'justify', // 对齐方式
        'quote', // 引用
        'emoticon', // 表情
        'image', // 插入图片
        'table', // 表格
        'video', // 插入视频
        'code', // 插入代码
        'undo', // 撤销
        'redo' // 重复
      ]
      // 更加编辑器类型去除不要的菜单项
      switch (this.editorType) {
        case 1: // 发布帖子编辑器
          this.removeList = ['视频']
          break
        case 2: // 编辑回复编辑器
          this.removeList = ['标题', '字号', '字体', '删除线', '链接', '列表', '表格', '对齐', '引用', '图片', '视频']
          break
        case 3: // 贡献题库编辑器
          this.myItem.showProblemLink = false
          this.removeList = ['标题', '字号', '斜体', '字体', '背景颜色', '列表', '代码', '视频', '引用', '链接', '删除线']
          break
        case 4: // 题解区陈述思路
          this.myItem.showProblemLink = false
          this.removeList = ['标题', '字号', '斜体', '字体', '删除线', '链接', '列表', '表格', '对齐', '引用', '代码', '图片', '视频']
          break
        default:
          break
      }

      // 将中文替换为英文
      for (var j = 0; j < AllItems.length; j++) {
        loc = this.removeList.indexOf(AllItems[j].chinese)
        if (loc < 0) {
          continue
        }
        this.removeList.splice(loc, 1, AllItems[j].english)
      }

      for (var i = 0; i < this.removeList.length; i++) {
        loc = haveItems.indexOf(this.removeList[i])
        if (loc < 0) {
          continue
        }
        haveItems.splice(loc, 1)
      }
      editor.customConfig.menus = haveItems
    },
    imageEvent: function () {
      const _this = this
      var container = this.$refs.editorContent
      var imageList = container.getElementsByTagName('img')
      // console.log(imageList)
      for (var i = 0; i < imageList.length; i++) {
        imageList[i].onclick = function (e) {
          _this.imageTemp = this
          _this.ifChangeSize = true
          _this.widthMin = 20
          _this.widthMax = container.clientWidth - 60
          _this.imageWidth = this.clientWidth
          event.stopPropagation()
        }
      }
      container.onclick = function (e) {
        _this.imageWidth = 0
        _this.imageTemp = null
        _this.ifChangeSize = false
        _this.widthMin = 0
        _this.widthMax = 0
      }
    },
    addQuestionLink: function () {
      if (!(/^[0-9]+$/.test(this.questionId))) {
        this.$message({
          message: '请填入正确题目序号！',
          type: 'warning',
          duration: 1000
        })
        return false
      }
      const _this = this
      api.getQuestionDetail('/problems/detail', parseInt(this.questionId)).then(function (res) {
        if (res.success === 'false') {
          _this.$message({
            message: '题库中没有该题目！',
            type: 'error',
            duration: 1000
          })
        } else {
          _this.showInput = false
          var container = _this.$refs.editorContent
          var content = container.getElementsByClassName('w-e-text')[0]
          var newElement = document.createElement('p')
          newElement.innerHTML = '<blockquote class="ud-question-box">' +
            '<a href="' + host + '/#/library/questionDetail?PID=' + res.data.problem_id + '" target="_blank">' +
            '<span class="ud-question-box-no">' + res.data.problem_id + '</span>' +
            '<span class="ud-question-box-title">' + res.data.info.title + '</span>' +
            '<i class="icon-right-arrow"></i>' +
            '</a><span class="ud-question-box-content">' + res.data.info.description + '</span></blockquote><p><br></p>'
          content.appendChild(newElement)
          _this.catchData(content.innerHTML) // 把这个html通过catchData的方法传入父组件
        }
      })
    }
  },
  watch: {
    imageWidth () {
      if (this.imageWidth !== 0) {
        this.imageTemp.style = 'width: ' + this.imageWidth + 'px'
      }
    }
  },
  mounted () {
    const _this = this
    var editor = new E(this.$refs.editorbar, this.$refs.editorContent) // 创建富文本实例

    editor.customConfig.onchange = (html) => {
      this.editorContent = html
      this.editorText = editor.txt.text()
      this.catchData(this.editorContent, this.editorText) // 把这个html通过catchData的方法传入父组件
    }
    // 开启debug模式
    // editor.customConfig.debug = true
    // 关闭粘贴内容中的样式
    editor.customConfig.pasteFilterStyle = false
    // 忽略粘贴内容中的图片
    // editor.customConfig.pasteIgnoreImg = true
    // 自定义处理粘贴的文本内容
    editor.customConfig.pasteTextHandle = function (content) {
      // content 即粘贴过来的内容（html 或 纯文本），可进行自定义处理然后返回
      if (content.length > 32768) {
        return content + '<p style="font-size: 12px; text-align: center; color: rgba(148, 148, 148, 0.6);">该粘贴内容太长，将保存失败！请改为右键纯文本粘贴或适当删减至长度适合！</p>'
      }
      return content
    }
    // 限制一次最多上传 20 张图片
    editor.customConfig.uploadImgMaxLength = 20
    // 将图片大小限制为 3M
    editor.customConfig.uploadImgMaxSize = 5 * 1024 * 1024
    // 自定义上传方法
    editor.customConfig.customUploadImg = function (files, insert) {
      // files 是 input 中选中的文件列表
      // insert 是获取图片 url 后，插入到编辑器的方法
      for (var i in files) {
        api.uploadImg('/forum/posts/image', files[i]).then(function (res) {
          insert(host + res.data)
          _this.imageEvent()
        })
      }
    }

    // 移除菜单项
    this.removeItem(editor)
    // 初始化表情
    this.initEmotions(editor)
    editor.customConfig.zIndex = 1
    editor.create()
    // 初始化内容
    editor.txt.html(this.content)
    this.editorContent = this.content
    this.editorText = editor.txt.text()
  }
}
</script>
<style scoped>
  @import "../../../static/editorCss/style.css";
  #wangeditor {
    margin-top: 10px;
    position: relative;
  }
  .toolbar {
    border: 1px solid rgba(148, 148, 148, 0.3);
    border-bottom: none;
    background-color: rgba(228, 228, 228, 0.8);
    border-radius: 5px 5px 0 0;
    position: relative;
  }
  .editorShow {
    border: 1px solid rgba(148, 148, 148, 0.3);
    height: 400px;
    height: 70vh;
    text-align: left;
    background-color: #fff;
    font-size: 16px;
    line-height: 2em;
    border-radius: 0 0 5px 5px;
  }
  .editorTip {
    font-size: 12px;
    color: #999;
    text-align: center;
    display: block;
    width: 100%;
    position: absolute;
    white-space: nowrap;
    bottom: 48%;
    left: 0%;
    z-index: 2;
    cursor: default;
  }
  .word-total {
    font-size: 12px;
    color: rgba(148, 148, 148, 0.6);
    margin-top: 5px;
    padding: 0px 5px;
    background-color: transparent;
    border: none;
  }
  .word-total:hover {
  }
  .word-total-over {
    color: rgb(245, 90, 90);
  }
  .changeImageSize-box {
    width: 200px;
    background-color: red;
    position: fixed;
    border-radius: 10px 0 0 10px;
    /*border: 1px solid #ccc;*/
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
    z-index: 2;
    bottom: 40%;
    right: 0px;
    transition: right .5s;
    overflow: hidden;
  }
  .changeImageSize-box-hide {
    right: -200px;
  }
  .changeImageSize-box .title {
    text-align: center;
    font-size: 12px;
    color:  #999;
    line-height: 2em;
    border-bottom: 1px solid #ccc;
  }
  .changeImageSize-box .main {
    padding: 0 10px;
    position: relative;
  }
  .changeImageSize-box .main /deep/ .el-slider .el-slider__button{
    border: 2px solid rgba(142, 204, 69, 1);
  }
  .changeImageSize-box .main /deep/ .el-slider .el-slider__bar {
    background-color: rgba(142, 204, 69, 1);
  }
  /*自定义组件样式*/
  .user-defined-module {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    position: relative;
  }
  .ud-module-btn {
    flex: 0 1 auto;
    background-color: transparent;
    padding: 5px 8px 5px 10px;
    cursor: pointer;
    position: relative;
  }
  .ud-module-btn i {
    color: #999;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    position: relative;
    bottom: -1px;
  }
  .ud-module-btn:hover i {
    color: #333;
  }
  .ud-module-tip {
    width: 80px;
    font-size: 12px;
    color: #fff;
    text-align: center;
    line-height: 2em;
    background-color: #333;
    border-radius: 5px;
    position: absolute;
    left: -5px;
    bottom: -2.5em;
    z-index: 3;
    display: none;
  }
  .ud-module-btn:hover .ud-module-tip {
    display: inline-block;
  }
  .ud-module-input-fade {
    overflow: hidden;
  }
  .ud-module-input {
    width: 100px;
    flex: 0 1 auto;
    line-height: 28px;
    transition: all .3s;
  }
  .ud-module-input .ud-module-input-tip {
    font-size: 10px;
    color: #999;
  }
  .ud-module-input input {
    width: 60px;
    height: 20px;
    font-size: 12px;
    color: #333;
    border-right: 3px solid #999;
    padding-left: 5px;
  }
  >>> .ud-module-float-right {
    margin-right: 2px;
    position: absolute !important;
    top: 0px;
    right: 0px;
  }
  >>> .ud-module-float-right .ud-module-tip {
    left: unset;
    right: -5px;
  }
  .show-left-enter-active, .show-left-leave-active {
    opacity: 1;
    max-width: 100px;
    transition: all .5s;
  }
  .show-left-enter, .show-left-leave-to {
    opacity: 0;
    max-width: 0;
  }
</style>



