<template>
  <div class="questionSolve" @click="closeLeftContainer">
    <h1 class="Title">题<br>解<br>区<br><i class="icon-class"></i></h1>
    <div class="btnBox" v-if="ticket">
        <button class="btn-openMy" @click.stop.prevent="openLeftContainer">
          <span v-if="ifShowLeftContainer">关<br>闭<br>历<br>程</span>
          <span v-if="!ifShowLeftContainer">我<br>的<br>分<br>享</span>
        </button>
        <button class="btn-share" v-if="canShowShaneBtn && !ifShowLeftContainer" @click="ifShowShareSolve = true">分<br>享</button>
      <button class="btn-back" @click="toBack"><i class="icon-fanhui"></i></button>
    </div>
    <div class="container" v-if="ticket" @click="closeLeftContainer">
      <div class="container-btn">
        <span class="container-title"><span style="font-weight: 600; color: #d43039;">{{PID}}</span>&nbsp;&nbsp;&nbsp;{{questionData.title}}</span>
        <button :class="(currentType===1)? 'btn-see-myself btn-active' : 'btn-see-myself'" @click="getList(1)">只看自己</button>
        <button :class="(currentType===0)? 'btn-see-all btn-active' : 'btn-see-all'" @click="getList(0)">查看全部</button>
      </div>
      <span v-if="ifShowMainList && total === 0" class="nolist">该题没有任何题解，赶紧<span class="nolist-toShare" @click="ifShowShareSolve = true">前往分享</span>你的题解！坐史上第一把板凳！</span>
      <transition name="main-fade">
        <div class="ul-container" v-if="ifShowMainList">
        <ul >
        <li :class="item.is_liked?'main-frame main-frame-like':'main-frame'" v-for="(item, index) in solutionsList" :key="index">
          <div class="left-frame">
            <img v-if="currentType===1" :src="info.avatarUrl ? host + info.avatarUrl : defaultImage">
            <img v-if="currentType===0" :src="item.avatarUrl ? host + item.avatarUrl : defaultImage">
            <h5 class="nick">
              <span v-if="currentType===1">{{info.nick}}</span>
              <span v-if="currentType===0">{{item.nick}}</span>
            </h5>
            <span class="identity" v-if="currentType===0">
              <span v-if="item.identity === '0'" style="background-color: rgba(204, 204, 204, 1);">用户题解</span>
              <span v-if="item.identity === '1'" style="background-color: rgb(255,189,28);">学生题解</span>
              <span v-if="item.identity === '2'" style="background-color: rgba(255, 153, 0, 0.8);">教师题解</span>
              <span v-if="item.identity === '3'" style="background-color: rgba(100, 149, 237, 0.8);">管理员题解</span>
              <span v-if="item.identity === '4'" style="background-color: rgba(142, 204, 69, 0.8);">官方题解</span>
            </span>
          </div>
          <div class="right-frame">
            <div class="content-top">
              <span v-if="currentType === 1">这篇题解&nbsp;&nbsp;</span>
              <span v-if="currentType === 0">Ta的题解&nbsp;&nbsp;</span>
              <span style="color: rgb(117,169,57);">耗时:</span>&nbsp;{{item.run_time}}&nbsp;&nbsp;
              <span style="color: rgb(53,75,24);">内存:</span>&nbsp;{{item.memory}}&nbsp;&nbsp;
              <span style="color: rgb(35,53,21);">代码类型:</span>&nbsp;
              <span class="language"  :style="'color:'+lgItem.color" v-if="item.language === lgItem.value" v-for="(lgItem, lgIndex) in languages" :key="lgIndex"> {{lgItem.title}} </span>
            </div>
            <div class="content-main editorShow" v-html="item.content" v-if="!ifNull(item.content)"></div>
            <div class="content-main editorShow no-content" v-if="ifNull(item.content)">该同学没有留下一点思路，真让人气愤<img src="http://img.t.sinajs.cn/t35/style/images/common/face/ext/normal/62/crazya_thumb.gif" alt="[抓狂]"></div>
            <!--<span v-if="ifOverHeight(index)">查看更多</span>-->
            <div class="content-bottom">
              <span class="time">
                分享于&nbsp;<span>{{item.date.replace('T', ' ')}}</span>
                &nbsp;&nbsp;
                <i class="el-icon-delete btn-del" v-if='currentType === 1' @click="delSolutions(item.forum_solution_id)"></i>
              </span>
              <span class="oper">
                <span>已有&nbsp;<span class="likes">{{item.likes}}</span>&nbsp;人点赞</span>
                &nbsp;&nbsp;|&nbsp;&nbsp;
                <span class="btn-show-code" @click="setShowCode(index)">{{(ifShowCode === index)?'关闭源代码':'查看源代码'}}</span>
              </span>
            </div>
            <transition name="code-fade">
              <div class="code-container" v-if="ifShowCode === index">
                <div class="code editorShow" v-html="highlight('<pre><code>'+item.source_code+'</code></pre>', 10)"></div>
                <button class="btn-like" @click="toLike(item.forum_solution_id, item.is_liked, index)"><i class="icon-zan"></i></button>
              </div>
            </transition>
          </div>
          <div class="main-frame-bottom"></div>
        </li>
        </ul>
          <span class="overlist" v-if="solutionsList.length === 5 && currentType === 1">每个问题最多允许有五篇题解，如若需要继续分享，请删除部分题解</span>
        </div>
      </transition>
      <div class="list-pagination" v-if="total > pageSize">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="Page"
          :page-size="pageSize"
          :pager-count="9"
          background
          layout="total, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
    <transition name="mySolutions-fade">
    <div class="left-container" v-if="ifShowLeftContainer" @click.stop.prevent="">
      <h3 class="title">我的分享历程</h3>
      <div class="mySolutions">
        <span class="nolist" v-if="mySolutionsList.length===0">尚未分享，赶紧前往分享</span>
        <ul class="mySolutions-ul" v-if="mySolutionsList.length!==0">
          <li class="mySolutions-item" v-for="(item, index) in mySolutionsList" :key="index">
            <span class="date" v-if="ifSameDay(index)">{{item.date.split('T')[0]}}</span>
            <div class="main">
              <span class="main-no">{{item.problem_id}}</span>
              <span class="main-title" @click="toQueSolve(item.problem_id)">{{item.problem_title}}</span>
            </div>
          </li>
        </ul>
      </div>
      <div class="mS_pagination" v-if="mS_total > mS_pagesize">
        <el-pagination
          @current-change="mS_handleCurrentChange"
          :current-page="mS_page"
          :page-size="mS_pagesize"
          :pager-count="5"
          layout="prev, pager, next"
          :total="mS_total">
        </el-pagination>
      </div>
    </div>
    </transition>
    <transition name="shareSolve-fade">
      <shareSolve
        v-if="ifShowShareSolve"
        :questionData="questionData"
        @getCloseMsg="closeMsg"
        @getChangeLangMsg="changeLangMsg">
      </shareSolve>
    </transition>
  </div>
</template>

<script>
import {host} from '@/api/config'
import api from '@/api/questionDetail'
import prism from 'prismjs'
import shareSolve from '@/base/question-detail/shareSolve'

export default {
  name: 'questionSolve',
  components: {
    shareSolve
  },
  data: function () {
    return {
      ticket: false,
      like: false,
      currentTabType: 0, // 记录查看个人还是全部，0-全部，1-个人
      canShowShaneBtn: false,
      ifShowShareSolve: false,
      ifShowLeftContainer: false,
      ifShowMainList: false,
      ifShowCode: 0,
      info: {},
      PID: '',
      languageActive: 0,
      languages: [
        {title: 'C', value: 0, color: '#8ecc45'},
        {title: 'C++', value: 1, color: '#843f10'},
        {title: 'Java', value: 3, color: '#d43039'},
        {title: 'Python3', value: 6, color: '#f577d0'}
      ],
      initPid: '',
      Page: 1,
      pageSize: 20,
      solutionsList: [],
      mySolutionsList: [],
      mS_page: 1,
      mS_pagesize: 20,
      mS_total: 0,
      questionData: {},
      total: 0,
      host: host,
      defaultImage: require('../../../static/image/acmlogo.jpg')
    }
  },
  methods: {
    ifOverHeight (index) {
      return true
    },
    ifSameDay (no) {
      if (no === 0) {
        return true
      } else {
        var next = new Date(this.mySolutionsList[no - 1].date.replace('T', ' '))
        var pre = new Date(this.mySolutionsList[no].date.replace('T', ' '))
        if (next.getFullYear() === pre.getFullYear() && next.getMonth() === pre.getMonth() && next.getDate() === pre.getDate()) {
          return false
        }
        return true
      }
    },
    ifNull (content) {
      if (content.trim() === '' || content.replace(/<p>/g, '').replace(/<\/p>/g, '').replace(/<br>/g, '').trim() === '') {
        return true
      }
      return false
    },
    setShowCode (no) {
      this.ifShowCode = this.ifShowCode === no ? -1 : no
    },
    handleSizeChange (val) {
      this.pageSize = val
      if (this.currentType === 0) {
        this.getMySolutionsAllUser()
      } else {
        this.getMySolutionsMyselfThis()
      }
    },
    handleCurrentChange (val) {
      this.Page = val
      if (this.currentType === 0) {
        this.getMySolutionsAllUser()
      } else {
        this.getMySolutionsMyselfThis()
      }
    },
    mS_handleCurrentChange (val) {
      this.mS_page = val
      this.getMySolutionsMyselfAll()
    },
    closeMsg (res) {
      this.ifShowShareSolve = res
      if (this.currentType === 0) {
        this.getMySolutionsAllUser()
      } else {
        this.getMySolutionsMyselfThis()
      }
    },
    changeLangMsg (res) {
      this.languageActive = res
      this.getQuestionData()
    },
    closeLeftContainer () {
      if (this.ifShowLeftContainer) {
        this.ifShowLeftContainer = !this.ifShowLeftContainer
      }
    },
    openLeftContainer () {
      if (!this.ifShowLeftContainer) {
        this.mS_page = 1
        this.mS_total = 0
        this.getMySolutionsMyselfAll()
      }
      this.ifShowLeftContainer = !this.ifShowLeftContainer
    },
    toQueSolve (newPid) {
      if (newPid === this.PID) {
        if (this.currentType === 1) {
          this.$message({
            message: '你已在该题解页面中！',
            type: 'warning',
            duration: 1200
          })
        } else {
          this.getList(1)
        }
      } else {
        this.$router.push({
          path: '/questionSolve',
          name: 'questionSolve',
          query: {PID: newPid}
        })
      }
    },
    toBack () {
      this.$router.push({
        path: '/library/questionDetail',
        query: {PID: this.initPid}
      })
    },
    toLike (id, ifLike, index) {
      const _this = this
      if (ifLike) {
        api.cancelLike('/forum/solutions/like', id).then(function (res) {
          if (res.success !== 'true') {
            _this.$message({
              message: '取消点赞失败，请检查网络链接！',
              type: 'error',
              duration: 1200
            })
          } else {
            _this.solutionsList[index].likes--
            _this.solutionsList[index].is_liked = false
          }
        })
      } else {
        api.setLike('/forum/solutions/like', id).then(function (res) {
          if (res.success !== 'true') {
            _this.$message({
              message: '点赞失败，请检查网络链接！',
              type: 'error',
              duration: 1200
            })
          } else {
            _this.solutionsList[index].likes++
            _this.solutionsList[index].is_liked = true
          }
        })
      }
    },
    delSolutions (id) {
      const _this = this
      this.$confirm('此操作将永久删除该题解, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        api.cancelShareMyCode('/forum/solutions/share', id).then(function (res) {
          if (res.success === 'true') {
            _this.$message({
              type: 'success',
              message: '删除成功!'
            })
            _this.getMySolutionsMyselfThis()
          }
        })
      }).catch(() => {

      })
    },
    highlight (content, maxLines) {
      // console.log(content)
      var language = 'clike'
      // content = content.replace(/<code>/g, '<code class="language-' + language + '">').replace(/<pre>/g, '<pre class="line-numbers">')
      var contentList = content.split('<pre><code>')
      var start = contentList[0]
      var needContent = ''
      for (var i = 1; i < contentList.length; i++) {
        needContent += '<span class="codeContain"><pre class="line-numbers"><code class="language-' + language + '">'
        var childList = contentList[i].split('</code></pre>')
        // 提取出的code内代码装在format内
        var format = childList[0].replace(/&lt;/g, '<').replace(/&gt;/g, '>').replace(/&amp;/g, '&').replace(/<br>/g, '\n')
        format = prism.highlight(format, prism.languages.clike, language)
        var formatList = format.split('\n')
        format = ''
        for (var j = 0; j < formatList.length || j < maxLines; j++) {
          if (j < formatList.length) {
            format += '<span class="line-numbers-rows">' + formatList[j] + ' </span>'
          } else {
            format += '<span class="line-numbers-rows-block"></span>'
          }
        }
        needContent += format
        needContent += '</code></pre><button class="copyContent-btn" style="cursor: pointer">复制</button></span>'
        needContent += childList[1]
      }
      return start + needContent
    },
    initQuestionData () {
      this.PID = this.$route.query.PID
      for (var i in this.languages) {
        if (this.languages[i].value === parseInt(this.$route.query.Lang)) {
          this.languageActive = i
          break
        }
      }
    },
    getQuestionData () {
      const _this = this
      api.getQuestionDetail('/problems/detail', {
        problem_id: _this.PID,
        language: _this.languages[_this.languageActive].value
      }).then(function (res) {
        _this.questionData = res.data
        _this.canShowShaneBtn = true
      })
    },
    getList (type) {
      this.page = 1
      this.total = 0
      this.currentType = type
      if (this.currentType === 0) {
        this.getMySolutionsAllUser()
      } else {
        this.getMySolutionsMyselfThis()
      }
    },
    getMySolutionsAllUser () {
      const _this = this
      _this.ifShowMainList = false
      api.getStatusHistoryList('/forum/solutions', {
        problem_id: _this.PID,
        page: _this.Page,
        pagesize: _this.pageSize
      }).then(function (res) {
        _this.title = res.data.problem_title
        _this.solutionsList = res.data.solutions_list
        _this.total = res.total
        setTimeout(function () {
          _this.ifShowMainList = true
        }, 500)
      })
    },
    getMySolutionsMyselfAll () {
      const _this = this
      api.getMySolutions('/forum/solutions/my_solutions', {
        page: _this.mS_page,
        pagesize: _this.mS_pagesize
      }).then(function (res) {
        _this.mySolutionsList = res.data.solutions_list
        _this.mS_total = res.total
      })
    },
    getMySolutionsMyselfThis () {
      const _this = this
      _this.ifShowMainList = false
      api.getMySolutions('/forum/solutions/my_solutions', {
        problem_id: _this.PID,
        page: _this.page,
        pagesize: _this.pagesize
      }).then(function (res) {
        _this.title = res.data.problem_title
        _this.solutionsList = res.data.solutions_list
        _this.total = res.total
        setTimeout(function () {
          _this.ifShowMainList = true
        }, 500)
      })
    },
    getListInfo () {
      const _this = this
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json !== 'undefined' && json !== null && json !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
        api.getInfoList('/users/detail', uid).then(function (res) {
          _this.info = res.data.user_info
          _this.getQuestionData()
        })
        _this.ticket = true
      } else {
        this.$message({
          message: '未登录或丢失缓存，不允许进入题解区！',
          type: 'error',
          duration: 1800
        })
        setTimeout(function () {
          _this.toBack()
        }, 800)
      }
    }
  },
  created () {
    this.initQuestionData()
    this.initPid = this.PID
    this.getListInfo()
    this.getList(0)
  },
  watch: {
    $route () {
      if (this.$route.name === 'questionSolve') {
        this.page = 1
        this.pageSize = 20
        this.initQuestionData()
        this.getQuestionData()
        this.getList(1)
      }
    }
  }
}
</script>

