    padding: 5px 15px;
  }
  .right-frame .content-top .language {
    font-weight: 600;
  }
  .right-frame .content-main {
    font-size: 14px;
    padding: 5px 15px;
    min-height: 122px;
  }
  .right-frame .init-content {
    max-height: 122px;
  }
  .right-frame .no-content {
    color: #999;
  }
  .right-frame .content-bottom {
    font-size: 12px;
    color: #9c9c9c;
    padding: 5px 15px;
    background-color: rgba(228, 228, 228, 0.41);
  }
  .right-frame .content-bottom .oper {
    float: right;
  }
  .right-frame .content-bottom .oper .likes {
    color: rgba(234, 140, 0, 1);
    font-weight: 600;
  }
  .content-bottom .oper .btn-show-code {
    cursor: pointer;
  }
  .right-frame ,code-container {
    position: relative;
    overflow: hidden;
  }
  .right-frame .code {
    max-height: 500px;
    overflow: hidden;
  }
  .right-frame .btn-del {
    cursor: pointer;
    position: relative;
    bottom: -1px;
  }
  .right-frame .btn-del:hover {
    color: rgb(245, 108, 108);
  }
  .main-frame .right-frame .btn-like,
  .main-frame-like .right-frame .btn-like{
    width: 46px;
    height: 46px;
    border-radius: 50%;
    /*background-color: rgba(124, 173, 65, 1);*/
    background-color: rgba(102, 102, 102, 1);
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
    display: inline-block;
    position: absolute;
    right: 35px;
    bottom: 20px;
    cursor: pointer;
    transition: all .8s;
  }
  .main-frame .right-frame .btn-like:hover {
    background-color: rgb(123, 123, 123);
    box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.2);
  }
  .main-frame-like .right-frame .btn-like {
    background-color: rgba(124, 173, 65, 1);
  }
  .main-frame-like .right-frame .btn-like:hover {
    background-color: rgb(140, 193, 69);
    box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.2);
  }
  .right-frame .btn-like i::before {
    font-size: 28px;
    color: #fff;
  }
  >>> .right-frame pre {
    max-height: 510px;
    border-radius: 0;
  }
  .left-container {
    width: 300px;
    height: calc(100vh - 60px);
    position: fixed;
    bottom: 0px;
    left: 0px;
    box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.2);
  }
  .left-container .nolist {
    display: block;
    font-size: 14px;
    color: #999;
    text-align: center;
    margin-top: 50%;
    cursor: default;
  }
  .left-container .title {
    font-size: 18px;
    font-weight: 300;
    color: #fff;
    text-shadow: 0 1px rgba(25, 30, 36, 1);
    line-height: 3em;
    padding-left: 30px;
    position: relative;
    background-color: #333;
  }
  .left-container .title::before {
    content: '';
    display: inline-block;
    width: 10px;
    height: 10px;
    background-color:  rgba(255, 153, 0, 1);
    margin-right: 10px;
    position: relative;
    bottom: 2px;
  }
  .left-container .mySolutions {
    position: relative;
    width: 100%;
    height: 100%;
    padding-bottom: 30px;
    background-color: rgba(63, 64, 68, 1);
    overflow-y: auto;
  }
  .left-container .mySolutions-ul {
    position: relative;
    width: 100%;
    min-height: 90%;
    padding: 10px 10px 50px 30px;
  }
  .left-container .mySolutions-ul::before {
    content: '';
    display: block;
    position: absolute;
    width: 2px;
    height: 100%;
    border-radius: 1px;
    background-color: rgba(133, 193, 71, 1);
    top: 5px;
    left: 15px;
  }
  .left-container .mySolutions::-webkit-scrollbar
  {
    width: 3px;
    height: 10px;
    background-color: #FFF;
  }

  /*定义滚动条轨道 内阴影+圆角*/
  .left-container .mySolutions::-webkit-scrollbar-track
  {
    /*-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);*/
    border-radius: 10px;
    background-color: #FFF;
  }

  /*定义滑块 内阴影+圆角*/
  .left-container .mySolutions::-webkit-scrollbar-thumb
  {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: rgba(0, 0, 0, .48);
  }
  .mySolutions-item {
    margin-bottom: 15px;
    border-radius: 3px;
    /*overflow: hidden;*/
    position: relative;
  }
  .mySolutions-item .date {
    display: block;
    position: relative;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.31);
    margin: 10px;
    left: -30px;
  }
  .mySolutions-item .date::before {
    content: '';
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    margin-right: 10px;
    position: relative;
    bottom: -1px;
    background-color: rgba(133, 193, 71, 1);
  }
  .mySolutions-item .main {
    background-color: #fff;
    border-radius: 5px;
    overflow: hidden;
  }
  .mySolutions-item .main-no {
    display: block;
    font-size: 12px;
    font-weight: 600;
    color: #fff;
    border-bottom: 1px solid rgba(153, 153, 153, 0.41);
    background-color: rgba(133, 193, 71, 1);
    padding: 3px 15px;
    cursor: default;
  }
  .mySolutions-item .main-title {
    display: block;
    padding: 8px 13px;
    cursor: pointer;
  }
  .mySolutions-item .main-title:hover {
    color: rgb(88, 129, 49);;
  }
  .main-fade-enter-active,
  .main-fade-leave-active {
    transition: all .5s ease;
  }
  .main-fade-enter-to, .main-fade-leave{
    opacity: 1;
  }
  .main-fade-enter, .main-fade-leave-to {
    opacity: 0;
  }
  .shareSolve-fade-enter-to, .shareSolve-fade-leave-to {
    opacity: 1;
    transition: all .5s;
  }
  .shareSolve-fade-enter, .shareSolve-fade-leave-to{
    opacity: 0;
  }

  .code-fade-enter-active {
     transition: all .5s ease;
   }
  .code-fade-leave-active {
    transition: all .8s ease;
  }
  .code-fade-enter-to, .code-fade-leave{
    max-height: 510px;
    opacity: 1;
  }
  .code-fade-enter, .code-fade-leave-to {
    max-height: 0px;
    opacity: 0;
  }
  .mySolutions-fade-enter-active,
  .mySolutions-fade-leave-active {
    transition: all .5s ease;
  }
  .mySolutions-fade-enter-to, .mySolutions-fade-leave{
    transform: translateX(0%);
    opacity: 1;
  }
  .mySolutions-fade-enter, .mySolutions-fade-leave-to {
    transform: translateX(-100%);
    opacity: 0;
  }
  .list-pagination {
    margin: 30px;
  }
  .list-pagination /deep/ .el-pagination .el-pager .active{
     background-color: rgba(255, 153, 0, 1) !important;
   }
  .list-pagination /deep/ .el-pagination .el-pagination__total{
    color: inherit;
  }
  .list-pagination /deep/ .el-pagination .el-pagination__jump{
    color: inherit;
  }
  .mS_pagination {
    position: relative;
    top: -87px;
    background-color: rgba(255, 255, 255, 0.1);
    transition: all .3s;
  }
  .mS_pagination:hover {
    background-color: rgba(51, 51, 51, 0.6);
  }
  .mS_pagination /deep/ .el-pagination button,
  .mS_pagination /deep/ .el-pagination .el-pager li{
    background-color: transparent;
  }
  .mS_pagination /deep/ .el-pagination .el-pager .active,
  .mS_pagination /deep/ .el-pagination .el-pager li:hover,
  .mS_pagination /deep/ .el-pagination button:hover{
    color: rgba(133, 193, 71, 1) !important;
  }
  .mS_pagination:hover /deep/ .el-pagination .el-pager li,
  .mS_pagination:hover /deep/ .el-pagination button{
    color: rgba(238, 238, 238, 0.56);
  }
  .mS_pagination /deep/ .el-pagination .el-pagination__jump{
    color: inherit;
  }
  @media only screen and (max-width: 1440px) {
    .Title {
      display: none;
    }
  }
</style>



