<template>
  <div class="myQuiz">
    <div class="myQuiz-header">
      <span>考试模块</span>
      <div class="myQuiz-header_select">
        <el-select v-model="courseId" size="mini" placeholder="全部课程" @change="courseChangeHandler">
          <el-option
            v-for="item in courseList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="curType" size="mini" placeholder="全部类型" @change="typeChangeHandler">
          <el-option
            v-for="item in typeList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="curStatus" size="mini" placeholder="全部状态" @change="statusChangeHandler">
          <el-option
            v-for="item in statusList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <el-button
        v-if="info.identity === '2' && courseId !== -1"
        class="btn-publish"
        type="danger"
        icon="el-icon-plus"
        size="mini"
        circle
        @click.native="toAddQuiz"></el-button>
      <el-button
        class="btn-changeMode"
        icon="el-icon-menu"
        size="mini"
        circle
        @click.native="changeMode()"></el-button>
    </div>
    <div class="myQuiz-body" v-loading="isLoadingList">
      <ul v-if="mode === 1">
        <li v-for="(item, index) in quiz_list" :key="index" class="myQuiz-item">
          <span class="no">{{(page-1) * pageSize + (index + 1)}}</span>
          <h5 class="myQuiz-item-header">{{item.title}}</h5>
          <div class="myQuiz-item-content" v-html="getItemHtml(item)" :key="item.status"></div>
          <div class="myQuiz-item-operBtns">
            <span class="btn" @click="toQuizDetail(item.quiz_id)"><i class="icon-guandian"></i>考试详情</span>
            <span class="btn" v-if="info.identity === '2'" @click="toQuizPreview(item.quiz_id)"><i class="el-icon-tickets"></i>查看试卷</span>
            <span class="btn" v-if="info.identity === '2' && item.status === 0"  @click="toEditQuiz(item.quiz_id)"><i class="icon-xiugai-copy"></i>试卷编辑</span>
            <span class="btn" v-if="info.identity === '2' && item.status === 0"  @click="toDelQuiz(item.quiz_id)"><i class="icon-huishouzhan"></i>删除试卷</span>

            <span class="btn" v-if="info.identity === '2' && item.start_time == null" @click="toBeginQuiz(item)"><i class="el-icon-setting"></i>手动开始考试</span>
            <span class="btn" v-if="info.identity === '2' && item.status === 2" @click="toQuizCheck(item.quiz_id)"><i class="icon-xiugai-copy"></i>立即前往批阅</span>

            <span class="btn" v-if="info.identity === '1' && item.status === 3"  @click="toQuizPreview(item.quiz_id, true)"><i class="el-icon-document"></i>查看我的答题结果</span>
            <span class="btn" v-if="info.identity === '1' && item.status === 1"  @click="toQuizStart(item.quiz_id)" style="color: #e03232"><i class="icon-tijiao-copy"></i>立即进入</span>
            <span class="btn" v-if="info.identity === '2' && item.status === 1" style="cursor: default; color: #e03232"><i class="icon-tijiao-copy"></i>正在进行中</span>
          </div>
          <div class="myQuiz-item-userScore" v-if="info.identity === '1' && item.status === 3">
            <span v-if="item.result.score === 0 || item.result.score">
              <span v-if="item.result.score === -1">缺考</span>
              <span v-if="item.result.score !== -1">{{item.result.score}}分</span>
            </span>
          </div>
        </li>
      </ul>
      <el-table
        v-show="mode === 2"
        stripe
        :data="quiz_list"
        style="width: 100%;"
        row-key="quiz_id">
        <el-table-column
          type="index"
          label="序号"
          align="center"
          width="60">
        </el-table-column>
        <el-table-column
          prop="title"
          label="考试标题"
          align="center">
        </el-table-column>
        <el-table-column
          prop="course_name"
          label="所属课程"
          align="center"
          width="150">
        </el-table-column>
        <el-table-column
          label="考试类型"
          align="center"
          width="80">
          <template slot-scope="scope">
            <span>{{scope.row.type === 1 ? '平时考试' : scope.row.type === 2 ? '期中考试' : '期末考试'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="开启时间"
          width="170">
          <template slot-scope="scope">
            <span v-if="scope.row.start_time == null">待手动开启</span>
            <span v-else>{{scope.row.start_time.replace('T', ' ')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="考试时长"
          width="80">
          <template slot-scope="scope"><span>{{scope.row.duration}}分钟</span></template>
        </el-table-column>
        <el-table-column
          align="center"
          label="考试状态"
          width="100">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 0" style="color: #5F90DD">未开始</span>
            <span v-else-if="scope.row.status === 1" style="color: #67C23A">进行中</span>
            <span v-else-if="scope.row.status === 2" style="color: #F56C6C">已结束</span>
            <span v-else  style="color: #f54234">已公布</span>
          </template>
        </el-table-column>
      </el-table>
      <span class="noList" v-if="mode === 1 && quizTotal < 5">-&nbsp;无更多考试信息 -</span>
    </div>
    <div class="myQuizList-footer myQuizListPage" v-if="quizTotal >= 5">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[5, 10, 20]"
        :page-size="pageSize"
        background
        prev-text="上一页"
        next-text="下一页"
        layout="total, sizes, prev, pager, next, jumper"
        :total="quizTotal">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import api from '@/api/quiz'
const CARD = 1
const TABLE = 2

export default {
  name: 'myQuiz',
  data () {
    return {
      mode: CARD,
      info: {},
      isLoadingList: true,
      quiz_list: [],
      courseName: '',
      courseId: -1,
      courseDetailList: [],
      courseList: [{
        value: -1,
        label: '全部课程'
      }],
      curType: -1,
      typeList: [{
        value: -1,
        label: '全部类型'
      }, {
        value: 1,
        label: '平时考试'
      }, {
        value: 2,
        label: '期中考试'
      }, {
        value: 4,
        label: '期末考试'
      }],
      curStatus: -1,
      statusList: [{
        value: -1,
        label: '全部状态'
      }, {
        value: 0,
        label: '未开始'
      }, {
        value: 1,
        label: '进行中'
      }, {
        value: 2,
        label: '已结束'
      }, {
        value: 3,
        label: '已公布成绩'
      }],
      page: 1,
      pageSize: 5,
      quizTotal: 0 // 总共多少个考试，分页需要用到的总数
    }
  },
  methods: {
    changeMode () {
      this.mode = this.mode === CARD ? TABLE : CARD
      this.pageSize = this.mode * 5
      this.getQuizList()
    },
    courseChangeHandler (val) {
      this.courseId = val
      this.getQuizList()
    },
    typeChangeHandler (val) {
      this.curType = val
      this.getQuizList()
    },
    statusChangeHandler (val) {
      this.curStatus = val
      this.getQuizList()
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.getQuizList()
    },
    handleCurrentChange (val) {
      this.page = val
      this.getQuizList()
    },
    toAddQuiz () {
      var obj = this.courseDetailList.filter(i => i.courses_id === this.courseId)[0]
      // 将数据存储在缓存中，达到页面传值的作用
      var storage = window.sessionStorage
      storage.removeItem('courseInfo')
      storage.setItem('courseInfo', JSON.stringify(obj))
      this.$router.push({name: 'quizAdd', params: {courseId: this.courseId}})
    },
    toQuizDetail (id) {
      this.$router.push({name: 'quizDetail', params: {id: id}})
    },
    toQuizPreview (id, isResult = false) {
      var params = {
        id: id
      }
      if (this.info.identity === '1' && isResult) params.uid = this.info.uid
      this.$router.push({
        name: 'quizPreview',
        params: params,
        query: {
          redirect: '/personal-center/4'
        }
      })
    },
    toQuizCheck (id) {
      this.$router.push({
        name: 'quizCheck',
        params: {
          id: id
        },
        query: {
          redirect: '/personal-center/4'
        }
      })
    },
    toQuizStart (id) {
      this.$router.push({
        name: 'quizStart',
        params: {
          id: id
        },
        query: {
          redirect: '/personal-center/4'
        }
      })
    },
    getListInfo () {
      const _this = this
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json !== 'undefined' && json !== null && json !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
        api.getInfoList('/users/detail', uid).then(function (res) {
          _this.info = res.data.user_info
        })
      }
    },
    toEditQuiz (id) {
      this.$confirm('此操作将下架该考试, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 调用接口，下架考试
        api.soldOutQuiz('/quiz/sold_out', id).then(res => {
          if (res.success === 'true') {
            // 调转至考试添加页
            this.$router.push({name: 'quizAdd', params: {courseId: this.courseId, id: id}})
          } else {
            this.$message({
              type: 'warning',
              message: '进入编辑失败，请稍后再试',
              duration: 1500
            })
          }
        })
      })
    },
    toDelQuiz (id) {
      this.$confirm('此操作将永久删除该考试, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        api.deleteQuiz('/quiz', id).then(res => {
          if (res.success === 'true') {
            this.$message({
              type: 'success',
              message: '删除成功!',
              duration: 1500
            })
            this.getQuizList()
          }
        })
      })
    },
    toBeginQuiz (item) {
      var timeFormat = function (date) {
        var d = new Date(date)
        return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
      }
      this.$confirm('此操作将开启该考试, 且不能中途取消，是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        api.manualBeginQuiz('/quiz/startway', item.quiz_id).then(res => {
          if (res.success === 'true') {
            this.$message({
              message: '您已成功手动开启考试！',
              type: 'success',
              showClose: true,
              duration: 1500
            })
            item.start_time = timeFormat(new Date())
            item.status = 1
          } else {
            this.$message({
              message: '您手动开启考试失败！请稍后再试！',
              type: 'error',
              showClose: true,
              duration: 1500
            })
          }
        })
      })
    },
    getCourseId () {
      if (!this.$route.query.courseId) {
        return
      }
      var storage = window.sessionStorage
      var jsonData = JSON.parse(storage.getItem('courseInfo'))
      if (jsonData === 'undefined' || jsonData == null) {
        this.$router.push({path: '/personal-center/1'})
        return
      }
      this.courseName = jsonData.courses_name
      this.courseId = parseInt(this.$route.query.courseId)
    },
    getQuizList () {
      this.isLoadingList = true
      api.getQuizList('/quiz/list', {
        courses_id: this.courseId,
        quiz_type: this.curType,
        status: this.curStatus,
        page: this.page,
        pagesize: this.pageSize
      }).then((res) => {
        if (res.success === 'false') {
          this.$message({
            message: '获取列表失败！请稍后再试！',
            type: 'error',
            showClose: true,
            duration: 1500
          })
          this.isLoadingList = false
          return
        }
        this.quiz_list = res.data.quiz_list.map(i => {
          if (i.start_time == null || i.status >= 2) return i
          // 处理时间
          var start = new Date(i.start_time).getTime()
          var now = new Date().getTime()
          i.status = now >= start + i.duration * 60 * 1000 ? 2 : now >= start ? 1 : 0
          return i
        })
        this.quizTotal = res.data.total
        this.isLoadingList = false
      })
    },
    getItemHtml (item) {
      // 获取班级列表整合
      var className = ''
      item.className_list.forEach((i, idx) => {
        className += i
        if (idx !== item.className_list.length - 1) {
          className += '、'
        }
      })
      // 加工输入内容
      var output = `<p>1) 本次考试属于<span>${item.course_name}</span>课程，` +
        `将作为<span>${className}</span>的<span>${this.getQuizType(item.type)}</span>，` +
        `仅限用<span>${this.getQuizLang(item.language)}</span>作为答题语言。</p>`
      // 判断是否手动开考
      output += `<p>2) `
      if (item.start_time == null) {
        output += `正在等待出卷教师手动开启考试，`
      } else {
        output += {
          0: `考试<span>${this.getQuizStartTime(item.start_time, true)}</span>，`,
          1: `考试<span>${this.getQuizStartTime(item.start_time, false)}</span>，<span>正在进行中。</span>`,
          2: `考试<span>${this.getQuizStartTime(item.start_time, false)}</span>，现已结束，<span>正在等待教师批阅。</span>`,
          3: `考试<span>${this.getQuizStartTime(item.start_time, false)}</span>，现已结束并<span>已公布成绩</span>。`
        }[item.status]
      }
      output += `考试<span>时长为${item.duration}分钟</span>，`
      // 判断是否允许提前交卷
      if (item.advance === 0) {
        output += `<span>过程中不允许提前交卷。</span>`
      } else {
        output += `<span>允许在考试结束前${item.advance}分钟内提前交卷</span>。`
      }
      output += `</p>`
      return output
    },
    getQuizStartTime (time, isNotBegin) {
      return isNotBegin ? `将在${time.replace('T', ' ')}准时开考` : `已在${time.replace('T', ' ')}开始`
    },
    getQuizType (type) {
      return {
        1: '平时考试',
        2: '期中考试',
        3: '期末考试'
      }[type]
    },
    getQuizLang (lang) {
      return {
        0: 'C语言',
        1: 'C++语言',
        3: 'Java语言',
        6: 'Python语言'
      }[lang]
    },
    getRemindType (type) {
      return {
        1: '不提醒',
        2: '简洁提醒',
        3: '详细提醒'
      }[type]
    },
    initCourseList () {
      api.getCourseList('/courses').then((res) => {
        if (res.success === 'false') return
        this.courseDetailList = res.data
        res.data.forEach(i => {
          this.courseList.push({
            value: i.courses_id,
            label: i.courses_name
          })
        })
      })
    }
  },
  created () {
    this.getListInfo()
    this.getCourseId()
    this.getQuizList()
    this.initCourseList()
  }
}
</script>

<style scoped>
  .myQuiz {
    min-height: 700px;
    background-color: white;
    padding: 20px 20px;
  }
  .myQuiz-header{
    padding-bottom: 5px;
    border-bottom: 1px solid gainsboro;
    line-height: 40px;
    font-size: 20px;
  }
  .btn-publish,
  .btn-changeMode {
    position: relative;
    top: -2px;
  }
  .el-button + .el-button {
     margin-left: 0px;
  }
  .btn-publish:hover{
    background-color: #d48118;
  }
  .myQuiz-header_className {
    font-size: 12px;
    cursor: default;
  }
  .myQuiz-header_select {
    display: inline-block;
    float: right;
  }
  .myQuiz-body {
    height: 560px;
    overflow-y: auto;
    padding: 10px 5px;
  }
  .myQuiz-body .myQuiz-item {
    width: 100%;
    background-color: rgba(240, 240, 240, 0.2);
    border-radius: 10px;
    box-shadow: -2px 5px 5px rgba(0, 0, 0, 0.1);
    position: relative;
    top: 0px;
    margin: 10px 0;
    overflow: hidden;
    transition: all .3s;
    position: relative;
  }
  .myQuiz-body .myQuiz-item:hover {
    background-color: rgba(240, 240, 240, 0.5);
    top: -3px;
  }
  .myQuiz-body .myQuiz-item .no {
    color: rgb(204, 204, 204);
    position: absolute;
    margin-left: 10px;
    line-height: 2;
  }
  /*考试标题样式*/
  .myQuiz-item-header {
    font-size: 13px;
    line-height: 2;
    border-bottom: 1px solid rgba(230, 230, 230, 1);
    text-align: center;
  }
  /*考试内容块样式设置*/
  .myQuiz-item-content {
    padding: 5px 15px 0 15px;
    margin-right: 55px;
  }
  .myQuiz-item-content >>> p {
    text-indent: 2em;
    font-size: 12px;
    line-height: 2;
    cursor: default;
  }
  .myQuiz-item-content:hover >>> p span {
    transition: all .5s;
    /*font-weight: 600;*/
    color: #e03232;
  }
  /*底部按钮样式设置*/
  .myQuiz-item-operBtns {
    text-align: right;
    margin-right: 10px;
    /*border-top: 1px solid rgba(230, 230, 230, 1);*/
  }
  .myQuiz-item-operBtns>.btn {
    display: inline-block;
    font-size: 12px;
    line-height: 2;
    color: rgb(178, 178, 178);
    margin: 0 8px;
    cursor: pointer;
    transition: all .3s;
  }
  .myQuiz-item-operBtns>.btn i{
    margin-right: 5px;
  }
  .myQuiz-item-operBtns>.btn:hover {
    color: rgb(244, 129, 24) !important;
  }
  /*分数样式*/
  .myQuiz-item-userScore {
    font-size: 20px;
    width: 3em;
    line-height: 3;
    color: #e03232;
    white-space: nowrap;
    text-align: center;
    border-radius: 2em;
    border: 1px dotted #e03232;
    transform: rotate(-20deg);
    /*box-shadow: -2px 2px 15px rgba(0, 0, 0, .05);*/
    position: absolute;
    top: 10px;
    right: 10px;
    transition: all .3s;
  }
  .myQuiz-item:hover .myQuiz-item-userScore {
    transform: translateY(-3px);
  }
  /*无更多提醒*/
  .noList {
    line-height: 3em;
    display: block;
    font-weight: 300;
    font-size: 12px;
    color: #AEAEAE;
    text-align: center;
    cursor: default;
  }
  /*底部页码*/
  .myQuizListPage{
    padding-top: 20px;
  }
  .myQuizListPage /deep/ .el-pagination .el-pager .active{
    background-color: #f48118 !important;
  }
  .myQuizListPage /deep/ .el-pagination .el-pagination__total{
    color: inherit;
  }
  .myQuizListPage /deep/ .el-pagination .el-pagination__jump{
    color: inherit;
  }
</style>



