<template>
  <div class="competitionRank">
    <div class="rank-head">
      <el-row>
        <el-col :span="12">
          <el-button type="primary" icon="el-icon-arrow-left" class="back" @click="back">返回</el-button>
          <span class="rank-context">{{tableData.title}}排行榜</span>
        </el-col>
        <!--<el-col :span="6" :offset="11">-->
          <!--<el-input size="small" placeholder="用户昵称/姓名搜索" class="input-with-select" >-->
            <!--<el-button scope="append" icon="el-icon-search"></el-button>-->
          <!--</el-input>-->
        <!--</el-col>-->
        <el-button id="refresh" type="primary" plain @click="refresh">刷新</el-button>
      </el-row>
    </div>
    <div class="rank-table">
      <el-table
        :data="ftableData"
        style="width: 100%"
        stripe
        v-loading="tableLoading"
      >
        <el-table-column
          fixed
          label="名次"
          align="center"
          >
          <template slot-scope="scope">
            <span v-if="scope.row.ranking == 1" style="color: rgb(248, 208, 28);"><i class="icon-diyi"></i></span>
            <span v-else-if="scope.row.ranking == 2" style="color: rgb(190, 197, 204);"><i class="icon-dier"></i></span>
            <span v-else-if="scope.row.ranking == 3" style="color: rgb(208, 153, 106);"><i class="icon-disan"></i></span>
            <span v-else>{{scope.row.ranking}}</span>
          </template>
        </el-table-column>
        <el-table-column
          fixed
          label="昵称"
          align="center"
          prop="nick">
        </el-table-column>
        <el-table-column
          fixed
          label="解决"
          align="center">
          <template slot-scope="scope">
            <span>{{scope.row.accept_num}}</span>
          </template>
        </el-table-column>
        <el-table-column
          fixed
          label="提交"
          align="center"
          prop="submit_num">
        </el-table-column>
        <el-table-column
          fixed
          label="通过率"
          align="center"
          prop="rate">
        </el-table-column>
        <el-table-column v-for="(item,index) in ftableData[0].reply" :key="index" :label="item.number.toString()" align="center">
          <template slot-scope="scope">
            <div :class="{greenback:scope.row.reply[index].reply,redback:!scope.row.reply[index].reply}">{{scope.row.submit[index].submit}}</div>
          </template>
        </el-table-column>
        <el-table-column
          fixed="right"
          label="总用时"
          align="center">
          <template slot-scope="scope">
            <span>{{scope.row.all_time}}ms</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="competition-pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="rankTotal">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import api from '@/api/contestsRank'

export default{
  data () {
    return {
      // 用来记录弹框的数据
      List: [
        {
          one: '1',
          two: '',
          tree: '',
          four: '',
          five: '',
          six: '',
          seven: '',
          eight: '',
          nine: '',
          ten: ''
        }
      ],
      // 用来显示弹框
      isShow: false,
      tableData: '',
      problemlist: [],
      ftableData: [{
        reply: []
      }],
      pagesize: 10,
      page: 1,
      rankTotal: 50,
      tableLoading: false
    }
  },
  methods: {
    // 返回上一级
    back () {
      const _this = this
      _this.tableData.back = true
      this.$router.push({
        path: '/test',
        name: 'test',
        params: {
          dataObj: _this.tableData
        }
      })
    },
    refresh () {
      if (typeof (this.tableData) !== 'undefined' && this.tableData !== '') {
        this.getContestRank(this.tableData.contest_id, this.page, this.pagesize)
      }
    },
    getParams () {
      // 取到路由带过来的参数
      let routerParams = this.$route.params.dataObj
      if (this.$route.params.dataObj === undefined) {
        this.$router.push({path: '/competition'})
        return
      }
      // 将数据放在当前组件的数据内
      this.problemlist = routerParams.problem_list
      if (!this.problemlist) {
        this.problemlist = this.$route.params.problemList
      }
      this.tableData = routerParams
    },
    handleSizeChange (val) {
      this.pagesize = val
      this.getContestRank(this.tableData.contest_id, this.page, val)
    },
    handleCurrentChange (val) {
      this.page = val
      this.getContestRank(this.tableData.contest_id, val, this.pagesize)
    },
    getContestRank (contestId = this.tableData.contest_id, page = 1, pagesize = 10) {
      const _this = this
      _this.tableLoading = true
      api.getcontentsRank('/contests/rank', contestId, page, pagesize).then(function (res) {
        _this.ftableData = res.data
        _this.rankTotal = res.total
        for (var i = 0; i < res.data.length; i++) {
          _this.ftableData[i].rate = (res.data[i].accept_num / res.data[i].submit_num * 100).toFixed(2) + '%'
          _this.ftableData[i].ranking = pagesize * (page - 1) + i + 1
        }
        _this.setReply()
        _this.tableLoading = false
      })
    },
    // 初始化答题情况
    setReply () {
      if (!this.problemlist) return
      let data = this.ftableData
      for (let i = 0; i < data.length; i++) {
        let replylist = data[i].finish.split('')
        let submitlist = data[i].submit.split(',')
        data[i]['reply'] = []
        data[i]['submit'] = []
        for (let j = 0; j < this.problemlist.length; j++) {
          data[i]['reply'].push({
            number: this.problemlist[j].problem_id,
            reply: replylist[j] === '1'
          })
          data[i]['submit'].push({
            number: this.problemlist[j].problem_id,
            submit: submitlist[j]
          })
        }
      }
    }
  },
  created () {
    this.getParams()
    this.getContestRank()
  }
}
</script>

<style scoped>
  .greenback,
  .redback{
    text-align: center;
    border-radius: 3px;
    height: 25px;
    line-height: 25px;
    width: 25px;
    color: #FFFFFF;
    font-weight:800;
    position:relative;
    left: 16px;
  }
  .greenback {
    background-color: rgba(142, 204, 69, .8);
  }
  .redback {
    background-color: rgba(255, 153, 0, .8);
  }
  .competitionRank{
    width: 1170px;
    margin: 0 auto;
    padding-top: 20px;
  }
  .back{
    padding: 5px 10px;
  }
  .rank-head{
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  .rank-context{
    line-height: 34px;
    color: #ff6c3f;
    margin-left: 15px;
  }
  .rank-table{
    margin-top: 20px;
    min-height: 700px;
  }
  #refresh{
    float:right;
  }
  .competition-pagination {
    padding: 20px 0;
  }
  .competition-pagination /deep/ .el-pagination .el-pager .active{
    background-color: #f48118 !important;
  }
  .competition-pagination /deep/ .el-pagination .el-pagination__total{
    color: inherit;
  }
  .competition-pagination /deep/ .el-pagination .el-pagination__jump{
    color: inherit;
  }
  .reply-list {
    width: 100%;
    display: flex;
    justify-content: space-around;
    text-align: center;
  }
  .reply-item {
    display: inline-block;
  }
  .reply-problem-id {
    margin-bottom: 10px;
  }
  .reply-status {}
</style>



