<template>
  <transition name="fade">
  <div class="rangeChoose">
    <div class="rangeChoose_container">
      <div :class="{'rangeChoose_choosedProblems': true, 'rangeChoose_choosedProblems_showRight': chooseType === 1}">
        <ul>
          <li v-for="item in hasChoosedProblemDetail" :key="item.problem_id">
            <span style="color: #ff9900; font-weight: 600;">{{item.problem_id}}</span>&nbsp;
            <span>{{item.title}}</span>
            <span class="btn_close" @click="problemClickHandler(item)"><i class="icon-chushaixuanxiang"></i></span>
          </li>
        </ul>
      </div>
      <div :class="{'rangeChoose_container_mid': true, 'rangeChoose_container_mid_showRight': chooseType === 1}">
        <div class="rangeChoose_base">
          <div class="rangeChoose_backBtn" @click="back()">
            <quiz-btn icon="icon-chushaixuanxiang" radius="50%" size="20px" theme="BACK" width="30px" height="30px"></quiz-btn>
          </div>
          <quiz-panel title="范围设置" padding="10px 10px" height="100%">
            <div v-loading="!ifHadInitData">
              <div class="rangeChoose_treeTags public-scrollStyle">
                <el-input
                  size="mini"
                  placeholder="输入关键字进行过滤"
                  v-model="filterText">
                </el-input>
                <el-tree
                  :data="tagsTreeData"
                  show-checkbox
                  accordion
                  node-key="id"
                  ref="tagTree"
                  :props="defaultProps"
                  :check-on-click-node="true"
                  :highlight-current="true"
                  :filter-node-method="filterTagNode"
                  @check-change="tagsChangeHandler">
                </el-tree>
              </div>
            </div>
            <div class="rangeChoose_tips">{{getTypeName}}&nbsp;·&nbsp;考题范围如下</div>
            <div class="rangeChoose_showSelected public-scrollStyle">
              <span v-for="(item, index) in hasChoosedTagsLabel" :key="index">
                {{item}}<span v-if="index !== hasChoosedTagsLabel.length - 1">、</span>
              </span>
              <span class="rangeChoose_showSelected_none" v-if="hasChoosedTagsLabel.length === 0">默认全部范围</span>
            </div>
            <div class="rangeChoose_operBtns">
              <div class="rangeChoose_operBtn" @click="showProblemDetail()">
                <quiz-btn icon="content-自主选择试题" size="16px" radius="25px" :theme="chooseType === 1 ? 'SURE': 'DEFAULT'" width="120px" height="50px"></quiz-btn>
              </div>
              <div class="rangeChoose_operBtn" @click="getAutoAddProblem()">
                <quiz-btn icon="content-随机生成试题" size="16px" radius="25px" :theme="chooseType === 0 ? 'SURE': 'DEFAULT'" width="120px" height="50px"></quiz-btn>
              </div>
            </div>
          </quiz-panel>
        </div>
        <div class="rangeChoose_problems">
          <quiz-panel title="自主选择试题" padding="10px 10px" height="100%" :isHidden="false">
            <div class="problems_detail">
              <div class="problems_detail_table public-scrollStyle">
                <table cellspacing="0">
                  <tr>
                    <th>PID</th>
                    <th>题目标签</th>
                    <th>题目标题</th>
                    <th>是否选择</th>
                  </tr>
                  <tr v-for="item in problemList" :key="item.problem_id">
                    <td>{{item.problem_id}}</td>
                    <td>
                      <span v-for="(tag, idx) in item.label" :key="idx">{{tag}}
                        <span v-if="idx !== item.label.length-1">、</span><wbr></span>
                    </td>
                    <td>{{item.title}}</td>
                    <td>
                      <span :class="{
                        'table_btn': true,
                        'table_btn_isChoosed': hasChoosedProblemValue.indexOf(item.problem_id) > -1}"
                        @click="problemClickHandler(item)"></span>
                    </td>
                  </tr>
                </table>
                <span class="noList" v-if="problemList.length === 0">该范围暂无考试题，请联系后台添加...</span>
              </div>
              <div class="public-pagination" v-if="total > pagesize">
                <el-pagination
                  @current-change="handleCurrentChange"
                  background
                  layout="prev, pager, next"
                  :current-page="page"
                  :page-size="pagesize"
                  :pager-count="5"
                  :total="total">
                </el-pagination>
              </div>
            </div>
          </quiz-panel>
        </div>
      </div>
    </div>
  </div>
  </transition>
</template>

<script>
import quizPanel from '@/base/quiz/quiz-panel'
import quizBtn from '@/base/quiz/quiz-btn'
import api from '@/api/quiz'
export default {
  name: 'rangeChoose',
  components: {
    quizPanel,
    quizBtn
  },
  computed: {
    getTypeName () {
      var typeNameList = {
        0: '题目',
        1: '填空题',
        2: '选择题',
        3: '编程题',
        4: '问答题',
        5: '判断题'
      }
      return typeNameList[this.problemType]
    }
  },
  props: {
    problemType: {
      type: Number,
      default: 0
    },
    needAmount: {
      type: Number,
      default: 0
    },
    rangeData: {
      type: Object,
      default: function () {
        return null
      }
    }
  },
  data () {
    return {
      ifHadInitData: false,
      filterText: '',
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      tagValToLabelList: [],
      tagsTreeData: [], // 标签树
      hasChoosedTagsValue: [], // 被选择的标签id
      hasChoosedTagsLabel: [], // 被选择的标签名
      hasChoosedProblemValue: [], // 被选中的题目id列表
      hasChoosedProblemDetail: [], // 被选中的题目详细内容列表
      chooseType: 0, // 题目选择类型，0-随机选择，1-自主选择
      problemList: [],
      page: 1,
      pagesize: 10,
      total: 0
    }
  },
  methods: {
    problemClickHandler (item) {
      var id = item.problem_id
      var idx = this.hasChoosedProblemValue.indexOf(id)
      if (idx > -1) {
        this.hasChoosedProblemDetail.splice(idx, 1)
        this.hasChoosedProblemValue.splice(idx, 1)
      } else {
        if (this.hasChoosedProblemValue.length === this.needAmount) {
          this.$message({
            message: `您设置了${this.getTypeName}的最大数量为${this.needAmount}道`,
            type: 'warning'
          })
          return
        }
        this.hasChoosedProblemDetail.push(item)
        this.hasChoosedProblemValue.push(id)
      }
    },
    handleCurrentChange (val) {
      this.page = val
      this.showProblemDetail()
    },
    back () {
      this.$emit('back', {
        type: this.problemType,
        tagsValue: this.hasChoosedTagsValue,
        tagsLabel: this.hasChoosedTagsLabel,
        problemValue: this.hasChoosedProblemValue,
        problemDetail: this.hasChoosedProblemDetail
      })
    },
    showProblemDetail () {
      const _this = this
      api.getQuizProblemList('/quiz/problems/find_by_tags_type', {
        type: _this.problemType,
        range: [..._this.hasChoosedTagsValue],
        page: _this.page,
        pagesize: _this.pagesize
      }).then(function (res) {
        _this.problemList = res.data.problem_list
        _this.total = res.data.total
        _this.ifHadInitData = true
      })
      this.chooseType = 1
    },
    // 自动补全题目
    getAutoAddProblem () {
      this.chooseType = 0
      if (this.needAmount === 0) {
        this.$message({
          message: `请设置题目数量，当前设置为0。`,
          type: 'warning'
        })
        return
      }
      if (this.hasChoosedProblemValue.length === this.needAmount) return
      var obj = {
        problemSet_list: [{
          need_amount: this.needAmount - this.hasChoosedProblemValue.length,
          type: this.problemType,
          range: this.hasChoosedTagsValue,
          ques_list: this.hasChoosedProblemValue
        }]
      }
      api.autoAddProblem('/quiz/auto_add', obj).then((res) => {
        res.data.detail[0].ques_list.forEach(i => {
          this.hasChoosedProblemValue.push(i.problem_id)
          this.hasChoosedProblemDetail.push(i)
        })
        if (res.data.detail[0].ques_list.length < this.needAmount - this.hasChoosedProblemValue.length) {
          this.$message({
            message: `后台数据库并未有足够数量的题目！请联系添加！`,
            type: 'warning'
          })
        }
      })
    },
    tagsChangeHandler () {
      var choosedNodeList = this.$refs.tagTree.getCheckedNodes()
      this.hasChoosedTagsValue = []
      this.hasChoosedTagsLabel = []
      choosedNodeList.forEach(i => {
        this.hasChoosedTagsValue.push(i.id)
        this.hasChoosedTagsLabel.push(i.label)
      })
      // 如果打开自选则跟着刷新题目
      if (this.chooseType === 1) {
        this.ifHadInitData = false
        this.showProblemDetail()
      }
      // 筛选题目，如果选择范围为空，则全范围，无需筛选
      if (this.hasChoosedTagsLabel.length === 0) return
      this.hasChoosedProblemDetail = this.hasChoosedProblemDetail.filter(i => {
        for (let j = 0; j < this.hasChoosedTagsLabel.length; j++) {
          if (i.label.indexOf(this.hasChoosedTagsLabel[j]) >= 0) {
            // console.log(i.problem_id + '在范围内可留下')
            return i
          }
        }
        // console.log(i.problem_id + '不在范围内不可留下')
        var removeId = this.hasChoosedProblemValue.indexOf(i.problem_id)
        this.hasChoosedProblemValue.splice(removeId, 1)
      })
    },
    filterTagNode (value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    * iterEntries (obj) {
      let keys = Object.keys(obj)
      for (let i = 0; i < keys.length; i++) {
        let key = keys[i]
        yield [key, obj[key]]
      }
    },
    formatTreeData (root, tree) {
      for (let [key, value] of this.iterEntries(tree)) {
        var node = {
          id: +key,
          label: this.tagValToLabelList[key],
          children: []
        }
        this.formatTreeData(node.children, value)
        root.push(node)
      }
    },
    copyPropsData () {
      this.hasChoosedTagsValue = [...this.rangeData.range]
      this.hasChoosedTagsLabel = [...this.rangeData.rangeLabelList]
      this.hasChoosedProblemValue = [...this.rangeData.problemList]
      this.hasChoosedProblemDetail = [...this.rangeData.problemDetail_list]
    },
    getTagsTree () {
      const _this = this
      api.getQuestionList('/tags').then(function (res) {
        _this.tagValToLabelList = res.data.tag_dict
        _this.formatTreeData(_this.tagsTreeData, res.data.tag_tree)
        // 必须延迟等等渲染完毕
        setTimeout(() => {
          _this.$refs.tagTree.setCheckedKeys(_this.hasChoosedTagsValue)
          _this.ifHadInitData = true
        }, 500)
      })
    }
  },
  mounted () {
    this.copyPropsData()
  },
  created () {
    this.getTagsTree()
  },
  watch: {
    filterText (val) {
      this.$refs.tagTree.filter(val)
    }
  }
}
</script>

<style scoped>
  .fade-enter-active, .fade-leave-active {
    transition: all .5s;
  }

  .fade-enter, .fade-leave-to {
    opacity: 0;
  }

  .rangeChoose {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.60);
    z-index: 100;
  }
  .rangeChoose_backBtn {
    position: absolute;
    z-index: 101;
    top: -13px;
    left: -13px;
  }
  .rangeChoose_container {
    width: 800px;
    height: 500px;
    margin: 120px auto 0 auto;
    position: relative;
  }
  .rangeChoose_container_mid {
    width: 300px;
    height: 500px;
    position: relative;
    left: calc(50% - 150px);
    display: flex;
    justify-content: space-around;
    transition: all .8s;
  }
  .rangeChoose_container_mid_showRight {
    width: 800px;
    left: calc(50% - 400px);
  }
  .rangeChoose_base {
    flex: 0 0 auto;
    width: 300px;
    position: relative;
  }
  .rangeChoose_showSelected {
    margin: 0 10px;
    background-color: rgba(242, 242, 242, 1);
    border-radius: 3px;
  }
  .rangeChoose_showSelected_none {
    display: block;
    text-align: center;
    color: #8b8b8b;
    cursor: default;
  }
  .rangeChoose_treeTags {
    height: 250px;
    overflow: auto;
    background-color: #fff;
    border: 1px solid rgba(242, 242, 242, 1);
    margin: 20px 10px 5px 10px;
    border-radius: 3px;
    padding: 3px;
  }
  .rangeChoose_treeTags >>> .el-tree {
    background-color: transparent !important;
  }
  .rangeChoose_showSelected {
    padding: 10px;
    font-size: 12px;
    line-height: 1.8;
    height: 100px;
    overflow-y: auto;
  }
  .rangeChoose_tips {
    text-align: center;
    font-size: 16px;
    color: #a4a4a4;;
    line-height: 2em;
  }
  .rangeChoose_operBtns {
    margin-top: 10px;
    display: flex;
    justify-content: space-around;
  }
  .rangeChoose_operBtn {
    text-align: center;
  }
  .rangeChoose_problems {
    width: 500px;
    margin-left: 10px;
    overflow: hidden;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
  }
  .rangeChoose_problems .problems_detail {
    width: 450px;
    height: 400px;
    padding: 10px 0 10px 10px;
    margin: 0 auto;
  }
  .public-pagination {
    margin-top: 15px;
  }
  /*成绩单表格样式设置*/
  .problems_detail_table {
    height: 400px;
    overflow-y: auto;
    /*border-bottom: 3px solid rgb(242, 242, 242);*/
  }
  .problems_detail_table table{
    width: 100%;
  }
  .problems_detail_table table>tr:nth-child(even) {
    background-color: rgb(242, 242, 242);
  }
  .problems_detail_table table>tr th,
  .problems_detail_table table>tr td {
    line-height: 3;
    text-align: center;
  }
  .problems_detail_table table>tr th {
    color: rgb(102, 102, 102);
  }
  .problems_detail_table table>tr td {
    font-size: 13px;
    width: 80px;
  }
  .problems_detail_table table>tr td:first-child {
    width: 40px;
    height: 3em;
  }
  .problems_detail_table table>tr td:last-child {
    width: 45px;
  }
  .problems_detail_table table .table_btn {
    width: 46px;
    height: 21px;
    margin: 0 auto;
    border-radius: 10px;
    display: block;
    background-color: #ededed;
    border: 1px solid transparent;
    cursor: pointer;
    transition: all .3s;
  }
  .problems_detail_table table .table_btn::after {
    content: '';
    display: block;
    width: 19px;
    height: 19px;
    background-color: rgb(102, 102, 102);
    border-radius: 10px;
    transition: all .3s;
    position: relative;
    left: 0px;
  }
  .problems_detail_table table .table_btn_isChoosed {
    background-color: #87c141;
  }
  .problems_detail_table table .table_btn_isChoosed::after {
    background-color: #fff;
    left: 25px;
  }
  /*展示已选题目*/
  .rangeChoose_choosedProblems {
    width: 180px;
    height: 460px;
    overflow: hidden;
    position: absolute;
    top: 20px;
    left: calc(50% + 135px);
    transition: all .8s;
  }
  .rangeChoose_choosedProblems ul{
    width: 200px;
    max-height: 460px;
    overflow-y: scroll;
  }
  .rangeChoose_choosedProblems_showRight{
    left: calc(50% + 390px);
  }
  .rangeChoose_choosedProblems ul>li {
    font-size: 10px;
    margin: 10px;
    padding: 0 12px 0 35px;
    line-height: 2;
    background-color: #fff;
    border-radius: 0 5px 5px 0;
    cursor: default;
    position: relative;
  }
  .rangeChoose_choosedProblems ul>li span:first-child {
    position: absolute;
    left: 5px;
  }
  .rangeChoose_choosedProblems .btn_close {
    color: #e03232;
    cursor: pointer;
    position: absolute;
    right: 3px;
    top: 0px;
    display: none;
  }
  .rangeChoose_choosedProblems ul>li:hover .btn_close{
    display: inline-block;
  }
</style>



