import {getRequest} from './config'

function getConpList (link, page, pagesize, contestId, type, state) {
  return getRequest(link, {
    page: page,
    pagesize: pagesize,
    contest_id: contestId,
    type: type,
    state: state
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}
function getContestsDetail (link) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}
const api = {
  getConpList, getContestsDetail
}

export default api



