        dayData: {
          type: '本周',
          nextType: '本月',
          childType: '日',
          xAxis: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
          accepted_list: [],
          submitted_list: [],
          note: ''
        },
        weekData: {
