          nextType: '本年',
          childType: '周',
          xAxis: ['第一周', '第二周', '第三周', '第四周', '第五周'],
          accepted_list: [],
          submitted_list: [],
          note: ''
        },
        monthData: {
          type: '本年',
          nextType: '本日',
          childType: '月',
          xAxis: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
          accepted_list: [],
          submitted_list: [],
          note: ''

        }
      },
      echartsStats: {
        type: '',
        nextType: '',
        xAxis: [],
        accepted_list: [],
        submitted_list: [],
        note: ''
      },
      flagDate: new Date(),
      Note: '',
      loading: true
    }
  },
  methods: {
    updateChart () {
      this.charts.setOption({
        title: {
          text: '数据统计',
          subtext: (this.echartsStats.note === '' || typeof (this.echartsStats.note) === 'undefined') ? '' : '统计范围：<' + this.echartsStats.note + '>'
        },
        legend: {
          data: ['通过总数', '递交总数']
        },
        xAxis: {
          data: this.echartsStats.xAxis
        },
        series: [{
          name: '通过总数',
          data: this.echartsStats.accepted_list
        }, {
          name: '递交总数',
          data: this.echartsStats.submitted_list
        }]
      })
    },
    clearChart () {
      this.echartsStats.note = ''
      this.charts.setOption({
        title: {
          text: '',
          subtext: ''
        },
        legend: {
          data: []
        },
        xAxis: {
          data: []
        },
        series: [{
          name: '通过总数',
          data: []
        }, {
          name: '递交总数',
          data: []
        }]
      })
    },
    drawPie (id) {
      const _this = this
      this.charts = echarts.init(document.getElementById(id))
      var option = {
        title: {
          text: '数据统计',
          subtext: (_this.echartsStats.note === '' || typeof (_this.echartsStats.note) === 'undefined') ? '' : '统计范围：<' + _this.echartsStats.note + '>'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['通过总数', '递交总数']
        },
        toolbox: {
          show: true,
          feature: {
            myTool2: {
              show: true,
              title: '回到现在',
              icon: 'path://M186.368 800.768c-65.536-81.92-102.4-182.272-102.4-288.768 0-253.952 206.848-460.8 460.8-460.8s460.8 206.848 460.8 460.8-206.848 460.8-460.8 460.8c-10.24 0-20.48-8.192-20.48-20.48 0-10.24 8.192-20.48 20.48-20.48 231.424 0 419.84-188.416 419.84-421.888s-188.416-421.888-419.84-421.888-419.84 188.416-419.84 421.888c0 98.304 32.768 190.464 94.208 264.192v-151.552c0-10.24 8.192-20.48 20.48-20.48s20.48 8.192 20.48 20.48v196.608c0 6.144-2.048 10.24-6.144 14.336-4.096 4.096-8.192 6.144-14.336 6.144h-196.608c-10.24 0-20.48-8.192-20.48-20.48 0-10.24 8.192-20.48 20.48-20.48h143.36z',
              onclick: function () {
                _this.flagDate = new Date()
                _this.clearChart()
                _this.getPeriodList('本日', false)
              }
            },
            myTool3: {
              show: true,
              title: '切换为日范围',
              icon: 'path d="M12 308 c-14 -14 -17 -275 -4 -296 6 -9 37 -12 118 -10 l109 3 3 149\n' +
                'c2 113 -1 151 -10 158 -21 13 -202 10 -216 -4z m206 -80 l3 -58 -96 0 -95 0 0\n' +
                '60 0 61 93 -3 92 -3 3 -57z m2 -143 l0 -55 -95 0 -95 0 0 55 0 55 95 0 95 0 0\n' +
                '-55z',
              onclick: function () {
                _this.clearChart()
                _this.getPeriodList('本日', false)
              }
            },
            myTool4: {
              show: true,
              title: '切换为周范围',
              icon: 'path d="M17 284 c13 -20 18 -57 22 -153 l6 -126 125 0 125 0 3 130 c4 166 1\n' +
                '175 -43 175 -18 0 -37 -4 -40 -10 -4 -6 7 -10 24 -10 l31 0 0 -77 0 -78 -91 3\n' +
                'c-56 2 -96 -1 -102 -7 -8 -8 1 -11 31 -11 31 0 42 -4 42 -15 0 -10 -10 -15\n' +
                '-29 -15 -17 0 -33 -4 -36 -10 -4 -6 8 -10 28 -10 27 0 36 -5 40 -21 6 -23 27\n' +
                '-17 27 7 0 10 12 14 41 14 24 0 38 4 34 10 -3 6 -22 10 -41 10 -24 0 -34 5\n' +
                '-34 15 0 11 12 15 45 15 l45 0 0 -50 0 -51 -102 3 -103 3 -6 115 c-5 116 -20\n' +
                '170 -46 170 -10 0 -10 -6 4 -26z"/>\n' +
                '<path d="M107 254 c-4 -4 -7 -27 -7 -51 l0 -43 66 0 65 0 -3 48 -3 47 -55 3\n' +
                'c-31 2 -59 0 -63 -4z m103 -49 c0 -23 -4 -25 -45 -25 -41 0 -45 2 -45 25 0 23\n' +
                '4 25 45 25 41 0 45 -2 45 -25z',
              onclick: function () {
                _this.clearChart()
                _this.getPeriodList('本周', false)
              }
            },
            myTool5: {
              show: true,
              title: '切换为月范围',
              icon: 'path d="M10 324 c0 -3 9 -18 20 -33 27 -38 39 -101 40 -197 0 -96 -3 -94 111\n' +
                '-94 122 0 119 -3 119 151 0 72 -5 139 -10 150 -8 15 -22 19 -60 19 -28 0 -50\n' +
                '-4 -50 -10 0 -5 20 -10 45 -10 l45 0 0 -55 0 -55 -88 0 -88 0 -11 43 c-12 44\n' +
                '-47 97 -64 97 -5 0 -9 -3 -9 -6z m260 -184 l0 -30 -90 0 -90 0 0 30 0 30 90 0\n' +
                '90 0 0 -30z m0 -80 l0 -30 -90 0 -90 0 0 30 0 30 90 0 90 0 0 -30z',
              onclick: function () {
                _this.clearChart()
                _this.getPeriodList('本月', false)
              }
            },
            myTool6: {
              show: true,
              title: '切换为年范围',
              icon: 'path d="M166 323 c-3 -3 -6 -26 -6 -50 l0 -43 -64 0 c-35 0 -71 -3 -80 -6\n' +
                '-24 -9 -9 -24 25 -24 27 0 29 -2 29 -40 l0 -40 45 0 c41 0 45 -2 45 -25 0 -22\n' +
                '-4 -25 -32 -25 -26 0 -37 6 -50 30 -17 30 -44 48 -54 37 -3 -3 4 -15 15 -26\n' +
                '11 -11 30 -39 43 -63 15 -30 23 -38 26 -26 3 15 18 18 95 20 100 3 127 21 40\n' +
                '26 -49 3 -53 5 -53 28 0 22 4 24 50 24 28 0 50 5 50 10 0 6 -22 10 -50 10 -49\n' +
                '0 -50 1 -50 30 0 29 1 29 63 32 34 2 62 8 62 13 0 6 -28 11 -62 13 l-63 3 0\n' +
                '44 c0 42 -10 62 -24 48z m-6 -153 c0 -28 -3 -30 -35 -30 -32 0 -35 2 -35 30 0\n' +
                '28 3 30 35 30 32 0 35 -2 35 -30z',
              onclick: function () {
                _this.clearChart()
                _this.getPeriodList('本年', false)
              }
            },
            // restore: {},
            // dataZoom: {
            //   yAxisIndex: 'none'
            // },
            magicType: {type: ['line', 'bar']},
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            formatter: '{value} '
          }
        },
        series: [
          {
            name: '通过总数',
            type: 'line',
            data: [],
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
            markLine: {
              data: [
                {type: 'average', name: '平均值'}
              ]
            }
          },
          {
            name: '递交总数',
            type: 'line',
            data: [],
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
            markLine: {
              data: [
                {type: 'average', name: '平均值'}
              ]
            }
          }
        ]
      }
      this.charts.setOption(option)
      this.clearChart()
      this.charts.on('click', {seriesName: '通过总数'}, function (res) {
        _this.gotoOtherDate(res.dataIndex)
      })
      this.charts.on('click', {seriesName: '递交总数'}, function (res) {
        _this.gotoOtherDate(res.dataIndex)
      })
    },
    gotoOtherDate (dataIndex) {
      if (this.echartsStats.childType !== '' && typeof (this.echartsStats.childType) !== 'undefined') {
        switch (this.echartsStats.childType) {
          case '日':
            this.flagDate.setDate((this.flagDate.getDay() === 0) ? this.flagDate.getDate() - 7 + dataIndex + 1 : this.flagDate.getDate() - this.flagDate.getDay() + dataIndex + 1)
            this.flagDate = new Date(this.flagDate.getFullYear() + '-' + (this.flagDate.getMonth() + 1) + '-' + this.flagDate.getDate() + ' 23:59:59')
            this.flagDate = (this.flagDate > new Date()) ? new Date() : this.flagDate
            this.Note = this.flagDate.getFullYear() + '-' + (this.flagDate.getMonth() + 1) + '-' + this.flagDate.getDate() + '所在当天'
            break
          case '周':
            let whichWeek = Math.ceil((this.flagDate.getDate() + 6 - this.flagDate.getDay()) / 7)
            this.flagDate.setDate(this.flagDate.getDate() - (whichWeek * 7) + (dataIndex + 1) * 7)
            this.flagDate.setDate((this.flagDate.getDay() === 0) ? this.flagDate.getDate() : this.flagDate.getDate() + (7 - this.flagDate.getDay()))
            this.flagDate = (this.flagDate > new Date()) ? new Date() : this.flagDate
            this.Note = this.flagDate.getFullYear() + '-' + (this.flagDate.getMonth() + 1) + '-' + this.flagDate.getDate() + '所在周'
            break
          case '月':
            this.flagDate = new Date(this.flagDate.getFullYear() + '-' + (dataIndex + 1 + 1) + '-1 23:59:59')
            this.flagDate.setDate(this.flagDate.getDate() - 1)
            this.flagDate = (this.flagDate > new Date()) ? new Date() : this.flagDate
            this.Note = this.flagDate.getFullYear() + '-' + (this.flagDate.getMonth() + 1) + '-' + this.flagDate.getDate() + '所在月'
            break
          default:
            this.Note = ''
            break
        }
        this.clearChart()
        this.getPeriodList('本' + this.echartsStats.childType, true)
      }
    },
    Format (date, fmt) {
      var o = {
        'M+': date.getMonth() + 1, // 月份
        'd+': date.getDate(), // 日
        'h+': date.getHours(), // 小时
        'm+': date.getMinutes(), // 分
        's+': date.getSeconds(), // 秒
        'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
        'S': date.getMilliseconds() // 毫秒
      }
      if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
      for (var k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
      }
      return fmt
    },
    getPeriodList (transType, ifEnter) {
      const _this = this
      let paramType = 'hour'
      let now = new Date()
      let d = 0
      switch (transType) {
        case '本周':
          paramType = 'day'
          d = Math.floor((now.getTime() - this.flagDate.getTime()) / (1000 * 60 * 60 * 24))
          if (d > 0 && !ifEnter) {
            if (d > 7) {
              this.flagDate.setDate((this.flagDate.getDay() === 0) ? this.flagDate : this.flagDate.getDate() + (7 - this.flagDate.getDay()))
            } else {
              this.flagDate.setDate((this.flagDate.getDay() === 0) ? this.flagDate : this.flagDate.getDate() + d)
            }
          }
          this.flagDate = (this.flagDate > new Date()) ? new Date() : this.flagDate
          this.Note = this.flagDate.getFullYear() + '-' + (this.flagDate.getMonth() + 1) + '-' + this.flagDate.getDate() + '所在周'
          break
        case '本月':
          paramType = 'week'
          d = Math.ceil((now.getTime() - this.flagDate.getTime()) / ((1000 * 60 * 60 * 24)) * 7)
          if (d > 0 && !ifEnter) {
            let whichWeek = Math.ceil((this.flagDate.getDate() + 6 - this.flagDate.getDay()) / 7)
            if (whichWeek + d > 4) {
              this.flagDate = new Date(this.flagDate.getFullYear() + '-' + (this.flagDate.getMonth() + 1 + 1) + '-1 23:59:59')
              this.flagDate.setDate(this.flagDate.getDate() - 1)
            } else {
              this.flagDate.setDate(this.flagDate.getDate() + d * 7)
            }
          }
          this.flagDate = (this.flagDate > new Date()) ? new Date() : this.flagDate
          this.Note = this.flagDate.getFullYear() + '-' + (this.flagDate.getMonth() + 1) + '-' + this.flagDate.getDate() + '所在月'
          break
        case '本年':
          paramType = 'month'
          d = (now.getFullYear() - this.flagDate.getFullYear()) * 12 + (now.getMonth() - this.flagDate.getMonth())
          if (d > 0 && !ifEnter) {
            if (this.flagDate.getMonth() + 1 + d > 12) {
              this.flagDate = new Date((this.flagDate.getFullYear() + 1) + '-1-1 23:59:59')
              this.flagDate.setDate(this.flagDate.getDate() - 1)
            } else {
              this.flagDate = new Date(this.flagDate.getFullYear() + '-' + (now.getMonth() + 1) + '-1 23:59:59')
            }
          }
          this.flagDate = (this.flagDate > new Date()) ? new Date() : this.flagDate
          this.Note = this.flagDate.getFullYear() + '-' + (this.flagDate.getMonth() + 1) + '-' + this.flagDate.getDate() + '所在年'
          break
        case '本日':
          paramType = 'hour'
          this.flagDate = (this.flagDate > new Date()) ? new Date() : this.flagDate
          this.Note = this.flagDate.getFullYear() + '-' + (this.flagDate.getMonth() + 1) + '-' + this.flagDate.getDate() + '所在日'
          break
        default:
          break
      }
      this.loading = true
      api.getPeriodList('/feedbacks/period', {
        type: paramType,
        date: _this.Format(this.flagDate, 'yyyy-MM-dd hh:mm:ss')
      }).then(function (res) {
        let index = (transType === '本周') ? 'dayData' : (transType === '本月') ? 'weekData' : (transType === '本年') ? 'monthData' : 'hourData'
        _this.echartsStats = _this.initData[index]
        _this.echartsStats.accepted_list = res.data.per_accepted
        _this.echartsStats.submitted_list = res.data.per_submitted
        _this.echartsStats.note = _this.Note
        _this.echartsStats.xAxis.splice(_this.echartsStats.accepted_list.length - 1, _this.echartsStats.accepted_list.xAxis - _this.echartsStats.accepted_list.length)
        _this.updateChart()
        _this.loading = false
      })
    }
  },
  mounted () {
    this.$nextTick(function () {
      this.drawPie('statsMain')
    })
  },
  created () {
    this.getPeriodList('本日', false)
  }
}
</script>

