<template>
    <div id="shareSolve">
      <div class="container">
        <div class="header">
          <span class="problem-no">{{questionData.id}}</span>
          <span class="title">{{questionData.title}}</span>
          <button class="btn-back" @click="toBack">返回</button>
          <button class="btn-share" @click="toShare">立即分享</button>
        </div>
        <div class="main">
          <div class="left-code">
            <div class="code editorShow" v-html="highlight('<pre><code>'+questionData.code+'</code></pre>', 17)"></div>
            <div class="langBox" @mouseover="ifShowChoose = true" @mouseout="ifShowChoose = false">
              <transition-group name="choose-fade" tag="p">
                <span
                  @click="toChangeLang(item.value, item.index)"
                  v-for="(item, index) in hadALangueges"
                  v-if="index === hadALangueges.length-1 || ifShowChoose"
                  :key="index">
                  {{item.title}}
                </span>
              </transition-group>
            </div>
          </div>
          <div class="right-content">
            <h2>解题思路陈述</h2>
            <div class="editor">
              <wangEditor
                :catchData="catchData"
                :content="initContent"
                :height="'456px'"
                :editorType="4"
                :editorTip="'在这里你可以写入你答题的思路，当然，你也可以一字不写。'"
                :ifLimited="false"></wangEditor>
            </div>
          </div>
        </div>
        <div class="footer">
        </div>
      </div>
    </div>
</template>

<script>
import wangEditor from '@/base/wangeditor/wangeditor'
import api from '@/api/questionDetail'
import prism from 'prismjs'

export default {
  name: 'shareSolve',
  components: {
    wangEditor
  },
  props: {
    questionData: {
      type: Object
    }
  },
  data () {
    return {
      ifShowChoose: false,
      editorContent: '',
      editorText: '',
      initContent: '',
      languages: [
        {title: 'C', value: 0, color: '#8ecc45'},
        {title: 'C++', value: 1, color: '#843f10'},
        {title: 'Java', value: 3, color: '#d43039'},
        {title: 'Python3', value: 6, color: '#f577d0'}
      ],
      hadALangueges: []
    }
  },
  methods: {
    catchData (value, text) {
      this.editorContent = value // 在这里接受子组件传过来的参数，赋值给data里的参数
      this.editorText = text
      // console.log(this.postText)
    },
    toBack () {
      this.$emit('getCloseMsg', false)
    },
    toChangeLang (newLang, index) {
      this.$emit('getChangeLangMsg', index)
      this.setALanguages(newLang)
    },
    toShare () {
      const _this = this
      api.shareMyCode('/forum/solutions/share', {
        solution_id: _this.questionData.solution_id,
        content: _this.editorContent
      }).then(function (res) {
        if (res.success === 'true') {
          _this.$message({
            message: '感想您的题解分享！',
            type: 'success',
            duration: 1000
          })
          _this.toBack()
        } else {
          _this.$message({
            message: '抱歉，您的题解分享失败！每个问题只能分享五篇题解！',
            type: 'error',
            duration: 1000
          })
        }
      })
    },
    setALanguages (lang) {
      var tempArray = this.questionData.accepted_languages
      // var tempArray = [0, 1, 3, 6]
      tempArray.splice(tempArray.indexOf(lang), 1)
      tempArray.push(lang)
      var array = []
      for (var i in tempArray) {
        for (var j in this.languages) {
          this.languages[j].index = j
          if (tempArray[i] === this.languages[j].value) {
            array.push(this.languages[j])
          }
        }
      }
      this.hadALangueges = array
    },
    highlight (content, maxLines) {
      // console.log(content)
      var language = 'clike'
      // content = content.replace(/<code>/g, '<code class="language-' + language + '">').replace(/<pre>/g, '<pre class="line-numbers">')
      var contentList = content.split('<pre><code>')
      var start = contentList[0]
      var needContent = ''
      for (var i = 1; i < contentList.length; i++) {
        needContent += '<span class="codeContain"><pre class="line-numbers"><code class="language-' + language + '">'
        var childList = contentList[i].split('</code></pre>')
        // 提取出的code内代码装在format内
        var format = childList[0].replace(/&lt;/g, '<').replace(/&gt;/g, '>').replace(/&amp;/g, '&').replace(/<br>/g, '\n')
        format = prism.highlight(format, prism.languages.clike, language)
        var formatList = format.split('\n')
        format = ''
        for (var j = 0; j < formatList.length || j < maxLines; j++) {
          if (j < formatList.length) {
            format += '<span class="line-numbers-rows">' + formatList[j] + ' </span>'
          } else {
            format += '<span class="line-numbers-rows-block"></span>'
          }
        }
        needContent += format
        needContent += '</code></pre><button class="copyContent-btn" style="cursor: pointer">复制</button></span>'
        needContent += childList[1]
      }
      return start + needContent
    }
  },
  created () {
    this.setALanguages(this.questionData.language)
  }
}
</script>

<style scoped>
  @import "../../../static/editorCss/style.css";
  @import "../../../static/highlight/prism.css";
  #shareSolve {
    position: fixed;
    top: 60px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 101;
  }
  .container {
    width: 1170px;
    margin: 20px auto;
    border-radius: 5px;
    background-color: rgba(228, 228, 228, 1);
    border: 1px solid transparent;
  }
  .header {
    line-height: 54px;
    border-radius: 5px 5px 0 0;
    background-color: rgba(255, 255, 255, 1);
    box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 6px;
  }
  .header .problem-no,
  .header .title{
    color: #111;
    font-size: 20px;
    font-weight: 800;
    text-overflow: ellipsis;
  }
  .header .problem-no {
    color: #d43039;
    padding: 0 15px;
  }
  .header .btn-share,
  .header .btn-cancel-share,
  .header .btn-back{
    border-radius: 1.2em;
    color: #fff;
    font-size: 14px;
    text-align: center;
    line-height: 2.4em;
    float: right;
    padding: 0 10px;
    margin-top: 10px;
    margin-left: 10px;
    cursor: pointer;
    transition: all .5s;
    background-color: rgba(102, 102, 102, 1);
  }
  .header .btn-back{
    margin-right: -1px;
    border-radius: 1.2em 0 0 1.2em;
    background-color: rgba(255, 153, 0, 1);
  }
  .header .btn-share:hover {
    background-color: rgba(133, 193, 71, 1);
  }
  .header .btn-cancel-share:hover,
  .header .btn-back:hover {
    background-color: rgba(234, 140, 0, 1);
  }
  .main {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    position: relative;
    margin: 15px 10px;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 6px;
    overflow: hidden;
    background-color: #fff;
  }
  .left-code {
    width: 50%;
    max-height: 530px;
    flex: 0 1 auto;
    overflow: auto;
    position: relative;
  }
  .left-code .langBox {
    width: 100px;
    position: absolute;
    right: 20px;
    bottom: 15px;
    /*z-index: 102;*/
    overflow: hidden;
  }
  .left-code .langBox span{
    display: block;
    font-size: 15px;
    text-align: center;
    color: rgba(142, 204, 69, .5);
    text-shadow: 0 1px rgba(0, 0, 0, 0.3);
    opacity: 0.6;
    cursor: pointer;
    margin: 5px 0;
    transition: all .3s;
  }
  .left-code .langBox span:last-child{
    font-size: 25px;
    color: rgba(142, 204, 69, .8);
  }
  .left-code .langBox span:hover{
    color: rgba(234, 140, 0, 1);
    opacity: 1;
  }
  >>> .left-code pre {
    max-height: 530px;
    border-radius: 0;
  }
  >>> .left-code .codeContain>.copyContent-btn {
    right: 15px;
  }
  .right-content {
    width: 50%;
    flex: 0 1 auto;
    padding-top: 10px;
    position: relative;
  }
  .right-content h2 {
    font-size: 16px;
    text-align: center;
    color: #666;
    cursor: default;
    transition: all .3s;
  }
  .right-content:hover h2 {
    color: rgba(142, 204, 69, 1);
    text-shadow: 0 1px rgba(0, 0, 0, 0.2);
  }
  >>> .right-content .toolbar {
    background-color: rgba(228, 228, 228, 0.8);
    border: none;
    border-radius: 0;
  }
  >>> .right-content .editorShow {
    border-radius: 0;
    border: none;
  }
  .choose-fade-enter-active,
  .choose-fade-leave-active {
    transition: all .3s;
  }
  .choose-fade-enter-to, .choose-fade-leave{
    transform: translateX(0);
    opacity: 1;
  }
  .choose-fade-enter, .choose-fade-leave-to {
    transform: translateX(200%);
    opacity: 0;
  }
</style>



