<template>
  <div class="conpetition-test">
    <div class="test-head">
      <a class="el-icon-back back" href="#/competition"></a>
      <p class="head-context">{{msg.title}}</p>
      <span class="head-state">{{msg.contest_id}}</span>
      <span class="head-rank" @click="toRank">排行榜</span>
    </div>
    <div class="test-middle">
      <el-row>
        <el-col :span="8">
          <label class="label-name">开始时间</label>
          <span class="label-context">{{msg.begin}}</span>
        </el-col>
        <el-col :span="8">
          <label class="label-name">结束时间</label>
          <span class="label-context">{{msg.end}}</span>
        </el-col>
        <el-col :span="4">
          <label class="label-name">问题总数</label>
          <span class="label-context">{{ListLength}}</span>
        </el-col>
        <el-col :span="4">
          <label class="label-name">完成数</label>
          <span class="label-context">0</span>
        </el-col>
      </el-row>
    </div>
    <div class="test-bottom">
      <el-table
        :data="tableData"
        style="width: 100%"
        stripe>
        <el-table-column
          type="index"
          align="center"
          label="序号"
          width="180">
        </el-table-column>
        <el-table-column
          prop="problem_id"
          label="题目编号"
          align="center"
          width="180">
        </el-table-column>
        <el-table-column
          prop="title"
          align="center"
          label="标题">
          <template slot-scope="scope">
            <span @click="toAnswerBoard(scope.row)" style="cursor: pointer" class="titleFont">
              {{scope.row.title}}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          label="解决人数"
          align="center"
          width="180">
          <template slot-scope="scope">
            <span>{{scope.row.accepted}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="通过率"
          align="center"
          width="180" >
          <template slot-scope="scope">
            <span :class="getPassRate(scope.row) >= 80 ? 'green' : getPassRate(scope.row) >= 40 ? 'blue' : 'red'">{{(getPassRate(scope.row))}}%</span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label="完成状态"
          width="180">
          <template slot-scope="scope">
            <span v-if="scope.row.finished" style="color: #00BC61">已完成</span>
            <span v-else style="color: red">未完成</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import api from '@/api/test'
export default {
  data () {
    return {
      tableData: [],
      msg: {},
      ListLength: 0,
      finish: 0,
      rankData: {}
    }
  },
  methods: {
    getPassRate (row) {
      return row.accepted ? ((row.accepted / row.submit) * 100).toFixed(2) : 0
    },
    getParams () {
      // 取到路由带过来的参数
      // 使用name引入,query使用路径引入
      if (this.$route.params.dataObj === undefined) {
        this.$router.push({path: '/competition'})
        return
      }
      let routerParams = this.$route.params.dataObj
      /*  if (!routerParams.contest_info) { */
      this.msg = routerParams
      if (this.msg.has_password !== 1) {
        this.getDetail()
      } else {
        if (routerParams.back) {
          let obj1 = this.$route.params.dataObj
          this.tableData = obj1.problem_list
          this.ListLength = obj1.problem_num
          this.msg.problem_list = obj1.problem_list
          this.msg.problem_num = obj1.problem_num
        } else {
          let obj = this.$route.params.detailObj
          this.tableData = obj.problem_list
          this.ListLength = obj.problem_num
          this.msg.problem_list = obj.problem_list
          this.msg.problem_num = obj.problem_num
        }
        //        console.log('a')
        //        let obj1 = this.$route.params.dataObj
        //        let obj = this.$route.params.detailObj
        //        console.log(obj1)
        //        this.tableData = obj.problem_list
        //        this.ListLength = obj.problem_num
        //        this.msg.problem_list = obj.problem_list
        //        this.msg.problem_num = obj.problem_num
      }
      /*  } else {
        this.msg = routerParams.contest_info
        this.tableData = routerParams.problem_info.problem_list
        this.ListLength = routerParams.problem_info.problem_num
      } */
      // 将数据放在当前组件的数据内
    },
    getDetail () {
      const _this = this
      api.getContestDetail('/contests/detail', {
        contest_id: _this.msg.contest_id
      }).then(function (res) {
        _this.tableData = res.data.problem_info.problem_list
        _this.ListLength = res.data.problem_info.problem_num
      })
    },
    // 跳转到排行榜
    toRank () {
      const _this = this
      if (this.msg.has_password !== 1) {
        this.$router.push({
          path: '/competitionRank',
          name: 'competitionRank',
          params: {
            dataObj: _this.msg,
            problemList: _this.tableData
          }
        })
      } else {
        this.$router.push({
          path: '/competitionRank',
          name: 'competitionRank',
          params: {
            dataObj: _this.msg
          }
        })
      }
    },
    toAnswerBoard (row) {
      var nowTime = new Date()
      var endTime = new Date(this.msg.end)
      if (endTime > nowTime) {
        const {href} = this.$router.resolve({
          path: '/library/questionDetail',
          query: {
            PID: row.problem_id,
            contest_id: this.msg.contest_id
          }
        })
        window.open(href, '_blank')
      } else {
        this.$alert('竞赛已结束，下次再来', '温馨提示', {
          confirmButtonText: '确定',
          type: 'warning'
        }).then(function () {
        })
      }
    }
  },
  watch: {
    // 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
    '$route': 'getParams'
  },
  created () {
    this.getParams()
  }
}
</script>

<style scoped>
  .conpetition-test{
    width: 1170px;
    margin: 0 auto;
    padding-top: 10px;
  }
  .test-head{
    line-height: 36px;
    padding: 10px 15px 10px 15px;
    border-bottom: 2px solid gainsboro;
  }
  .head-context{
    display: inline-block;
    font-size: 20px;
    color: #555555;
    margin-left: 15px;
  }
  .head-state{
    margin-left: 15px;
    vertical-align: middle;
    color: white;
    background-color: #8ecc45;
    padding: 0 10px;
    border-radius: 10px;
  }
  .head-rank{
    float: right;
    padding: 0 10px;
    border-radius: 10px;
    background-color: #f48118;
    color: #ffffff;
    cursor: pointer;
  }
  .head-rank:hover{
    background-color: #ff6600;
  }
  .test-middle{
    margin-top: 20px;
  }
  .label-name{
    margin-top: 10px;
    padding: 5px 15px;
    border-radius: 10px;
    color: white;
    background-color: #3385ff;
  }
  .label-context{
    padding-left: 15px;
  }
  .test-bottom{
    margin-top: 20px;
  }
  .conpetition-test .green {
    color: #8ecc45;
  }
  .conpetition-test .blue {
    color: #6495ed;
  }
  .conpetition-test .red {
    color: #ff6c3f;
  }
  .titleFont:hover{
    font-size: 16px;
  }
</style>



