<template>
  <div class="previewChoose">
    <div class="previewChoose_container">
      <quiz-panel title="选择历史编辑试卷" padding="10px 10px" height="100%">
        <ul class="previewChoose_container-quizs public-scrollStyle">
          <li
            :class="{
              'previewChoose_quiz-item': true,
              'previewChoose_quiz-curItem': quizId === item.quiz_id}"
              v-for="(item, index) in quizList" :key="item.quiz_id"
              @click="quizId = item.quiz_id">
            <span class="index">{{index + 1}}</span>
            <p class="title">{{item.title}}</p>
            <p class="update-time">上次更新时间：{{item.update_time ? item.update_time.replace('T', ' ') : '无'}}</p>
          </li>
        </ul>
      </quiz-panel>
      <div class="previewChoose_sureBtn" @click="chooseHandler()">
        <quiz-btn icon="icon-queding" radius="50%" size="25px" theme="SURE" width="40px" height="40px"></quiz-btn>
      </div>
    </div>
  </div>
</template>

<script>
import quizPanel from '@/base/quiz/quiz-panel'
import quizBtn from '@/base/quiz/quiz-btn'
// import api from '@/api/quiz'
export default {
  name: 'previewChoose',
  components: {
    quizPanel,
    quizBtn
  },
  props: {
    quizList: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {
      quizId: this.quizList[0].quiz_id
    }
  },
  methods: {
    chooseHandler () {
      this.$emit('chooseHandler', this.quizId)
    }
  },
  created () {
  }
}
</script>

<style scoped>
  .previewChoose {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.60);
    z-index: 100;
  }
  .previewChoose_container {
    width: 350px;
    height: 300px;
    margin: 150px auto 0 auto;
    position: relative;
  }
  .previewChoose_container-quizs {
    height: 250px;
    overflow-y: auto;
  }
  .previewChoose_quiz-item {
    line-height: 2;
    cursor: pointer;
    padding: 5px 10px;
    position: relative;
  }
  .previewChoose_quiz-item .index {
    position: absolute;
    margin-left: 5px;
    color: #918f8f;
  }
  .previewChoose_quiz-item .title {
    text-align: center;
    font-size: 13px;
    background-color: rgba(242, 242, 242, 1);
    border-radius: 5px 5px 0 0;
    transition: all .5s;
  }
  .previewChoose_quiz-item:hover .title {
    background-color: rgba(232, 232, 232, 1);
  }
  .previewChoose_quiz-item .update-time {
    text-align: center;
    font-size: 12px;
    border-radius: 0 0 5px 5px;
    border: 1px solid rgba(242, 242, 242, 1);
    transition: all .5s;
  }
  .previewChoose_quiz-item:hover .update-time {
    border: 1px solid rgba(232, 232, 232, 1);
  }
  .previewChoose_quiz-curItem .index {
    color: white;
  }
  .previewChoose_quiz-curItem .title {
    color: white;
    background-color: #87c141;
  }
  .previewChoose_quiz-curItem:hover .title {
    background-color: #7ab441;
  }
  .previewChoose_quiz-curItem .update-time {
    border: 1px solid #87c141;
  }
  .previewChoose_quiz-curItem:hover .update-time {
    border: 1px solid #7ab441;
  }
  .previewChoose_sureBtn {
    position: absolute;
    right: 10px;
    bottom: 10px;
  }
</style>



