<template>
  <div class="itemContribution">
    <div class="contribution-head">
      <span>题库贡献</span>
      <button class="edit" @click="openEditBox">发布</button>
    </div>
    <div class="contribution-content">
      <el-table
        :data="showContributionListPartData"
        border
        stripe
        header-row-class-name="head-class"
        style="width: 100%">
        <el-table-column
          type="index"
          label="序号"
          align="center"
          width="60"
          :index="indexMethod">
        </el-table-column>
        <el-table-column
          prop="title"
          label="题目标题"
          align="center">
        </el-table-column>
        <!--<el-table-column-->
          <!--align="center"-->
          <!--label="题目标签"-->
          <!--width="120">-->
          <!--<template slot-scope="scope">-->
            <!--<p v-for="(item,index) in scope.row.tagids" :key="index">{{ getTag (item) }}</p>-->
          <!--</template>-->
        <!--</el-table-column>-->
        <el-table-column
          align="center"
          label="贡献时间"
          width="180"
          prop="in_date">
        </el-table-column>
        <el-table-column
          align="center"
          label="审核情况"
          width="120">
          <template slot-scope="scope">
            <span v-if="scope.row.verify_status === 0" style="color: #6495ed; font-weight: 600;">待审核</span>
            <span v-if="scope.row.verify_status === 1" style="color: #8ecc45; font-weight: 600;">已通过</span>
            <span v-if="scope.row.verify_status === 2" style="color: #ff6c3f; font-weight: 600;">不通过</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="public-pagination">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-size="pageSize"
        background
        layout="total, prev, pager, next, jumper"
        :total="showContributionListData.length">
      </el-pagination>
    </div>

    <div class="Box" id="Box" v-if="isShowDialog">
      <div class="box-header">
        <span class="box-title">题库贡献</span>
        <i  id="btn" class="el-icon-close" @click="closeDialog"></i>
      </div>
      <div class="box-content">
        <el-form :rules="contributeRule" hide-required-asterisk status-icon ref="post" inline-message label-position="top" :model="post">
          <el-form-item label="题目标题" prop="title">
            <el-input placeholder="请输入标题" v-model="post.title"></el-input>
          </el-form-item>
          <el-form-item prop="tag">
            <span style="font-size: 14px;color: #606266;line-height: 40px;">题目标签</span>
            <div  class="tag-item" @click="isShowTagDialog = !isShowTagDialog"><i class="el-icon-plus"></i></div>
            <ul>
              <span class="list-item" v-for="(item, index) in newTags"  :key="index">{{item.label}}<i  class="el-icon-close" @click="delLabel(index)" style="cursor: pointer"></i></span>
            </ul>
          </el-form-item>
          <el-form-item label="题目描述" prop="titleDesc">
            <div id="titleDesc">
              <wangEditor ref="tDesc" :editorType="3" class="wang" :catchData="getTitleDesc" :content="post.description" :height="'280px'"></wangEditor>
            </div>
          </el-form-item>
          <el-form-item label="输入描述" prop="inputDesc">
            <div id="inputDesc">
              <wangEditor :editorType="3" :catchData="getInput" class="wang" :content="post.input" :height="'280px'"></wangEditor>
            </div>
          </el-form-item>
          <el-form-item label="输出描述" prop="outputDesc">
            <div id="outputDesc">
              <wangEditor :editorType="3" :content="post.output" class="wang" :catchData="getOutput" :height="'280px'"></wangEditor>
            </div>
          </el-form-item>
          <el-form-item label="输入样例" prop="sample_input">
            <el-input
              v-model="post.sample_input"
              type="textarea"
              :rows="8"
              placeholder="请输入内容"
              resize="none">
            </el-input>
          </el-form-item>
          <el-form-item label="输出样例" prop="sample_output">
            <el-input
              v-model="post.sample_output"
              type="textarea"
              :rows="8"
              placeholder="请输入内容"
              resize="none">
            </el-input>
          </el-form-item>
          <el-form-item label="题目提示" prop="tips">
            <div id="tips">
              <wangEditor :editorType="3" :catchData="getHint" :content="post.hint" class="wang" :height="'280px'"></wangEditor>
            </div>
          </el-form-item>
          <el-form-item label="题目来源" prop="source">
            <el-input v-model="post.source" placeholder="请输入题目来源"></el-input>
          </el-form-item>
          <el-form-item label="运行时间限制（单位S）" prop="time_limit">
            <el-input v-model.number="post.time_limit" placeholder="请输入运行时间限制"></el-input>
          </el-form-item>
          <el-form-item label="运行内存限制（单位MB）" prop="memory_limit">
            <el-input v-model.number="post.memory_limit" placeholder="请输入运行内存限制"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div class="footer">
        <el-button @click="closeDialog" style="width: 50px;height: 30px">取 消</el-button>
       <!-- <el-button @click="resetForm" type="primary" style="width: 50px;height: 30px">重置</el-button>-->
        <el-button type="primary" @click="sendMyContribution" style="width: 50px;height: 30px">提交</el-button>
      </div>
    </div>
    <el-dialog
      :visible.sync="isShowTagDialog">
      <el-button type="primary" @click="addTags" style="width: 50px;height: 30px">保存</el-button>
      <div style="margin-top: 10px">
        <el-tree
          :data="treeData"
          show-checkbox
          node-key="value"
          ref="addTagTree"
          check-strictly
          highlight-current
          :props="defaultProps"
          @check-change="handleCheckChange">
        </el-tree>
      </div>
      <div class="selected-tag">
        <div v-for="(item, index) in addTagArr" :key="index" class="inline-list-item">
          {{ item.label }}
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import api from '@/api/questionBank'
import wangEditor from '@/base/wangeditor/wangeditor'
import apiContribute from '@/api/personCenter'

export default {
  name: 'itemContribution',
  components: {
    wangEditor
  },
  data () {
    return {
      isShowDialog: false,
      isShowTagDialog: false,
      treeData: [],
      tagTree: [],
      saveContent: 'hello',
      addTagArr: [],
      tagValue: [],
      content: '',
      newTags: [],
      post: {
        tagids: [],
        title: '',
        description: '',
        input: '',
        output: '',
        sample_input: '',
        sample_output: '',
        hint: '',
        source: '',
        time_limit: null,
        memory_limit: null
      },
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      showContributionListData: [],
      showContributionListPartData: [],
      page: 1,
      pageSize: 8,
      contributeRule: {
        title: [
          {
            required: true,
            message: '标题不能为空',
            trigger: 'blur'
          }
        ],
        /* tag: [
            {
              required: true,
              message: '请选择题目标签',
              trigger: 'change'
            }
          ], */
        /* inputDesc: [
            {
              required: true,
              message: '输入描述不能为空',
              trigger: 'change'
            }
          ],
          outputDesc: [
            {
              required: true,
              message: '输出描述不能为空',
              trigger: 'change'
            }
          ], */
        /* titleDesc: [
            {
              required: true,
              message: '题目提示不能为空',
              trigger: 'change'
            }
          ], */
        sample_input: [
          {
            required: true,
            message: '样例输入不能为空',
            trigger: 'blur'
          }
        ],
        sample_output: [
          {
            required: true,
            message: '样例输出不能为空',
            trigger: 'blur'
          }
        ],
        /* tips: [
            {
              required: true,
              message: '题目提示不能为空',
              trigger: 'change'
            }
          ], */
        source: [
          {
            required: true,
            message: '题目来源不能为空',
            trigger: 'blur'
          }
        ],
        time_limit: [
          {
            required: true,
            message: '运行时间限制不能为空',
            trigger: 'blur'
          },
          {
            type: 'number',
            message: '运行时间限制必须为数字值'
          }
        ],
        memory_limit: [
          {
            required: true,
            message: '运行内存限制不能为空',
            trigger: 'blur'
          },
          { type: 'number',
            message: '运行内存限制必须为数字值'
          }
        ]
      }
    }
  },
  methods: {
    openEditBox () {
      window.scrollTo(0, 0)
      this.isShowDialog = !this.isShowDialog
    },
    indexMethod (index) {
      return index - (this.pageSize - 1) + (this.page * this.pageSize)
    },
    recursiveData (Object, arr, ObjectArr, index) {
      for (let i in Object) {
        let obj = {
          value: i,
          label: ObjectArr[i],
          isFather: index,
          children: []
        }
        arr.push(obj)
        if (Object[i] !== {}) {
          this.recursiveData(Object[i], arr, ObjectArr, i)
        } else {
          break
        }
      }
      return arr
    },
    initTagTree1 () {
      const _this = this
      let tagDict = []
      let tagTree = []
      let arr = []
      let ObjArr = []
      api.getQuestionList('/tags').then(function (res) {
        tagDict = res.data.tag_dict
        tagTree = res.data.tag_tree
        _this.recursiveData(tagTree, arr, tagDict, '0')
        for (let i = 0; i < arr.length; i++) {
          for (let j = 0; j < arr.length; j++) {
            if (arr[i].value === arr[j].isFather) {
              arr[i].children.push(arr[j])
            }
          }
        }
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].isFather === '0') {
            ObjArr.push(arr[i])
          }
        }
        _this.treeData = ObjArr
      })
    },
    resetForm () {
      this.$refs.post.resetFields()
      this.post = {
        tagids: [],
        title: '',
        description: '',
        input: '',
        output: '',
        sample_input: '',
        sample_output: '',
        hint: '',
        source: '',
        time_limit: null,
        memory_limit: null
      }
      this.newTags = []
      this.tagValue = []
      this.addTagArr = []
      this.$refs.addTagTree.setCheckedKeys(this.tagValue)
    },
    initTagTree () {
      let self = this
      let tagDict = []
      let tagTree = []
      api.getQuestionList('/tags').then(function (res) {
        // 把得到的数据转成数组
        tagDict = res.data.tag_dict
        tagTree = res.data.tag_tree
        for (let key in tagTree) {
          let obj = {
            key: key,
            label: tagDict[key],
            children: self.initTree(tagDict, tagTree[key])
          }
          self.tagTree.push(obj)
        }
        // console.log(self.tagTree)
      })
    },
    objEmpty (obj) {
      for (let key in obj) {
        return false
      }
      return true
    },
    initTree (data, tree) {
      if (this.objEmpty(tree)) {
        return []
      } else {
        let List = []
        for (let tag in tree) {
          let obj = {
            value: tag,
            label: data[tag]
          }
          List.push(obj)
          // console.log(obj.id)
        }
        return List
      }
    },
    handleCurrentChange (val) {
      this.page = val
      this.showContributionListPartData = null
      this.showContributionListPartData = this.showContributionListData.slice((this.page - 1) * this.pageSize, this.page * this.pageSize)
    },
    handleCheckChange (data, checked, indeterminate) {
      // console.log(data, checked, indeterminate)
      // 判断obj是否在arr数组里面
      let isInArray = function (arr, obj) {
        for (let i = 0; i < arr.length; i++) {
          if (obj.value === arr[i].value && obj.label === arr[i].label) {
            return true
          }
        }
        return false
      }
      // 如果被选中
      if (checked) {
        let flag = true
        if (this.addTagArr.length >= 5) {
          this.$refs.addTagTree.setCheckedKeys(this.tagValue)
          flag = false
          this.$message.error({
            message: '最多只能选择5个标签!',
            duration: 1000
          })
        }
        for (let i = 0; i < this.addTagArr.length; i++) {
          if (data.value === this.addTagArr[i].value) {
            flag = false
            break
          }
        }
        // 被选中的节点是否已经在addTagArr
        if (flag) {
          var obj = {
            value: data.value,
            label: data.label,
            children: data.children
          }
          if (!isInArray(this.addTagArr, obj) && obj.children.length === 0) {
            this.tagValue.push(obj.value)
            this.addTagArr.push(obj)
            // console.log(obj.children.length)
          }
          if (!isInArray(this.addTagArr, obj) && obj.children.length !== 0) {
            this.$refs.addTagTree.setCheckedKeys(this.tagValue)
            this.$message.error({
              message: '父节点不作选择，请选择其子节点！',
              duration: 1000
            })
          }
        }
      } else {
        let num = this.addTagArr.findIndex(item => item.value === data.value)
        if (num !== -1) {
          this.addTagArr.splice(num, 1)
          this.tagValue.splice(num, 1)
        }
      }
      // console.log(this.addTagArr)
    },
    delLabel (index) {
      this.$delete(this.newTags, index)
      this.$delete(this.post.tagids, index)
      this.$delete(this.addTagArr, index)
      this.$delete(this.tagValue, index)
      this.$refs.addTagTree.setCheckedKeys(this.tagValue)
    },
    getTitleDesc (value) {
      this.post.description = value
      // console.log(this.post.title)
    },
    getInput (value) {
      this.post.input = value
      // console.log(this.post.input)
    },
    getOutput (value) {
      this.post.output = value
      // console.log(this.post.output)
    },
    getHint (value) {
      this.post.hint = value
    },
    addTags () {
      if (this.addTagArr.length === 0) {
        this.$message.error({
          message: '请选择标签！',
          duration: 1000
        })
      } else {
        this.newTags = []
        for (let i = 0; i < this.addTagArr.length; i++) {
          this.newTags[i] = this.addTagArr[i]
          /* this.tagValue[i] = String.valueOf(this.addTagArr[i].value) */
          this.post.tagids[i] = parseInt(this.addTagArr[i].value)
        }
        this.isShowTagDialog = false
      }
    },
    sendMyContribution () {
      const _this = this
      _this.$refs.post.validate(function (valid) {
        if (valid && _this.post.tagids.length !== 0 && _this.post.description.replace(/<[^>]+>/g, '').replace(/&nbsp;/gi, '') !== '' && _this.post.input.replace(/<[^>]+>/g, '').replace(/&nbsp;/gi, '') !== '' && _this.post.output.replace(/<[^>]+>/g, '').replace(/&nbsp;/gi, '') !== '' && _this.post.hint.replace(/<[^>]+>/g, '').replace(/&nbsp;/gi, '') !== '') {
          apiContribute.publishContribution('/problems/contribute', {
            tagids: _this.post.tagids,
            title: _this.post.title,
            description: _this.post.description,
            input: _this.post.input,
            output: _this.post.output,
            sample_input: _this.post.sample_input,
            sample_output: _this.post.sample_output,
            hint: _this.post.hint,
            source: _this.post.source,
            time_limit: _this.post.time_limit,
            memory_limit: _this.post.memory_limit
          }).then(function (res) {
            if (res.success === 'true') {
              _this.resetForm()
              // _this.$router.push({path: '/PersonalCenter'})
              _this.$message.success({
                message: '提交成功，请等待审核!',
                duration: 1000
              })
              _this.getContributionList()
              _this.isShowDialog = false
            } else {
              _this.$message.error({
                message: '提交失败!',
                duration: 1000
              })
              _this.isShowDialog = false
            }
          })
        } else {
          if (_this.post.tagids.length === 0 && _this.post.description.replace(/<[^>]+>/g, '').replace(/&nbsp;/gi, '') !== '' && _this.post.input.replace(/<[^>]+>/g, '').replace(/&nbsp;/gi, '') !== '' && _this.post.output.replace(/<[^>]+>/g, '').replace(/&nbsp;/gi, '') !== '' && _this.post.hint.replace(/<[^>]+>/g, '').replace(/&nbsp;/gi, '') !== '') {
            _this.$message.error({
              message: '请选择题目标签!',
              duration: 1000
            })
          } else {
            _this.$message.error({
              message: '请完整填写选项!',
              duration: 1000
            })
          }
          return false
        }
      })
    },
    getContributionList () {
      const _this = this
      apiContribute.getUserContribution('/problems/contribute/my_contributions', {}).then(function (res) {
        _this.showContributionListData = res.data.problem_list
        _this.showContributionListData.map(item => {
          item.in_date = item.in_date.replace('T', ' ')
          return item
        })
        // console.log(_this.showContributionListData)
        _this.handleCurrentChange(1)
      })
    },
    closeDialog () {
      this.isShowDialog = false
      this.resetForm()
    }
  },
  created () {
    this.initTagTree()
    this.initTagTree1()
    this.getContributionList()
  }
}
</script>

<style scoped>
  .itemContribution{
    min-height: 700px;
    background-color: white;
    padding: 20px 20px;
  }
  .contribution-head{
    padding-bottom: 5px;
    border-bottom: 1px solid gainsboro;
    line-height: 40px;
    font-size: 20px;
  }
  .edit{
    border: none;
    padding: 10px 40px;
    background: #f48118;
    border-radius: 10px;
    color: white;
    float: right;
    cursor: pointer;
  }
  .edit:hover{
    background-color: #d48118;
  }
  .Box {
    opacity: 1;
    background-color: white;
    width: 100%;
    /*height: 100%;*/
    position: absolute;
    height: auto;
    top:0;
    left:0;
    z-index: 999;
    margin-top: -130px;
    padding: 30px;
  }
  .box-header {
    border-bottom: 1px solid #DBDBDB;
    margin-top: -20px;
  }
  .box-header .box-title {
    font-size: 20px;
    line-height: 45px;
    /*padding-left: 20px;*/
  }
  .box-header #btn {
    cursor: pointer;
    float: right;
    margin-top: 18px;
    margin-right: 20px;
  }
  #btn:hover{
    color:  #6495ed;
  }
  .footer{
    height: 30px;
    float: right;
    margin-top: 50px;
    margin-right: 30px;
  }
  .tag-item{
    line-height: 2em;
    height: unset;
    /*padding: 0 15px;*/
    /*margin: 5px;*/
    cursor: pointer;
    background-color: rgba(242, 242, 242, 1);
    border-radius: 10px;
    width: 30px;
    text-align: center;
  }
  .tag-item:hover{
    color: #fff;
    background-color: rgba(133, 193, 71, 1);
  }
  .selected-tag {
    padding: 10px;
  }
  .list-item {
    display: inline-block;
    text-align: center;
    margin-right: 5px;
    margin-bottom: 5px;
    /*width: auto;*/
    min-width: 115px;
    line-height: 24px;
    border-radius: 3px;
    background-color: #fa8118;
    color: #ffffff;
    opacity: 0.5;
  }
  .list-item:hover{
    opacity: 1;
  }
  .inline-list-item{
    display: inline-block;
    text-align: center;
    margin-right: 5px;
    margin-bottom: 5px;
    /*width: auto;*/
    min-width: 115px;
    line-height: 24px;
    border-radius: 3px;
    background-color: #fa8118;
    color: #ffffff;
  }
  .box-content /deep/ .el-icon-close:before{
    margin-left: 2px;
  }
  .contribution-content{
    margin: 20px 0;
  }
  .public-pagination{
    margin-top: 20px;
  }
  .wang{
    line-height: 0px!important;
   }
</style>



