<template>
  <div class="competition">
    <div class="competition-table">
      <div class="competition-search">
        <el-row :gutter="20">
          <el-col :span="6" :offset="3"><el-input size="small" v-model="cid" placeholder="请输入竞赛编号" @keyup.enter.native="searchContest"></el-input></el-col>
          <el-col :span="6"><el-select size="small" v-model="state" placeholder="请选择当前状态" @change="searchContest">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          </el-col>
          <el-col :span="6"><el-select size="small" v-model="type" placeholder="请选择竞赛类型" @change="searchContest">
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          </el-col>
          <el-col :span="2">
            <div class="statusButton">
              <el-button size="small" type="primary" icon="el-icon-search" @click="searchContest">搜索</el-button>
            </div>
          </el-col>
        </el-row>
      </div>
      <el-table
        :data="tableData"
        v-loading="tableLoading"
        style="width: 100%"
        :row-class-name="rowclass"
        @row-click="clickCall"
        stripe>
        <el-table-column
          label="编号"
          type="index"
          align="center"
          width="70px">
        </el-table-column>
        <el-table-column
          label="竞赛编号"
          align="center"
          prop="contest_id"
          width="100px">
        </el-table-column>
        <el-table-column
          label="标题"
          align="center"
          prop="title">
        </el-table-column>
        <el-table-column
          label="开始时间"
          align="center"
          prop="begin">
        </el-table-column>
        <el-table-column
          label="结束时间"
          align="center"
          prop="end">
        </el-table-column>
        <el-table-column
          label="当前状态"
          align="center"
          width="100px">
          <template slot-scope="scope">
            <span v-if="new Date() < new Date(scope.row.begin)" style="color: #5F90DD">待进行</span>
            <span v-if="new Date() > new Date(scope.row.end)" style="color: #F56C6C">已结束</span>
            <span v-if="new Date() >= new Date(scope.row.begin) && new Date() <= new Date(scope.row.end)" style="color: #67C23A">进行中</span>
          </template>
        </el-table-column>
        <el-table-column
          label="开启者"
          align="center"
          prop="holder"
          width="100px">
        </el-table-column>
        <el-table-column
          label="类型"
          align="center"
          width="120px">
          <template slot-scope="scope">
            <span v-if="scope.row.has_password == 1" style="color: #F56C6C">Password密码</span>
            <span v-if="scope.row.has_password == 0" style="color: #67C23A">Public公开</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="competition-pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-size="pagesize"
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import api from '@/api/competition'

export default {
  data () {
    return {
      page: 1,
      pagesize: 20,
      total: 0,
      rowclass: 'rowclass',
      cid: '',
      state: '',
      type: '',
      typeList: [{
        value: 1,
        label: 'Password密码'
      }, {
        value: 2,
        label: 'Public公开'
      }, {
        label: '全部类型'
      }
      ],
      options: [{
        value: 0,
        label: '已结束'
      }, {
        value: 1,
        label: '进行中'
      }, {
        label: '全部状态'
      }],
      tableData: [],
      tableLoading: false
    }
  },
  methods: {
    getList (contestId, type, state, page = 1, pageSize = 20) {
      const _this = this
      _this.tableLoading = true
      var data = []
      api.getConpList('/contests', page, pageSize, contestId, type, state).then(function (res) {
        data = res.data
        for (var i = 0; i < data.length; i++) {
          var begin = data[i].begin.split('T')
          var end = data[i].end.split('T')
          data[i].begin = begin[0] + ' ' + begin[1]
          data[i].end = end[0] + ' ' + end[1]
        }
        _this.tableData = data
        _this.total = res.total
        _this.tableLoading = false
      })
    },
    searchContest () {
      if (this.page !== 1) {
        this.page = 1
      }
      this.getList(this.cid, this.type, this.state)
    },
    clickCall (row) {
      const _this = this
      let date = new Date()
      if (date < new Date(row.begin)) {
        this.$alert('竞赛还未开始，请等待开始时间')
        return
      }
      var storage = window.sessionStorage
      var userData = storage.getItem('userInfo')
      if (userData === 'undefined' || userData === null || userData === undefined) {
        this.$alert('请先登录账号', '温馨提示', {
          confirmButtonText: '去登陆'
        }).then(function () {
          _this.$router.push({path: '/login'})
        })
        return
      }
      if (row.has_password) {
        this.$prompt('请输入密码', '验证', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputType: 'password'
        }).then(function ({ value }) {
          api.getContestsDetail('/contests/detail?contest_id=' + row.contest_id + '&password=' + value).then(function (res) {
            if (res.success !== 'true') {
              _this.$message({
                message: res.msg,
                type: 'error'
              })
            } else {
              var info = res.data.contest_info
              var endArr = res.data.contest_info.end.split('T')
              var beginArr = res.data.contest_info.begin.split('T')
              info.end = endArr[0] + ' ' + endArr[1]
              info.begin = beginArr[0] + ' ' + beginArr[1]
              _this.$router.push({
                path: '/test',
                name: 'test',
                params: {
                  dataObj: info,
                  detailObj: res.data.problem_info
                }
              })
            }
          })
        }).catch(function () {})
      } else {
        this.$router.push({
          path: '/test',
          name: 'test',
          params: {
            dataObj: row
          }
        })
      }
    },
    handleSizeChange (val) {
      this.pagesize = val
      this.getList(this.cid, this.type, this.state, this.page, val)
    },
    handleCurrentChange (val) {
      this.getList(this.cid, this.type, this.state, val, this.pagesize)
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style scoped>
  .competition{
    width: 1170px;
    margin: 0 auto;
    padding-top: 20px;
  }
  .competition-search {
    float: right;
    margin: 0 0px 20px 20px;
  }
  .competition-pagination {
    padding: 20px 0;
  }
  .competition-pagination /deep/ .el-pagination .el-pager .active{
    background-color: #f48118 !important;
  }
  .competition-pagination /deep/ .el-pagination .el-pagination__total{
    color: inherit;
  }
  .competition-pagination /deep/ .el-pagination .el-pagination__jump{
    color: inherit;
  }
  .competition-table /deep/ .rowclass {
    cursor: pointer !important;
  }
  .statusButton /deep/ .el-button {
    background-color: #f48118;
    border: 1px solid #f48118;
  }
  .statusButton /deep/ .el-button:hover{
    background-color: orange;
    border: 1px solid orange;
  }
</style>



