<template>
  <div :class="{'quizCheck': true, 'blackTheme': step === 2 && !isMorning}">
    <div class="bgTheme"></div>
    <div class="quizCheck_header">
      <h3 class="quizCheck_header_title">{{quizInfo.title}}&nbsp;|&nbsp;教师改卷环节 - {{currentNo + 1}}号考卷</h3>
      <span class="quizCheck_header_rightFlame">
        <span v-if="!getNotCorrected && quizInfo.status === 3">考试已公布成绩</span>
        <span v-if="!getNotCorrected && quizInfo.status === 2">全部批改完成，待公布成绩</span>
        <span v-if="getNotCorrected && quizInfo.status === 2 && currentNo !== -1">剩余：&nbsp;{{getNotCorrected}}&nbsp;份未批改</span>
      </span>
    </div>
    <div class="quizCheck_container">
      <!--处于考前须知页面-->
      <template v-if="step === 1">
        <div class="quizCheck_step1" v-loading="!isShowTips">
          <quiz-panel :padding="getMainPanelPadding">
            <div class="quizCheck_step1_container">
              <h4>改卷前须知要点</h4>
              <div class="quizCheck_step1_content">
                <p v-for="(item, index) in quizCheckStartTips" :key="index">{{index+1}}.&nbsp;
                  <span v-html="item"></span>
                </p>
              </div>
              <div :class="{'quizCheck_step1_btns': true}">
                <span v-if="getNotCorrected && currentNo === 0" @click="changeCurStep(2)">前往改卷</span>
                <span v-if="getNotCorrected && currentNo !== 0" @click="changeCurStep(2)">继续批改</span>
                <span v-if="!getNotCorrected && quizInfo.status === 2" @click="announceGrade()">公布成绩</span>
                <span v-if="!getNotCorrected && quizInfo.status === 3">成绩已公布</span>
                <span
                  class="quizCheck_step1_subBtns"
                  v-if="!getNotCorrected && quizInfo.status === 2 && currentNo >= 0 && currentNo < student_list.length"
                  @click="changeCurStep(2)">继续批改</span>
              </div>
            </div>
          </quiz-panel>
        </div>
      </template>
      <!--处于考试答题页面-->
      <template v-else-if="step === 2">
        <div class="quizCheck_step2" :key="changeDate">
          <div class="quizCheck_step2_detail">
            <quiz-panel :title="curProblemNo === -1 ? '机器评定错题展示' : '自选试题展示'" :padding="getMainPanelPadding">
              <div class="quizCheck_step2_detail_container">
                <div v-for="(problem, problemNo) in quizInfo.detail[curType-1].problem_info" :key="problem.problem_id">
                  <template
                    v-if="(curProblemNo === -1
                      && (parseFloat(userAnswer[curType-1].problem_info[problemNo].score) < quizInfo.detail[curType-1].value
                      || userAnswer[curType-1].problem_info[problemNo].score === ''))
                      || curProblemNo === problemNo">
                    <h5 class="label">
                      {{problemType[curType]}}&nbsp;
                      {{problemNo+1}}&nbsp;
                      (&nbsp;{{quizInfo.detail[curType-1].value}}&nbsp;分)
                    </h5>
                    <!--填空题-->
                    <div v-if="curType === 1" class="problemDetail_type1">
                      <div class="problemDetail_title">
                        <span v-for="(word, idx) in problem.description.split('{}')" :key="idx">
                          <span v-html="word"></span>
                          <span v-if="idx !== problem.description.split('{}').length-1">
                            <span class="problemDetail_input">
                              {{userAnswer[curType-1].problem_info[problemNo].student_answer[idx]}}
                              <template
                                v-if="userAnswer[curType-1].problem_info[problemNo].student_answer[idx]
                                  !== quizInfo.detail[curType-1].problem_info[problemNo].answer[idx]">
                                <span class="problemDetail_input_answer">（{{quizInfo.detail[curType-1].problem_info[problemNo].answer[idx]}}）</span>
                              </template>
                            </span>
                          </span>
                        </span>
                      </div>
                    </div>
                    <!--选择题-->
                    <div v-else-if="curType === 2" class="problemDetail_type2">
                      <div class="problemDetail_title">
                        <span>(&nbsp;{{problem.isMulti ? '多选题': '单选题'}}&nbsp;)</span>
                        <span v-for="(word, idx) in problem.description.split('{}')" :key="idx">
                          <span v-html="word"></span>
                          <span v-if="idx !== problem.description.split('{}').length-1">
                            ____{{userAnswer[curType-1].problem_info[problemNo].student_answer.toString()}}
                            <span class="correctAnswer">
                              ({{quizInfo.detail[curType-1].problem_info[problemNo].answer.toString()}})</span>____
                          </span>
                        </span>
                      </div>
                      <ul class="chooseList">
                        <!--1-没选择且不是答案灰色 2-没选择是答案绿色 3-选择了不是答案红绿 4-选择了是答案绿色-->
                        <li
                          v-for="(eng, engIdx) in orderEngList" :key="engIdx"
                          :class="{
                            'chooseList_defaultItem': true,
                            'chooseList_chooseCorrect': userAnswer[curType-1].problem_info[problemNo].student_answer.indexOf(eng)>= 0 &&
                              quizInfo.detail[curType-1].problem_info[problemNo].answer.indexOf(eng)>= 0,
                            'chooseList_chooseWrong': (userAnswer[curType-1].problem_info[problemNo].student_answer.indexOf(eng)>= 0 &&
                              quizInfo.detail[curType-1].problem_info[problemNo].answer.indexOf(eng)< 0),
                            'chooseList_noChooseCorrect': (userAnswer[curType-1].problem_info[problemNo].student_answer.indexOf(eng)< 0 &&
                              quizInfo.detail[curType-1].problem_info[problemNo].answer.indexOf(eng)>= 0)}">
                          <b>{{eng}}:</b>&nbsp;&nbsp;<span v-html="problem['option'+eng]"></span>
                        </li>
                      </ul>
                    </div>
                    <!--编程题-->
                    <div v-else-if="curType === 3"  class="problemDetail_type3">
                      <!--<span>{{problem.problem_id}}</span>-->
                      <h5 class="type3_title"><span v-html="problem.title"></span></h5>
                      <div class="type3_limit" onselectstart="return false;">
                        <span><i class="icon-project_space"></i>&nbsp;空间限制：{{problem.memory_limit}}</span>
                        <span><i class="icon-shijian"></i>&nbsp;时间限制：{{problem.time_limit}}</span>
                        <span
                          :class="{'type3_btn': true, 'type3_open_btn': isShowTitle !== problemNo}"
                          @click="isShowTitle = isShowTitle === problemNo ? -1: problemNo">
                          <template v-if="isShowTitle !== problemNo">查看题目</template>
                          <template v-if="isShowTitle === problemNo">查看代码</template>
                        </span>
                      </div>
                      <transition name="fade">
                      <div v-if="isShowTitle === problemNo">
                        <div class="type3_content">
                          <span>题目描述</span>
                          <p v-html="problem.description"></p>
                        </div>
                        <div class="type3_content">
                          <span>输入描述</span>
                          <p v-html="problem.input"></p>
                        </div>
                        <div class="type3_content">
                          <span>输出描述</span>
                          <p v-html="problem.output"></p>
                        </div>
                        <div class="type3_content">
                          <span>样本输入</span>
                          <p v-html="problem.sample_input.replace(/\n/g, '<br>')"></p>
                        </div>
                        <div class="type3_content">
                          <span>样本输出</span>
                          <p v-html="problem.sample_output.replace(/\n/g, '<br>')"></p>
                        </div>
                      </div>
                      </transition>
                      <transition name="fade">
                      <div v-if="isShowTitle !== problemNo">
                        <div class="type3_Answer">
                          <div class="type3_Answer_header">
                            <span class="type3_Answer_label">答题代码</span>
                          </div>
                          <textarea class="type3_Answer_content" v-html="userAnswer[curType-1].problem_info[problemNo].student_answer || '该考生没有作答。'" readonly="true"></textarea>
                          <span
                            v-if="userAnswer[curType-1].problem_info[problemNo].result !== -1"
                            class="type3_Answer_result"
                            :style="{'color': getResultStyle(userAnswer[curType-1].problem_info[problemNo].result)}">
                            {{status[userAnswer[curType-1].problem_info[problemNo].result]}}</span>
                        </div>
                      </div>
                      </transition>
                    </div>
                    <!--问答题-->
                    <div v-else-if="curType === 4" class="problemDetail_type4">
                      <div class="problemDetail_title">
                        <span v-html="problem.description"></span>
                      </div>
                      <div class="problemDetail_input">
                        <span class="inputLabel">考生答案</span>
                        <textarea  class="inputContent" v-html="userAnswer[curType-1].problem_info[problemNo].student_answer || '该考生没有作答。'" readonly="true"></textarea>
                      </div>
                    </div>
                    <!--判断题-->
                    <div v-else class="problemDetail_type5">
                      <div class="problemDetail_title">
                        <span><span v-html="problem.description"></span>（&nbsp;{{userAnswer[curType-1].problem_info[problemNo].student_answer[0] || ''}}&nbsp;）</span>
                        <span class="correctAnswer">{{quizInfo.detail[curType-1].problem_info[problemNo].answer}}</span>
                      </div>
                      <ul class="chooseList">
                        <li
                          :class="{
                            'chooseList_defaultItem': true,
                            'chooseList_chooseCorrect': userAnswer[curType-1].problem_info[problemNo].student_answer === 'T' &&
                              quizInfo.detail[curType-1].problem_info[problemNo].answer === 'T',
                            'chooseList_chooseWrong': userAnswer[curType-1].problem_info[problemNo].student_answer === 'T' &&
                              quizInfo.detail[curType-1].problem_info[problemNo].answer === 'F',
                            'chooseList_noChooseCorrect': userAnswer[curType-1].problem_info[problemNo].student_answer === '' &&
                              quizInfo.detail[curType-1].problem_info[problemNo].answer === 'T'}">
                          <b>正确</b></li>
                        <li
                          :class="{
                            'chooseList_defaultItem': true,
                            'chooseList_chooseCorrect': userAnswer[curType-1].problem_info[problemNo].student_answer === 'F' &&
                              quizInfo.detail[curType-1].problem_info[problemNo].answer === 'F',
                            'chooseList_chooseWrong': userAnswer[curType-1].problem_info[problemNo].student_answer === 'F' &&
                              quizInfo.detail[curType-1].problem_info[problemNo].answer === 'T',
                            'chooseList_noChooseCorrect': userAnswer[curType-1].problem_info[problemNo].student_answer === '' &&
                              quizInfo.detail[curType-1].problem_info[problemNo].answer === 'F'}">
                          <b>错误</b></li>
                      </ul>
                    </div>
                  </template>
                </div>
              </div>
            </quiz-panel>
          </div>
          <div class="quizCheck_step2_score">
            <quiz-panel title="评分项一览" :padding="getMainPanelPadding" width="290px">
              <div class="quizCheck_step2_score_container">
                <div v-for="(item, index) in quizInfo.detail" :key="item.type" class="quizStart_step2_menuItem">
                  <h5 class="label">
                    {{orderList[index]}}、
                    <span
                      :class="{'curType': curType === item.type}"
                      @click="curType = item.type;  curProblemNo = -1;">
                        {{problemType[item.type]}}&nbsp;(共&nbsp;{{item.problem_info.length}}&nbsp;题, 每题&nbsp;{{item.value}}&nbsp;分)</span>
                  </h5>
                  <div class="problemInput">
                    <div :class="{
                          'scoreIpt':true,
                          'scoreIpt_correct': parseInt(userAnswer[index].problem_info[idx].score) === item.value,
                          'scoreIpt_wrong': parseInt(userAnswer[index].problem_info[idx].score) === 0,
                          'scoreIpt_partCorrect': parseInt(userAnswer[index].problem_info[idx].score) > 0 && parseInt(userAnswer[index].problem_info[idx].score) < item.value}"
                         v-for="(problem, idx) in item.problem_info"
                         :key="problem.problem_id">
                      <span class="problemNo" @click="curType = item.type; curProblemNo = idx;">
                        <span class="problemNo_main">{{idx + 1}}</span>
                      </span>
                      <div class="problemMain">
                        <quiz-input
                          type="number"
                          :floatLen="1"
                          :range="[0, item.value]"
                          v-model="userAnswer[index].problem_info[idx].score"
                          lineHeight="30px"
                          fontColor="#fff"
                          :openAnimation="false"
                          :hideUnderline="true"></quiz-input>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </quiz-panel>
          </div>
        </div>
      </template>
      <div style="text-align: right">
        <div style="position: absolute; display: inline-block">
          <transition name="fade">
          <div class="quizCheck_studentList" v-show="isOpenStudentList">
            <quiz-panel :height="'calc(100vh - 230px)'" :topLine="false">
              <h5>考卷列表</h5>
              <ul class="quizCheck_studentList-container">
                <li
                  :class="{
                    'quizCheck_beCorrected': item.isCorrected,
                    'quizCheck_curStudent': currentNo === index
                   }"
                  v-for="(item, index) in student_list"
                  :key="item.user_id"
                  @click="changeCurStudent(index)">
                  <span>
                    {{index + 1}}
                    <template v-if="!item.isAttended">[缺考]</template>
                  </span>
                </li>
              </ul>
            </quiz-panel>
          </div>
          </transition>
          <div class="quizCheck_rightBtns" v-show="true">
            <template v-if="!isOpenStudentList">
              <quiz-btn
                v-if="step === 1"
                icon="icon-fanhui2"
                size="20px"
                radius="default"
                theme="BACK"
                width="45px"
                height="45px"
                @click.native="backToList()"></quiz-btn>
              <template v-if="step === 2">
                <quiz-btn
                  icon="content-须知<br>要点"
                  size="12px"
                  radius="default"
                  theme="FORBID"
                  width="45px"
                  height="45px"
                  @click.native="changeCurStep(1)"></quiz-btn>
                <quiz-btn
                  v-if="currentNo < student_list.length"
                  icon="content-批改<br>结束"
                  size="12px"
                  radius="default"
                  theme="BACK"
                  width="45px"
                  height="45px"
                  @click.native="saveCheckData(currentNo + 1)"></quiz-btn>
                <quiz-btn
                  v-if="isMorning"
                  icon="icon-yejianmoshi-"
                  size="25px"
                  radius="default"
                  theme="BLACK"
                  width="45px"
                  height="45px"
                  @click.native="isMorning  = !isMorning"></quiz-btn>
                <quiz-btn
                  v-if="!isMorning"
                  icon="icon-rijianmoshi"
                  size="25px"
                  radius="default"
                  theme="SURE"
                  width="45px"
                  height="45px"
                  @click.native="isMorning  = !isMorning"></quiz-btn>
              </template>
            </template>
            <quiz-btn
              icon="icon-yonghu"
              size="25px"
              radius="default"
              :theme="isOpenStudentList?'BACK':'BLACK'"
              width="45px"
              height="45px"
              @click.native="isOpenStudentList  = !isOpenStudentList"></quiz-btn>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api/quiz'
import quizPanel from '@/base/quiz/quiz-panel'
import quizBtn from '@/base/quiz/quiz-btn'
import quizInput from '@/base/quiz/quiz-input'

export default {
  name: 'quizCheck',
  components: {
    quizPanel,
    quizBtn,
    quizInput
  },
  data () {
    return {
      isShowTips: false,
      isMorning: true,
      isShowTitle: -1,
      isOpenStudentList: false,
      step: 1,
      quizId: -1,
      quizInfo: {},
      userAnswer: {},
      student_list: [],
      currentNo: -1,
      changeDate: new Date().getTime(), // 用于重新渲染评改数据
      quizCheckStartTips: [],
      curType: 1,
      curProblemNo: -1,
      orderList: '一二三四五',
      orderEngList: 'ABCD',
      problemType: {
        '1': '填空题',
        '2': '选择题',
        '3': '编程题',
        '4': '问答题',
        '5': '判断题'
      },
      status: [
        'Waiting...',
        'Waiting...',
        'Compiling...',
        'Running',
        'Accept',
        'Presenting Error',
        'Wrong Answer',
        'Time Limit Exceed',
        'Memory Limit Exceed',
        'Output Limit Exceed',
        'Runtime Error',
        'Compile Error',
        '正在判题中...'
      ]
    }
  },
  computed: {
    getMainPanelPadding () {
      return '10px 10px'
    },
    getNotCorrected () {
      return this.student_list.reduce((total, i) => total + (i.isCorrected ? 0 : 1), 0)
    }
  },
  methods: {
    changeCurStep (newStep) {
      if (newStep === 2) {
        this.isOpenStudentList = false
        if (this.currentNo >= this.student_list.length) {
          this.currentNo = 0
        }
      }
      this.step = newStep
    },
    changeCurStudent (val) {
      if (this.currentNo >= this.student_list.length) return
      if (!this.student_list[this.currentNo].isAttended &&
        !this.student_list[this.currentNo].isCorrected) { // 如果选中的学生缺考直接保存当前数据
        this.saveCheckData(val)
      } else {
        this.getNextStudentData(val)
      }
    },
    backToList () {
      this.$router.push({name: 'PersonalCenter', params: {isShow: 4}})
    },
    initData () {
      this.isShowTitle = -1
      this.curType = 1
      this.curProblemNo = -1
    },
    getResultStyle (result) {
      return (result === 0 || result === 1 || result === 12)
        ? '#8ecc45' : result === 2
          ? '#888' : result === 3
            ? '#888' : result === 4
              ? '#8ecc45' : result === 5
                ? '#f577d0' : result === 6
                  ? '#d43039' : result === 7
                    ? '#6495ed' : result === 8
                      ? '#6495ed' : result === 9
                        ? '#284cad' : result === 10
                          ? '#ff6c3f' : '#843f10'
    },
    getQuizId () {
      this.quizId = this.$route.params.id
    },
    getQuizData () {
      // 调用接口拿到考试信息
      api.getQuizInfo('/quiz/preview', this.quizId, true).then(res => {
        // 调整时间
        res.data.start_time = res.data.start_time.replace('T', ' ')
        res.data.detail = res.data.detail.map(i => {
          i.problem_info = i.problem_info.map(j => {
            if (i.type === 1) j.description = j.description.replace(/<input.+?\/>/g, '{}')
            if (i.type === 2) {
              j.description = j.description.replace(/<input.+?\/>/g, '{}')
              j.optionA = j.optionA.replace(/<\/?.+?\/?>/g, '')
              j.optionB = j.optionB.replace(/<\/?.+?\/?>/g, '')
              j.optionC = j.optionC.replace(/<\/?.+?\/?>/g, '')
              j.optionD = j.optionD.replace(/<\/?.+?\/?>/g, '')
            }
            return j
          })
          return i
        })
        this.quizInfo = res.data
        // 展示提示
        this.isShowTips = true
        // 获取考试提示
        this.getCheckTips()
        // 获取考生列表
        this.getStudentList()
      })
    },
    // 判断是否有题目没有给分数
    isFilledAnswer (resultArr) {
      for (let i = 0; i < this.userAnswer.length; i++) {
        let temp = {
          type: this.userAnswer[i].type,
          problem_info: []
        }
        for (let j = 0; j < this.userAnswer[i].problem_info.length; j++) {
          let part = this.userAnswer[i].problem_info[j]
          if (part.score === '') {
            return false
          } else {
            temp.problem_info.push({
              problem_id: part.problem_id,
              score: +part.score
            })
          }
        }
        resultArr.push(temp)
      }
      return true
    },
    async saveCheckData (next) {
      if (this.currentNo < this.student_list.length) {
        const tempResult = []
        if (!this.isFilledAnswer(tempResult)) {
          this.changeCurStep(2)
          this.$message({
            message: '您未完全为该考生评分，不允许保存现有评分！',
            type: 'warning',
            duration: 1500
          })
          return
        }
        const result = await api.saveCheckData('/quiz/teacher_ckeck/teacher_grade', {
          quiz_id: parseInt(this.quizId),
          user_id: this.student_list[this.currentNo].user_id,
          result: tempResult
        })
        if (result.success === 'false') {
          this.$message({
            message: '保存数据失败，请重新尝试！',
            type: 'warning',
            duration: 1500
          })
          return
        }
        this.$message({
          message: (this.currentNo + 1) + '号考卷保存批改数据成功！',
          type: 'success',
          duration: 1500
        })
        // 更改批改状态
        this.student_list[this.currentNo].isCorrected = true
      }
      // 判断是否批改下一位考生考卷
      if (next >= 0) {
        this.getNextStudentData(next)
      } else {
        // 否则则返回详情须知
        this.changeCurStep(1)
      }
    },
    getNextStudentData (next) {
      this.currentNo = next
      // 取到学生答题数据
      this.getStudentAnswer()
      // 初始化各类数据
      this.initData()
    },
    async announceGrade () {
      const result = await api.announceGradeData('/quiz/teacher_ckeck/announce_results', this.quizId)
      if (result.success === 'true') {
        this.$message({
          message: '公布成绩成功！',
          type: 'success',
          duration: 1500
        })
        this.quizInfo.status = 3
      } else {
        this.$message({
          message: '公布成绩失败，请稍后再试！',
          type: 'error',
          duration: 1500
        })
      }
    },
    async getStudentAnswer () {
      if (this.currentNo >= this.student_list.length) {
        this.changeCurStep(1)
        return
      }
      // 调用接口拿到用户答题数据
      const result = await api.getOneStudentAnswer('/quiz/teacher_ckeck/student_answers', {
        quiz_id: this.quizId,
        student_id: this.student_list[this.currentNo].user_id
      })
      if (result.success === 'false') return
      const userAnswerData = []
      this.quizInfo.detail.forEach(i => {
        var answerData = result.data.detail[i.type - 1].problem_info
        var typeObj = {
          type: i.type,
          problem_info: []
        }
        i.problem_info.forEach(problem => {
          var answerObj = {
            problem_id: problem.problem_id,
            student_answer: (i.type >= 3) ? '' : [],
            score: this.student_list[this.currentNo].isAttended ? -1 : 0
          }
          answerData.forEach(answer => {
            if (answer.problem_id === answerObj.problem_id) {
              answerObj.student_answer = answer.student_answer
              answerObj.score = answer.score
            }
          })
          typeObj.problem_info.push(answerObj)
        })
        userAnswerData.push(typeObj)
      })
      // 机器评分
      this.userAnswer = userAnswerData.map((item) => {
        var score = 0
        var dscore = 0
        var quizDetail = this.quizInfo.detail[item.type - 1]
        item.problem_info.map((problem, idx) => {
          if (problem.score !== -1) return problem
          score = 0
          if (item.type === 1 || item.type === 2) {
            for (let i in problem.student_answer) {
              if (item.type === 1) { // 填空题
                dscore = quizDetail.value / quizDetail.problem_info[idx].answer.length
                if (quizDetail.problem_info[idx].answer.indexOf(problem.student_answer[i]) >= 0) {
                  score += dscore
                }
              } else if (item.type === 2) { // 选择题
                dscore = quizDetail.value / quizDetail.problem_info[idx].answer.length
                if (quizDetail.problem_info[idx].answer.indexOf(problem.student_answer[i]) >= 0) {
                  score += dscore
                } else { // 多选为0分
                  score = 0
                  break
                }
              }
            }
            score = Math.floor(score)
          } else if (item.type === 3) { // 编程题
            score = ''
            problem.result = -1
            if (problem.student_answer.length !== 0) {
              problem.result = 12
              api.postAnswer('/problems/submit', {
                problem_id: problem.problem_id,
                protype: 3,
                source: problem.student_answer,
                language: this.quizInfo.language
              }).then(() => {
                api.getQuestionResult('/problems/submit', problem.problem_id).then((res) => {
                  this.userAnswer[item.type - 1].problem_info[idx].result = res.data.result
                })
              })
            } else {
              score = 0
            }
          } else if (item.type === 4) { // 问答题
            score = (problem.student_answer.length !== 0) ? '' : 0
          } else if (item.type === 5) { // 判断题
            dscore = quizDetail.value
            if (quizDetail.problem_info[idx].answer === problem.student_answer) {
              score += dscore
            }
          }
          problem.score = score
          return problem
        })
        return item
      })
      // 重新渲染数据
      this.changeDate = new Date().getTime()
    },
    getStudentList () {
      api.getStudentAnswerList('/quiz/teacher_ckeck/student_paper_list', this.quizId).then(res => {
        if (res.success === 'true') {
          res.data.answer_sheet.forEach((i, index) => {
            this.student_list.push(i)
            if (this.currentNo === -1 && !i.isCorrected) {
              this.currentNo = index
            }
          })
          this.currentNo = this.currentNo === -1 ? this.student_list.length : this.currentNo
          // 如果序号大于学生列表总数，则是意味着已经改完
          if (this.currentNo >= this.student_list.length) {
            this.changeCurStep(1)
            return
          }
          // 取到当前批改的学生答题卷数据
          this.getStudentAnswer()
        }
      })
    },
    getCheckTips () {
      this.quizCheckStartTips = [
        `因采用匿名改卷机制，改卷期间无法得知答卷学生信息，只能得知待改份数。`,
        `所有考试皆采用机器评分与人工评分相结合，其中，由于选择题，填空题，判断题有确切答案，所以此三种类型会先进行机器评分，而编程题与问答题需要教师手动评分，编程题会带有运行结果。`,
        `由于改卷是个长时间的工作，本平台同时提供了夜间模式与日间模式两个界面UI，尽可能为各位教师提供一个好的改卷体验。`,
        `在评分项一栏内有各种颜色的圆球，分别有以下含义：<span style="color: rgb(161, 204, 69)">绿色</span>-机器已评分，满分项；` +
          `<span style="color: rgb(255, 153, 0)">橙色</span>-机器已评分，失分项；` +
          `<span style="color: rgb(255, 51, 129)">红色</span>-机器已评分，未答项；` +
          `<span style="color: rgb(102, 102, 122)">灰色</span>-编程题与问答题，不进行机器评分，需要用户手动评分，评分后，若满分则变为绿色，否则为橙色。`,
        `单击圆球进行评分，单击圆球前的数字展示该题，单击题目类型（如“填空题”），展示此类所有题目。`,
        `缺考的学生教师可直接点击批改结束跳过，同时对其的所有评分不作效。`,
        `若评改部分后退出重进，可接着评改，但也可按本页下“从第一项开始进入”从第一项开始批改。`
      ]
    }
  },
  created () {
    this.getQuizId()
    this.getQuizData()
  }
}
</script>

