<template>
  <div class="rankBox" :style="{float: float}">
    <div class="title">
      <p class="title-cn">{{rankTitle}}</p>
      <p class="title-en">{{titleEn}}<span style="color:#f48118">list</span></p>
    </div>
    <table class="rank-table" v-loading="rankData.length === 0">
      <thead class="t-head">
        <tr>
          <td>名次</td>
          <td>昵称</td>
          <td>通过率</td>
          <td>通过数</td>
        </tr>
      </thead>
      <tbody class="t-body">
        <tr class="row" v-if="item.submit > 0" v-for="(item, index) in rankData" :key="index">
          <td class="rank-num">
            <span style="width: 70px">{{index + 1}}</span>
          </td>
          <td class="nick">
            <span class="overflow-text" @click="getUserInfo(item.uid)"  style="width: 200px; display: block" :title="item.nick">{{item.nick}}</span>
          </td>
          <td class="rate-num">
            <span style="width: 80px">{{getPassRate(item.accept, item.submit)}}</span>
          </td>
          <td class="pass-num">
            <span style="width: 70px">{{item.accept}}</span>
          </td>
        </tr>
        <tr class="row" v-if="rankData.length === 0" v-for="(item, index) in 8" :key="index">
          <td colspan="4" v-if="index === 0" style="text-align: center;color: #808080">暂无数据</td>
          <td colspan="4" v-else></td>
        </tr>
        <tr class="row" v-if="item.submit === 0" v-for="(item, index) in rankData" :key="index">
          <td colspan="4" v-if="index === 0" style="text-align: center;color: #808080">暂无数据</td>
          <td colspan="4" v-else></td>
        </tr>
      </tbody>
    </table>
    <div class="bottom">
      <button @click="toLookMore" class="look-more" :data-ranktype="titleEn">查看更多</button>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    rankTitle: {
      type: String,
      default: '总榜'
    },
    titleEn: {
      type: String,
      default: 'total'
    },
    float: {
      type: String,
      default: 'none'
    },
    rankData: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {
    }
  },
  methods: {
    toLookMore (e) {
      this.$emit('toRank', e)
    },
    getUserInfo (uid) {
      this.$emit('getUserInfo', uid)
    },
    getPassRate (accept, submit) {
      if (submit) {
        return ((accept / submit).toFixed(2) * 100).toFixed(0) + '%'
      } else {
        return 0
      }
    }
  }
}
</script>
<style scoped>
  .rankBox {
    width: 100%;
    overflow: hidden;
  }
  .title {
    display: block;
    background-color: #f48118;
    border-radius: 50%;
    color: white;
    width: 60px;
    height: 60px;
    margin: 0 auto;
    padding-top: 5px;
    margin-bottom: 30px;
  }
  .title-cn {
    font-size: 20px;
    /*margin-top: 10px;*/
    text-align: center;
  }
  .title-en {
    /*margin-left: 5px;*/
    width: 95px;
    text-align: center;
    font-size: 16px;
  }
  .title-en > span {
    margin-left: 10px;
  }
  .rank-table {
    background-color: white;
    box-shadow: 0px 0px 27px 0px rgba(210, 210, 210, 0.25);
    width: 550px;
    border-collapse: collapse;
    border-radius: 5px;
    margin: 0px auto;
  }
  .rank-table .t-head {
  }
  .rank-table .t-head tr {
    height: 44px;
    line-height: 44px;
    border-bottom: 1px solid #e4e8f6;
  }
  .rank-table .t-head tr td {
    font-size: 16px;
    color: #2b303e;
    font-weight: normal;
  }
  .rank-table .t-head tr td:first-child {
    padding-left: 20px;
    box-sizing: border-box;
  }
  .rank-table .t-head tr td:last-child {
    padding-right: 20px;
    box-sizing: border-box;
  }

  .rank-table .t-body {
  }
  .rank-table .t-body .row {
    height: 34px;
    line-height: 34px;
  }
  .rank-table .t-body .row td span {
    display: block;
    height: 22px;
    line-height: 22px;
    text-align: left;
    font-size: 14px;
    color: #2b303e;
  }
  .rank-table .t-body .row .rank-num  {
    padding-left: 20px;
    box-sizing: border-box;
  }
  .rank-table .t-body .row .nick span {
    padding-right: 20px;
    box-sizing: border-box;
    cursor: pointer;
  }
  .bottom {
    width: 100%;
    height: 24px;
    margin-top: 31px;
    text-align: center;
    margin-bottom: 20px;
  }
  .look-more {
    width: 109px;
    height: 26px;
    line-height: 26px;
    font-size: 14px;
    color: #ffffff;
    background-color: #584f64;
    border: none;
    outline: none;
    cursor: pointer;
  }

</style>



