              label="完成情况">
              <template slot-scope="scope">
                <span>已完成{{scope.row.solved_count}}题</span>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              label="操作">
              <template slot-scope="scope">
                <div v-if="scope.row.status === 1 && info.identity === '2'" style="display: inline-block">
                  <el-button type="danger" icon="el-icon-delete" size="small" @click.stop="endTest(scope.row)" class="delete button-hover"></el-button>
                  <el-button type="primary" icon="el-icon-edit" size="small" @click.stop="updateTestInfo(scope.row)" class="edit button-hover"></el-button>
                </div>
                <el-button v-if="info.identity === '1'" type="primary" icon="el-icon-view" size="small" @click.stop="changeTestProblem(scope.row)" class="lookTest button-hover"></el-button>
                <el-button v-else type="primary" icon="el-icon-view" size="small" @click.stop="checkStudentTestBindClass(scope.row)" class="lookTest button-hover"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div :class="(info.identity === '1')?'':'questionList-container'">
        <el-dialog
          :title="info.identity === '1'?'测试成绩':'试题列表 | 学生总人数：'+studentCount"
          :visible.sync="testDialog"
          width='60%'>
          <div>
            <div style="margin-top: 20px">
              <el-table
                :data="problemTableData"
                style="width: 100%;cursor: pointer"
              >
                <el-table-column
                  type="index"
                  align="center"
                  width="80"
                  label="编号">
                </el-table-column>
                <el-table-column
                  align="center"
                  width="120"
                  label="题目编号">
                  <template slot-scope="scope">
                    <span class="testGradeProblemId" @click="toTestProblemDetail(scope.row)">{{scope.row.problem_id}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="title"
                  align="center"
                  label="标题">
                </el-table-column>
                <el-table-column
                  prop="submit"
                  width="120"
                  align="center"
                  label="通过人数"
                  v-if="info.identity === '2'">
                </el-table-column>
                <el-table-column
                  prop="accept"
                  width="120"
                  align="center"
                  label='总通过数'
                  v-if="info.identity === '2'">
                </el-table-column>
                <el-table-column
                  prop="submit"
                  width="120"
                  align="center"
                  :label="info.identity === '1'?'提交数':'总提交数'">
                </el-table-column>
                <el-table-column
                  align="center"
                  width="120"
                  label="完成状态"
                  v-if="info.identity === '1'">
                  <template slot-scope="scope">
                    <span v-if="scope.row.accept === 0" style="color: red">未完成</span>
                    <span v-if="scope.row.accept > 0" style="color: #6d9c35">已完成</span>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="testDialog = false">确 定</el-button>
          </span>
        </el-dialog>
        </div>
        <el-dialog
          title="修改测验信息"
          :visible.sync="updateTestInfoDialog"
          width="25%">
          <el-form :model="updateTestInfoFrom" label-width="100px">
            <el-form-item label="测验名称">
              <el-input v-model="updateTestInfoFrom.name" placeholder="请填写测验名称"></el-input>
            </el-form-item>
            <el-form-item label="测试结束时间">
              <el-col :span="24">
                <el-form-item prop="date1">
                  <el-date-picker type="date" placeholder="选择日期" v-model="updateTestInfoFrom.endTime" style="width: 100%;"></el-date-picker>
                </el-form-item>
              </el-col>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="updateTestInfoDialog = false">取 消</el-button>
            <el-button type="primary" @click="postTestInfo">确 定</el-button>
          </span>
        </el-dialog>
        <el-dialog
          :title="testStudentInfo.exam_name"
          :visible.sync="testStudentInfoDialog"
          width="1000px">
          <div v-loading="teacherTestLoading">
            <div class="testStudentInfo-head">
              <div class="testStudentInfo-head-inline">
                <span>总人数:</span>
                <span class="content">{{testStudentInfo.student_count}}</span>
              </div>
              <div class="testStudentInfo-head-inline">
                <span>通过人数:</span>
                <span class="content">{{testStudentInfo.pass_student_count}}</span>
              </div>
              <div class="testStudentInfo-head-inline">
                <span>题目数:</span>
                <span class="content">{{testStudentInfo.problem_count}}</span>
              </div>
              <div class="testStudentInfo-head-inline">
                <span>测验状态:</span>
                <span class="processing" v-if="testStudentInfo.status === 1">正在进行中</span>
                <span class="end" v-else>已结束</span>
              </div>
            </div>
            <div class="testStudentInfo-table">
              <el-table
                :data="testStudentInfo.student_list"
                stripe
                style="width: 100%">
                <el-table-column
                  prop="nick"
                  align="center"
                  label="昵称">
                </el-table-column>
                <el-table-column
                  prop="code"
                  align="center"
                  label="学号">
                </el-table-column>
                <el-table-column
                  prop="accepted"
                  align="center"
                  label="通过数">
                </el-table-column>
                <el-table-column
                  prop="submit"
                  align="center"
                  label="提交数">
                </el-table-column>
                <el-table-column
                  align="center"
                  label="完成情况">
                  <template slot-scope="scope">
                    <span v-if="scope.row.finished === 1" style="color: #67c23a">已完成</span>
                    <span v-else style="color: red">未完成</span>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="testStudentInfoDialog = false">取 消</el-button>
            <el-button type="primary" @click="testStudentInfoDialog = false">确 定</el-button>
          </span>
        </el-dialog>
        <el-dialog
          title="请先选择班级"
          :visible.sync="bindClassOfTest"
          width="20%">
          <el-select size="small" v-model="bindClassIdOfTest" placeholder="请选择班级" style="width: 100%">
            <el-option
              v-for="item in bindClassOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <span slot="footer" class="dialog-footer">
            <el-button @click="bindClassOfTest = false">取 消</el-button>
            <el-button type="primary" @click="checkStudentTest">确 定</el-button>
          </span>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'
import api from '@/api/personCenter'
import personalHead from '@/base/personal/person-head'
import Mycourse from '@/base/personal/Mycourse'
import Information from '@/base/personal/Information'
import Setting from '@/base/personal/Setting'
import brushingResults from '@/base/personal/brushing-results'
import CallFeedback from '@/base/personal/CallFeedback'
import itemContribution from '@/base/personal/itemContribution'
import MyExam from '@/base/personal/MyExam'
import MyEssay from '@/base/personal/MyEssay'
import MyMessage from '@/base/personal/MyMessage'
import checkPost from '@/base/personal/checkPost'

export default{
  components: {
    personalHead,
    Mycourse,
    Information,
    Setting,
    brushingResults,
    CallFeedback,
    itemContribution,
    MyExam,
    MyEssay,
    MyMessage,
    checkPost
  },
  data () {
    return {
      reviewBindClassId: '',
      reviewTotal: 0,
      bindClassIdOfTest: '',
      bindClassOptions: [],
      bindClassOfTest: false,
      toExamData: {},
      testStudentInfo: {},
      testStudentInfoDialog: false,
      updateTestInfoFrom: {
        name: '',
        endTime: ''
      },
      problemTableData: [],
      updateTestInfoDialog: false,
      testDialog: false,
      disable: true,
      info: {},
      isShow: '1',
      tabPosition: 'left',
      problemInfo: {},
      hasCourse: false,
      courseData: [],
      courseBackdata: {},
      testTabledata: [
        {
          testId: '0001',
          courseName: '第一次作业',
          authorizationStatus: '教师授权',
          status: '已结束',
          questionNum: 20,
          starTime: '2016-04-13 19:02:43',
          endTime: '2016-04-13 21:02:43',
          completion: '完成14道题'
        }
      ],
      studentCount: 0,
      courseId: '',
      updateTestInfoData: {},
      theRowId: '',
      ReviewData: [],
      ReviewPage: 1,
      ReviewPageSize: 10,
      getAllReviewData: [],
      reviewBindClassIdList: [],
      theReviewData: {},
      teacherTestLoading: true,
      testStatus: 0
    }
  },
  methods: {
    changeCoursesTable (data) {
      if (data === true) {
        this.getCourse()
      }
    },
    toExamList (data) {
      this.isShow = '4'
      this.toExamData = data
    },
    updateTestInfo (row) {
      this.updateTestInfoDialog = true
      this.updateTestInfoFrom.name = row.exam_name
      this.updateTestInfoFrom.endTime = row.stopTime
      this.updateTestInfoData = row
    },
    postTestInfo () {
      const _this = this
      let obj = {
        exam_id: this.updateTestInfoData.exam_id,
        exam_name: this.updateTestInfoFrom.name,
        stop_time: this.timeFormat(this.updateTestInfoFrom.endTime)
      }
      api.putUpdateTestInfo('/courses/exams', obj).then(function (res) {
        if (res.success === 'true') {
          _this.$message({
            message: '修改测验信息成功',
            type: 'success'
          })
          setTimeout(function () {
            api.getCoursesTest('/courses/exams', _this.courseId).then(function (res) {
              _this.testTabledata = res.data
              for (var i = 0; i < res.data.length; i++) {
                let starTime = res.data[i].create_time.split('T')
                _this.testTabledata[i].createTime = starTime[0] + ' ' + starTime[1]
                let endTime = res.data[i].stop_time.split('T')
                _this.testTabledata[i].stopTime = endTime[0] + ' ' + endTime[1]
              }
            })
          }, 300)
        } else {
          _this.$message.error('修改测验信息失败')
        }
      })
      this.updateTestInfoDialog = false
    },
    endTest (row) {
      const _this = this
      api.deleteTest('/courses/exams', row.exam_id).then(function () {
        var userObj
        var storage = window.sessionStorage
        // 将JSON字符串转换成为JSON对象输出
        var user = storage.getItem('userInfo')
        if (user !== 'undefined' && user !== null && user !== undefined) {
          userObj = JSON.parse(user)
        } else {
          console.log(userObj)
        }
        _this.$message({
          message: '结束成功',
          type: 'success'
        })
        api.getCoursesTest('/courses/exams', _this.courseId).then(function (res) {
          _this.testTabledata = res.data
          for (var i = 0; i < res.data.length; i++) {
            let starTime = res.data[i].create_time.split('T')
            _this.testTabledata[i].createTime = starTime[0] + ' ' + starTime[1]
            let endTime = res.data[i].stop_time.split('T')
            _this.testTabledata[i].stopTime = endTime[0] + ' ' + endTime[1]
          }
        })
      })
    },
    changeTestProblem (row) {
      const _this = this
      _this.problemTableData = []
      var userObj
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var user = storage.getItem('userInfo')
      if (user !== 'undefined' && user !== null && user !== undefined) {
        userObj = JSON.parse(user)
      } else {
        console.log(userObj)
      }
      api.getTestProblems('/courses/exams/problems', row.exam_id).then(function (res) {
        _this.problemTableData = res.data.problem_list
        _this.testStatus = res.data.status
        _this.studentCount = res.data.student_count
      })
      this.testDialog = true
    },
    checkStudentTestBindClass (row) {
      this.bindClassOfTest = true
      this.bindClassOptions = []
      if (this.courseBackdata !== undefined && this.courseBackdata.class_id.length > 0) {
        for (let i = 0; i < this.courseBackdata.class_id.length; i++) {
          let json = {
            value: this.courseBackdata.class_id[i],
            label: this.courseBackdata.class_name[i]
          }
          this.bindClassOptions.push(json)
        }
      }
      this.theRowId = row.exam_id
      //      this.checkStudentTest(row)
    },
    checkStudentTest () {
      this.testStudentInfoDialog = true
      this.bindClassOfTest = false
      const _this = this
      let obj = {
        examId: this.theRowId,
        classId: this.bindClassIdOfTest
      }
      api.getStudentTestList('/courses/exams/result', obj).then(function (res) {
        _this.testStudentInfo = res.data
        _this.teacherTestLoading = false
      })
    },
    selectItem (data) {
      this.isShow = data
      this.$router.push({
        name: 'PersonalCenter',
        params: {
          isShow: data
        }
      })
    },
    backPreson () {
      this.disable = !this.disable
    },
    addTest () {
      this.$router.push({path: '/addTest', query: {courses_id: this.courseBackdata.courses_id}})
    },
    getListInfo () {
      const _this = this
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json !== 'undefined' && json !== null && json !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
        api.getInfoList('/users/detail', uid).then(function (res) {
          _this.info = res.data.user_info
          if (_this.info.identity !== '0') {
            _this.hasCourse = true
          }
          _this.problemInfo = res.data.problem_info
          if (_this.info.identity === '1' || _this.info.identity === '2') {
            _this.getCourse()
          }
        })
      }
    },
    refreshCourse () {
      this.getCourse()
    },
    getCourse () {
      const _this = this
      //      let data = {
      //        uid: _this.info.uid
      //      }
      api.getCourseList('/courses').then(function (res) {
        _this.courseData = res.data
        if (_this.courseData === undefined || _this.courseData === '' || _this.courseData === null) {
          _this.courseData = []
        }
      })
    },
    disablePerson (disablePerson) {
      const _this = this
      this.courseBackdata = disablePerson
      this.courseId = disablePerson.courses_id
      var userObj
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var user = storage.getItem('userInfo')
      if (user !== 'undefined' && user !== null && user !== undefined) {
        userObj = JSON.parse(user)
      } else {
        console.log(userObj)
      }
      api.getCoursesTest('/courses/exams', this.courseId).then(function (res) {
        _this.testTabledata = res.data
        for (var i = 0; i < res.data.length; i++) {
          let starTime = res.data[i].create_time.split('T')
          _this.testTabledata[i].createTime = starTime[0] + ' ' + starTime[1]
          let endTime = res.data[i].stop_time.split('T')
          _this.testTabledata[i].stopTime = endTime[0] + ' ' + endTime[1]
        }
      })
      this.disable = disablePerson.disable
    },
    getAddTestBack () {
      var coursesID = this.$route.query.coursesID
      this.courseId = coursesID
      if (coursesID !== undefined) {
        const _this = this
        var userObj
        var storage = window.sessionStorage
        // 将JSON字符串转换成为JSON对象输出
        var user = storage.getItem('userInfo')
        if (user !== 'undefined' && user !== null && user !== undefined) {
          userObj = JSON.parse(user)
        } else {
          console.log(userObj)
        }
        api.getCoursesTest('/courses/exams', this.courseId).then(function (res) {
          _this.testTabledata = res.data
          for (var i = 0; i < res.data.length; i++) {
            let starTime = res.data[i].create_time.split('T')
            _this.testTabledata[i].createTime = starTime[0] + ' ' + starTime[1]
            let endTime = res.data[i].stop_time.split('T')
            _this.testTabledata[i].stopTime = endTime[0] + ' ' + endTime[1]
          }
        })
        this.disable = this.$route.query.showCourseTest
      }
    },
    timeFormat (date) {
      var d = new Date(date)
      return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
    },
    updateInfo (data) {
      const _this = this
      api.updateUserInfo('/users', data).then(function (res) {
        if (res.success === 'true') {
          _this.$message({
            message: '修改信息成功',
            type: 'success'
          })
          var storage = window.sessionStorage
          storage.setItem('userInfo', JSON.stringify(data))
          _this.getListInfo()
          _this.$refs.userInfo.handleEdit()
        } else {
          _this.$message({
            message: '修改失败，请稍候再试',
            type: 'warning'
          })
          _this.$refs.userInfo.handleEdit()
        }
      })
    },
    updatePass (form) {
      const _this = this
      api.updatePassword('/users/password', form).then(function (res) {
        if (res.success === 'true') {
          _this.$message({
            message: '恭喜你，这是一条成功消息',
            type: 'success'
          })
        }
      })
    },
    // 拿到所有的批阅试卷时的数据
    getReviewData (reviewData, classId = null, isFirst = true) {
      this.theReviewData = reviewData
      this.reviewBindClassIdList = []
      const _this = this
      _this.getAllReviewData = []
      var json
      if (reviewData) {
        for (let i = 0; i < reviewData.class_list.length; i++) {
          let obj = {
            value: reviewData.class_list[i].class_id,
            label: reviewData.class_list[i].class_name
          }
          _this.reviewBindClassIdList.push(obj)
          if (isFirst === true) {
            _this.reviewBindClassId = _this.reviewBindClassIdList[0].value
          }
        }
        if (classId === null) {
          json = {
            class_id: reviewData.class_list[0].class_id,
            quiz_id: reviewData.quiz_id
          }
        } else {
          json = {
            class_id: classId,
            quiz_id: reviewData.quiz_id
          }
        }
        api.getQuizStudents('/quiz/students', json).then(function (res) {
          _this.getAllReviewData = res.data
          _this.reviewTotal = res.data.length
          _this.getReviewDataList()
        })
      }
    },
    // 用拿到的批阅试卷的数据进行处理
    getReviewDataList (page = this.ReviewPage, pagesize = this.ReviewPageSize) {
      this.ReviewData = []
      for (var i = 0; i < this.getAllReviewData.length; i++) {
        if (i < ((page - 1) * pagesize + (pagesize - 1)) && i >= ((page - 1) * pagesize)) {
          this.ReviewData.push(this.getAllReviewData[i])
        }
      }
    },
    reviewPageSizeChange (val) {
      this.ReviewPageSize = val
      this.getReviewDataList(this.ReviewPage, val)
    },
    reviewPageChange (val) {
      this.ReviewPage = val
      this.getReviewDataList(val, this.ReviewPageSize)
    },
    reviewBindClassIdChange (val) {
      this.ReviewPage = 1
      this.getReviewData(this.theReviewData, val, false)
    },
    toStarReview (row) {
      this.$router.push({
        path: '/previewExam',
        name: 'previewExam',
        query: {
          isMarkExam: true,
          code: row.code,
          quizId: this.theReviewData.quiz_id
        }
      })
    },
    toTestProblemDetail (row) {
      if (this.testStatus === 0) {
        this.$message({
          showClose: true,
          type: 'error',
          message: '该测验已经结束，不能在此答题'
        })
      } else {
        if (row.problem_type === 1) {
        } else if (row.problem_type === 2) {
        } else if (row.problem_type === 3) {
          // 如果非竞赛题目可以直接跳转
          const {href} = this.$router.resolve({
            path: '/library/questionDetail',
            query: {PID: row.problem_id}
          })
          window.open(href, '_blank')
        } else if (row.problem_type === 4) {
        } else if (row.problem_type === 5) {
        }
      }
    },
    queryInit () {
      // console.log(this.$route.params)
      if (this.$route.params.isShow) {
        this.isShow = this.$route.params.isShow.toString()
      }
    },
    clickCall (row) {
      if (this.info.identity === '2') {
        this.changeTestProblem(row)
      }
    }
  },
  computed: {
    ...mapGetters({
      userInfo: 'user'
    })
  },
  created () {
    this.queryInit()
    this.getListInfo()
    this.getAddTestBack()
  },
  watch: {
    $route () {
      if (this.$route.name === 'PersonalCenter') {
        this.queryInit()
      }
    }
  }
}
</script>

<style scoped>
  .personal {
    width: 1170px;
    margin: 0 auto;
    padding-top: 20px;
  }
  .personal-right /deep/ .el-col-18 {
    width: 77%;
  }
  .personal-right /deep/ .el-col-offset-1 {
    margin-left: 2.15%;
  }
  .personal .courseTest {
    width: 100%;
    height: 500px;
    background-color: white;
  }
  .el-menu-vertical-demo {
    min-height: 700px;
  }
  .personal .courseTest .courseTest-head {
    width: 100%;
    height: 66px;
    padding: 15px 10px;
    border-bottom: 1px solid gainsboro;
    box-sizing: border-box;
  }
  .personal .courseTest .courseTest-head .back {
    margin-top: 5px;
    float: left;
  }
  .personal .courseTest .courseTest-head .courseName {
    float: left;
    line-height: 35px;
    height: 35px;
    font-size: 20px;
    color: #555;
    margin-left: 15px;
    font-weight: 500;
  }
  .margin-r {
    margin-left: 40px;
  }
  .testHead {
  }
  .fright {
    float: right;
  }
  .button-hover{
    width: 40px;
    transition: width 0.5s;
    -moz-transition: width 0.5s;
    -webkit-transition: width 0.5s;
    -o-transition: width 0.5s;
  }
  .delete:hover:after{
    content: '结束';
  }
  .edit{
    margin-left: 0px;
  }
  .edit:hover:after{
    content: '编辑';
  }
  .lookTest:hover:after{
    content: '查看测试';
  }
  .button-hover:hover{
    width: 80px;
  }
  .testStudentInfo-head{
    width: 100%;
    height: 40px;
  }
  .testStudentInfo-head-inline{
    padding: 0 40px;
    display: inline-block;
  }
  .testStudentInfo-head-inline span{
    display: inline-block;
  }
  .testStudentInfo-head-inline .content{
    margin-left: 20px;
    display: inline-block;
    vertical-align: middle;
    padding: 0 12px;
    height: 20px;
    background-color: #f48118;
    text-align: center;
    color: white;
    border-radius: 10px;
  }
  .testStudentInfo-head-inline .processing{
    margin-left: 20px;
    display: inline-block;
    vertical-align: middle;
    padding: 0 12px;
    height: 20px;
    background-color: #67c23a;
    text-align: center;
    color: white;
    border-radius: 10px;
  }
  .testStudentInfo-head-inline .end{
    margin-left: 20px;
    display: inline-block;
    vertical-align: middle;
    padding: 0 12px;
    height: 20px;
    background-color: #f56c6c;
    text-align: center;
    color: white;
    border-radius: 10px;
  }
  .reviewDataDiv{
    padding: 0 20px 40px 20px;
    box-sizing: content-box;
    background-color: white;
    min-height: 700px;
  }
  .reviewDataDiv-head{
    height: 50px;
    line-height: 50px;
    font-size: 20px;
    text-align: center;
  }
  .inReviewButton{
    height: 30px;
    line-height: 30px;
    padding: 0 20px;
    background-color: #f48118;
    color: white;
  }
  .changeReviewButton{
    height: 30px;
    line-height: 30px;
    padding: 0 20px;
    background-color: #8ecc45;
    color: white;
  }
  .reviewPagination{
    margin-top: 30px;
    margin-left: 110px;
    padding: 10px 0 0 0;
  }
  .reviewStudentList{
    display: block;
    color: #333333;
    font-size: 26px;
    font-weight: 600;
  }
  .reviewClassSelect{
    float: right;
    margin-top: -30px;
  }
  .testGradeProblemId{
    font-size: 14px;
    color: #333333;
    transition: all .4s;
    cursor: pointer;
  }
  .testGradeProblemId:hover{
    font-size: 18px;
    color: #f48118;
  }
  /*试题列表*/
  .questionList-container /deep/ .el-dialog__wrapper .el-dialog__header {
    background-color: #F2F2F2;
  }
  .questionList-container /deep/ .el-dialog__wrapper .el-dialog__body {
    padding: 10px 0;
  }
</style>



