<template>
  <div class="setting">
    <div class="password">
      <p class="qq-title">密码</p>
      <div class="qqFrom">
        <el-form ref="passwordForm" :rules="passwordRule" :model="changePassword" label-width="80px">
          <el-form-item label="旧密码" prop="ori_password">
            <el-input v-model="changePassword.ori_password" placeholder="输入旧密码" type="password"></el-input>
          </el-form-item>
          <el-form-item label="新密码" prop="new_password">
            <el-input v-model="changePassword.new_password" placeholder="输入新密码" type="password"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="re_password">
            <el-input v-model="changePassword.re_password" placeholder="再次输入密码" type="password"></el-input>
          </el-form-item>
        </el-form>
        <div class="button">
          <el-button size="medium" type="success" @click="updatePassword">保存</el-button>
        </div>
      </div>
    </div>
    <div class="QQ">
      <p class="qq-title">手机号</p>
      <div class="qqFrom" v-if="formdata.qq !== '' && formdata.qq !== null">
        <el-form ref="form" :model="formdata" label-width="80px">
          <el-form-item label="旧的qq">
            <el-input v-model="formdata.qq" placeholder="输入旧的手机号" disabled></el-input>
          </el-form-item>
          <el-form-item label="新的qq">
            <el-input v-model="formdata.newqq" placeholder="输入新的手机号"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input v-model="formdata.password" placeholder="输入验证码"></el-input>
          </el-form-item>
        </el-form>
        <div class="button">
          <el-button size="medium" type="success">保存</el-button>
        </div>
      </div>
      <div class="dataNull" v-else>
        <img src="/static/image/empty3.png" alt="">
        <div class="dataNull-div">
          <span>你暂时还没有绑定手机号</span>
          <el-button @click="isShowQ = true">去绑定</el-button>
        </div>
      </div>
      <div class="qqDialog">
        <el-dialog
          title="绑定手机号"
          :visible.sync="isShowQ"
          width="30%">
          <el-form ref="form" :model="checkdata" label-width="80px">
            <el-form-item label="手机号码">
              <el-input v-model="checkdata.qq" placeholder="输入手机号码"></el-input>
            </el-form-item>
            <el-form-item label="验证码">
              <el-input v-model="checkdata.password" placeholder="输入验证码"></el-input>
            </el-form-item>
            <div class="submit">
              <el-button @click="checkQQ">确认</el-button>
            </div>
          </el-form>
        </el-dialog>
      </div>
    </div>
    <div class="email">
      <p class="email-title">邮箱</p>
      <div class="emailFrom" v-if="User.email !== '' && User.email !== null">
        <el-form ref="form" :model="User" label-width="80px">
          <el-form-item label="旧邮箱">
            <el-input v-model="User.email" placeholder="输入旧邮箱地址" disabled></el-input>
          </el-form-item>
          <el-form-item label="新邮箱">
            <el-input v-model="formdata.newemail" placeholder="输入新邮箱邮箱"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input v-model="formdata.password" placeholder="输入密码"></el-input>
          </el-form-item>
        </el-form>
        <div class="button">
          <el-button size="medium" type="success">保存</el-button>
        </div>
      </div>
      <div class="dataNull" v-else>
        <img src="/static/image/empty3.png" alt="">
        <div class="dataNull-div">
          <span>你暂时还没有绑定邮箱</span>
          <el-button @click="isShowE = true">去绑定</el-button>
        </div>
      </div>
      <div class="EmailDialog">
        <el-dialog
          title="绑定邮箱"
          :visible.sync="isShowE"
          width="30%">
          <el-form ref="form" :model="checkdata" label-width="80px">
            <el-form-item label="邮箱地址">
              <el-input v-model="checkdata.email" placeholder="输入邮箱地址"></el-input>
            </el-form-item>
            <el-form-item label="验证码">
              <el-input v-model="checkdata.password" placeholder="输入验证码"></el-input>
            </el-form-item>
            <div class="submit">
              <el-button @click="checkEmail">确认</el-button>
            </div>
          </el-form>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
export default{
  data () {
    var rePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.changePassword.new_password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      User: {},
      // 控制邮箱弹框的变量
      isShowE: false,
      // 控制QQ弹框的变量
      isShowQ: false,
      formdata: {
        qq: '',
        newqq: '',
        password: '',
        oldemail: '',
        newemail: '',
        newpassword: '',
        Checkpassword: ''
      },
      checkdata: {
        qq: '',
        newqq: '',
        password: '',
        email: '',
        newpassword: '',
        Checkpassword: ''
      },
      changePassword: {
        ori_password: '',
        new_password: '',
        re_password: ''
      },
      passwordRule: {
        ori_password: [
          {required: true, message: '请输入旧密码', trigger: 'blur'}
        ],
        new_password: [
          {required: true, message: '请输入新密码', trigger: 'blur'},
          {min: 6, max: 16, message: '密码长度在6到16个字符之间', trigger: 'blur'}
        ],
        re_password: [
          {validator: rePassword}
        ]
      }
    }
  },
  methods: {
    checkQQ () {
      const _this = this
      if (_this.formdata.password === _this.checkdata.password) {
        _this.formdata.qq = _this.checkdata.qq
        this.isShowQ = false
      } else {
        _this.$message.error('你输入的密码不正确')
      }
    },
    checkEmail () {
      const _this = this
      if (_this.formdata.password === _this.checkdata.password) {
        _this.formdata.oldemail = _this.checkdata.email
        this.isShowE = false
      } else {
        _this.$message.error('你输入的密码不正确')
      }
    },
    getUserStore () {
      var storage = window.sessionStorage
      var userData = storage.getItem('userInfo')
      if (userData !== 'undefined' && userData !== null && userData !== undefined) {
        var jsonObj = JSON.parse(userData)
        this.User = jsonObj
      }
    },
    updatePassword () {
      const _this = this
      this.$refs.passwordForm.validate(function (valid) {
        if (valid) {
          _this.$emit('updatePass', _this.changePassword)
        }
      })
    }
  },
  created () {
    this.getUserStore()
  }
}
</script>

<style scoped>
  .setting{
    background-color: white;
    padding: 10px 20px 20px;
  }
  .qq-title{
    font-weight: 700;
    font-size: 16px;
    margin-top: 13px;
    line-height: 22px;
    padding-bottom: 10px;
    border-bottom: 1px solid gainsboro;
    margin-bottom: 20px;
  }
  .qqFrom{
    padding: 0 350px 0 100px;
  }
  .button /deep/ .el-button--medium{
    margin-left: 160px;
    padding: 10px 40px;
  }
  .email-title{
    font-weight: 700;
    font-size: 16px;
    margin-top: 13px;
    line-height: 22px;
    padding-bottom: 10px;
    border-bottom: 1px solid gainsboro;
    margin-bottom: 20px;
  }
  .emailFrom{
    padding: 0 350px 0 100px;
  }
  .dataNull{
    position: relative;
    padding: 20px 300px 20px 100px;
  }
  .dataNull-div{
    position: absolute;
    top:110px;
    left: 300px;
    display: inline-block;
  }
  .dataNull-div span{
    display: block;
    margin-bottom: 10px;
  }
  .dataNull-div /deep/ .el-button{
    background: #00BC9B;
    color: white;
    padding: 10px 40px;
  }
  .submit /deep/ .el-button{
    display: block;
    margin: auto auto;
    background: #00BC9B;
    color: white;
    padding: 10px 40px;
  }
</style>



