import {getRequest} from './config'

function getcontentsRank (link, id, page, pagesize) {
  return getRequest(link, {
    contest_id: id,
    page: page,
    pagesize: pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}
const api = {
  getcontentsRank
}

export default api



