import {getRequest, postFromData, delFromData, postJsonData, postEditorImg, putFromData} from './config'

function getInfoList (link, data) {
  return getRequest(link, {
    uid: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getInfoData (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getHotTagsList (link, data) {
  return getRequest(link, {
    page: data.page,
    pagesize: data.pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getPostStats (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getPostsList (link, data) {
  return getRequest(link, {
    type: data.type,
    search: data.search,
    page: data.page,
    pagesize: data.pageSize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getTopPostsList (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

function postGiveLike (link, postId) {
  return postFromData(link, {
    post_id: postId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function delGiveLike (link, postId) {
  return delFromData(link, {
    post_id: postId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getPostSave (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

function publishPost (link, data) {
  return postJsonData(link, {
    params: {
      title: data.title,
      labels: data.labels,
      content: data.content,
      type: data.type,
      if_save: data.if_save,
      if_epic_post: data.if_epic_post
    }
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function deletePost (link, postId) {
  return delFromData(link, {
    post_id: postId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getPostDetail (link, data) {
  return getRequest(link, {
    post_id: data.post_id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function sendReply (link, data) {
  return postJsonData(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getRootReplies (link, data) {
  return getRequest(link, {
    post_id: data.post_id,
    page: data.page,
    pagesize: data.pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getChildReplies (link, data) {
  return getRequest(link, {
    reply_id: data.reply_id,
    page: data.page,
    pagesize: data.pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function postReadPost (link, postId) {
  return postFromData(link, {
    post_id: postId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function uploadImg (link, file) {
  return postEditorImg(link, {
    file: file
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getQuestionDetail (link, pid) {
  return getRequest(link, {
    problem_id: pid
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function putPostTop (link, pid) {
  return putFromData(link, {
    post_id: pid
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function delPostTop (link, postId) {
  return delFromData(link, {
    post_id: postId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function deleteReply (link, replyId) {
  return delFromData(link, {
    reply_id: replyId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

const api = {
  getInfoList,
  getInfoData,
  getHotTagsList,
  getPostStats,
  getPostsList,
  getTopPostsList,
  postGiveLike,
  delGiveLike,
  getPostSave,
  publishPost,
  deletePost,
  getPostDetail,
  sendReply,
  getRootReplies,
  getChildReplies,
  postReadPost,
  uploadImg,
  getQuestionDetail,
  putPostTop,
  delPostTop,
  deleteReply
}

export default api



