<template>
  <div class="footer">
    <div class="footer-top">
      <div
        class="footer-top_menuItem"
        v-for="(item, index) in menuList" :key="index">
        <template v-if="item.center">
          <div class="footer-top_menu_middle">
            <div class="footer-top_menu_middle_title">
              <img src="../../../static/image/logo.png" alt="" class="logo">
              <div class="footer-top_menu_middle_titleRight">
                <h4>开放，自由的学习平台</h4>
                <h5>DI&nbsp;Studio</h5>
              </div>
              <span class="centerSpan"></span>
            </div>
            <div class="footer-top_menu_middle_content">
              <p>"我热爱算法，虽然没有达到狂热的程度，但注定我已经离不开它，它就像有生命似的，改变了我、我的生活，而我也坚信，算法能改变人们、人们的生活以及全世界。所以我高傲地称自己为ACM达人，而你就是下一个,WELCOME！GO！GO！GO！" </p>
              <br/>
              <span>——&nbsp;DI&nbsp;Studio</span>
            </div>
          </div>
        </template>
        <template v-else>
          <h4 class="footer-top_menuItem_title">{{item.title}}</h4>
          <h5 class="footer-top_menuItem_subTitle">{{item.subTitle}}</h5>
          <ul
            class="footer-top_menuItem_childMenu"
            v-for="(childItem, childIdx) in item.children" :key="childIdx">
            <li @click="toUrl(childItem)">{{childItem.title}}</li>
          </ul>
        </template>
      </div>
    </div>
    <div class="footer-bottom">
      <p><a href="http://www.miitbeian.gov.cn" style="color: white" target="_blank">@copyright www.gpnuacm.com 粤ICP备18111867号</a></p>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      menuList: [{
        title: '关于我们',
        subTitle: 'Know',
        children: [{
          title: '往期公告',
          url: '/postSearch',
          query: {},
          params: {
            type: 3
          }
        }, {
          title: '团队信息',
          url: '/about',
          query: {},
          params: {}
        }, {
          title: '加入我们',
          url: ''
        }]
      }, {
        title: '帮助中心',
        subTitle: 'Assist',
        children: [{
          title: '新手指南',
          url: '/postDetail',
          query: {
          },
          params: {
            id: 78
          }
        }, {
          title: '教师指南',
          url: '/postDetail',
          query: {
          },
          params: {
            id: 55
          }
        }, {
          title: '操作详解',
          url: '/postDetail',
          query: {
          },
          params: {
            id: 77
          }
        }]
      }, { center: true }, {
        title: '关注我们',
        subTitle: 'Follow',
        children: [{
          title: '新浪微博',
          url: ''
        }, {
          title: '官方微信',
          url: ''
        }, {
          title: '公众号',
          url: ''
        }]
      }, {
        title: '联系我们',
        subTitle: 'Contact',
        children: [{
          title: '联系我们',
          url: ''
        }, {
          title: '电子邮箱',
          url: ''
        }, {
          title: '反馈意见',
          url: 'FUNCTION:callFeedback'
        }]
      }]
    }
  },
  methods: {
    toUrl ({url, query, params}) {
      if (url.indexOf('FUNCTION') >= 0) {
        this[url.split(':')[1]]()
        return
      }
      if (!url) {
        this.$message('敬请期待')
        return
      }
      for (let i in params) {
        url += '/' + params[i]
      }
      this.$router.push({path: url, query: query})
    },
    callFeedback () {
      this.$emit('callFeedback')
    }
  }
}
</script>

<style scoped>
  .footer{
    min-width: 1170px;
    /*padding-top: 50px;*/
    background-color: white;
  }
  .footer-top {
    max-width: 1300px;
    margin: 0 auto;
    padding: 0 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .footer-top_menuItem_title {
    font-size: 14px;
    cursor: default;
    padding-top: 60px;
  }
  .footer-top_menuItem_title::before,
  .footer-top_menuItem_title::after{
    content: '';
    display: inline-block;
    position: absolute;
    width: 20px;
    height: 1px;
    background-color: rgb(233, 233, 233);
    margin-top: 0.7em;
  }
  .footer-top_menuItem_title::before{
    margin-left: -30px;
  }
  .footer-top_menuItem_title::after{
    margin-left: 10px;
  }
  .footer-top_menuItem_subTitle {
    font-size: 11px;
    color: #f48118;
    font-weight: 300;
    cursor: default;
    margin-bottom: 10px;
  }
  .footer-top_menuItem_childMenu>li {
    display: inline-block;
    font-size: 12px;
    padding: 5px 15px;
    line-height: 1.8;
    cursor: pointer;
    transition: all .2s;
  }
  .footer-top_menuItem_childMenu>li:hover {
    color: rgb(104, 144, 50);
  }
  .footer-top_menuItem_childMenu>li:hover::before,
  .footer-top_menuItem_childMenu>li:hover::after{
    content: '';
    display: inline-block;
    position: absolute;
    width: 10px;
    height: 2px;
    background-color: rgb(104, 144, 50);
    margin-top: 0.9em;
  }
  .footer-top_menuItem_childMenu>li:hover::before{
    margin-left: -15px;
  }
  .footer-top_menuItem_childMenu>li:hover::after{
    margin-left: 5px;
  }
  .footer-top_menu_middle {
    width: 30vw;
    min-width: 350px;
    max-width: 450px;
    height: 100%;
    /*background-color: pink;*/
    cursor: default;
  }
  .footer-top_menu_middle_title {
    /*height: 90px;*/
    text-align: center;
  }
  .footer-top_menu_middle_title h4 {
    line-height: 2;
    color: #f48118;
  }
  .footer-top_menu_middle_title img {
    width: 70px;
    height: 70px;
    vertical-align: middle;
    position: relative;
    top: -8px;
  }
  .footer-top_menu_middle_titleRight {
    text-align: left;
    display: inline-block;
    vertical-align: middle;
  }
  .centerSpan {
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }
  .footer-top_menu_middle_content {
    font-size: 12px;
    line-height: 2;
    padding: 10px 20px 0px 20px;
    border-left: 1px solid rgb(233, 233, 233);
    border-right: 1px solid rgb(233, 233, 233);
  }
  .footer-top_menu_middle_content span {
    display: block;
    text-align: right;
    margin-right:15px;
  }
  .footer-bottom {
    width:100%;
    text-align: center;
    background-color: #494b4f;
    border-top: 2px solid #cdcbcb;
    font-size: 12px;
    line-height: 2em;
    padding-bottom: 3px;
    margin-top: 20px;
  }
  .footer > div > p {
    color: #2b303e;
  }
</style>



