<template>
  <transition name="fade">
    <div class="quiz-dialog" v-show="parent[showProperty]">
      <div class="quiz-dialog_container" :style="getStyle">
        <quiz-panel :width="width+'px'" :height="height+'px'" :radius="radius" :title="title" padding="10px 8px">
          <div class="quiz-dialog_content" :style="getContentStyle"><slot></slot></div>
          <div class="quiz-dialog_backBtn" align="center" @click="close()">
            <quiz-btn
              :icon="'content-'+closeTips"
              size="14px"
              radius="23px"
              theme="BACK"
              width="120px"
              height="46px"
              @click.native="clickHandler()"></quiz-btn>
          </div>
        </quiz-panel>
      </div>
    </div>
  </transition>
</template>

<script>
import quizPanel from '@/base/quiz/quiz-panel'
import quizBtn from '@/base/quiz/quiz-btn'
export default {
  name: 'quiz-dialog',
  components: {
    quizPanel,
    quizBtn
  },
  props: {
    showProperty: {
      type: String,
      default: ''
    },
    title: {
      type: String
    },
    closeTips: {
      type: String,
      default: '返回'
    },
    width: {
      type: Number,
      default: 400
    },
    height: {
      type: Number,
      default: 200
    },
    radius: {
      type: String,
      default: 'default'
    }
  },
  computed: {
    getStyle () {
      return {
        marginLeft: `calc(50vw - ${this.width / 2}px)`,
        marginTop: `calc(50vh - ${this.height / 2}px)`
      }
    },
    getContentStyle () {
      return {
        height: `${this.height - 46 - 60}px`
      }
    }
  },
  data () {
    return {
      parent: null
    }
  },
  methods: {
    clickHandler () {
      this.$emit('clickHandler')
    },
    close () {
      this.parent[this.showProperty] = !this.parent[this.showProperty]
    }
  },
  created () {
    var tempParent = this.$parent
    var maxFloor = 5
    for (var i = 1; i <= maxFloor; i++) {
      if (tempParent.hasOwnProperty(this.showProperty)) {
        this.parent = tempParent
        break
      }
      if (!tempParent.$parent || i === maxFloor) {
        this.parent = {
          showProperty: false
        }
        break
      }
      tempParent = tempParent.$parent
    }
  }
}
</script>

<style scoped>
  .fade-enter-active, .fade-leave-active {
    transition: all .5s;
  }
  .fade-enter, .fade-leave-to {
    opacity: 0;
  }
  .quiz-dialog_content {
    color: #333;
    line-height: 2em;
    min-height: 90px;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 5px 20px;
    margin: 20px 0 10px 0;
  }
  .quiz-dialog {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.60);
    z-index: 100;
  }
  /*定义滚动条轨道*/
  .quiz-dialog_content::-webkit-scrollbar-track
  {
    background-color: #f9f9f9;
    -webkit-box-shadow: inset 0 0 6px transparent;
  }
  /*定义滚动条高宽及背景*/
  .quiz-dialog_content::-webkit-scrollbar
  {
    width: 10px;
    background-color: #333;
  }
  /*定义滚动条*/
  .quiz-dialog_content::-webkit-scrollbar-thumb
  {
    background-color: #8b8b8b;
    border-radius: 10px;
  }
</style>



