<template>
  <div class="search-container">
    <div class="search-bar">
      <p class="catalog" v-if="showCatalog">
        <span class="backToIndex" style="cursor: pointer;" @click="backToIndex">讨论区</span>
        &nbsp;>&nbsp;
        <template v-if="!$route.query.search || typeof ($route.query.search) === 'undefined' || $route.query.search === ''">
          <span >搜索页</span>
        </template>
        <template v-else>
          <span style="color: #FF9900">搜索“{{$route.query.search}}”</span>
        </template>
      <p>
      <div class="search-input-box">
        <input type="text" placeholder="请输入关键词" v-model="searchKey" @keyup.enter="search">
        <i class="el-icon-search" @click="search" ></i>
      </div>
    </div>
    <div class="tags-bar">
      <ul class="tags-list" onselectstart="return false">
        <li class="title" @click="searchAll" @mouseover="changeTips(true)" @mouseleave="changeTips(false)" :style="getCursor"><span>{{searchTip}}</span></li>
        <li class="tag" v-for="(item, index) in hotTags_list" :key="index" @click="gotoSearch(item)"><span>{{item}}</span></li>
        <li class="change-btn" @click="changeTags"><span>换一批</span></li>
      </ul>
    </div>
  </div>
</template>

<script>
import api from '@/api/forums'

export default {
  props: {
    showCatalog: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      searchTip: '热门搜索',
      searchKey: this.$route.query.search,
      hotTags_list: [],
      tags_counts: 0,
      tags_maxPage: 3,
      tags_currentPage: 1,
      tags_size: 7
    }
  },
  computed: {
    getCursor () {
      if (this.searchKey !== '') {
        return {
          cursor: 'pointer'
        }
      }
    }
  },
  methods: {
    changeTips (ifOver) {
      if (this.searchKey && this.searchKey !== '') {
        this.searchTip = ifOver ? '返回全部' : '热门搜索'
      }
    },
    searchAll () {
      this.searchKey = ''
      this.$router.push({
        name: 'postSearch',
        params: {
          type: 0,
          page: 1
        }
      })
      this.searchTip = '热门搜索'
    },
    backToIndex () {
      this.searchKey = ''
      this.$router.push({path: '/forums'})
    },
    gotoSearch (key) {
      this.searchKey = key
      this.search()
    },
    search () {
      if (this.searchKey == null || this.searchKey === '') {
        this.searchAll()
      } else {
        this.$router.push({
          name: 'postSearch',
          params: {
            type: 0,
            page: 1
          },
          query: {
            search: this.searchKey
          }
        })
      }
    },
    getTags (order, size) {
      const _this = this
      api.getHotTagsList('/forum/hot_search', {
        page: order,
        pagesize: size
      }).then(function (res) {
        _this.hotTags_list = res.data.hot_search_list
        _this.tags_counts = res.total
      })
    },
    changeTags () {
      if (Math.floor(this.tags_counts / this.tags_size) === this.tags_currentPage) {
        this.tags_currentPage = 1
      } else {
        if (this.tags_counts === 0) {
          this.tags_currentPage = 1
        } else {
          this.tags_currentPage++
        }
      }
      this.getTags(this.tags_currentPage, this.tags_size)
    }
  },
  created () {
    this.getTags(this.tags_currentPage, this.tags_size)
  }
}
</script>

<style scoped>
  .search-container {
    width: 100%;
    min-width: 1170px;
    padding-top: 10px;
    height: 110px;
    box-sizing: content-box;
    background: #333 url('../../../static/image/forumBg.png') no-repeat center center;
    background-size: 100% auto;
    border-bottom: 2px solid #FF9900;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
    border-top: 1px solid #cdcbcb;
  }
  .search-bar {
    width: 100%;
    height: 59px;
    background-color: rgba(255, 255, 255, .05);
    position: relative;
  }
  .catalog {
    height: 22px;
    font-weight: 300;
    color: rgba(215, 215, 215, 0.6);
    line-height: 59px;
    display: inline-block;
    position: absolute;
    background-color: transparent;
    left: 0px;
    top: 0px;
    padding-left: 25px;
    cursor: default;
  }
  .backToIndex:hover {
    color: #FF9900;
  }
  .search-input-box {
    width: 436px;
    height: 33px;
    border-top: 15px solid transparent;
    margin: 0 auto;
    position: relative;
  }
  .search-input-box input{
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    color: rgba(242, 242, 242, .2);
    width: 100%;
    line-height: 33px;
    background-color: transparent;
    border: 1px solid rgba(242, 242, 242, .2);
    border-radius: 10px;
    padding: 0px 50px 0px 20px;
    box-sizing: border-box;
    transition: all .2s;
  }
  .search-input-box input:hover,
  .search-input-box input:focus {
    color: #333;
    background-color: rgba(242, 242, 242, .8);
    box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.4);
  }
  .search-input-box i{
    position: absolute;
    top: 10px;
    right: 10px;
    font-weight: 300;
    color: #666666;
    cursor: pointer;
  }
  .search-input-box i:hover{
    color: #f48118;
  }
  /*热门标签*/
  .tags-bar {
    margin: 0 auto;

  }
  .tags-bar {
    padding: 10px 0;
    text-align: center;
  }
  .tags-bar ul>li {
    font-size: 14px;
    font-weight: 300;
    letter-spacing: 0.1em;
    display: inline-block;
    padding: 0 15px;
    line-height: 28px;
    background-color: rgba(255, 255, 255, .3);
    box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.4);
    border: none;
    border-radius: 14px;
    margin-top: 1px;
    margin-left: 15px;
    position: relative;
  }
  .tags-bar ul>li{
    cursor: pointer;
  }
  .tags-bar span{
    position: relative;
    z-index: 2;
  }
  .tags-bar ul>li:hover {
    color: #333;
  }
  .tags-bar ul>li::after{
    content: '';
    display: block;
    width: 100%;
    height: 28px;
    border-radius: 14px;
    background-color: rgba(244, 129, 24, 0);
    position: absolute;
    top: 0;
    left: 0;
    transition: all .3s;
    z-index: 1;
  }
  .tags-bar ul>li:not(:first-child):hover::after{
    background-color: rgba(240, 240, 240, .8);
  }
  .tags-bar ul>.title {
    font-weight: 600;
    font-size: 14px;
    color: #fff;
    letter-spacing: 0.1em;
    display: inline-block;
    padding: 0 15px;
    line-height: 30px;
    background-color: #FF9900;
    border: none;
    border-radius: 15px;
    margin: 0px;
    cursor: default;
  }
  .tags-bar ul>.title:hover {
    color: #fff;
    background-color: #FF9900;
  }
  .tags-bar ul>.title:hover::after{
    width: 0%;
  }
  .tags-bar ul>.change-btn {
    background-color: rgba(240, 240, 240, .8);
  }
</style>



