<template>
  <div class="quiz-choose"  :style="getBaseStyle">
    <span class="quiz-choose_label" v-if="label">{{label}}&nbsp;:</span>
    <div class="quiz-choose_content" :style="getContentStyle">
      <span
        v-for="(item, index) in dataList"
        :key="index"
        :class="{'quiz-choose_actived':selectList.indexOf(item.value)>=0}"
        @click="selectHandler(item.value)">{{item.name}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'quiz-choose',
  props: {
    label: {
      type: String
    },
    name: {
      type: String
    },
    ifMulti: {
      type: Boolean,
      default: false
    },
    dataList: {
      type: Array,
      default: function () {
        return []
      }
    },
    fontSize: {
      type: Number,
      default: 16
    },
    isCenter: {
      type: Boolean,
      default: true
    },
    hideUnderline: {
      type: Boolean,
      default: false
    },
    updateFuc: {
      type: Function
    }
  },
  computed: {
    getBaseStyle () {
      return {
        fontSize: this.fontSize + 'px',
        lineHeight: (this.fontSize + 10) + 'px'
      }
    },
    getContentStyle () {
      return {
        fontSize: this.fontSize + 'px',
        height: (this.fontSize + 10) + 'px',
        cursor: this.disabled ? 'no-drop' : 'normal',
        textAlign: this.isCenter ? 'center' : 'normal',
        borderBottom: this.hideUnderline ? 'none' : '1px solid #636363'
      }
    },
    getDefaultInputStyle () {
      return {
        fontSize: this.fontSize + 'px',
        lineHeight: (this.fontSize + 10) + 'px',
        textAlign: this.isCenter ? 'center' : 'normal'
      }
    }
  },
  data () {
    return {
      selectList: []
    }
  },
  methods: {
    updateSelectedData () {
      var newData = this.dataList.map(i => {
        i.selected = this.selectList.indexOf(i.value) >= 0
        return i
      })
      this.$parent.$emit('updateSelectedData-' + this.name, newData)
    },
    selectHandler (value) {
      var idx = this.selectList.indexOf(value)
      if (idx >= 0) {
        if (this.selectList.length > 1) {
          this.selectList.splice(idx, 1)
        }
      } else {
        if (this.ifMulti) {
          this.selectList.push(value)
        } else {
          this.selectList = [value]
        }
      }
      this.updateSelectedData()
    }
  },
  created () {
    const _this = this
    this.$parent.$on('updateSelectedData-' + this.name, function (res) {
      _this.updateFuc(_this.name, res)
    })
    // 默认第一项
    if (this.dataList.length > 0) {
      for (var dataListKey in this.dataList) {
        if (this.dataList[dataListKey].selected) {
          this.selectList.push(this.dataList[dataListKey].value)
        }
      }
      if (this.selectList.length === 0) {
        this.selectList.push(this.dataList[0].value)
      }
      this.updateSelectedData()
    }
  }
}
</script>

<style scoped>
  .quiz-choose {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .quiz-choose_label {
    color: #636363;
    flex: 1 0 auto;
  }
  .quiz-input:hover .quiz-choose_label {
    color: #272822;
  }
  .quiz-choose_content {
    width: 100%;
    height: 20px;
    margin-left: 15px;
  }
  .quiz-choose_content span {
    display: none;
    margin: 0 20px;
    color: #8b8b8b;
    cursor: pointer;
  }
  .quiz-choose_content:hover span {
    display: inline;
  }
  .quiz-choose_actived {
    display: inline !important;
    color: #333333 !important;
    cursor: default !important;
  }
</style>



