<template>
  <div class="about-us">
    <el-row>
      <el-col>
        <user-card imgSize="200px" marginTop="20px" v-if="boss.id" :user="boss"></user-card>
      </el-col>
    </el-row>
    <el-row>
      <el-col  :xs="24" :sm="24" :md="24" :lg="24">
        <el-collapse v-model="activeIndex">
          <el-collapse-item name="1" v-if="grade2012.length !== 0">
            <template slot="title">
              <p style="text-align: center">2012级</p>
            </template>
            <el-row type="flex" justify="space-around">
              <el-col :xs="24" :sm="12" :md="6" :lg="6"
                      v-for="(user, index) in grade2012" :key="index">
                <user-card v-if="user.id" :user="user"></user-card>
              </el-col>
            </el-row>
          </el-collapse-item>
          <el-collapse-item name="2" v-if="grade2013.length !== 0">
            <template slot="title">
              <p style="text-align: center">2013级</p>
            </template>
            <el-row type="flex" justify="space-around">
              <el-col :xs="24" :sm="12" :md="6" :lg="6"
                      v-for="(user, index) in grade2013" :key="index">
                <user-card v-if="user.id" :user="user"></user-card>
              </el-col>
            </el-row>
          </el-collapse-item>
          <el-collapse-item name="3" v-if="grade2014.length !== 0">
            <template slot="title">
              <p style="text-align: center">2014级</p>
            </template>
            <el-row type="flex" justify="space-around">
              <el-col :xs="24" :sm="12" :md="6" :lg="6"
                      v-for="(user, index) in grade2014" :key="index">
                <user-card v-if="user.id" :user="user"></user-card>
              </el-col>
            </el-row>
          </el-collapse-item>
          <el-collapse-item name="4" v-if="grade2015.length !== 0">
            <template slot="title">
              <p style="text-align: center">2015级</p>
            </template>
            <el-row type="flex" justify="space-around">
              <el-col :xs="24" :sm="12" :md="6" :lg="6"
                      v-for="(user, index) in grade2015" :key="index">
                <user-card v-if="user.id" :user="user"></user-card>
              </el-col>
            </el-row>
          </el-collapse-item>
          <el-collapse-item name="5" v-if="grade2016.length !== 0">
            <template slot="title">
              <p style="text-align: center">2016级</p>
            </template>
            <el-row style="text-align: center;">
              <div v-for="(user, index) in grade2016" :key="index" style="display: inline-block; width: 20%;">
                <user-card v-if="user.id" :user="user"></user-card>
              </div>
            </el-row>
          </el-collapse-item>
          <el-collapse-item name="6" v-if="grade2018.length !== 0">
            <template slot="title">
              <p style="text-align: center">2018级</p>
            </template>
            <el-row style="text-align: center;">
              <div v-for="(user, index) in grade2018" :key="index" style="display: inline-block; width: 20%;">
                <user-card v-if="user.id" :user="user"></user-card>
              </div>
            </el-row>
          </el-collapse-item>
        </el-collapse>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import userCard from '../../base/about/user-card.vue'
import api from '@/api/aboutUs'

export default {
  components: {
    userCard
  },
  data () {
    return {
      boss: {
        image: '',
        name: '',
        desc: '',
        badges: [
        ]
      },
      users: [
      ],
      grade2012: [],
      grade2013: [],
      grade2014: [],
      grade2015: [],
      grade2016: [],
      grade2018: [],
      activeIndex: ['1', '2', '3', '4', '5', '6']
    }
  },
  methods: {
    getUsList () {
      const _this = this
      api.getUsList('/about_us?pagesize=100').then(function (res) {
        _this.boss = res.data[0]
        res.data.shift()
        res.data.sort(function (val1, val2) {
          return val1.grade - val2.grade
        })
        _this.users = res.data
        for (let i = 0; i < res.data.length; i++) {
          _this['grade' + res.data[i].grade].push(res.data[i])
        }
        _this.sort('2016', ['刘欢进', '卢丽莎', '谢嘉慧', '林凯', '唐富'], ['李豪'])
        _this.sort('2018', ['陈惠香'])
      })
    },
    // 一个排前后顺序的函数
    sort (grade, firstOrder, lastOrder) {
      // var rightArray = this.grade2016.filter((i, index) => i.name === orderArray[index])
      var firstArray = []
      var lastArray = []
      for (let i of firstOrder) {
        firstArray.push(this['grade' + grade].find(j => j.name === i))
      }
      for (let i of lastOrder) {
        lastArray.unshift(this['grade' + grade].find(j => j.name === i))
      }
      this['grade' + grade].forEach((value) => {
        if ([...firstArray, ...lastArray].indexOf(value) < 0) {
          firstArray.push(value)
        }
      })
      this['grade' + grade] = [...firstArray, ...lastArray]
    }
  },
  created () {
    this.getUsList()
  }
}
</script>

<style scoped>
  .about-us {
    width: 1170px;
    margin: 0 auto;
    padding: 45px 0 80px 0;
  }
</style>



