<template>
  <div class="addTest">
    <div class="addTest-head">
      <el-button type="primary" round size="small" class="back" @click="backCourseTest">返回</el-button>
      <span>添加测验</span>
    </div>
    <el-row style="margin-top: 20px">
      <el-col :span="7">
        <div class="addTest-info">
          <el-form :model="testForm" label-width="100px">
            <el-form-item label="测试名称">
              <el-input v-model="testForm.name" placeholder="请填写测试名称"></el-input>
            </el-form-item>
            <el-form-item label="起始时间">
              <el-col :span="24">
                <el-date-picker type="date" placeholder="选择日期" v-model="testForm.starDate" style="width: 100%;" disabled></el-date-picker>
              </el-col>
            </el-form-item>
            <el-form-item label="截止时间">
              <el-col :span="24">
                <el-date-picker type="date" placeholder="选择时间" v-model="testForm.endDate" style="width: 100%;"></el-date-picker>
              </el-col>
            </el-form-item>
            <el-form-item label="已选择题目">
              <el-col :span="24">
                <el-input v-if="testForm.problem.length <= 0 || testForm.problem === undefined" placeholder="请在右边框选择题目" disabled></el-input>
              </el-col>
            </el-form-item>
            <div class="problem-list" v-if="testForm.problem.length > 0" v-for="(item, index) in testForm.problem" :key="index">
              <span>{{item.PID}}</span>
              <span>{{item.title}}</span>
              <i class="el-icon-error deleteIcon" @click="deleteProblem(item)"></i>
            </div>
          </el-form>
        </div>
        <div style="text-align: center;margin-top: 20px">
          <el-button @click="createdTest" type="primary">创建测验</el-button>
        </div>
      </el-col>
      <el-col :span="16" :offset="1">
        <div class="addtest-sreach">
          <el-row :gutter="20">
            <el-col :span="6" :offset="3"><el-input size="small" v-model="testPid" placeholder="请输入题目编号"></el-input></el-col>
            <el-col :span="6">
              <el-input size="small" v-model="testTitle" placeholder="请输入题目标题"></el-input>
            </el-col>
            <el-col :span="6"><el-select size="small" v-model="testType" placeholder="请选择题目类型">
              <el-option
                v-for="item in problemTypes"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            </el-col>
            <el-col :span="2">
              <div class="statusButton">
                <el-button size="small" type="primary" icon="el-icon-search" @click="searchProblem">搜索</el-button>
              </div>
            </el-col>
          </el-row>
        </div>
        <div class="table-area question-bank-table-area">
          <el-table
            :data="tableData"
            :header-row-style="headerStyle"
            stripe
            style="width: 100%"
            @row-click="showDetail">
            <el-table-column
              label="已测试"
              width="80px"
              align="center">
              <template slot-scope="scope">
                <i class="icon-success" style="color: #075f60" v-if="scope.row.wasUsed"></i>
              </template>
            </el-table-column>
            <el-table-column
            property="PID"
            label="PID"
            width="100px"
            align="center">
            </el-table-column>
            <el-table-column
              label="标签"
              width="180px"
              align="center">
              <template slot-scope="scope">
                <span v-for="(item, index) in scope.row.label" :key="index" class="type">{{item}}</span>
              </template>
            </el-table-column>
            <el-table-column
              property="title"
              show-overflow-tooltip
              label="题目标题"
              align="center">
              <template slot-scope="scope">
                <span class="problem-title" v-html="scope.row.title"></span>
              </template>
            </el-table-column>
            <el-table-column
              label="通过率"
              width="104px"
              align="center">
              <template slot-scope="scope">
                <span :class="scope.row.passRate >= 80 ? 'green' : scope.row.passRate >= 40 ? 'blue' : 'red'">{{(scope.row.passRate)}}%</span>
              </template>
            </el-table-column>
            <el-table-column
              label="选择题目"
              width="120px"
              align="center">
              <template slot-scope="scope">
                <el-switch v-model="scope.row.select" @change="checkProblems(scope.row)" @click.stop.native="">
                </el-switch>
              </template>
            </el-table-column>
          </el-table>
          <div class="addTest-pagination">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-size="10"
              background
              :current-page="testPage"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import api from '@/api/questionBank'
import testApi from '@/api/addTest'

export default{
  data () {
    return {
      testPid: '',
      testTitle: '',
      testType: '',
      problemTypes: [
        {value: 1, label: '填空题'},
        {value: 2, label: '选择题'},
        {value: 3, label: '编程题'},
        {value: 4, label: '问答题'},
        {value: 5, label: '判断题'},
        {label: '全部类型'}
      ],
      value3: false,
      testForm: {
        name: '',
        starDate: new Date(),
        endDate: '',
        problem: []
      },
      tableData: [],
      questionData: [],
      headerStyle: {
        backgroundColor: 'red'
      },
      total: 0,
      testPage: 1,
      typeObj: {},
      coursesID: ''
    }
  },
  methods: {
    backCourseTest () {
      this.$router.push({name: 'PersonalCenter', params: {isShow: 2}, query: {showCourseTest: false, coursesID: this.coursesID}})
    },
    searchProblem () {
      let obj = {
        title: this.testTitle,
        problemId: this.testPid,
        type: this.testType
      }
      this.typeObj = obj
      if (this.testPage !== 1) {
        this.testPage = 1
      }
      this.getProblems(obj, this.testPage)
    },
    checkProblems (data) {
      if (this.notify != null) {
        this.notify.close()
      }
      if (data.select === true) {
        this.testForm.problem.push(data)
      } else {
        for (var i = 0; i < this.testForm.problem.length; i++) {
          if (data.PID === this.testForm.problem[i].PID) {
            this.testForm.problem.splice(i, 1)
          }
        }
      }
    },
    deleteProblem (data) {
      for (var i = 0; i < this.testForm.problem.length; i++) {
        if (data.PID === this.testForm.problem[i].PID) {
          this.testForm.problem.splice(i, 1)
          data.select = false
        }
      }
      for (var j = 0; j < this.tableData.length; j++) {
        if (data.PID === this.tableData[j].PID) {
          this.tableData[j].select = false
        }
      }
    },
    getQuestionData (pid) {
      if (this.notify != null) {
        this.notify.close()
      }
      const _this = this
      api.getQuestionDetail('/problems/detail', pid).then(function (res) {
        _this.questionData = res.data
        var tagList = ''
        for (var i = 0; i < _this.questionData.tagnames.length; i++) {
          tagList += '<li style="display: inline-block;background-color: #6495ed;min-width: 24px;height: 24px;line-height: 24px;border-radius: 12px;margin: 5px 2px;color: white;padding: 0 10px;cursor: default;">' + _this.questionData.tagnames[i] + '</li>'
        }
        _this.notify = _this.$notify({
          title: '第' + _this.questionData.problem_id + '题 | ' + _this.questionData.info.title,
          position: 'bottom-left',
          dangerouslyUseHTMLString: true,
          duration: 12000, // 不自动关闭
          customClass: 'questionDetail',
          message: '<div id="questionDetail" style="cursor: pointer;"><ul class="content-top" style="width: 100%; text-align: center;">' + tagList + '</ul><div class="content-middle" style="width: 100%; max-height: 68vh; overflow: auto; margin-top: 20px;color: #333;padding: 5px 10px;margin-bottom: 10px;font-size: 14px;line-height: 20px;white-space: pre-wrap;"><p>' + _this.questionData.info.description + '</p></div><div class="content-bottom" style="text-align: right; font-size: 10px;">已提交' + _this.questionData.submit + '次&nbsp;&nbsp;已通过' + _this.questionData.accepted + '次</div></div>'
        })
        _this.notify.onClick = function () {
          _this.oProblemDetail(pid)
        }
      })
    },
    showDetail (row) {
      this.getQuestionData(row.PID)
    },
    getProblems (type, page = 1, tagId = null) {
      const _this = this
      api.getProblemsList('/problems', page, 10, type, tagId, _this.coursesID).then(function (res) {
        _this.total = res.total
        _this.tableData = _this.filterTableDateList(res.data)
        _this.judgmentIsSelect()
      })
    },
    filterTableDateList (list) {
      let tableData = []
      for (let i = 0; i < list.length; i++) {
        let data = {}
        data.PID = list[i].problem_id
        data.title = list[i].title
        data.label = list[i].tagnames
        data.passRate = list[i].submit ? (list[i].accepted / list[i].submit * 100).toFixed(2) : '0.00'
        data.select = false
        data.wasUsed = list[i].was_used
        tableData.push(data)
      }
      return tableData
    },
    createdTest () {
      const _this = this
      var list = []
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json.uid !== null || json.uid !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
      }
      for (var i = 0; i < this.testForm.problem.length; i++) {
        list.push(this.testForm.problem[i].PID)
      }
      var list1 = '[' + list + ']'
      var time = this.timeFormat(this.testForm.endDate)
      let testObj = {
        exam_name: this.testForm.name,
        stop_time: time,
        courses_id: this.coursesID,
        uid: uid,
        problem_list: list1
      }
      testApi.postCreatedTest('/courses/exams', testObj).then(function (res) {
        if (res.success === 'true') {
          _this.$message({
            message: '测验创建成功,即将返回测验页面',
            type: 'success'
          })
          setTimeout(function () {
            _this.backCourseTest()
          }, 300)
        } else {

        }
      })
    },
    judgmentIsSelect () {
      for (var i = 0; i < this.tableData.length; i++) {
        for (var j = 0; j < this.testForm.problem.length; j++) {
          if (this.tableData[i].PID === this.testForm.problem[j].PID) {
            this.tableData[i].select = true
          }
        }
      }
    },
    handleSizeChange (val) {
    //      console.log(val)
    },
    handleCurrentChange (val) {
      this.getProblems(this.typeObj, val)
      this.testPage = val
    },
    getCourseID () {
      this.coursesID = this.$route.query.courses_id
    },
    timeFormat (date) {
      var d = new Date(date)
      return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
    },
    oProblemDetail (id) {
      const {href} = this.$router.resolve({
        path: '/library/questionDetail',
        query: {PID: id}
      })
      window.open(href, '_blank')
    }
  },
  created () {
    this.getCourseID()
    this.getProblems()
  },
  beforeDestroy () {
    if (this.notify != null) {
      this.notify.close()
    }
  }
}
</script>

<style scoped>
  .addTest {
    width: 1370px;
    height: 700px;
    margin: 20px auto;
    padding: 20px;
    background-color: white;
  }
  .addTest .addTest-head{
    height: 25px;
    padding-bottom: 15px;
    border-bottom: 1px solid gainsboro;
    text-align: center;
    box-sizing: content-box;
  }
  .addTest .addTest-head .back{
    float: left;
  }
  .addTest .addTest-head .createTest{
    float: left;
  }
  .addTest .addTest-head span{
    font-size: 22px;
    color: #333333;
    font-weight: 600;
  }
  .addTest-pagination {
    padding: 20px 0;
  }
  .addTest-pagination /deep/ .el-pagination .el-pager .active{
    background-color: #f48118 !important;
  }
  .addTest-pagination /deep/ .el-pagination .el-pagination__total{
    color: inherit;
  }
  .addTest-pagination /deep/ .el-pagination .el-pagination__jump{
    color: inherit;
  }
  .addTest .problem-list{
    margin-top: 4px;
    width: 100%;
    height: 34px;
    line-height: 34px;
    background-color: orange;
    border-radius: 10px;
    cursor: pointer;
  }
  .addTest .problem-list:first-child{
    margin-top: 0;
  }
  .addTest .problem-list span:first-child{
    color: white;
    font-size: 14px;
    padding: 0px 20px;
  }
  .addTest .problem-list i{
    float: right;
    line-height: 34px;
    margin-right: 20px;
    display: none;
  }
  .addTest .problem-list:hover i{
    display: block !important;
  }
</style>



