<template>
  <div class="fill-blank-question"
       @mouseenter="handleMouseEnter"
       @mouseleave="handleMouseLeave" ref="fillBlank">
    <p class="current-question" v-if="isExamPaper">{{question.belongTypeTitle}}{{question.questionNum}} ({{question.questionBonus}}分)</p>
    <p class="question-title" :class="{'max-height': maxHeight, 'preview-title': isBrowse}" v-if="question.questionTitle" v-html="question.questionTitle"></p>
    <div class="answer-area-error" v-if="question.questionAnswer !== undefined && question.userAnswer !== undefined && !judgIsTrue">
      <span v-if="!isBrowse">很抱歉，您做错了！</span>
      <span class="question-answer">正确答案是：{{getQuestionAnswer}}</span>
    </div>
    <div class="answer-area-pass" v-if="!isBrowse && judgIsTrue">
      <span>恭喜，您答对了！</span>
    </div>
  </div>
</template>

<script>
import {judgTwoArrayIsEqual} from '@/utils/index'
export default {
  props: {
    question: {
      type: Object
    },
    isExamPaper: {
      type: Boolean,
      default: false
    },
    isBrowse: {
      type: Boolean,
      default: false
    },
    maxHeight: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {}
  },
  computed: {
    getQuestionAnswer () {
      let res = ''
      for (let i = 0; i < this.question.questionAnswer.length; i++) {
        if (i === this.question.questionAnswer.length - 1) {
          res += this.question.questionAnswer[i]
        } else {
          res += this.question.questionAnswer[i] + '、'
        }
      }
      return res
    },
    judgIsTrue () {
      return judgTwoArrayIsEqual(this.question.questionAnswer, this.question.userAnswer)
    }
  },
  methods: {
    handleMouseEnter () {
      this.$emit('enter')
    },
    handleMouseLeave () {
      this.$emit('leave')
    },
    getInputValue () {
      let answer = []
      let fillBlankInputs = this.$refs.fillBlank.getElementsByClassName('fillblank')
      for (let i = 0; i < fillBlankInputs.length; i++) {
        answer.push(fillBlankInputs[i].value)
      }
      return answer
    },
    setInputValue (list) {
      list = typeof list === 'string' ? list.split(',') : list
      let fillBlankInputs = this.$refs.fillBlank.getElementsByClassName('fillblank')
      for (let i = 0; i < list.length; i++) {
        fillBlankInputs[i].value = list[i]
      }
    },
    setUserAnswer (init) {
      if (init) {
        let fillBlankInputs = this.$refs.fillBlank.getElementsByClassName('fillblank')
        for (let i = 0; i < fillBlankInputs.length; i++) {
          fillBlankInputs[i].value = ''
        }
        return
      }
      if (typeof this.question.userAnswer !== 'undefined') {
        this.setInputValue(this.question.userAnswer)
      }
    }
  },
  mounted () {
    if (this.isBrowse) {
      let fillBlankInputs = this.$refs.fillBlank.getElementsByClassName('fillblank')
      for (let i = 0; i < fillBlankInputs.length; i++) {
        fillBlankInputs[i].setAttribute('disabled', 'true')
        if (this.question.userAnswer) {
          this.setInputValue(this.question.userAnswer)
          if (this.question.userAnswer[i] === this.question.questionAnswer[i]) {
            fillBlankInputs[i].setAttribute('class', fillBlankInputs[i].getAttribute('class') + ' pass')
          } else {
            fillBlankInputs[i].setAttribute('class', fillBlankInputs[i].getAttribute('class') + ' error')
          }
        } else {
          this.setInputValue(this.question.questionAnswer)
          fillBlankInputs[i].setAttribute('class', fillBlankInputs[i].getAttribute('class') + ' pass')
        }
      }
    } else if (this.question.userAnswer && !this.question.questionAnswer) {
      this.setInputValue(this.question.userAnswer)
    } else if (this.question.questionAnswer) {
      let fillBlankInputs = this.$refs.fillBlank.getElementsByClassName('fillblank')
      let userAnswers = typeof this.question.userAnswer === 'string' ? this.question.userAnswer.split(',') : this.question.userAnswer
      let questionAnswers = typeof this.question.questionAnswer === 'string' ? this.question.questionAnswer.split(',') : this.question.questionAnswer
      for (let i = 0; i < fillBlankInputs.length; i++) {
        fillBlankInputs[i].setAttribute('disabled', 'true')
        fillBlankInputs[i].value = userAnswers[i]
        if (userAnswers[i] === questionAnswers[i]) {
          fillBlankInputs[i].setAttribute('class', fillBlankInputs[i].getAttribute('class') + ' pass')
        } else {
          fillBlankInputs[i].setAttribute('class', fillBlankInputs[i].getAttribute('class') + ' error')
        }
      }
    }
  },
  watch: {
    question: {
      handler (newV) {
        const self = this
        this.$nextTick(function () {
          if (!self.isBrowse && newV.questionAnswer) {
            let fillBlankInputs = self.$refs.fillBlank.getElementsByClassName('fillblank')
            let userAnswers = typeof newV.userAnswer === 'string' ? newV.userAnswer.split(',') : newV.userAnswer
            let questionAnswers = typeof newV.questionAnswer === 'string' ? newV.questionAnswer.split(',') : newV.questionAnswer
            for (let i = 0; i < fillBlankInputs.length; i++) {
              fillBlankInputs[i].setAttribute('disabled', 'true')
              fillBlankInputs[i].value = userAnswers[i]
              if (userAnswers[i] === questionAnswers[i]) {
                fillBlankInputs[i].setAttribute('class', fillBlankInputs[i].getAttribute('class') + ' pass')
              } else {
                fillBlankInputs[i].setAttribute('class', fillBlankInputs[i].getAttribute('class') + ' error')
              }
            }
          }
        })
      },
      deep: true,
      immediate: true
    }
  }
}
</script>

<style scoped>
  .fill-blank-question {
    width: 100%;
    font-size: 14px;
    position: relative;
  }
  .fill-blank-question .current-question {
    font-weight: 600;
    width: 100%;
    /*height: 30px;*/
    line-height: 30px;
    padding: 25px 15px 15px 15px;
    box-sizing: border-box;
    font-size: 16px;
  }
  .fill-blank-question .question-title {
    width: 100%;
    min-height: 110px;
    line-height: 40px;
    padding: 10px 30px 40px 30px;
    box-sizing: border-box;
  }
  .fill-blank-question .preview-title {
    min-height: auto;
    padding: 10px 30px;
  }
  .fill-blank-question .max-height {
    overflow-y: auto;
    max-height: 400px;
  }
  .fill-blank-question .answer-area-error {
    width: 100%;
    min-height: 50px;
    line-height: 24px;
    padding: 10px;
    background-color: #f99090;
    font-size: 14px;
    color: #d43039;
  }
  .fill-blank-question .answer-area-error .question-answer {
    font-size: 18px;
  }
  .fill-blank-question .answer-area-pass {
    width: 100%;
    min-height: 50px;
    line-height: 24px;
    padding: 10px;
    background-color: #90EE90;
    font-size: 14px;
    color: #00cc45;
  }
  .fill-blank-question .answer-area-pass .question-answer {
    font-size: 18px;
  }

  .fill-blank-question .question-title /deep/ .fillblank {
    display: inline-block;
    height: 28px;
    line-height: 28px;
    padding: 0 10px;
    margin: 0px 4px;
    border-radius: 4px;
    font-size: 16px;
    background-color: #f0f0f0;
    box-shadow: 0 1px 3px #ddd;
    text-align: center;
  }
  .fill-blank-question .question-title /deep/ .fillblank:focus {
    background-color: #f48118;
    color: #ffffff;
  }
  .fill-blank-question .question-title /deep/ .pass {
    color: #00cc45;
  }
  .fill-blank-question .question-title /deep/ .error {
    color: #ff6c3f;
  }
  .fill-blank-question .question-title /deep/ .width-10 {
    width: 100px;
  }
  .fill-blank-question .question-title /deep/ .width-20 {
    width: 200px;
  }
</style>



