// 阻止浏览器滚动事件
export const preventScrollEvent = {
  data () {
    return {
      showDoPlan: false
    }
  },
  methods: {
    disabledMouseWheel () {
      if (this.showDoPlan) {
        document.addEventListener('DOMMouseScroll', this.scrollFunc, false)
        document.addEventListener('mousewheel', this.scrollFunc, false)
      }
    },
    cancelDisMouseWheel () {
      document.removeEventListener('DOMMouseScroll', this.scrollFunc, false)
      document.removeEventListener('mousewheel', this.scrollFunc, false)
    },
    scrollFunc (evt) {
      evt = evt || window.event
      if (evt.preventDefault) {
        // Firefox
        evt.preventDefault()
        evt.stopPropagation()
      } else {
        // IE
        evt.cancelBubble = true
        evt.returnValue = false
      }
      return false
    }
  },
  beforeDestroy () {
    this.cancelDisMouseWheel()
  },
  watch: {
    showDoPlan (newV) {
      if (newV) {
        this.disabledMouseWheel()
      } else {
        this.cancelDisMouseWheel()
      }
    }
  }
}



