import {
  getRequest,
  postJsonData,
  deleteRequest,
  putJsonData,
  delJsonData,
  delFromData,
  putFromData,
  postRequest
} from './config'

// 获得个人信息
function getInfoList (link, data) {
  return getRequest(link, {
    uid: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}
// 获得模板列表
function getTemplateList (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}
// 删除模板
function deleteTemplate (link, data) {
  return deleteRequest(link, {
    template_id: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}
// 更新模板
function updateTemplate (link, data) {
  return putJsonData(link, {
    template_id: data.template_id,
    name: data.name,
    question_type: data.question_type
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}
// 添加模板
function addTemplate (link, data) {
  return postJsonData(link, {
    name: data.name,
    question_type: data.question_type
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取标签树
function getQuestionList (link, data) {
  return getRequest(link, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取教师编辑但未生成的考试的id列表
function checkOnlyPreviewQuiz (link, id) {
  return getRequest(link, {
    courses_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取预览但未生成的试卷数据
function getPreviewInitData (link, id) {
  return getRequest(link, {
    quiz_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取考试题目
function getQuizProblemList (link, data) {
  return postJsonData(link, {
    type: data.type,
    range: data.range,
    page: data.page,
    pagesize: data.pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 自动补全题目
function autoAddProblem (link, data) {
  return postJsonData(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取考试列表
function getQuizList (link, data) {
  return getRequest(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取详细考试信息
function getQuizInfo (link, id, isAnswer) {
  return getRequest(link, {
    quiz_id: id,
    isAnswer: isAnswer
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取学生答题答案
function getStudentAnswer (link, id) {
  return getRequest(link, {
    quiz_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 保存学生答题答案
function saveStudentAnswer (link, data, isFinish) {
  return putJsonData(link, {
    quiz_id: data.quiz_id,
    answer_list: data.answer_list,
    isFinish: isFinish
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 学生标记问题
function markProblem (link, data) {
  return putJsonData(link, {
    quiz_id: data.quiz_id,
    problem_id: data.problem_id,
    item_id: data.item_id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 学生取消标记问题
function cancelMarkProblem (link, data) {
  return delJsonData(link, {
    quiz_id: data.quiz_id,
    problem_id: data.problem_id,
    item_id: data.item_id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 预览考试
function getPreviewQuiz (link, id, showAnswer) {
  return getRequest(link, {
    quiz_id: id,
    isAnswer: showAnswer || false
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 手动开启考试
function manualBeginQuiz (link, id) {
  return putFromData(link, {
    quiz_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 添加或预览考试
function postOrPreviewQuiz (link, data, isPreview) {
  return postJsonData(link, {
    quiz_id: data.quiz_id,
    courses_id: data.courses_id,
    isPreview: isPreview ? 1 : 0 || 0,
    info: data.quizInfo,
    template_id: data.template_id,
    problemRange: data.problemRange,
    detail: data.problemList
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 下架考试
function soldOutQuiz (link, id) {
  return putFromData(link, {
    quiz_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 删除考试
function deleteQuiz (link, id) {
  return delFromData(link, {
    quiz_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 教师改卷环节
// 获取答题卷列表
function getStudentAnswerList (link, id) {
  return getRequest(link, {
    quiz_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取某个考生的答题卷数据
function getOneStudentAnswer (link, data) {
  return getRequest(link, {
    quiz_id: data.quiz_id,
    user_id: data.student_id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 保存批改数据
function saveCheckData (link, data) {
  return putJsonData(link, {
    quiz_id: data.quiz_id,
    user_id: data.user_id,
    result: data.result
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 考试公布成绩
function announceGradeData (link, id) {
  return putFromData(link, {
    quiz_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 用于考试详情页
// 获取考试详情数据
function getQuizDetailInfo (link, id) {
  return getRequest(link, {
    quiz_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取考试结果排行
function getResultRank (link, data) {
  return getRequest(link, {
    quiz_id: data.quizId,
    size: data.size
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取考试结果详细排行
function getResultRankDetail (link, id, classId) {
  return getRequest(link, {
    quiz_id: id,
    class_id: classId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 提交编程题数据
function postAnswer (link, data) {
  return postRequest(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取编程题结果
function getQuestionResult (link, pid) {
  return getRequest(link, {
    problem_id: pid
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取课程列表
function getCourseList (link) {
  return getRequest(link).then((res) => {
    return Promise.resolve(res.data)
  })
}

const api = {
  // 个人信息
  getInfoList,
  // 模板
  getTemplateList,
  deleteTemplate,
  updateTemplate,
  addTemplate,
  // 标签树
  getQuestionList,
  getQuizProblemList,
  // 获取教师编辑但未生成的考试
  checkOnlyPreviewQuiz,
  // 获取预览但未生成的试卷数据
  getPreviewInitData,
  // 自动补全题目
  autoAddProblem,
  getQuizList,
  // 获取详细考试信息
  getQuizInfo,
  // 获取学生答题答案
  getStudentAnswer,
  // 保存学生答题答案
  saveStudentAnswer,
  // 学生标记问题
  markProblem,
  // 学生取消标记问题
  cancelMarkProblem,
  // 添加或预览考试卷
  postOrPreviewQuiz,
  // 预览考试
  getPreviewQuiz,
  // 手动开启考试
  manualBeginQuiz,
  // 下架考试
  soldOutQuiz,
  // 删除考试
  deleteQuiz,
  // 教师改卷环节
  // 获取答题卷列表
  getStudentAnswerList,
  // 获取某个考生的答题卷数据
  getOneStudentAnswer,
  // 保存批改数据
  saveCheckData,
  // 考试公布成绩
  announceGradeData,
  // 用于考试详情页
  // 获取考试详情数据
  getQuizDetailInfo,
  // 获取考试结果排行
  getResultRank,
  // 获取考试结果详细排行
  getResultRankDetail,
  // 提交编程题数据
  postAnswer,
  // 获取编程题结果
  getQuestionResult,
  // 获取课程列表
  getCourseList
}

export default api



