<template>
  <div class="problem-rank-card" :style="{float: float}">
    <p class="title" v-if="item.title">
      {{item.title}}
    </p>
    <table class="table">
      <thead class="thead">
        <tr>
          <th v-for="(th, index) in ths" :key="index" class="th" :style="{textAlign: th.textAlign}">{{th.title}}</th>
        </tr>
      </thead>
      <tbody class="tbody">
        <tr v-for="(row, index) in item.list" :key="index" class="row" @click="toDetail(row.problem_id)">
          <td class="name"><span class="overflow-text" style="width: 185px" :title="row.title">{{row.title}}</span></td>
          <td class="problem-num center"><span class="overflow-text" style="width: 90px;">{{row.problem_id}}</span></td>
          <td class="pass-rate center"><span class="overflow-text" style="width: 150px;text-align: center;">{{(row.accept_rate * 100)+"%"}}</span></td>
          <td class="submit-num"><span class="overflow-text" style="width: 90px;text-align: center;">{{row.submit}}</span></td>
        </tr>
      </tbody>
    </table>
    <div class="bottom">
      <router-link to="/library" tag="button" class="look-more">查看更多</router-link>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      default: function () {
        return {}
      }
    },
    ths: {
      type: Array,
      default: function () {
        return []
      }
    },
    float: {
      type: String,
      default: 'left'
    }
  },
  data () {
    return {}
  },
  methods: {
    toDetail (pid) {
      const {href} = this.$router.resolve({
        path: '/library/questionDetail',
        query: {PID: pid}
      })
      window.open(href, '_blank')
    }
  }
}
</script>

<style scoped>
  .problem-rank-card {
    width: 564px;
    overflow: hidden;
  }
  .title {
    text-align: center;
    color: #ffffff;
    font-size: 16px;
  }
  .table {
    margin-top: 30px;
    width: 100%;
    background-color: #ffffff;
    border-radius: 5px;
    border-collapse: collapse;
  }
  .table .thead {
  }
  .table .thead tr {
    height: 44px;
    line-height: 44px;
    border-bottom: 1px solid #e4e8f6;
  }
  .table .thead .th {
    font-size: 16px;
    color: #2b303e;
    font-weight: normal;
  }
  .table .thead .th:first-child {
    padding-left: 35px;
    box-sizing: border-box;
  }
  .table .thead .th:last-child {
    padding-right: 25px;
    box-sizing: border-box;
  }
  .table .tbody {

  }
  .table .tbody .row {
    height: 40px;
    line-height: 40px;
    cursor: pointer;
  }
  .table .tbody .row td span {
    display: block;
    height: 22px;
    line-height: 22px;
    text-align: left;
    font-size: 14px;
    color: #2b303e;
  }
  .table .tbody .row .name span {
    padding-left: 25px;
    padding-right: 10px;
    box-sizing: border-box;
  }
  .table .tbody .row .center span {
    text-align: center;
  }
  .table .tbody .row .problem-num span {
    background-color: #f48118;
    border-radius: 10px;
    color: #ffffff;
  }
  .table .tbody .row .pass-rate span {
    padding-left: 10px;
    box-sizing: border-box;
  }
  .bottom {
    width: 100%;
    height: 24px;
    margin-top: 31px;
    text-align: center;
  }
  .look-more {
    width: 109px;
    height: 26px;
    line-height: 26px;
    font-size: 14px;
    color: #ffffff;
    background-color: rgba(53, 53, 53, 0.2);
    border: none;
    outline: none;
    cursor: pointer;
  }
</style>



