<template>
  <div class="myMessage" @click="closeContentBox">
    <div class="myMessage-header">
      <span>我的消息</span>
      <span :class="(currentType === 1)? 'btn-type btn-current-type' : 'btn-type'" @click="changeType(1)">系统通知</span>
      <span :class="(currentType === 2)? 'btn-type btn-current-type' : 'btn-type'" @click="changeType(2)">评论回复</span>
    </div>
    <div class="myMessage-body">
      <ul>
        <li class="myMessage-item"  v-if="currentType === 1" v-for="(item , index) in message_list" :key="index">
          <div class="myMessage-item-top" @click.stop="openContent(index, item.read, item.id)">
            <div class="user_info">
              <img :src="item.avatarUrl ? host + item.avatarUrl : defaultImage">
              <span class="user-name">{{item.nick}}</span>
            </div>
            <div :class="(showAllContentNo === index)? 'editorShow ud-editor-message all-content':'editorShow ud-editor-message'" v-html="highlight(item.content)"></div>
            <div class="date">{{getDate(item.date)}}</div>
          </div>
          <div class="myMessage-item-bottom">
            <div class="source">
              <p class="myMessage-item-left">来自&nbsp;<span>系统通知</span></p>
              <div class="myMessage-item-right">
                <span v-if="item.read">已读</span>
                <span style="color: rgba(234, 140, 0, 1)" v-if="!item.read">未读</span>
              </div>
            </div>
          </div>
        </li>
        <li v-if="currentType === 2" v-for="(item , index) in replies_list" :key="index" class="myMessage-item">
          <div class="myMessage-item-top" @click.stop="openContent(index, item.read, item.id)">
            <div class="user_info">
              <img :src="item.avatarUrl ? host + item.avatarUrl : defaultImage">
              <span class="user-name">{{item.nick}}</span>
            </div>
            <div :class="(showAllContentNo === index)? 'editorShow ud-editor-message all-content':'editorShow ud-editor-message'" v-html="highlight(item.content)"></div>
            <div class="date">{{getDate(item.date)}}</div>
          </div>
          <div class="myMessage-item-bottom">
            <div class="source">
              <p class="myMessage-item-left">
                <span>来自帖子 ></span>
                &nbsp;
                <span class="title" @click="toPostDetail(item.detail.post_id)">{{item.detail.title}}</span>
              </p>
              <div class="myMessage-item-right">
                <span v-if="item.read">已读</span>
                <span style="color: rgba(234, 140, 0, 1)" v-if="!item.read">未读</span>
                &nbsp;&nbsp;&nbsp;
                <span class="btn-goRep" @click.stop.prevent="openEditor(index, item.detail.reply_count !== 0, item.id, item.read)">回复<span v-if="item.detail.reply_count !== 0">({{item.detail.reply_count}})</span></span>
              </div>
            </div>
            <transition name="editor-fade">
              <div v-if="showEditorNo === index">
                <div class="editor ud-editor-message">
                  <wangEditor :catchData="catchData" :height="'165px'" :editorType="2"></wangEditor>
                  <button class="btn-reply" @click="sendReply(item.detail.post_id, item.id)">回复他</button>
                </div>
                <ul>
                  <li class="childReplyList-item" v-for="(replyItem, index) in childReplyList" :key="index">
                    <img :src="replyItem.author.avatarUrl ? host + replyItem.author.avatarUrl : defaultImage">
                    <span class="floor">{{index+1}}楼</span>
                    <span class="name">{{replyItem.author.nick}}<span v-if="replyItem.target_object && item.content !== replyItem.target_object.content">&nbsp;&nbsp;<span style="color: rgba(255, 153, 0, 1);">回复</span>&nbsp;&nbsp;{{replyItem.target_object.nick}}</span></span>
                    <span class="content editorShow ud-editor-message" v-html="highlight(replyItem.content)"></span>
                    <span class="date">发表于&nbsp;&nbsp;{{replyItem.publish_date.replace('T', ' ')}}</span>
                  </li>
                </ul>
                <div class="toMore" v-if="childReplyListTotal > childPagesize">
                  <span @click="toPostDetail(item.detail.post_id)">此处省略{{childReplyListTotal-3}}条回复，更多请查看帖子详情</span>
                </div>
              </div>
            </transition>
          </div>
        </li>
      </ul>
      <span class="noList" v-if="total < 5">-&nbsp;无更多消息 -</span>
    </div>
    <div class="myMessage-footer myMessagePage" v-if="total >= 5">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[5, 10, 20]"
        :page-size="pagesize"
        background
        prev-text="上一页"
        next-text="下一页"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import {host} from '@/api/config'
import wangEditor from '@/base/wangeditor/wangeditor'
import prism from 'prismjs'

import api from '@/api/personCenter'

export default {
  name: 'myEssay',
  components: {
    wangEditor
  },
  props: {
    userId: {
      type: String
    }
  },
  data () {
    return {
      currentType: 1,
      page: 1,
      pagesize: 10,
      total: 0,
      message_list: [],
      replies_list: [],
      showEditorNo: -1,
      showAllContentNo: -1,
      editorContent: '',
      editorText: '',
      childReplyList: [],
      childPagesize: 3,
      childReplyListTotal: 0,
      host: host,
      defaultImage: require('../../../static/image/acmlogo.jpg')
    }
  },
  methods: {
    catchData (value, text) {
      this.editorContent = value // 在这里接受子组件传过来的参数，赋值给data里的参数
      this.editorText = text
      // console.log(this.postText)
    },
    changeType (type) {
      this.currentType = type
      this.page = 1
      if (this.currentType === 1) {
        this.getNoticeList()
      } else {
        this.getRepliesList()
      }
    },
    closeContentBox () {
      if (this.showAllContentNo !== this.showEditorNo) {
        this.showAllContentNo = -1
      }
    },
    highlight (content) {
      // console.log(content)
      var language = 'clike'
      // content = content.replace(/<code>/g, '<code class="language-' + language + '">').replace(/<pre>/g, '<pre class="line-numbers">')
      var contentList = content.split('<pre><code>')
      var start = contentList[0]
      var needContent = ''
      for (var i = 1; i < contentList.length; i++) {
        needContent += '<span class="codeContain"><pre class="line-numbers"><code class="language-' + language + '">'
        var childList = contentList[i].split('</code></pre>')
        // 提取出的code内代码装在format内
        var format = childList[0].replace(/&lt;/g, '<').replace(/&gt;/g, '>').replace(/&amp;/g, '&').replace(/<br>/g, '\n')
        format = prism.highlight(format, prism.languages.clike, language)
        var formatList = format.split('\n')
        format = ''
        for (var j = 0; j < formatList.length; j++) {
          format += '<span class="line-numbers-rows">' + formatList[j] + ' </span>'
        }
        needContent += format
        needContent += '</code></pre><button class="copyContent-btn" style="cursor: pointer">复制</button></span>'
        needContent += childList[1]
      }
      return start + needContent
    },
    openEditor (no, ifHadReplies, replyId, ifRead) {
      const _this = this
      if (this.showEditorNo === no) {
        this.showEditorNo = -1
        this.showAllContentNo = no
      } else {
        this.showEditorNo = no
        this.showAllContentNo = this.showEditorNo
        if (!ifRead) {
          api.setHadRead('/forum/read_message', {
            target_id: replyId,
            type: _this.currentType
          }).then(function (res) {
            if (res.success === 'true') {
              if (_this.currentType === 1) {
                _this.message_list[no].read = true
              } else {
                _this.replies_list[no].read = true
              }
            }
          })
        }
        if (ifHadReplies) {
          api.getChildReplies('/forum/posts/child_replies', {
            reply_id: replyId,
            page: 1,
            pagesize: _this.childPagesize
          }).then(function (res) {
            _this.childReplyList = res.data.child_replies_list
            _this.childReplyListTotal = res.total
          })
        }
      }
    },
    openContent (no, ifRead, replyId) {
      this.showAllContentNo = no
      if (!ifRead) {
        const _this = this
        api.setHadRead('/forum/read_message', {
          target_id: replyId,
          type: _this.currentType
        }).then(function (res) {
          if (res.success === 'true') {
            if (_this.currentType === 1) {
              _this.message_list[no].read = true
            } else {
              _this.replies_list[no].read = true
            }
          }
        })
      }
    },
    sendReply (postId, targetId) {
      if (this.editorContent.length < 1000) {
        var clearInitContent = this.editorText
        // 第一步：去除空格&nbsp;
        clearInitContent = clearInitContent.replace(/&nbsp;/g, '')
        // console.log(clearInitContent)
        // 第二步：去除换行
        clearInitContent = clearInitContent.replace(/<br>/g, '')
        // console.log(clearInitContent)
        if (clearInitContent === '' || typeof (this.editorContent) === 'undefined') {
          this.$message({
            message: '回复内容不能为空！',
            type: 'error',
            showClose: true,
            duration: 1800
          })
          return false
        } else if (!(/.*[\u4e00-\u9fa5]+.*$/.test(clearInitContent)) && !(/[0-9a-z]/.test(clearInitContent))) {
          this.$message({
            message: '未输入有意义内容！',
            type: 'error',
            showClose: true,
            duration: 1800
          })
          return false
        }
      } else if (this.editorContent.length > 32768) {
        this.$message.warning({
          message: '内容太长！若是直接复制桌面文档，请改为右键纯文本粘贴！',
          type: 'error',
          duration: 1800
        })
        return false
      }

      const _this = this
      api.sendReply('/forum/posts/send_reply', {
        params: {
          post_id: postId,
          target_id: targetId,
          content: _this.editorContent
        }
      }).then(function (res) {
        if (res.success === 'true') {
          _this.showEditorNo = -1
          _this.$message({
            message: '成功发送回复信息！',
            type: 'success',
            showClose: true,
            duration: 1000
          })
        }
      })
    },
    getRepliesList () {
      // 调用接口获取服务器数据
      const _this = this
      api.getRepliesList('/forum/replies', {
        page: _this.page,
        pagesize: _this.pagesize
      }).then(function (res) {
        _this.replies_list = res.data.reply_list
        _this.total = res.total
      })
    },
    getNoticeList () {
      // 调用接口获取服务器数据
      const _this = this
      api.getRepliesList('/forum/messages', {
        page: _this.page,
        pagesize: _this.pagesize
      }).then(function (res) {
        _this.message_list = res.data.message_list
        _this.total = res.total
      })
    },
    toPostDetail (targetId) {
      this.$router.push({name: 'postDetail', params: {id: targetId}})
    },
    handleSizeChange (val) {
      this.page = 1
      this.pagesize = val
      if (this.currentType === 1) {
        this.getNoticeList()
      } else {
        this.getRepliesList()
      }
    },
    handleCurrentChange (val) {
      this.page = val
      if (this.currentType === 1) {
        this.getNoticeList()
      } else {
        this.getRepliesList()
      }
    },
    getDate (date) {
      var postDate = new Date(date.replace('T', ' '))
      var now = new Date()
      var yearD = now.getFullYear() - postDate.getFullYear()
      var monthD = now.getMonth() - postDate.getMonth()
      var dayD = now.getDate() - postDate.getDate()
      var returnDate = (yearD > 0) ? yearD + '年前' : (yearD === 0 && monthD > 0) ? monthD + '月前' : (yearD === 0 && monthD === 0) ? dayD + '天前' : date.replace('T', ' ')
      var d = now.getTime() - postDate.getTime()
      if (d < 60 * 1000) {
        return Math.floor(d / 1000) + '秒前'
      } else if (d < 60 * 60 * 1000) {
        return Math.floor(d / (60 * 1000)) + '分钟前'
      } else if (d < 60 * 60 * 1000 * 24) {
        return Math.floor(d / (60 * 60 * 1000)) + '小时前'
      } else {
        return returnDate
      }
    }
  },
  created () {
    this.getNoticeList()
  }
}
</script>

<style scoped>
  @import "../../../static/editorCss/style.css";
  @import "../../../static/highlight/prism.css";

  .myMessage{
    min-height: 700px;
    background-color: white;
    padding: 20px 20px;
  }
  .myMessage-header{
    padding-bottom: 5px;
    border-bottom: 1px solid gainsboro;
    line-height: 40px;
    font-size: 20px;
  }
  .myMessage-header span:first-child {
    padding-right: 20px;
  }
  .myMessage-header .btn-type {
    margin-top: 10px;
    padding: 2px 10px;
    line-height: 26px;
    font-size: 14px;
    background-color: transparent;
    border-radius: 13px;
    cursor: pointer;
  }
  .myMessage-header  .btn-current-type {
    color: #fff;
    background-color: #FF9900;
  }
  .myMessage-body {
    height: 560px;
    overflow-y: scroll;
    padding: 10px 5px;
  }
  .myMessage-body .noList {
    line-height: 3em;
    display: block;
    font-weight: 300;
    font-size: 12px;
    color: #AEAEAE;
    text-align: center;
    cursor: default;
  }
  .myMessage-body .myMessage-item {
    width: 100%;
    background-color: rgba(240, 240, 240, 0.2);
    border-radius: 10px;
    box-shadow: -2px 5px 5px rgba(0, 0, 0, 0.1);
    position: relative;
    top: 0px;
    margin: 10px 0;
    transition: all .3s;
  }
  .myMessage-body .myMessage-item:hover {
    background-color: rgba(240, 240, 240, 0.5);
