    border-radius: 50%;
    position: relative;
    top: 8px;
  }
  .user_info .user-name{
    display: inline-block;
    width: 60px;
    text-align: center;
    font-size: 12px;
    line-height: 1.5em;
    position: absolute;
    color: #666;
    top: 50%;
    left: 55px;
    transform: translateY(-50%);
  }
  .myMessage-item-top .editorShow {
    font-size: 14px;
    line-height: 1.5em;
    padding-left: 130px;
    min-height: 3em;
    max-height: 4.5em;
    overflow: hidden;
    transition: all .3s;
  }
  .myMessage-item-top .all-content {
    min-height: 3.5em;
    max-height:  400px;
    overflow-y: auto;
  }
  .myMessage-item-top .date {
    font-size: 12px;
    color: #AEAEAE;
    text-align: right;
    padding-right: 19px;
    position: relative;
    bottom: -5px;
  }
  .myMessage-item-bottom {
    border-top: 1px solid rgba(230, 230, 230, 1);
    padding-top: 3px;
  }
  .myMessage-item-bottom .source{
    font-size: 12px;
    color: #cccccc;
    cursor: default;
    padding: 0 20px;
  }
  .myMessage-item-bottom p,
  .myMessage-item-bottom span {
    line-height: 20px;
  }
  .myMessage-item-bottom .title {
    cursor: pointer;
  }
  .myMessage-item-bottom .title:hover {
    color: #FF9900;
  }
  >>> .myMessage-item-bottom  .repContent img{
    height: 20px;
    vertical-align: top;
    padding: 0 2px;
  }
  .myMessage-item-bottom .myMessage-item-left {
    display: inline-block;
    width: 550px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .myMessage-item-bottom .myMessage-item-right {
    float: right;
  }
  .myMessage-item-bottom .myMessage-item-right .btn-goRep {
    cursor: pointer;
  }
  .myMessage-item-bottom .myMessage-item-right .btn-goRep:hover {
    color: #f48118;
  }
  .myMessagePage{
    padding-top: 20px;
  }
  .myMessagePage /deep/ .el-pagination .el-pager .active{
    background-color: #f48118 !important;
  }
  .myMessagePage /deep/ .el-pagination .el-pagination__total{
    color: inherit;
  }
  .myMessagePage /deep/ .el-pagination .el-pagination__jump{
    color: inherit;
  }
  .editor {
    max-height: 300px;
    padding: 0 20px 0 20px;
    background-color: rgba(235, 235, 235, 1);
    box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.05);
