<template>
  <div class="index">
    <carousel :carousels="carousels"></carousel>
    <systemStats :showStatsBox = "isShowStatsBox" :systemStats = "systemStats"></systemStats>
    <div class="carousel-data">
      <ul>
        <li><i class="iconfont icon-timu"></i>&nbsp;题目总数:&nbsp;<strong>{{systemStats.problem_counts}}</strong></li>
        <li><i class="iconfont icon-yonghu"></i>&nbsp;活跃人数:&nbsp;<strong class="color-orange">{{systemStats.online_counts}}</strong></li>
        <li><i class="iconfont icon-tijiao"></i>&nbsp;今日提交总数:&nbsp;<strong class="color-green">{{systemStats.today_submitted}}</strong></li>
        <li><i class="iconfont icon-kaihuxiangguan"></i>&nbsp;用户总数:&nbsp;<strong>{{systemStats.user_counts}}</strong></li>
        <li><i class="iconfont icon-btn_userx"></i>&nbsp;学生总数:&nbsp;<strong>{{systemStats.student_counts}}</strong></li>
        <li :class="isShowStatsBox ? 'statsBox-btn statsBox-show' : 'statsBox-btn statsBox-hide'" @click="isShowStatsBox = !isShowStatsBox"><i class="iconfont icon-tongji"></i>&nbsp;统计详情</li>
      </ul>
    </div>
    <div class="rank-box">
      <div class="container">
        <el-row type="flex" justify="space-center">
          <el-col>
            <rank-box rankTitle="今日" titleEn="day" float="right" :rankData="dayRankData" @getUserInfo="getUserInfo" @toRank="toRank"></rank-box>
          </el-col>
          <el-col>
            <rank-box rankTitle="本周" titleEn="week" float="right" :rankData="weekRankData" @getUserInfo="getUserInfo" @toRank="toRank"></rank-box>
          </el-col>
        </el-row>
        <el-row type="flex" justify="space-center">
          <el-col>
            <rank-box rankTitle="本月" titleEn="month" :rankData="monthRankData" @toRank="toRank" @getUserInfo="getUserInfo" float="right"></rank-box>
          </el-col>
          <el-col>
            <rank-box float="right" :rankData="totalRankData" @toRank="toRank" @getUserInfo="getUserInfo"></rank-box>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="news-card-all">
      <div class="news-card-container">
          <div class="new-right">
            <div class="new-Rhaed">
              <img src="static/image/logo.png" alt="">
              <h5>通知公告</h5>
              <span>Announcements</span>
              <div style="width: 137px;height: 1px;background-color: #ffb400;margin: 8px auto 0;"></div>
            </div>
            <div class="new-Rbottom">
              <ul>
                <li v-for="(item, index) in notices" :key="index">
                  <a @click="showNewsContent(item.post_id)">{{item.title}}</a>
                  <span>{{item.publish_date.split('T')[0]}}</span>
                </li>
              </ul>
            </div>
          </div>
          <div class="new-left">
            <news-card title="精华帖子" :newsData="hotpaper"></news-card>
            <div class="inline"></div>
            <news-card title="最新帖子" :newsData="newpaper"></news-card>
          </div>
        </div>
    </div>
    <div class="problem-rank-area">
      <div class="container">
        <el-row style="overflow: hidden">
          <el-col :span="12">
            <problem-rank-card v-if="highOfProblemRank.list.length" :ths="ths" :item="highOfProblemRank"></problem-rank-card>
          </el-col>
          <el-col :span="12">
            <problem-rank-card v-if="lowOfProblemRank.list.length" :ths="ths" :item="lowOfProblemRank" float="right"></problem-rank-card>
          </el-col>
        </el-row>
      </div>
    </div>
    <el-dialog
      title="用户信息"
      :visible.sync="userDialog"
      width="60%">
      <Information :information="infoUid"></Information>
    </el-dialog>
    <my-footer @callFeedback="callFeedback"></my-footer>
    <el-dialog
      title="反馈意见"
      :visible.sync="feedbackDialog"
      width="60%"
    >
      <el-form ref="form" :rules="feedbackRule" :model="feedbackForm" label-width="80px">
        <el-form-item label="标题" prop="title">
          <el-row>
            <el-col :span="24">
              <el-input v-model="feedbackForm.title" placeholder="请输入标题"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="反馈类型">
          <el-row>
            <el-col :span="24">
              <el-select v-model="feedbackForm.type" placeholder="请选择反馈类型">
                <el-option
                  v-for="item in feedbackType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="反馈内容" prop="content">
          <el-row>
            <el-col :span="24">
              <el-input
                type="textarea"
                :rows="10"
                placeholder="请输入内容"
                v-model="feedbackForm.content"
                resize="none">
              </el-input>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="feedbackDialog = false">取 消</el-button>
        <el-button type="primary" @click="sendFeedback">提交我的建议</el-button>
      </span>
    </el-dialog>
    <!--强制修改不符合规格的用户名-->
    <change-uname v-if="isShowChangeUname" :info="User" @changeUname = "updateInfo"></change-uname>
  </div>
</template>

<script>
import {host} from '@/api/config'
import Carousel from '@/base/carousel/h-carousel'
import systemStats from '../../base/index/systemStats.vue'
import rankBox from '@/base/index/rankbox'
import problemRankCard from '@/base/index/problem-rank-card'
import newsCard from '@/base/index/news-card'
import myFooter from '@/base/footer/index.vue'
import Information from '@/base/infomation/infomation'
import changeUname from '@/base/index/change-uname'

import api from '@/api/index'

export default {
  components: {
    Carousel,
    systemStats,
    rankBox,
    problemRankCard,
    newsCard,
    myFooter,
    Information,
    changeUname
  },
  data () {
    return {
      host: host,
      // host: 'http://localhost:8080/',
      carousels: [],
      systemStats: {},
      isShowChangeUname: false,
      isShowStatsBox: false,
      notices: [],
      hotpaper: [],
      newpaper: [],
      ths: [
        {title: '题目名', textAlign: 'left'},
        {title: '题目号', textAlign: 'center'},
        {title: '通过率', textAlign: 'center'},
        {title: '用户提交数', textAlign: 'left'}
      ],
      highOfProblemRank: {
        title: '通过率最高的题目',
        list: []
      },
      lowOfProblemRank: {
        title: '通过率最低的题目',
        list: []
      },
      totalRankData: [],
      monthRankData: [],
      weekRankData: [],
      dayRankData: [],
      dialogVisible: false,
      userDialog: false,
      newsContent: {
        title: '',
        time: '',
        content: '',
        nick: ''
      },
      infoUid: '',
      feedbackDialog: false,
      feedbackType: [
        {
          label: '用户体验',
          value: 1
        },
        {
          label: '用户界面',
          value: 2
        },
        {
          label: '题库',
          value: 3
        },
        {
          label: '后台效率',
          value: 4
        },
        {
          label: '发现BUG',
          value: 5
        },
        {
          label: '新功能',
          value: 6
        },
        {
          label: '其他',
          value: 7
        }
      ],
      feedbackForm: {
        // uid: -1,
        title: '',
        content: '',
        type: 1
      },
      feedbackRule: {
        title: [
          {
            required: true,
            message: '标题不能为空',
            trigger: 'blur'
          }
        ],
        content: [
          {
            required: true,
            message: '内容不能为空',
            trigger: 'blur'
          }
        ]
      },
      User: {}
    }
  },
  methods: {
    toRank (e) {
      let rankType = e.target.dataset.ranktype
      this.$router.push({path: '/rank', query: {rankType: rankType}})
    },
    getTotalList () {
      const _this = this
      api.getRankList('/ranks', 8).then(function (res) {
        _this.totalRankData = _this.initRankData(res.data.all_list)
        _this.monthRankData = _this.initRankData(res.data.month_list)
        _this.weekRankData = _this.initRankData(res.data.week_list)
        _this.dayRankData = _this.initRankData(res.data.day_list)
      })
    },
    initRankData (data) {
      if (data.length < 8) {
        let temp = 8 - data.length
        for (let i = 0; i < temp; i++) {
          let obj = {
            uid: -1,
            nick: '',
            accept: 0,
            submit: 0
          }
          data.push(obj)
        }
        return data
      } else {
        return data
      }
    },
    getNews () {
      const _this = this
      api.getPostsList('/forum/posts', {
        type: 3,
        page: 1,
        pageSize: 6
      }).then(function (res) {
        _this.notices = res.data.post_list
      })
    },
    showNewsContent (id) {
      this.$router.push({name: 'postDetail', params: {id: id}})
    },
    getHighPro () {
      const _this = this
      api.getProList('/problems?page=1&pagesize=5&sort=desc&defunct=N').then(function (res) {
        _this.$delete(_this.highOfProblemRank, 'list')
        _this.$set(_this.highOfProblemRank, 'list', res.data)
      })
    },
    getLowPro () {
      const _this = this
      api.getProList('/problems?page=1&pagesize=5&sort=asc&defunct=N').then(function (res) {
        _this.$delete(_this.lowOfProblemRank, 'list')
        _this.$set(_this.lowOfProblemRank, 'list', res.data)
      })
    },
    getUserInfo (uid) {
      this.userDialog = true
      this.infoUid = uid
    },
    getPostsList () {
      const _this = this
      api.getPostsList('/forum/posts', {
        type: 2,
        page: 1,
        pageSize: 4
      }).then(function (res) {
        _this.hotpaper = res.data.post_list
      })
      api.getPostsList('/forum/posts', {
        type: 1,
        page: 1,
        pageSize: 4
      }).then(function (res) {
        _this.newpaper = res.data.post_list
      })
    },
    callFeedback () {
      this.getUserStore()
      if (this.User.uid) {
        this.feedbackDialog = true
      } else {
        this.$message.error('请登录账号之后再进行建议反馈')
      }
    },
    sendFeedback () {
      const _this = this
      _this.$refs.form.validate(function (valid) {
        if (valid) {
          //          _this.feedbackForm.uid = _this.User.uid
          api.postFeedback('/feedbacks', _this.feedbackForm).then(function (res) {
            if (res.success === 'true') {
              _this.$message({
                message: '反馈成功，我们会及时处理您的建议',
                type: 'success'
              })
              _this.feedbackDialog = false
            } else {
              _this.$message.error('反馈上传失败，请稍候再试')
              _this.feedbackDialog = false
            }
          })
        } else {
          return false
        }
      })
    },
    getUserStore () {
      var storage = window.sessionStorage
      var userData = storage.getItem('userInfo')
      if (userData !== 'undefined' && userData !== null && userData !== undefined) {
        var jsonObj = JSON.parse(userData)
        this.User = jsonObj
      } else {
        this.User = {}
      }
    },
    getSystemStats () {
      const _this = this
      api.getSystemStats('/stats', {}).then(function (res) {
        _this.systemStats = res.data
      })
    },
    getBannerList () {
      const _this = this
      api.getBannerList('/banners', {}).then(function (res) {
        _this.carousels = res.data.banner_list
      })
    },
    checkUserName () {
      this.getUserStore()
      if (this.User.uid) {
        // 判断是否是竞赛小组名，是的话允许20长度
        var reg = this.User.nick.indexOf('GPNU_') === 0
          ? /^[-\w\u4e00-\u9fa5]{2,20}$/ : /^[-\w\u4e00-\u9fa5]{2,14}$/
        if (!reg.test(this.User.nick)) {
          // 强制用户修改用户名，否则不让进
          this.isShowChangeUname = true
        } else {
          // 如果是学生，判断其是否有没有考试正在进行或即将开始，有即通知该学生前往
          if (this.User.identity === '1') this.checkQuizStatus()
        }
      }
    },
    async checkQuizStatus () {
      const result = await api.checkQuizStatus('/quiz/student/find_quiz_by_time')
      if (result.success === 'false') return
      // 检测是否有正在进行的考试，有即保存下来并向学生用户发送通知
      var quizingList = result.data.quiz_list
      quizingList.forEach((i, idx) => {
        var now = new Date().getTime()
        var startTime = new Date(i.start_time).getTime()
        var tip = (startTime > now && startTime - now <= 10 * 60 * 1000) ? '有考试即将开始' : '有考试正在进行'
        this.$notify.info({
          title: tip,
          dangerouslyUseHTMLString: true,
          message: `<p style="font-size: 13px;">您${tip},该考试为“${i.title}”,请点击<a style="cursor: pointer; color: #ff9900;" href="${this.host + '#/quizStart/' + i.quiz_id}">此处</a>跳转至开考页。</p>`,
          offset: 60 + (100 * idx),
          duration: 3000
        })
      })
    },
    updateInfo (data) {
      const _this = this
      api.updateUserInfo('/users', data).then(function (res) {
        if (res.success === 'true') {
          _this.$message({
            message: '修改信息成功',
            type: 'success'
          })
          var storage = window.sessionStorage
          storage.setItem('userInfo', JSON.stringify(data))
          _this.isShowChangeUname = false
        } else {
          _this.$message({
            message: '修改失败，请稍候再试',
            type: 'warning'
          })
        }
      })
    }
  },
  created () {
    this.getBannerList()
    this.getTotalList()
    this.getNews()
    this.getHighPro()
    this.getLowPro()
    // 获取论坛帖子列表
    this.getPostsList()
    // 获取系统统计数据
    this.getSystemStats()
    // 用户名规范检测
    this.checkUserName()
  }
}
</script>

<style scoped>
  .index {
    width: 100%;
    min-width: 1170px;
  }
  .rank-box{
    height: 1000px;
    margin-bottom: 18px;
    background-color: #f0f0f0;
  }
  .rank-box .container {
    height: 100%;
    width: 1170px;
    margin: 0px auto;
    padding: 40px 0 30px 0;
    box-sizing: border-box;
  }
  .news-card-all{
    width: 100%;
    height: 440px;
    background-color: white;
  }
  .problem-rank-area {
    width: 100%;
    height: 410px;
    background: url("../../../static/image/problem-rank-bg.png") no-repeat;
    background-size: cover;
  }
  .problem-rank-area .container {
    width: 1170px;
    height: 100%;
    margin: 0px auto;
    padding: 30px 0;
    box-sizing: border-box;
  }
  .news-card-container{
    width: 1170px;
    height: 100%;
    margin: 0px auto;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .new-right{
    width: 460px;
    height: 360px;
    background-color: #feca3e;
    /*float: left;*/
    display: inline-block;
    border-radius: 15px;
  }
  .new-left .news-template:first-child::after {
    display: inline-block;
    content: '';
    width: 95%;
    height: 1px;
    position: absolute;
    margin-top: 15px;
    background-color: #f2f2f2;
  }
  .new-Rhaed img{
    display: block;
    margin: 15px auto;
    /*width: 44px;*/
    height: 55px;
  }
  .new-Rhaed h5{
    font-size: 16px;
    line-height: 1.2;
    padding-bottom: 6px;
    color: #2b303e;
    text-align: center;
  }
  .new-Rhaed span{
    display: block;
    font-size: 12px;
    padding-bottom: 6px;
    color: #fff;
    text-align: center;
    height: 13px;
  }
  .new-Rbottom ul{
    padding: 30px 34px 38px 29px;
  }
  .new-Rbottom ul li{
    font-size: 14px;
    margin-top: 15px;
    line-height: 14px;
    cursor: pointer;
  }
  .new-Rbottom ul li:first-child{
    margin-top: 0;
  }
  .new-Rbottom ul li a{
    display:inline-block;
    width:250px;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
    cursor: pointer;
  }
  .new-Rbottom ul li span{
    cursor: pointer;
    float: right;
  }
  .new-Rbottom ul li:hover a,
  .new-Rbottom ul li:hover span{
    transition: color .3s;
    color: rgb(105, 146, 49);
  }
  .carousel-data ul>li{
    display: inline-block;
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
    line-height: 70px;
    font-size: 16px;
    cursor: default;
  }
  .statsBox-show:hover {
    cursor: pointer;
    color: white;
    background-color: rgba(255, 153, 0, 1);
  }
  .statsBox-hide:hover {
    cursor: pointer;
    color: white;
    background-color: rgba(142, 204, 69, 1);
  }
  i{
    margin-right: 4px;
  }
  .color-orange{
    color: rgba(255, 153, 0, 1);
  }
  .color-green{
    color: rgba(142, 204, 69, 1);
  }
  .carousel-data{
    height: 70px;
    background-color: #fff;
    /*border-bottom: 2px solid rgba(142, 204, 69, 1);*/
    -webkit-box-shadow: 0px -3px 8px rgba(0, 0, 0, .05);
    box-shadow: 0px -3px 8px rgba(0, 0, 0, 0.05);
    text-align: center;
    position: relative;
    /*`index: 3;*/
  }

</style>



