<template>
  <div class="Information">
    <div class="Info-head">
      <span>基本信息</span>
      <button class="edit" @click="handleEdit" v-if="!isEditing">编辑</button>
      <button class="edit submit" @click="saveChange('fromData')" v-if="isEditing">保存</button>
    </div>
    <div class="Info-from">
      <el-form :model="fromData" label-position="right" label-width="80px" :rules="infoRules" ref="fromData">
        <div class="info-area base-info">
          <el-form-item label="用户昵称" prop="nick">
            <el-input v-model="fromData.nick"  status-icon auto-complete="off" clearable :disabled="isBaseDisable||isNameDisable"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio-group v-model="fromData.sex" size="medium" :disabled="isBaseDisable">
              <el-radio border :label='0'>男</el-radio>
              <el-radio border :label='1'>女</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="QQ" prop="qq">
            <el-input v-model="fromData.qq" clearable :disabled="isBaseDisable"></el-input>
          </el-form-item>
          <el-form-item label="邮箱">
            <el-input v-model="fromData.email" clearable :disabled="isBaseDisable"></el-input>
          </el-form-item>
          <el-form-item label="联系方式">
            <el-input v-model="fromData.contact" clearable :disabled="isBaseDisable"></el-input>
          </el-form-item>
          <el-form-item label="个性签名" prop="signature">
            <el-input type="textarea"
                      :autosize="{ minRows: 4}"
                      v-model="fromData.signature"
                      status-icon
                      auto-complete="off"
                      :disabled="isBaseDisable"
                      :resize="isBaseDisable ? 'none' : 'vertical'"></el-input>
          </el-form-item>
        </div>
        <div class="info-area real-info" v-if="Info.identity === '2' || Info.identity === '1'">
          <el-form-item label="学校">
            <span class="from-data">{{Info.school_name}}</span>
          </el-form-item>
          <el-form-item label="学院">
            <span class="from-data">{{Info.academy}}</span>
          </el-form-item>
          <el-form-item label="班级" v-if="Info.identity == '1'">
            <span class="from-data">{{Info.class_name}}</span>
          </el-form-item>
          <el-form-item label="年级" v-if="Info.identity == '1'">
            <span class="from-data">{{Info.grade}}级</span>
          </el-form-item>
          <el-form-item label="学号" v-if="Info.identity == '1'">
            <span class="from-data">{{Info.code}}</span>
          </el-form-item>
          <el-form-item label="教工号" v-if="Info.identity == '2'">
            <span class="from-data">{{Info.code}}</span>
          </el-form-item>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
export default{
  props: {
    Info: {
      type: Object
    }
  },
  data () {
    return {
      fromData: {
        uid: '',
        nick: '',
        email: '',
        sex: '',
        qq: '',
        signature: '',
        contact: ''
      },
      infoRules: {
        signature: [
          {validator: this.checkSign, trigger: 'blur'}
        ],
        nick: [
          {validator: this.checkName, trigger: 'blur'}
        ],
        qq: [
          {validator: this.checkQQ, trigger: 'blur'}
        ]
      },
      isEditing: false,
      isBaseDisable: true,
      isRealDisable: true,
      isNameDisable: false,
      maxLength: 60,
      maxname: 20
    }
  },
  methods: {
    checkSign (rule, value, callback) {
      if (!value) {
        callback()
        return
      }
      const _this = this
      // console.log(value)
      var signlen = 0
      for (var i = 0; i < value.length; i++) {
        // 判断是否为中文字符
        if (value.charAt(i).charCodeAt() > 255) {
          signlen += 2
        } else {
          signlen++
        }
      }
      // console.log(signlen + ':' + _this.maxLength)
      var timer = setTimeout(() => {
        if (signlen > _this.maxLength) {
          callback(new Error('签名字数超出限制'))
        } else {
          callback()
        }
        clearTimeout(timer)
      }, 300)
    },
    checkName (rule, value, callback) {
      var timer = setTimeout(() => {
        // 判断是否是竞赛小组名，是的话允许20长度
        var reg = value.indexOf('GPNU_') === 0
          ? /^[-\w\u4e00-\u9fa5]{2,20}$/ : /^[-\w\u4e00-\u9fa5]{2,14}$/
        if (!reg.test(value)) {
          callback(new Error('昵称不符合规格'))
        } else {
          callback()
        }
        clearTimeout(timer)
      }, 300)
    },
    checkQQ (rule, value, callback) {
      if (!value) {
        callback()
        return
      }
      var timer = setTimeout(() => {
        // 判断是否是竞赛小组名，是的话允许20长度
        var reg = /^[0-9]{6,10}$/
        if (!reg.test(value)) {
          callback(new Error('请输入正确的QQ号码'))
        } else {
          callback()
        }
        clearTimeout(timer)
      }, 300)
    },
    handleEdit () {
      this.isEditing = !this.isEditing
      if (this.isEditing) {
        this.isBaseDisable = false
      } else {
        this.isBaseDisable = true
      }
    },
    getInfo () {
      if (this.Info) {
        this.fromData.uid = this.Info.uid
        this.fromData.nick = this.Info.nick
        this.fromData.sex = this.Info.sex
        if (this.Info.identity === '1') {
          this.isNameDisable = true
        }
        if (this.Info.qq) {
          this.fromData.qq = parseInt(this.Info.qq)
        }
        this.fromData.contact = this.Info.contact
        this.fromData.email = this.Info.email
        this.fromData.signature = this.Info.signature
      }
    },
    saveChange (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$emit('updateInfo', this.fromData)
        } else {
          console.log('输入不规范！')
          return false
        }
      })
    }
  },
  watch: {
    'info': 'getInfo'
  },
  created () {
    this.getInfo()
  }
}
</script>

<style>
  .Information .Info-from .info-area .el-form-item__label {
    font-weight: bold;
    font-size: 14px;
  }
</style>

<style scoped>
  .Information{
    min-height: 700px;
    background-color: white;
    padding: 20px 20px;
  }
  .Info-head{
    padding-bottom: 5px;
    border-bottom: 1px solid gainsboro;
  }
  .Info-head{
    line-height: 40px;
    font-size: 20px;
  }
  .edit{
    border: none;
    padding: 10px 40px;
    background: #f48118;
    border-radius: 10px;
    color: white;
    float: right;
    cursor: pointer;
  }
  .edit:hover{
    background-color: #d48118;
  }
  .submit {
    background-color: #00BC9B;
  }
  .submit:hover {
    background-color: #00BC61;
  }
  .Info-from{
    /*padding: 0px 330px 0px 100px;*/
    margin-top: 20px;
    font-size: 0;
  }
  .Info-from .info-area {
    display: inline-block;
    width: 50%;
    padding: 0 30px;
    box-sizing: border-box;
    vertical-align: top;
  }
  .Info-from .info-area .from-data {
    color: #606266;
    padding-left: 16px;
    box-sizing: border-box;
  }
</style>



