<!--suppress ALL -->
<template>
    <span class="quiz-timer">
      <template v-if="!isEnd">{{getFormatTime}}</template>
    </span>
</template>

<script>
export default {
  props: {
    initTime: {
      type: String,
      default: (new Date()).toString(),
      request: true
    },
    duration: {
      type: Number,
      default: 0,
      request: true
    },
    advance: {
      type: Number,
      default: 0,
      request: true
    },
    // 是否开始计时
    isBeginTimer: {
      type: Boolean,
      default: false
    }
  },
  name: 'quiz-timer',
  computed: {
    getFormatTime () {
      return this.isFirstBegin
        ? this.formatTime(this.atTimer)
        : this.formatTime(Math.abs(this.atTimer) - this.duration * 60 * 1000)
    }
  },
  data () {
    return {
      beginTime: '',
      atTimer: 0,
      timer: null,
      isFirstBegin: false, // 是否是第一次开始
      ishadTip: false, // 是否已经提醒
      isEnd: false // 是否结束计时
    }
  },
  methods: {
    // 一开始进行计时时的处理事件
    enterTimerHandler () {
      this.$emit('enterTimerHandler')
    },
    // 开始时间未到提示事件
    notBeginTimerHandler () {
      this.$emit('notBeginTimerHandler')
    },
    // 计时过程每刻执行的事件
    duringTimerHandler () {
      this.$emit('duringTimerHandler', this.atTimer)
    },
    // 计时器到时处理的事件
    endTimerHander () {
      this.$emit('endTimerHander')
    },
    // 计时器到提醒时间时处理的事件
    tipsTimerHander () {
      this.$emit('tipsTimerHander', this.atTimer)
    },
    // 开始时已经计时结束
    initHadFinishHandler () {
      this.$emit('initHadFinishHandler')
    },
    startTimer () {
      if (!this.timer) {
        var now = new Date()
        var tempTimer = now
        this.timer = setInterval(() => {
          now.setTime(now.getTime() + 1000)
          tempTimer = (this.duration * 60 * 1000) + this.beginTime - now.getTime()
          // 判断开始时间是否还未到，未到则提示
          if (this.beginTime - now.getTime() > 0) {
            // 开始时间未到提示事件
            this.notBeginTimerHandler()
          } else {
            // 一开始进行计时时的处理事件
            if (!this.isFirstBegin) {
              this.enterTimerHandler()
              // 标记已经执行过开始计时器事件
              this.isFirstBegin = true
            }
          }
          // 第一次进入提醒范围内需要调用提醒事件
          if (!this.ishadTip && tempTimer < this.advance * 60 * 1000) {
            this.tipsTimerHander()
            // 标记已经执行过提醒事件
            this.ishadTip = true
          }
          // 如果计时器结束执行结束事件
          if (tempTimer < 0) {
            this.endTimerHander()
            this.endTimer()
          } else {
            // 否则更新最新计时器
            this.atTimer = tempTimer
            // 并执行每秒执行事件
            this.duringTimerHandler()
          }
        }, 1000)
      }
    },
    endTimer () {
      if (!this.timer) return
      clearInterval(this.timer)
      this.isEnd = true
    },
    formatTime (time) {
      if (time <= 0) {
        return ''
      }
      var tempTime = time
      var str = ''
      var t = ''
      t = parseInt(tempTime / (1000 * 60 * 60 * 24))
      str += (t === 0) ? '' : t + '天'
      tempTime %= (1000 * 60 * 60 * 24)
      t = parseInt(tempTime / (1000 * 60 * 60))
      str += (t === 0) ? '' : t + '小时'
      tempTime %= (1000 * 60 * 60)
      t = parseInt(tempTime / (1000 * 60))
      str += (t === 0) ? '' : t + '分钟'
      tempTime %= (1000 * 60)
      str += parseInt(tempTime / 1000) + '秒'
      return str
    }
  },
  created () {
    if (this.initTime === '-1') return
    var now = new Date()
    this.beginTime = new Date(this.initTime).getTime()
    this.atTimer = (this.duration * 60 * 1000) + this.beginTime - now.getTime()
    // 是否一开始就已经结束计时
    if (this.atTimer <= 0) {
      this.isEnd = true
      this.initHadFinishHandler()
      return
    }
    // 是否开始计时
    if (this.isBeginTimer) {
      this.startTimer()
    } else {
      this.endTimer()
    }
  },
  destroyed () {
    clearInterval(this.timer)
  },
  watch: {
    isBeginTimer () {
      if (this.isBeginTimer) {
        this.startTimer()
      } else {
        this.endTimer()
      }
    }
  }
}
</script>

<style scoped>
  .quiz-timer {
    cursor: default;
  }
</style>



