<template>
    <div id="postDetail">
      <div class="header">
        <div class="header-left">
          <p class="postHead-left" @click="backToIndex">讨论区</p>
          <p class="postHead_block">></p>
          <p class="postHead-right">发布贴子</p>
        </div>
        <div class="header-right">
          <div class="btn-publish" v-if="ifLogin" @click="toPublish">发布帖子</div>
          <div class="btn-publish" v-if="!ifLogin" @click="toLogin(2)">登录发布</div>
          <div class="search-input-box">
            <input type="text" placeholder="请输入关键词" v-model="searchKey" @keyup.enter="toSearch">
            <i class="el-icon-search" @click="toSearch"></i>
          </div>
        </div>
      </div>
      <div class="container">
        <div class="left-module" v-loading="loading">
          <div class="left-module-top">
            <div class="user_info" style="width: unset">
              <img :src="postDetail.author.avatarUrl ? host + postDetail.author.avatarUrl : defaultImage">
              <span class="user-name" style="max-width: unset">{{(postDetail.type === 3)? 'OJ平台管理员' : postDetail.author.nick}}</span>
            </div>
            <div class="module-top">
              <h3 class="title">
                <span @click="toPostDetail(postDetail.post_id)">{{postDetail.title}}</span>
                <span class="tags tags-essence" v-if="postDetail.type === 2">精华</span>
                <span class="tags tags-notice" v-if="postDetail.type === 3">系统公告</span>
                <span class="tags tags-guide" v-if="postDetail.type === 4">新手指南</span>
              </h3>
              <div class="tagKey">
                <span class="tags tags-key" v-for="(subItem, index) in postDetail.labels" :key="index">{{subItem}}</span>
                <span class="tags tags-key" v-if="postDetail.labels.length === 0">普通帖子</span>
                <h5 class="authorAndDate">{{postDetail.publish_date.replace('T', ' ')}}</h5>
              </div>
              <div class="mainContent editorShow" v-html="highlight(postDetail.content)"></div>
            </div>
            <div class="block"></div>
            <div class="btn-prePost" v-if="postDetail.pre_post.title !== null">
              <div class="btn" @click="toNewPost(postDetail.pre_post.post_id)">上一篇帖子</div>
              <h5>{{postDetail.pre_post.title}}</h5>
            </div>
            <div class="btn-nextPost" v-if="postDetail.next_post.title !== null && !loading">
              <div class="btn" @click="toNewPost(postDetail.next_post.post_id)">下一篇帖子</div>
              <h5>{{postDetail.next_post.title}}</h5>
            </div>
            <div class="btn-opers" v-if="ifLogin">
              <button :class="(postDetail.if_liked)? 'btn-green btn-to-grey':'btn-grey btn-to-green'" @click="giveLike(postDetail.post_id, postDetail.if_liked)"><i class="icon-zan"></i></button>
              <button :class="showEditor? 'btn-grey btn-to-orange':'btn-orange btn-to-grey'" @click="toReplyPost()"><i class="icon-huifu"></i></button>
              <button :class="(!postDetail.is_top)? 'btn-grey btn-to-orange':'btn-orange btn-to-grey'" v-if="info.identity === '3'" @click="toTopPost(postDetail.post_id)"><i class="icon-stick-copy"></i></button>
            </div>
          </div>
          <transition name="editor-fade">
          <div class="left-module-middle" v-if="showEditor">
            <span class="tip">回复帖子</span>
            <div class="editor ud-editor-message">
              <wangEditor :catchData="catchData" :height="'165px'" :editorType="2"></wangEditor>
              <button class="btn-reply" @click="sendReply(postDetail.post_id, -1)">回复他</button>
            </div>
            <div class="block" style="height: 10px"></div>
          </div>
          </transition>
          <span class="stats" v-if="postDetail.replies !== 0">共有&nbsp;{{postDetail.replies}}&nbsp;个回复</span>
          <div class="left-module-bottom">
            <h5 class="noMore" v-if="replyList.length === 0">暂时无人评论</h5>
              <ul class="replylist" v-if="replyList.length !== 0">
                <li class="replylist-item" v-for="(item, index) in replyList" :key="index">
                <span class="whichFloor">{{(page-1)*pagesize+index+1}}楼</span>
                <div class="reply-content editorShow ud-editor-message"><p v-html="highlight(item.content)"></p></div>
                <div class="replylist-bottom">
                  <span class="date">
                    发表于&nbsp;&nbsp;{{item.publish_data.replace('T', ' ')}}&nbsp;&nbsp;
                    <i v-if="info.user_id === item.author.user_id" class="el-icon-delete btn-del" @click="delMsg(item.reply_id, 1)"></i>
                  </span>
                  <template v-if="ifLogin">
                    <button :class="(showEditorNoFirst !== index)?'btn btn-orange':'btn btn-grey'" @click="openRootEditor(index)" v-if="item.replies!==0">{{(showEditorNoFirst !== index)?'回复('+item.replies+')':'取消'}}</button>
                    <button :class="(showEditorNoFirst !== index)?'btn btn-orange':'btn btn-grey'" @click="openRootEditor(index)" v-if="item.replies===0">{{(showEditorNoFirst !== index)?'回复':'取消'}}</button>
                  </template>
                  <template v-else>
                    <button class="btn btn-grey" @click="toLogin(1)">登录回复</button>
                  </template>
                  <button class="btn btn-open-childReplies btn-green" @click="openChildReplies(item.reply_id)" v-if="item.replies!==0">{{(item.reply_id === replyId)?'关闭评论':'展开评论'}}</button>
                </div>
                <transition name="editor-fade">
                  <div class="editor replyEditor ud-editor-message" v-if="showEditorNoFirst === index">
                    <wangEditor :catchData="catchData" :height="'200px'" :editorType="2"></wangEditor>
                    <button class="btn-reply" @click="sendReply(postDetail.post_id, item.reply_id)">回复他</button>
                  </div>
                </transition>
                <transition name="ul-fade">
                  <div class="childReplyList" v-if="item.reply_id === replyId">
                  <ul>
                    <li v-for="(childItem, childIndex) in childReplyList" :key="childIndex">
                      <div class="childReplylist-item">
                        <img :src="childItem.author.avatarUrl ? host + childItem.author.avatarUrl : defaultImage">
                        <div class="text">
                          <span class="childReplylist-name">{{childItem.author.nick}}<span v-if="childItem.target_object">&nbsp;&nbsp;<span style="color: rgba(255, 153, 0, 1);">回复</span>&nbsp;&nbsp;{{childItem.target_object.nick}}</span></span>
                          <div class="childReplylist-content editorShow ud-editor-message"><p v-html="highlight(childItem.content)"></p></div>
                          <span class="childReplylist-date">
                            发表于&nbsp;&nbsp;{{childItem.publish_date.replace('T', ' ')}}&nbsp;&nbsp;
                            <i v-if="info.user_id === item.author.user_id" class="el-icon-delete btn-del" @click="delMsg(childItem.reply_id, 2)"></i>
                          </span>
                        </div>
                        <button :class="(showEditorNoSecond !== childIndex)?'btn btn-orange':'btn btn-grey'" @click="openChildEditor(childIndex)" v-if="ifLogin">{{(showEditorNoSecond !== childIndex)?'回复':'取消'}}</button>
                      </div>
                      <transition name="editor-fade">
                        <div class="editor replyEditor ud-editor-message" v-if="showEditorNoSecond === childIndex">
                          <wangEditor :catchData="catchData" :height="'200px'" :editorType="2"></wangEditor>
                          <button class="btn-reply" @click="sendReply(postDetail.post_id, childItem.reply_id)">回复他</button>
                        </div>
                      </transition>
                    </li>
                    <div class="childReplies-pagination"  v-if="childRepliesTotal >= childPagesize">
                      <el-pagination
                        @current-change="handleChildCurrentChange"
                        layout="prev, pager, next"
                        :current-page="childPage"
                        :page-size="childPagesize"
                        :pager-count="5"
                        :total="childRepliesTotal">
                      </el-pagination>
                    </div>
                  </ul>
                </div>
                </transition>
                  <div class="user_info">
                    <img :src="item.author.avatarUrl ? host + item.author.avatarUrl : defaultImage">
                    <span class="user-name">{{(item.type === 3)? 'OJ平台管理员' : item.author.nick}}</span>
                  </div>
              </li>
              </ul>
          </div>
          <div class="rootReplies-pagination" v-if="rootRepliesTotal >= pagesize">
            <el-pagination
              @current-change="handleCurrentChange"
              background
              layout="prev, pager, next"
              :current-page="page"
              :page-size="pagesize"
              :pager-count="9"
              :total="rootRepliesTotal">
            </el-pagination>
          </div>
        </div>
        <div class="right-module">
          <rightFrame :whilePage="1" :postDetail="postDetail" @toLike="ifLike"></rightFrame>
        </div>
      </div>
    </div>
</template>

<script>
import {host} from '@/api/config'
import rightFrame from '@/base/forums/rightFrame'
import wangEditor from '@/base/wangeditor/wangeditor'
import prism from 'prismjs'

import api from '@/api/forums'

export default {
  name: 'postDetail',
  components: {
    rightFrame,
    wangEditor
  },
  data () {
    return {
      info: {},
      searchKey: '',
      postDetail: {
        author: {
          nick: '',
          user_id: '',
          avatarUrl: ''
        },
        content: '',
        if_liked: false,
        is_top: false,
        labels: [],
        likes: 0,
        next_post: {
          title: '',
          post_id: -1
        },
        post_id: 28,
        pre_post: {title: null, post_id: null},
        publish_date: '',
        replies: 0,
        title: '',
        type: 0,
        views: 0
      },
      replyId: -1,
      childReplyList: [],
      ifLogin: false,
      loading: true,
      replyList: [],
      page: 1,
      pagesize: 10,
      rootRepliesTotal: 0,
      childPage: 1,
      childPagesize: 5,
      childRepliesTotal: 0,
      showEditor: false,
      showEditorNoFirst: -1,
      showEditorNoSecond: -1,
      editorContent: '',
      editorText: '',
      host: host,
      defaultImage: require('../../../static/image/acmlogo.jpg')
    }
  },
  methods: {
    catchData (value, text) {
      this.editorContent = value // 在这里接受子组件传过来的参数，赋值给data里的参数
      this.editorText = text
      // console.log(this.postText)
    },
    toPublish () {
      this.$router.push({path: '/postPublish'})
    },
    toLogin (pageNo) {
      // 1-登录后回到本页，2-登录后去到发布页
      if (pageNo === 1) {
        this.$router.push({
          name: 'login',
          query: {
            redirect: '/postDetail/' + this.postDetail.post_id
          }
        })
      } else if (pageNo === 2) {
        this.$router.push({
          name: 'login',
          query: {
            redirect: '/postPublish'
          }
        })
      }
    },
    toSearch () {
      this.$router.push({
        name: 'postSearch',
        params: {
          type: 0,
          page: 1
        },
        query: {
          search: this.searchKey
        }
      })
    },
    toReplyPost () {
      if (!this.ifLogin) {
        return false
      }
      this.showEditorNoFirst = -1
      this.showEditorNoSecond = -1
      this.showEditor = !this.showEditor
    },
    toTopPost (postId) {
      const _this = this
      if (_this.postDetail.is_top) {
        api.delPostTop('/forum/posts/top', postId).then(function (res) {
          if (res.success === 'true') {
            _this.$message({
              message: '帖子取消置顶成功！',
              type: 'success',
              duration: 1000
            })
            _this.postDetail.is_top = false
          } else {
            _this.$message({
              message: '帖子取消置顶失败！',
              type: 'error',
              duration: 1000
            })
          }
        })
      } else {
        api.putPostTop('/forum/posts/top', postId).then(function (res) {
          if (res.success === 'true') {
            _this.$message({
              message: '帖子置顶成功！',
              type: 'success',
              duration: 1000
            })
            _this.postDetail.is_top = true
          } else {
            _this.$message({
              message: '帖子置顶失败！最多只允许三个帖子置顶！',
              type: 'error',
              duration: 1000
            })
          }
        })
      }
    },
    openRootEditor (no) {
      if (!this.ifLogin) {
        return false
      }
      this.showEditor = false
      this.showEditorNoSecond = -1
      this.showEditorNoFirst = (this.showEditorNoFirst === no) ? -1 : no
    },
    openChildEditor (no) {
      if (!this.ifLogin) {
        return false
      }
      this.showEditor = false
      this.showEditorNoFirst = -1
      this.showEditorNoSecond = (this.showEditorNoSecond === no) ? -1 : no
    },
    delMsg (id, type) {
      const _this = this
      this.$confirm('此操作将永久删除该评论, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        api.deleteReply('/forum/posts/send_reply', id).then(function (res) {
          if (res.success === 'true') {
            _this.$message({
              type: 'success',
              message: '删除成功!'
            })
            if (type === 2) {
              _this.childPage = 1
              _this.getChildReplies()
            }
            _this.getRootReplies()
          }
        })
      }).catch(() => {

      })
    },
    ifLike (oper) {
      if (oper) {
        this.giveLike(this.postDetail.post_id, this.postDetail.if_liked)
      }
    },
    highlight (content) {
      // console.log(content)
      var language = 'clike'
      // content = content.replace(/<code>/g, '<code class="language-' + language + '">').replace(/<pre>/g, '<pre class="line-numbers">')
      var contentList = content.split('<pre><code>')
      var start = contentList[0]
      var needContent = ''
      for (var i = 1; i < contentList.length; i++) {
        needContent += '<span class="codeContain"><pre class="line-numbers"><code class="language-' + language + '">'
        var childList = contentList[i].split('</code></pre>')
        // 提取出的code内代码装在format内
        var format = childList[0].replace(/&lt;/g, '<').replace(/&gt;/g, '>').replace(/&amp;/g, '&').replace(/<br>/g, '\n')
        format = prism.highlight(format, prism.languages.clike, language)
        var formatList = format.split('\n')
        format = ''
        for (var j = 0; j < formatList.length; j++) {
          format += '<span class="line-numbers-rows">' + formatList[j] + ' </span>'
        }
        needContent += format
        needContent += '</code></pre><button class="copyContent-btn" style="cursor: pointer">复制</button></span>'
        needContent += childList[1]
      }
      return start + needContent
    },
    backToIndex () {
      this.$router.push({path: '/forums'})
    },
    toNewPost (id) {
      this.$router.push({
        name: 'postDetail',
        params: {
          id: id
        }
      })
      this.getPostDetail()
    },
    handleCurrentChange (val) {
      this.page = val
      this.replyId = -1
      this.childPage = 1
      this.childReplyList = []
      this.getRootReplies()
    },
    handleChildCurrentChange (val) {
      this.childPage = val
      this.getChildReplies()
    },
    // 点赞和取消赞
    giveLike (postId, ifGood) {
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json === 'undefined' || json === null || json === undefined) {
        return false
      }
      const _this = this
      this.postDetail.if_liked = !this.postDetail.if_liked
      if (ifGood) { // 取消
        api.delGiveLike('/forum/like', postId).then(function (res) {
          if (res.success === 'true') {
            _this.postDetail.likes--
          }
        })
      } else { // 点赞
        api.postGiveLike('/forum/like', postId).then(function (res) {
          if (res.success === 'true') {
            _this.postDetail.likes++
          }
        })
      }
    },
    sendReply (postId, targetId) {
      if (this.editorContent.length < 1000) {
        var clearInitContent = this.editorText
        // 第一步：去除空格&nbsp;
        clearInitContent = clearInitContent.replace(/&nbsp;/g, '')
        // console.log(clearInitContent)
        // 第二步：去除换行
        clearInitContent = clearInitContent.replace(/<br>/g, '')
        // console.log(clearInitContent)
        if (clearInitContent === '' || typeof (this.editorContent) === 'undefined') {
          this.$message({
            message: '回复内容不能为空！',
            type: 'error',
            showClose: true,
            duration: 1800
          })
          return false
        } else if (!(/.*[\u4e00-\u9fa5]+.*$/.test(clearInitContent)) && !(/[0-9a-z]/.test(clearInitContent))) {
          this.$message({
            message: '未输入有意义内容！',
            type: 'error',
            showClose: true,
            duration: 1800
          })
          return false
        }
      } else if (this.editorContent.length > 32768) {
        this.$message.warning({
          message: '内容太长！若是直接复制桌面文档，请改为右键纯文本粘贴！',
          type: 'error',
          duration: 1800
        })
        return false
      }

      const _this = this
      api.sendReply('/forum/posts/send_reply', {
        params: {
          post_id: postId,
          target_id: (targetId === -1) ? null : targetId,
          content: _this.editorContent
        }
      }).then(function (res) {
        if (res.success === 'true') {
          _this.getRootReplies()
          _this.getChildReplies()
          _this.showEditor = false
          _this.showEditorNoFirst = -1
          _this.showEditorNoSecond = -1
          _this.$message({
            message: '成功发送回复信息！',
            type: 'success',
            showClose: true,
            duration: 1000
          })
        }
      })
    },
    getPostDetail () {
      this.loading = true
      const _this = this
      this.postDetail.post_id = this.$route.params.id
      // console.log(this.postDetail.post_id)
      if (this.post_id !== '' && typeof (this.postDetail.post_id) !== 'undefined' && this.postDetail.post_id !== null) {
        api.getPostDetail('/forum/posts/detail', {
          post_id: _this.postDetail.post_id
        }).then(function (res) {
          if (typeof (res) !== 'object') {
            _this.$message({
              type: 'error',
              message: '没有该文章！',
              duration: '2000'
            })
            _this.$router.push({path: '/forums'})
          } else {
            _this.postDetail = res.data
            _this.loading = false
            _this.getRootReplies()
            if (_this.ifLogin) {
              _this.setHadRead()
            }
          }
        })
      }
    },
    getRootReplies () {
      const _this = this
      api.getRootReplies('/forum/posts/root_replies', {
        post_id: _this.postDetail.post_id,
        page: _this.page,
        pagesize: _this.pagesize
      }).then(function (res) {
        _this.replyList = res.data.reply_list
        _this.rootRepliesTotal = res.total
      })
    },
    openChildReplies (replyId) {
      this.replyId = this.replyId === replyId ? -1 : replyId
      if (this.replyId !== -1) {
        this.childPage = 1
        this.getChildReplies()
      }
    },
    getChildReplies () {
      const _this = this
      if (_this.replyId === -1) {
        return false
      }
      api.getChildReplies('/forum/posts/child_replies', {
        reply_id: _this.replyId,
        page: _this.childPage,
        pagesize: _this.childPagesize
      }).then(function (res) {
        _this.childReplyList = res.data.child_replies_list
        _this.childRepliesTotal = res.total
      })
    },
    setHadRead () {
      api.postReadPost('/forum/posts/view', this.postDetail.post_id).then(function (res) {})
    },
    getListInfo () {
      const _this = this
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json !== 'undefined' && json !== null && json !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
        api.getInfoList('/users/detail', uid).then(function (res) {
          _this.info = res.data.user_info
          _this.ifLogin = true
          _this.setHadRead()
        })
      }
    }
  },
  created () {
    this.getListInfo()
    this.getPostDetail()
  }
}
</script>

