    position: relative;
  }
  .container .left-module-top{
    min-height: 450px;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
    padding: 5px 5px 80px 5px;
    margin-bottom: 15px;
    position: relative;
  }
  .container .left-module-bottom {
    min-height: 450px;
    /*background-color: rgba(255, 255, 255, 1);*/
    border-radius: 10px;
    /*box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);*/
    position: relative;
  }
  .container .left-module-middle {
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
    /*padding-bottom: 10px;*/
    position: relative;
    height: auto;
  }
  .container .left-module-middle .tip {
    display: block;
    text-align: center;
    font-weight: 400;
    font-size: 12px;
    line-height: 2em;
    color: #C9C9C9;
  }
  .container .left-module-bottom {
    min-height: auto;
    padding-bottom: 10px;
  }
  /*头像*/
  .left-module-top .user_info{
    width: 120px;
    font-size: 12px;
    line-height: 3.5em;
    text-align: center;
    background-color: rgba(228, 228, 228, 0.8);
    border-radius: 0 0 20px 20px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
    padding-left: 10px;
    padding-right: 5px;
    overflow: hidden;
    position: absolute;
    top: 0px;
    right: 30px;
  }
  .left-module-top .user_info img{
    width: 33px;
    height: 33px;
    border-radius: 50%;
    vertical-align: middle;
    position: relative;
    top: -2px;
  }
  .left-module-top .user_info .user-name{
    min-width: 60px;
    max-width: 66px;
    display: inline-block;
    text-align: center;
    font-size: 12px;
    color: #5E5E5E;
    white-space: nowrap;
  }
  /*标题*/
  .module-top .title {
    font-size: 25px;
    font-weight: 600;
    line-height: 3em;
    padding-left: 30px;
    padding-right: 300px;
    overflow: hidden;
    white-space: nowrap;
    cursor: auto;
  }
  .module-top .tags{
    height: 1.6em;
    font-weight: 300;
    font-size: 14px;
    text-align: center;
    line-height: 1.6em;
    color: #FFFFFF;
    background-color: rgba(204, 204, 204, 1);;
    display: inline-block;
    border-radius: 0.2em;
    padding: 0 0.4em;
    margin: 0 0.5em;
    transition: all .3s;
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0);
    cursor: default;
  }
  .module-top .tags:hover{
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.1);
  }
  .module-top .tags-essence {
    background-color: rgba(255, 5, 14, 0.8)
  }
  .module-top .tags-notice {
    background-color: rgba(100, 149, 237, 0.8);
  }
  .module-top .tags-guide {
    background-color: rgba(255, 153, 0, 0.8);
  }
  .tagKey {
    padding: 0 20px 5px 20px;
    border-bottom: 1px solid rgba(242, 242, 242, 1);
  }
  .module-top .tags-key {
    height: 1.8em;
    font-weight: 300;
    font-size: 12px;
    text-align: center;
    line-height: 1.8em;
    color: #333;
    background-color: rgba(228, 228, 228, 0.8);
    display: inline-block;
    border-radius: 0.9em;
    padding: 0 1em;
    margin: 0 0.5em;
    transition: all .3s;
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0);
    position: relative;
    top: -5px;
    cursor: default;
  }
  .module-top .tags-key:hover{
    color: #fff;
    background-color: rgba(142, 204, 69, 0.8);
  }
  .module-top .authorAndDate {
    font-weight: 300;
    font-size: 12px;
    color: #AEAEAE;
    float: right;
    line-height: 1.8em;
  }
  /*.left-module-top .block {*/
    /*width: 100%;*/
    /*height: 100px;*/
    /*!*background-color: #333;*!*/
    /*border-radius: 0 0 10px 10px;*/
    /*border-top: 1px solid rgba(242, 242, 242, 1);*/
    /*position: absolute;*/
    /*left: 0;*/
    /*bottom: 0;*/
  /*}*/
  .left-module .mainContent {
    font-size: 14px;
    line-height: 2.5em;
    min-height: 400px;
    padding: 15px 60px;
    margin-bottom: 10px;
  }
  /*上一篇*/
  .btn-prePost,
  .btn-nextPost {
    position: absolute;
    bottom: 12px;
  }
  .btn-prePost {
    text-align: left;
    left: 0;
  }
  .btn-nextPost {
    text-align: right;
    right: 0;
  }
  .btn-prePost .btn,
  .btn-nextPost .btn{
    display: inline-block;
    width: 100px;
    line-height: 3.4em;
    background-color: #333;
    font-weight: 600;
    font-size: 14px;
    color: #fff;
    text-align: center;
    cursor: pointer;
    transition: all .3s;
  }
  .btn-prePost .btn:hover,
  .btn-nextPost .btn:hover{
    background-color: rgba(255, 153, 0, 1);
  }
  .btn-prePost h5,
  .btn-nextPost h5 {
    font-weight: 600;
    font-size: 12px;
    color: #cbcbcb;
    margin-top: 15px;
    padding: 0 15px;
  }
  .btn-prePost .btn {
    border-radius: 0 1.7em 1.7em 0;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
  }
  .btn-nextPost .btn {
    border-radius: 1.7em 0 0 1.7em;
    box-shadow: -2px 2px 10px rgba(0, 0, 0, 0.2);
  }
  .btn-opers {
    width: 40%;
    text-align: center;
    position: absolute;
    left: 30%;
    bottom: 41px;
  }
  .btn-opers button {
    line-height: 48px;
    width: 48px;
    border-radius: 24px;
    color: #fff;
    font-size: 30px;
    text-align: center;
    margin: 0 5px;
    cursor: pointer;
    transition: all .5s;
  }
  .btn-opers button i::before{
    font-size: 26px !important;
  }
  .btn-orange {
    background-color: rgba(255, 153, 0, 1);
  }
  .btn-orange:hover {
    background-color: rgba(234, 140, 0, 1);
  }
  .btn-green {
    background-color: rgba(142, 204, 69, 1);
  }
  .btn-green:hover {
    background-color: rgba(133, 193, 71, 1);
  }
  .btn-grey {
    background-color: rgba(102, 102, 102, 1);
  }
  .btn-grey:hover {
    background-color: rgba(87, 87, 87, 1);
  }
  .btn-to-orange:hover {
    box-shadow: -2px 2px 10px rgba(234, 140, 0, 0.4);
  }
  .btn-to-green:hover {
    box-shadow: -2px 2px 10px rgba(133, 193, 71, 0.4);
  }
  .btn-to-grey:hover {
    box-shadow: -2px 2px 10px rgba(87, 87, 87, 0.4);
  }
  .stats {
    display: inline-block;
    font-weight: 600;
    font-size: 12px;
    padding-left: 20px;
    margin: 10px 0 10px 0;
  }
  /*评论区样式*/
  .noMore {
    line-height: 3em;
    display: block;
    font-weight: 300;
    font-size: 12px;
    color: #AEAEAE;
    text-align: center;
  }
  .replylist .replylist-item {
    position: relative;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 10px;
    overflow: hidden;
    transition: all .3s;
  }
  .replylist .replylist-item:hover {
    /*background-color: rgba(240, 240, 240, 0.5);*/
  }
  .replylist .user_info {
    width: 120px;
    height: 100%;
    font-size: 14px;
    text-align: center;
    background-color: rgba(228, 228, 228, 0.8);
    border-right: 10px solid rgba(204, 204, 204, 0.3);
    padding-left: 10px;
    padding-right: 5px;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    transition: all .5s
  }
  .replylist .user_info img{
    width: 70px;
    height: 70px;
    border: 2px solid #fff;
    border-radius: 8px;
    position: relative;
    margin-top: 20px;
  }
  .replylist .user_info .user-name{
    min-width: 60px;
    max-width: 98px;
    display: inline-block;
    text-align: center;
    font-size: 12px;
    line-height: 1.5em;
    color: #5e5e5e;
    /* white-space: nowrap; */
    word-wrap:break-word;
    margin-top: 1em;
    overflow: hidden;
  }
  .replylist .whichFloor {
    display: block;
    text-align: right;
    font-weight: 600;
    font-size: 12px;
    line-height: 32px;
    color: rgba(204, 204, 204, 0.3);
    padding-right: 20px;
    margin-top: 5px;
    transition: all .2s
  }
  .replylist .replylist-item:hover .user_info{
    border-right: 10px solid rgba(204, 204, 204, 1);
  }
  .replylist .replylist-item:hover .whichFloor {
    color: rgba(204, 204, 204, 1);
  }
  /*内容展示*/
  .left-module .reply-content {
    min-height: 100px;
    margin: 0 30px 5px 150px;
  }
  .replylist .replylist-item>.replylist-bottom {
    font-size: 16px;
    line-height: 1.6em;
    margin: 0 10px;
    padding: 5px 10px 5px 100px;
  }
  .btn-del {
    cursor: pointer;
  }
  .btn-del:hover {
    color: rgb(245, 108, 108);
  }
  .replylist .replylist-item>.replylist-bottom .date {
    font-size: 12px;
    color: #BCBCBC;
    padding: 0 40px;
  }
  .replylist .replylist-item>.replylist-bottom .btn {
    font-size: 16px;
    color: #fff;
    float: right;
    line-height: 1.6em;
    border-radius: 0.8em;
    padding: 0 15px;
    margin-left: 20px;
    cursor: pointer;
    position: relative;
    top: -5px;
  }
  .replylist .replylist-item>.replylist-bottom .btn-open-childReplies {
    display: none;
  }
  .replylist .replylist-item:hover>.replylist-bottom .btn-open-childReplies {
    display: inline-block;
  }

  .childReplyList .childReplylist-item {
    padding: 10px 30px 0 150px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    border-top: 1px solid rgba(204, 204, 204, 0.5);
    overflow: hidden;
    position: relative;
    transition: all .3s;
  }
  .childReplyList .childReplylist-item:hover {
    background-color: rgba(240, 240, 240, 0.7);
  }
  .childReplyList .childReplylist-item>img {
    flex: 0 1 auto;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    position: relative;
    bottom: -3px;
  }
  .childReplyList .childReplylist-item>.text {
    flex: 1 1 auto;
    max-width: 95%;
  }
  .childReplyList .childReplylist-item>button {
    position: absolute;
    font-size: 16px;
    color: #fff;
    line-height: 1.6em;
    border-radius: 0.8em;
    padding: 0 15px;
    cursor: pointer;
    bottom: 10px;
    right: 20px;
    display: none;
  }
  .childReplyList .childReplylist-item:hover>button {
    display: inline-block;
  }
  .childReplyList .childReplylist-item>.text .childReplylist-name {
    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    color: #5E5E5E;
    padding-left: 10px;
  }
  .childReplyList .childReplylist-item>.text .childReplylist-content {
    padding-left: 10px;
    padding-top: 10px;
    min-height: 100px;
    position: relative;
    max-width: 100%;
  }
  .childReplyList .childReplylist-item>.text .childReplylist-date {
    display: inline-block;
    font-size: 12px;
    color: #BCBCBC;
    line-height: 2em;
    padding-left: 10px;
    padding-bottom: 5px;
  }
  .replylist .replylist-item:hover .childReplyList .btn-showReplies {
    height: 20px;
  }
  /*分页*/
  .rootReplies-pagination {
    margin-top: 20px;
  }
  .childReplies-pagination {
    margin-left: 150px;
  }
  .childReplies-pagination /deep/ .el-pagination .number,
  .childReplies-pagination /deep/ .el-pagination .btn-prev,
  .childReplies-pagination /deep/ .el-pagination .btn-next {
    background-color: transparent;
  }
  .rootReplies-pagination /deep/ .el-pagination .number:hover,
  .childReplies-pagination /deep/ .el-pagination .number:hover,
  .childReplies-pagination /deep/ .el-pagination .btn-prev:hover,
  .childReplies-pagination /deep/ .el-pagination .btn-next:hover {
    color: #f48118 !important;
  }
  .rootReplies-pagination /deep/ .el-pagination .el-pager .active {
    color: #fff !important;
    background-color: #f48118 !important;
  }
  .childReplies-pagination /deep/ .el-pagination .el-pager .active {
    color: #f48118 !important;
  }
  /*编辑器*/
  .editor {
    max-height: 300px;
    padding: 0 20px;
    background-color: rgba(235, 235, 235, 0.6);
    box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.05);
    text-align: right;
    position: relative;
    border-top: 1px solid transparent;
  }
  .replyEditor {
    margin-left: 120px;
  }
  .editor .btn-reply {
    font-size: 12px;
    color: #fff;
    line-height: 2em;
    border-radius: 1em;
    padding: 0 10px;
    background-color: #FF9900;
    transition: all .3s;
    margin: 6px 0;
    cursor: pointer;
  }
  .editor .btn-reply:hover {
    background-color: #f48118;
  }
  .editor-fade-enter-active, .editor-fade-leave-active {
    max-height: 400px;
    opacity: 1;
    transition: all .5s;
  }
  .editor-fade-enter, .editor-fade-leave-to {
    max-height: 0px;
    opacity: 0;
  }
  .ul-fade-enter-active, .ul-fade-leave-active {
    max-height: 1200px;
    max-height: 120vh;
    opacity: 1;
    transition: all .7s;
  }
  .ul-fade-enter, .ul-fade-leave-to {
    max-height: 0vh;
    opacity: 0;
  }
  >>> .w-e-panel-container {
    border-radius: 3px;
  }
</style>



