<template>
  <div class="carousel">
    <transition-group tag="ul" name="image">
      <li v-for="(item, index) in carousels" :key="item.id" v-show="index===isActiveOfIndex" @mouseenter="stop" @mouseleave="move">
        <img class="carousel-img" :src="item.image ? host + '/' + item.image : ''" @click="toLink(item.url)">
        <div class="desc-area">
          <p class="title" @click="toLink(item.url)">{{item.title}}</p>
          <p class="desc">{{item.sub_title}}</p>
        </div>
        <img :src="item.attachment" alt="" class="attachment" v-if="item.attachment">
      </li>
    </transition-group>
    <div class="carousel-index" :style="{marginLeft: getMarginLeft}">
      <i v-for="(item, index) in carousels" :class="isActiveOfIndex===index? 'active': ''" :key="index" @mouseover="change(index)"></i>
    </div>
    <img class="acm-logo" :src="acmLogo" alt="">
  </div>
</template>
<script>
import {host} from '@/api/config'

export default {
  props: {
    carousels: {
      type: Array
    }
  },
  data () {
    return {
      second: '4500',
      timer: null,
      host: host,
      isActiveOfIndex: 0,
      acmLogo: '../../static/image/acmlogo.jpg'
    }
  },
  computed: {
    getMarginLeft () {
      return this.carousels.length && '-' + (this.carousels.length * 43 + (this.carousels.length - 1) * 9) / 2 + 'px'
    }
  },
  methods: {
    autoPlay () {
      this.isActiveOfIndex++
      if (this.isActiveOfIndex > this.carousels.length - 1) {
        this.isActiveOfIndex = 0
      }
    },
    play () {
      this.timer = setInterval(this.autoPlay, this.second)
    },
    change (i) {
      this.isActiveOfIndex = i
    },
    stop () {
      clearInterval(this.timer)
      this.timer = null
    },
    move () {
      this.timer = setInterval(this.autoPlay, this.second)
    },
    toLink (link) {
      if (link !== '' && typeof (link) !== 'undefined') {
        if (link === '#') {
          window.location.href = host
        } else {
          window.location.href = host + '/' + link
        }
      }
    }
  },
  created () {
    this.$nextTick(() => {
      this.timer = setInterval(() => {
        this.autoPlay()
      }, this.second)
    })
  }
}
</script>

<style scoped>
  .carousel{
    width: 100%;
    min-width: 1170px;
    height: 380px;
    overflow: hidden;
    position: relative;
  }
  .carousel ul{
    width: 100%;
    height: 100%;
  }
  .carousel ul li{
    width: 100%;
    height: 100%;
    position: absolute;
  }
  .carousel ul li .carousel-img{
    display: block;
    width: 100%;
    height: 380px;
  }
  .carousel ul li .desc-area {
    position: absolute;
    width: 100%;
    height: 69px;
    top: 50%;
    margin-top: -35px;
    text-align: center;
    color: #ffffff;
    cursor: default;
  }
  .carousel ul li .desc-area .title{
    font-size: 41px;
    height: 69px;
    line-height: 69px;
    cursor: pointer;
  }
  .carousel ul li .desc-area .desc{
    font-size: 24px;
    height: 25px;
    line-height: 25px;
  }
  .carousel ul li .attachment{
    height: 150px;
    position: absolute;
    top: 20px;
    left: 20px;
  }
  .carousel-index{
    /*width: 199px;*/
    height: 2px;
    position: absolute;
    left: 50%;
    /*margin-left: -100px;*/
    top: 100%;
    margin-top: -18px;
  }
  .carousel-index>i{
    float: left;
    width: 43px;
    height: 2px;
    margin-right: 9px;
    background-color: #ffffff;
  }
  .carousel-index>i:last-child{
    margin-right: 0px;
  }
  .carousel-index .active{
    background-color: #fc9e4a;
  }

  .carousel .acm-logo {
    position: absolute;
    right: 18px;
    bottom: 18px;
    border-radius: 4px;
    width: 100px;
    height: 100px;
  }

  .image-enter-to {
    transition: all 1.5s ease;
    transform: translateX(0);
  }

  .image-leave-active {
    transition: all 1.5s ease;
    transform: translateX(-100%)
  }

  .image-enter {
    transform: translateX(100%)
  }

  .image-leave {
    transform: translateX(0)
  }
</style>



