<template>
  <div id="app">
    <my-header :class="{'hide': showHeader !== undefined && !showHeader}"></my-header>
    <transition mode='out-in'>
      <router-view/>
    </transition>
    <!--<my-footer :class="{'hide':!showFooter}"></my-footer>-->
  </div>
</template>

<script>
import myHeader from './base/header/index.vue'
import myFooter from './base/footer/index.vue'
import {mapGetters, mapMutations} from 'vuex'
export default {
  name: 'App',
  components: {
    myHeader, myFooter
  },
  data () {
    return {
      inlineUser: {}
    }
  },
  computed: {
    ...mapGetters({
      showHeader: 'getHeaderToggle',
      showFooter: 'getFooterToggle'
    })
  },
  methods: {
    ...mapMutations([
      'SET_USER'
    ]),
    getUserInfo () {
      var storage = window.sessionStorage
      this.inlineUser = storage.getItem('userInfo') || {}
      this.SET_USER(this.inlineUser)
    }
  },
  created () {
    this.getUserInfo()
    // 用于全局的代码复制
    const _this = this
    window.document.body.onclick = function (event) {
      if (event.target.className === 'copyContent-btn') {
        var codeNode = event.target.previousSibling
        var newInput = document.createElement('textarea')
        newInput.value = codeNode.innerText
        document.body.appendChild(newInput)
        newInput.select()
        document.execCommand('Copy')
        document.body.removeChild(newInput)
        _this.$message({
          message: '复制成功！',
          type: 'success',
          duration: 1000
        })
      }
    }
  }
}
</script>

<style>
  @import '../static/iconfont/iconfont.css';
  *,
  ::before,
  ::after{
    margin: 0;
    padding: 0;

    -webkit-tap-highlight-color: transparent;/*清除点击默认的高亮效果*/

    -webkit-box-sizing: border-box;/*以你的border开始计算你的宽度*/

  }
  html {
    overflow-y: scroll;
  }
  body{
    font-size: 14px;
    font-family: "Microsoft YaHei",sans-serif;/*第二个是手机的一个默认字体*/
    background-color: #f0f0f0;
    /*padding-left: calc(100vw - 100%);*/
  }
  a{
    text-decoration: none;
    color: #000;
  }

  ul{
    list-style: none;
  }

  input,textarea, button{
    border: none;
    resize: none;
    outline: none;/*清除选中效果*/

    -webkit-appearance: none;/*清除webkit内核浏览器 默认样式*/
  }
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }
  input[type="number"]{
    -moz-appearance: textfield;
  }

  /*清除浮动*/
  .clearfix::before,
  .clearfix::after{
    content: ".";
    display: block;
    height: 0;
    line-height: 0;
    visibility: hidden;
    clear: both;
  }

  #app {
    padding-top: 59px;
  }
  /* 公用1：文本超出省略号显示 */
  .overflow-text {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .hide {
    display: none;
  }
  /* 公用2： */
  /*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
  .el-table__body-wrapper::-webkit-scrollbar
  {
    width: 3px;
    height: 10px;
    background-color: #FFF;
  }

  /*定义滚动条轨道 内阴影+圆角*/
  .el-table__body-wrapper::-webkit-scrollbar-track
  {
    /*-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);*/
    border-radius: 10px;
    background-color: #FFF;
  }

  /*定义滑块 内阴影+圆角*/
  .el-table__body-wrapper::-webkit-scrollbar-thumb
  {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: rgba(0, 0, 0, .48);
  }

  /* 公用3：分页按钮样式 */
  .public-pagination /deep/ .el-pagination .el-pager .active {
    background-color: #f48118 !important;
  }
  .public-pagination /deep/ .el-pagination .el-pager>li:hover{
    color: #f48118 !important;
  }
  .public-pagination /deep/ .el-pagination .el-pager>.active:hover{
    color: #fff !important;
  }
  /* 公用4：动画 */
  /* 动画：下20到上 */
  .fade-enter-to,.fade-leave-to {
    transition: all 0.5s;
  }
  .fade-enter,.fade-leave-to{
    opacity: 0;
    transform: translate3d(0,20px,0);
  }
  /* 动画：上20到下 */
  .bTtFade-enter-to,.bTtFade-leave-to {
    transition: all 0.5s;
  }
  .bTtFade-enter,.bTtFade-leave-to{
    opacity: 0;
    transform: translate3d(0, -20px, 0);
  }
  /*** element加载动画 ***/
  .el-loading-spinner .el-icon-loading:before {
    font-size: 16px;
    color: #f18118;
  }
  .el-loading-spinner .el-loading-text {
    font-size: 14px;
    color: #f18118;
  }
  .el-loading-mask {
    z-index: 0;
  }
  /*element全局样式设置*/
  .el-pagination {
    text-align: center;
  }

  /*以下为部分页面为了适应修改elementUI而做的样式设置*/
  /*测试添加页 ->题目弹出层一览样式设置*/
  .questionDetail {
    width: 450px;
    border-radius: 0 8px 8px 0;
    border-left: 4px solid #f48118;
  }
  .el-notification__group {
    margin-left: 13px;
    width: 100%;
  }
  /*公用5：加载圆圈统一绿色*/
  .el-loading-spinner .path {
    stroke: rgba(142, 204, 69, 1);
  }
  .el-table--border::after, .el-table--group::after, .el-table::before {
    z-index: 0;
  }
  /*公用6：定义滚动条轨道*/
  /*定义滚动条轨道*/
  .public-scrollStyle::-webkit-scrollbar-track
  {
    background-color: #f9f9f9;
    -webkit-box-shadow: inset 0 0 6px transparent;
  }
  /*定义滚动条高宽及背景*/
  .public-scrollStyle::-webkit-scrollbar
  {
    width: 10px;
    background-color: #333;
  }
  /*定义滚动条*/
  .public-scrollStyle::-webkit-scrollbar-thumb
  {
    background-color: #8b8b8b;
    border-radius: 10px;
  }
  /*公用7：暂无提示*/
  .noList {
    line-height: 3em;
    display: block;
    font-weight: 300;
    font-size: 12px;
    color: #AEAEAE;
    text-align: center;
    cursor: default;
  }
</style>



