<!--suppress ALL -->
<template>
  <div class="rightFrame">
    <div class="infoBox">
      <template v-if="whilePage === 0 && hasLogined">
        <div class="userInfo-box">
          <img :src="infoData.avatarUrl ? host + infoData.avatarUrl : defaultImage">
          <div class="content">
            <h5>{{infoData.nick}}</h5>
            <span>{{getIdentity}}</span>
          </div>
        </div>
      </template>
      <div class="itemsBox">
        <template v-if="whilePage === 0 && hasLogined">
          <div class="item myPosts cursor-pointer" @click="toMyPosts">
            <h5>{{infoData.myPost_count}}</h5><span>我的帖子</span>
          </div>
          <div class="item myReplys cursor-default">
            <h5>{{infoData.myReply_count}}</h5><span>我的回复</span>
          </div>
          <div class="item myMsgs cursor-pointer" @click="toMyMessage">
            <h5><span :class="(infoData.ifNewMsg)?'newMsg':''">{{infoData.myMsg_count}}</span></h5><span>我的信息</span>
          </div>
        </template>
        <template v-else-if="whilePage === 0 && !hasLogined">
          <div class="item cursor-default">
            <h5 >{{postsStats.post_count}}</h5><span>总帖子数</span>
          </div>
          <div class="item cursor-default">
            <h5>{{postsStats.best_post_count}}</h5><span>总精华数</span>
          </div>
        </template>
        <template v-else-if="whilePage === 1 && postDetail != null">
          <div :class="(postDetail.if_liked && hasLogined)? 'item item-hasLike cursor-default': 'item cursor-default'">
            <h5 class="cursor-default">{{postDetail.likes}}</h5><span>{{((postDetail.if_liked && hasLogined)?'已点赞':'点赞')}}</span>
          </div>
          <div class="item cursor-default">
            <h5>{{postDetail.replies}}</h5><span>回复</span>
          </div>
          <div class="item cursor-default">
            <h5>{{postDetail.views}}</h5><span>浏览</span>
          </div>
        </template>
      </div>
      <div class="btnsBox">
        <template v-if="whilePage === 0 && hasLogined">
          <button class="btn btn-orange" @click="toPublish">发布帖子</button>
        </template>
        <template v-if="!hasLogined">
          <button class="btn btn-grey" @click="toLogin">前往登录</button>
          <button class="btn btn-orange" @click="toRegister">马上注册</button>
        </template>
        <template v-if="whilePage === 1 && hasLogined">
          <button :class="postDetail.if_liked? 'btn btn-green': 'btn btn-grey'" @click="toLike">{{postDetail.if_liked?'取消点赞':'为他点赞'}}</button>
          <!--<button class="btn btn-orange" @click="toReplay">回复帖子</button>-->
        </template>
      </div>
    </div>
    <!--登录提醒-->
    <!--<div class="infoBox loginTip-box"></div>-->
    <!--我的信息-->
    <div class="msgBox myMsg-box" v-if="whilePage === 0 && hasLogined">
      <h5 class="title">我的信息</h5>
      <ul class="msgBox-content">
        <li v-for="(item, index) in infoData.replyMsg_list"  v-if="index < rightEssayMax" :key="index">
          <div class="body editorShow ud-editor-rightFrame" v-html="item.content"></div>
          <div class="footer">
            <span><img :src="item.avatarUrl ? host + item.avatarUrl : defaultImage"/><span class="username">{{item.nick}}</span></span>
            <span class="date">{{getDate(item.date)}}</span>
          </div>
        </li>
      </ul>
      <template v-if="infoData.replyMsg_list.length > rightEssayMax">
        <span class="more" @click="toMyMessage" >查看更多</span>
      </template>
      <template v-else>
        <span class="more nomore">无更多消息</span>
      </template>
    </div>
    <!--系统通告-->
    <div class="msgBox noticeMsg-box" v-if="whilePage === 0  && !hasLogined">
      <h5 class="title">系统公告</h5>
      <ul class="msgBox-content">
        <li v-for="(item, index) in postsList" v-if="index < rightEssayMax" :key="index">
          <div class="body editorShow ud-editor-rightFrame" v-html="item.content"></div>
          <div class="footer">
            <span class="username">OJ平台管理员</span>
            <span class="date">{{getDate(item.publish_date)}}</span>
          </div>
        </li>
      </ul>
      <template v-if="postsList.length > rightEssayMax">
        <span class="more" @click="toSearchPage(3)">查看更多</span>
      </template>
      <template v-else>
        <span class="more nomore">无更多消息</span>
      </template>
    </div>
    <!--更多热帖-->
    <div class="msgBox hotPosts-box" v-if="whilePage === 1">
      <h5 class="title">更多热帖</h5>
      <ul class="msgBox-content">
        <li v-for="(item, index) in postsList" v-if="index < rightEssayMax" :key="index">
          <div class="body">
            <div class="title" style="cursor: pointer" @click="toPostDetail(item.post_id)">
              <span v-if="item.type === 2">精华&nbsp;|&nbsp;</span>
              <span v-else-if="item.type === 3">公告&nbsp;|&nbsp;</span>
              <span v-else-if="item.type === 4">指南&nbsp;|&nbsp;</span>
              <span v-else>普通&nbsp;|&nbsp;</span>
              <span class="main">{{item.title}}</span>
            </div>
          </div>
          <div class="footer">
            <span><img :src="item.author.avatarUrl ? host + item.author.avatarUrl : defaultImage"/><span class="username">{{item.author.nick}}</span></span>
            <span class="date">{{item.publish_date.split('T')[0]}}</span>
          </div>
        </li>
      </ul>
      <template v-if="postsList.length > rightEssayMax">
        <span class="more" @click="toSearchPage(0)">查看更多</span>
      </template>
      <template v-else>
        <span class="more nomore">无更多消息</span>
      </template>
    </div>
  </div>
</template>

<script>
import {host} from '@/api/config'
import api from '@/api/forums'

export default {
  props: {
    // 哪个页面的右侧，0-讨论区首页，1-详情页
    whilePage: {
      type: Number,
      default: 0
    },
    postDetail: {
      type: Object
    }
  },
  computed: {
    getIdentity () {
      return (this.infoData.identity === '1') ? '学生' : (this.infoData.identity === '2') ? '教师' : (this.infoData.identity === '3') ? '管理员' : '注册用户'
    }
  },
  data () {
    return {
      info: {},
      host: host,
      postsStats: {},
      rightEssayMax: 8,
      hasLogined: false,
      infoData: {
        replyMsg_list: []
      },
      postsList: [],
      defaultImage: require('../../../static/image/acmlogo.jpg')
    }
  },
  methods: {
    toPublish () {
      this.$router.push({path: '/postPublish'})
    },
    toLogin () {
      if (this.whilePage === 0) {
        this.$router.push({
          name: 'login',
          query: {
            redirect: '/forums'
          }
        })
      } else if (this.whilePage === 1) {
        this.$router.push({
          name: 'login',
          query: {
            redirect: '/postDetail/' + this.postDetail.post_id
          }
        })
      }
    },
    toLike () {
      if (this.whilePage === 1 && this.hasLogined) {
        this.$emit('toLike', true)
      }
    },
    toMyPosts () {
      this.$router.push({name: 'PersonalCenter', params: {isShow: '8'}})
    },
    toMyMessage () {
      this.$router.push({name: 'PersonalCenter', params: {isShow: '9'}})
    },
    toPostDetail (postId) {
      this.$router.push({name: 'postDetail', params: {id: postId}})
    },
    toSearchPage (type) {
      this.$router.push({
        name: 'postSearch',
        params: {
          type: type
        }
      })
    },
    getDate (date) {
      var postDate = new Date(date.replace('T', ' '))
      var now = new Date()
      var yearD = now.getFullYear() - postDate.getFullYear()
      var monthD = now.getMonth() - postDate.getMonth()
      var dayD = now.getDate() - postDate.getDate()
      var returnDate = (yearD > 0) ? yearD + '年前' : (yearD === 0 && monthD > 0) ? monthD + '月前' : (yearD === 0 && monthD === 0) ? dayD + '天前' : date.replace('T', ' ')
      var d = now.getTime() - postDate.getTime()
      if (d < 60 * 1000) {
        return Math.floor(d / 1000) + '秒前'
      } else if (d < 60 * 60 * 1000) {
        return Math.floor(d / (60 * 1000)) + '分钟前'
      } else if (d < 60 * 60 * 1000 * 24) {
        return Math.floor(d / (60 * 60 * 1000)) + '小时前'
      } else {
        return returnDate
      }
    },
    toRegister () {
      this.$router.push({path: '/login', query: {show: -1}})
    },
    getListInfo () {
      const _this = this
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json !== 'undefined' && json !== null && json !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
        api.getInfoList('/users/detail', uid).then(function (res) {
          _this.info = res.data.user_info
          _this.hasLogined = true
          _this.getInfoData()
        })
      }
      this.getRightContent()
    },
    getInfoData () {
      const _this = this
      api.getInfoData('/forum/my_info', {}).then(function (res) {
        _this.infoData = res.data
      })
    },
    getPostStats () {
      const _this = this
      api.getPostStats('/forum/stupid_count', {}).then(function (res) {
        _this.postsStats = res.data
      })
    },
    getRightContent () {
      if (this.whilePage === 0 && this.hasLogined) {
        return
      }
      const _this = this
      var obj = {
        type: _this.whilePage === 0 ? 3 : 1,
        page: 1,
        pageSize: this.rightEssayMax + 1
      }
      api.getPostsList('/forum/posts', obj).then(function (res) {
        _this.postsList = res.data.post_list
      })
    }
  },
  created () {
    this.getListInfo()
    this.getPostStats()
  }
}
</script>

<style scoped>
  @import "../../../static/editorCss/style.css";
  .rightFrame {
    width: 250px;
  }
  .infoBox {
    background-color: rgba(228, 228, 228, 0.8);
    border-radius: 10px;
    position: relative;
    padding: 10px 5px;
  }
  /*选项集合*/
  .itemsBox {
    width: 100%;
    display: -webkit-flex; /* Safari */
    padding: 0 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .itemsBox>.item{
    width: 60px;
    font-weight: 600;
    font-size: 16px;
    color: #868686;
    line-height: 25px;
    text-align: center;
    margin-top: 10px;
  }
  .itemsBox>.item>h5 {
    margin: 0 10px;
    position: relative;
  }
  .itemsBox>.item>span {
    font-size: 12px;
  }
  .itemsBox>.item-hasLike {
    color: rgba(142, 204, 69, 1);
  }
  /*按钮集合*/
  .btnsBox {
    position: relative;
    padding: 5px 10px 0px 10px;
  }
  .btnsBox .btn {
    display: block;
    width: 100%;
    line-height: 2em;
    background: inherit;
    background-color: rgba(174, 174, 174, 1);
    border: none;
    border-radius: 1em;
    font-weight: 600;
    font-size: 17px;
    color: #FFFFFF;
    text-align: center;
    margin: 5px 0;
    cursor: pointer;
    transition: all .3s;
  }
  .btnsBox .btn:hover {
    background-color: rgba(130, 130, 130, 1);
  }
  .btnsBox .btn-orange {
    background-color: rgba(255, 153, 0, 1);
  }
  .btnsBox .btn-orange:hover {
    background-color: rgba(234, 140, 0, 1);
  }
  .btnsBox .btn-green {
    background-color: rgba(142, 204, 69, 1);
  }
  .btnsBox .btn-green:hover {
    background-color: rgba(133, 193, 71, 1);
  }
  .btnsBox .btn-grey {
    background-color: rgba(102, 102, 102, 1);
  }
  .btnsBox .btn-grey:hover {
    background-color: rgba(87, 87, 87, 1);
  }
  .cursor-default {
    cursor: default !important;
  }
  .cursor-pointer {
    cursor: pointer !important;
  }
  /*列表框*/
  .msgBox {
    width: 100%;
    min-height: 184px;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
    padding: 5px 5px 0 5px;
    position: relative;
    margin: 10px 0;
  }
  .msgBox>.title {
    font-weight: 600;
    font-size: 14px;
    line-height: 3em;
    padding-left: 12px;
    position: relative;
  }
  .msgBox>.title::before {
    content: '';
    display: inline-block;
    width: 8px;
    height: 8px;
    background-color: rgba(255, 153, 0, 1);
    margin-right: 6px;
    position: relative;
    top: -1px;
  }
  .msgBox-content>li {
    background-color: rgba(255, 255, 255, 0);
    border-bottom: 1px solid rgba(204, 204, 204, 0.3);
    padding: 5px;
    /*overflow: hidden;*/
  }
  .msgBox-content>li>.body {
    width: 103%;
    min-height: 5.4em;
    max-height: 150px;
    font-size: 14px;
    line-height: 1.8em;
    word-break: break-all;
    /*display: -webkit-box;*/
    /*-webkit-line-clamp: 4;*/
    /*-webkit-box-orient:vertical;*/
    overflow-y: hidden;
    padding: 5px 10px 5px 0;
    margin-bottom: 10px;
    position: relative;
  }
  /*定义滚动条轨道*/
  .msgBox-content>li>.body::-webkit-scrollbar-track
  {
    background-color: #f9f9f9;
    -webkit-box-shadow: inset 0 0 6px transparent;
  }
  /*定义滚动条高宽及背景*/
  .msgBox-content>li>.body::-webkit-scrollbar
  {
    width: 10px;
    background-color: #333;
  }
  /*定义滚动条*/
  .msgBox-content>li>.body::-webkit-scrollbar-thumb
  {
    background-color: #8b8b8b;
    border-radius: 10px;
  }
  .msgBox-content>li>.body:hover {
    padding-right: 0px;
    overflow-y: scroll;
  }
  .msgBox-content>li:hover {
    background-color: rgba(204, 204, 204, 0.1);
  }
  .msgBox-content>li>.body .title {
    font-size: 16px;
    line-height: 2em;
  }
  .msgBox-content>li>.footer {
    font-size: 12px;
    color: #AEAEAE;
    line-height: 1.5em;
    max-height: 1.5em;
    cursor: default;
    position: relative;
  }
  .msgBox-content>li>.footer .username {
    display: inline-block;
    max-width: 12em;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .msgBox-content>li>.footer .date {
    float: right;
  }
  .msgBox-content>li>.footer img {
    width: 18px;
    height: 18px;
    border-radius: 13px;
    vertical-align: top;
    margin-right: 5px;
    position: relative;
  }
  .msgBox>.more {
    line-height: 3em;
    display: block;
    font-weight: 300;
    font-size: 12px;
    color: #AEAEAE;
    text-align: center;
    cursor: pointer;
    transition: all .3s;
  }
  .msgBox>.nomore {
    cursor: default;
  }
  .msgBox>.more:hover {
    color: #FF9900;
  }
  /*个人信息*/
  .userInfo-box {
    padding: 10px 15px;
    border-bottom: 1px solid rgba(148, 148, 148, 0.3);
  }
  .userInfo-box img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    vertical-align: bottom;
  }
  .userInfo-box>.content {
    max-width: 140px;
    padding-left: 10px;
    display: inline-block;
    line-height: 30px;
    cursor: default;
  }
  .userInfo-box>.content h5 {
    font-size: 16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .userInfo-box>.content span {
    font-size: 12px;
    color: #999999;
  }
  .itemsBox>.item .newMsg {
    position: relative;
  }
  .itemsBox>.item .newMsg::after {
    content: '';
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: rgba(255, 5, 14, 0.8);
    display: inline-block;
    position: absolute;
    top: -3px;
    right: -6px;
  }
  /*热门帖子栏目样式设置*/
  .hotPosts-box .msgBox-content>li>.body {
    min-height: 2em;
  }
  .hotPosts-box .msgBox-content>li>.body .title {
    font-size: 14px;
  }
</style>



