<template>
  <span class="icon">
    <i :class="name"></i><br><span v-if="tips" class="tips">{{tips}}</span>
  </span>
</template>

<script>
export default {
  name: 'icon',
  props: {
    name: {
      type: String,
      request: true
    },
    tips: {
      type: String
    }
  }
}
</script>

<style scoped>
  .icon {
    text-align: center;
    cursor: pointer;
  }
  .tips {
    position: absolute;
    display: none;
    min-width: 32px;
    font-size: 10px;
    white-space:nowrap;
    color: #fff;
    background-color: #333;
    padding: 2px 5px;
    border-radius: 3px;
    transform: translate(-35px, 3px);
  }
  .icon:hover .tips {
    display: inline-block;
  }
  .tips::after {
    content: '';
    position: absolute;
    top: -4px;
    right: 15px;
    border-bottom: 5px solid #333;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
  }
</style>



