<template>
<div id="forums">
  <template v-if="!ifLock">
    <search-bar v-if="$route.name === 'forums' || $route.name === 'postSearch'" :showCatalog = "$route.name === 'postSearch'"></search-bar>
    <transition>
      <router-view/>
    </transition>
    <div class="container" v-if="$route.name === 'forums'">
      <div class="left-module">
        <ul class="tab-items">
          <li :class="(currentTabType === item.type)?'currentTab':''" v-for="(item, index) in tabList" :key="index">
            <span @click="changeTab(item.type)">{{item.name}}</span>
            <template v-if="index < tabList.length-1">
              &nbsp;/ &nbsp;
            </template>
          </li>
        </ul>
        <div class="tab-content" v-loading="loading">
          <ul>
            <li :class="item.is_top? 'top-post-item':''" v-for="(item, index) in postsList" :key="index">
              <div class="post-body-left">
                <img :src="item.author.avatarUrl ? host + item.author.avatarUrl : defaultImage">
              </div>
              <div class="post-body-middle">
                <h3 class="title">
                  <span  style="cursor: pointer" v-html="item.title" @click="toPostDetail(item.post_id)"></span>
                  <span class="tags tags-top" v-if="item.is_top">置顶</span>
                  <span class="tags tags-essence" v-if="item.type === 2">精华</span>
                  <span class="tags tags-notice" v-if="item.type === 3">系统公告</span>
                  <span class="tags tags-guide" v-if="item.type === 4">新手指南</span>
                  <span class="tags" v-for="(subItem, index) in item.labels" :key="index">{{subItem}}</span>
                </h3>
                <h5 class="authorAndDate">{{(item.type === 3)? 'OJ平台管理员' : item.author.nick}}&nbsp;| &nbsp;{{item.publish_date.replace('T', ' ')}}</h5>
              </div>
              <div :class="noLogin ? 'post-body-right noLogin' : 'post-body-right'" >
                <div class="good-tips" style="color: #8ECC45" @click="giveLike(item.post_id, item.is_liked, index)">
                  <template v-if="!noLogin && item.is_liked">
                    <h5 class="hasGood">{{item.likes}}</h5><span>已点赞</span>
                  </template>
                  <template v-else>
                    <h5 >{{item.likes}}</h5><span>点赞</span>
                  </template>
                </div>
                <div class="reply-tips" style="color: #FF9900">
                  <h5>{{item.replies}}</h5><span>回复</span>
                </div>
                <div class="see-tips" style="color: #868686">
                  <h5>{{item.views}}</h5><span>浏览</span>
                </div>
              </div>
            </li>
          </ul>
          <span class="noList" v-if="postsTotal <= pageSize">无更多帖子，请尝试<span class="refresh-btn" @click="refresh">刷新</span>页面</span>
        </div>
        <div class="public-pagination" v-if="postsTotal > pageSize">
          <el-pagination
            @current-change="handleCurrentChange"
            background
            layout="prev, pager, next"
            :page-size="pageSize"
            :pager-count="9"
            :total="postsTotal">
          </el-pagination>
        </div>
      </div>
      <div class="right-module">
        <rightFrame></rightFrame>
      </div>
    </div>
  </template>
  <div class="lock" v-if="ifLock">
    <div class="tips">建设中，请输入密码：</div>
    <input type="password" v-model="password" @keyup.enter="unLocked">
  </div>
</div>
</template>

<script>
import {host} from '@/api/config'
import searchBar from '@/base/forums/search-bar'
import rightFrame from '@/base/forums/rightFrame'

import api from '@/api/forums'

export default {
  name: 'forums',
  components: {
    searchBar,
    rightFrame
  },
  data () {
    return {
      ifLock: false,
      password: '',
      tabList: [
        { name: '最新帖子', type: 0 },
        { name: '最热帖子', type: 1 },
        { name: '精华帖子', type: 2 },
        { name: '新手指南', type: 4 }
      ],
      currentTabType: 0,
      postsList: [],
      topPostsList: [],
      postsTotal: 0,
      loading: true,
      page: 1,
      pageSize: 20,
      info: {},
      host: host,
      noLogin: true,
      defaultImage: require('../../../static/image/acmlogo.jpg')
    }
  },
  methods: {
    unLocked () {
      if (this.password === 'hhh333---') {
        this.ifLock = false
      }
    },
    refresh () {
      location.reload()
    },
    changeTab (type) {
      this.currentTabType = type
      this.page = 1
      // 此处调用api重新请求相应数据
      this.getPostsList()
    },
    toPostDetail (id) {
      this.$router.push({name: 'postDetail', params: {id: id}})
    },
    handleCurrentChange (val) {
      this.page = val
      this.getPostsList()
    },
    // 点赞和取消赞
    giveLike (postId, ifGood, listNo) {
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json === 'undefined' || json === null || json === undefined) {
        return false
      }
      const _this = this
      this.postsList[listNo].is_liked = !this.postsList[listNo].is_liked
      if (ifGood) { // 取消
        api.delGiveLike('/forum/like', postId).then(function (res) {
          if (res.success === 'true') {
            _this.postsList[listNo].likes--
          }
        })
      } else { // 点赞
        api.postGiveLike('/forum/like', postId).then(function (res) {
          if (res.success === 'true') {
            _this.postsList[listNo].likes++
          }
        })
      }
    },
    getListInfo () {
      const _this = this
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json !== 'undefined' && json !== null && json !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
        api.getInfoList('/users/detail', uid).then(function (res) {
          _this.info = res.data.user_info
          _this.expandTabItem()
          _this.noLogin = false
        })
      }
    },
    expandTabItem () {
      this.tabList.push({ name: '系统公告', type: 3 })
      this.showNoticeMsg = false
    },
    getTopPostsList () {
      const _this = this
      api.getTopPostsList('/forum/posts/top', {}).then(function (res) {
        _this.topPostsList = res.data.post_list
      })
    },
    getPostsList (search) {
      window.scrollTo(0, 0)
      this.loading = true
      const _this = this
      var obj = {
        type: _this.currentTabType,
        page: _this.page,
        pageSize: _this.pageSize
      }
      if (arguments.length === 1) {
        obj.search = search
      }
      api.getPostsList('/forum/posts', obj).then(function (res) {
        _this.postsList = res.data.post_list
        _this.postsTotal = res.total
        for (var i = 0; i < _this.topPostsList.length; i++) {
          _this.topPostsList[i].is_top = true
          _this.postsList.unshift(_this.topPostsList[i])
        }
        _this.loading = false
      })
    }
  },
  created () {
    this.getListInfo()
    this.getTopPostsList()
    this.getPostsList()
  },
  watch: {
    $route () {
      if (this.$route.name === 'forums') {
        this.page = 1
        this.getPostsList()
      }
    }
  }
}
</script>

<style scoped>
  #forums {
    width: 100%;
  }
  .container {
    max-width: 1170px;
    margin: 18px auto;

    display: -webkit-flex; /* Safari */
    display: flex;
    flex-direction: row;
  }
  .container > div {
    position: relative;
  }
  .container .left-module {
    min-width: 700px !important;
    flex: 1 1 auto;
    margin-right: 15px;
  }
  .container .right-module {
    min-width: 250px;
    max-width: 250px;
  }
  /*tab菜单*/
  .left-module .tab-items {
    margin-bottom: 15px;
  }
  .left-module .tab-items>li {
    font-weight: 600;
    font-size: 14px;
    color: #999;
    display: inline-block;
  }
  .left-module .tab-items>.currentTab span{
    color: #FF9900;
  }
  .left-module .tab-items>li span {
    transition: all .3s;
    cursor: pointer;
  }
  .left-module .tab-items>li span:hover {
    color: #FF9900;
  }
  /*帖子列表*/
  .left-module .tab-content {
    width: 100%;
    min-height: 450px;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
    /*padding: 5px;*/
    position: relative;
    overflow: hidden;
  }
  .tab-content /deep/ .el-loading-spinner .path {
    stroke: rgba(142, 204, 69, 1);
  }
  .tab-content .noList {
    line-height: 3em;
    display: block;
    font-weight: 300;
    font-size: 12px;
    color: #AEAEAE;
    text-align: center;
    cursor: default;
  }
  .tab-content .refresh-btn {
    cursor: pointer;
  }
  .tab-content .refresh-btn:hover {
    color: #FF9900;
  }
  .tab-content ul {
    margin-bottom: 15px;
  }
  .tab-content ul>li {
    min-height: 74px;
    background-color: rgba(255, 255, 255, 0);
    border-bottom: 1px solid rgba(204, 204, 204, 0.3);
    padding: 10px 0 7px 0;
    display: -webkit-flex; /* Safari */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    transition: all .3s;
  }
  .tab-content ul>.top-post-item {
    background-color: rgba(174, 174, 174, 0.08);
  }
  .tab-content ul>li:hover {
    background-color: rgba(150, 150, 150, 0.1);
  }
  /*列表左侧头像*/
  .post-body-left {
    min-width: 80px;
    text-align: center;
    /*background-color: pink;*/
  }
  .post-body-left img {
    width: 44px;
    height: 44px;
    border-radius: 22px;
    margin: 8px 0;
  }
  /*列表中部内容*/
  .post-body-middle {
    flex: 1 1 auto;
    padding: 0 10px;
    line-height: 30px;
    cursor: default;
  }
  .post-body-middle .title {
    font-weight: 400;
    font-size: 18px;
  }
  .post-body-middle .tags{
    height: 1.4em;
    font-weight: 300;
    font-size: 12px;
    text-align: center;
    line-height: 1.4em;
    color: #FFFFFF;
    background-color: rgba(204, 204, 204, 1);
    display: inline-block;
    border-radius: 0.2em;
    padding: 0 0.4em;
    margin: 0 0.2em;
    transition: all .3s;
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0);
  }
  .post-body-middle .tags:hover{
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.1);
  }
  .post-body-middle  .tags-top{
    background-color: rgba(142, 204, 69, 0.8);
  }
  .post-body-middle .tags-essence {
    background-color: rgba(255, 5, 14, 0.8)
  }
  .post-body-middle .tags-notice {
    background-color: rgba(100, 149, 237, 0.8);
  }
  .post-body-middle .tags-guide {
    background-color: rgba(255, 153, 0, 0.8);
  }
  .post-body-middle .authorAndDate {
    font-weight: 300;
    font-size: 12px;
    color: #AEAEAE;
  }
  /*列表点赞等情况*/
  .post-body-right {
    width: 180px;
    display: inline-block;
    display: -webkit-flex; /* Safari */
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .post-body-right>div{
    width: 60px;
    font-weight: 600;
    font-size: 16px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
  }
  .noLogin>div {
    cursor: default;
  }
  .post-body-right>div>h5{
    margin: 0 10px;
  }
  .post-body-right>div>span{
    font-size: 12px;
  }
  .post-body-right>div .hasGood {
    color: #fff;
    background-color: rgba(142, 204, 69, 0.8);
    border-radius: 5px;
  }
  /*分页*/
  .left-module .public-pagination {
    margin-top: 20px;
  }
  /*密码锁*/
  .lock {
    position: fixed;
    width: 300px;
    border-radius: 5px;
    background-color: #333;
    box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.2);
    left: 50%;
    margin-left: -150px;
    top: 38%;
  }
  .lock .tips {
     text-align: center;
     color: #eeeeee;
     line-height: 2em;
     border-bottom: 1px solid #eeeeee;
     padding-top: 5px;
  }
  .lock input {
    width: 80%;
    font-size: 14px;
    color: #999;
    line-height: 2em;
    padding: 0 10px;
    margin: 15px 10% 20px 10%;
    border-radius: 5px;
  }
</style>



