<template>
  <div class="pagination2">
    <div class="pagination2-container">
      <div class="page-item" :class="{disabled: selectPageIndex === 1}" @click="switchListPrev(1)">上一页</div>
      <div v-if="showPrev" class="page-item" @click="changeSelectPageIndex(1)">{{1}}</div>
      <span v-if="showPrev" class="page-omit" @click="switchListPrev(5)">. . .</span>
      <div class="page-item" v-for="(item, index) in pages" :key="index" :class="{active: item === selectPageIndex}" @click="changeSelectPageIndex(item)">{{item}}</div>
      <span v-if="showNext" class="page-omit" @click="switchListNext(5)">. . .</span>
      <div v-if="showNext" class="page-item" @click="changeSelectPageIndex(pageTotal)">{{pageTotal}}</div>
      <div class="page-item" :class="{disabled: selectPageIndex === pageTotal}" @click="switchListNext(1)">下一页</div>
      <span>跳至</span><input type="text" class="input" v-model="inputValue" @keydown.enter="changeSelectPageIndex(inputValue)"><span>页</span>
      <div class="page-item" @click="changeSelectPageIndex(inputValue)">Go</div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      selectPageIndex: this.page, // 当前被选中的页码
      // pageTotal: 1 返回的总页数
      showPrev: false,
      showNext: false,
      inputValue: 1, // 输入框内的值
      pages: []
    }
  },
  props: {
    pageTotal: { // 总页数
      type: Number,
      required: true,
      default: 1
    },
    page: { // 当前页码
      type: Number,
      default: 1
    },
    currentPage: { // 页码改变回调函数
      type: Function
    }
  },
  methods: {
    createFunc () {
      this.resetPagination(this.selectPageIndex)
    },
    // 重置页码
    resetPagination (page) {
      this.inputValue = page
      //                this.$router.push({query: {page: page}}); // 改变当前url得参数值page
      var end = 0
      if (this.pageTotal === 5 || this.pageTotal === 6) { // 页码的临界值为5
        page = 1
        end = page + 5
        this.showPrev = false
        this.showNext = false
      } else if (page <= 4) {
        page = 1
        end = page + 5
        this.showPrev = false
      } else if (page >= this.pageTotal - 4) {
        page = this.pageTotal - 4
        end = page + 5
        this.showNext = false
        this.showPrev = true
      } else {
        page -= 2
        end = page + 5
        this.showPrev = true
      }
      this.pages = []
      while (page < end) {
        if (page <= this.pageTotal) {
          this.pages.push(page)
        } else {
          this.showNext = false
          break
        }
        page++
      }
      if (page === end && page < this.pageTotal) {
        this.showNext = true
      } else {
        if (page === this.pageTotal) {
          this.pages.push(page)
        }
        this.showNext = false
      }
    },
    switchListPrev (number) {
      this.changeSelectPageIndex(this.selectPageIndex - number)
    },
    switchListNext (number) {
      this.changeSelectPageIndex(this.selectPageIndex + number)
    },
    changeSelectPageIndex (idx) {
      var index = parseInt(idx) // 将文本数据转化为整数
      if (isNaN(index)) {
        // 若文本数据合法，则不跳转
        return
      }
      if (index < 1) { // 如果超出下限
        this.selectPageIndex = 1
      } else if (index > this.pageTotal) { // 如果超出上限
        this.selectPageIndex = this.pageTotal
      } else if (index === this.selectPageIndex) { // 如果点击当前已经激活的页码，则返回
        return
      } else {
        this.selectPageIndex = index
      }
      this.resetPagination(this.selectPageIndex) // 根据当前的页码下标调整视图
      this.$emit('currentPage', this.selectPageIndex)
    }
  },
  watch: {
    pageTotal: function () {
      this.selectPageIndex = 1
      this.resetPagination(this.selectPageIndex) // 监听pageTotal的改变
    },
    page: function () { // 监听page的改变
      this.selectPageIndex = this.page
      this.inputValue = this.page
    },
    '$route': function () {
      console.log('router change......')
      var page = parseInt(this.$route.query.page) || 1
      this.changeSelectPageIndex(page)
    }
  },
  created () {
    this.createFunc()
  }
}

</script>

<style scoped>
  .pagination2 {
    margin-top: 15px;
    position: relative;
    font-family: "Microsoft YaHei";
  }
  .pagination2-container {
    font-size: 14px;
    line-height: 30px;
  }
  .page-item {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    background-color: #fafafa;
    display: inline-block;
    border: 1px solid #dcdcdc;
    margin: 0 5px;
    cursor: pointer;
    user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
  }
  .page-item.active {
    color: #fff;
    background-color: #f48118;
  }
  .page-item:hover, .page-omit:hover {
    color: #f48118;
  }
  .page-item.active:hover {
    color: #fff;
  }
  .page-omit {
    vertical-align: top;
    font-weight: 900;
    cursor: pointer;
    user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
  }
  .input {
    height: 30px;
    width: 40px;
    border: 1px solid #dcdcdc;
    box-sizing: border-box;
    padding: 0 5px;
    display: inline-block;
    margin: 0 10px;
  }
  .disabled {
    cursor: not-allowed;
    color: #646464 !important;
    opacity: 0.8;
  }
</style>



