<template>
  <div class="login">
    <div class="header">
      <div style="width: 1170px;margin: 0 auto;">
        <span>您好，欢迎来到本平台</span>
      </div>
    </div>
    <div class="form-pad">
      <router-link to="/" tag="a" class="back">返回首页</router-link>
      <img class="login-bg" src="../../../static/image/login_bg.jpg">
      <transition name="fade" mode="in-out">
          <div v-if="loginShow" class="login-pad">
            <p class="login-title">登录，开始</p>
            <el-form class="login-from" :model="loginFrom">
              <el-form-item>
                <el-input
                  prefix-icon="icon-name"
                  placeholder="请输入账号"
                  v-model="loginFrom.user"
                  @keyup.enter.native="keyDownLogin"></el-input>
              </el-form-item>
              <el-form-item>
                <el-input
                  type="password"
                  prefix-icon="icon-password"
                  placeholder="请输入密码"
                  v-model="loginFrom.password"
                  @keyup.enter.native="keyDownLogin"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="Login">立即登录</el-button>
                <!--<el-button>重置</el-button>-->
              </el-form-item>
            </el-form>
            <span style="margin-left: 20px">还没有账号? <a style="cursor:pointer;color: #228899" @click="showRegister">立即注册</a></span>
          </div>
          <div class="register-pad" v-else>
            <p class="login-title">开始您的算法之旅</p>
            <el-form class="login-from" :model="registFrom">
              <el-form-item>
                <el-input
                  prefix-icon="icon-email"
                  v-model="registFrom.username"
                  placeholder="请输入邮箱，该邮箱将作为你的登录账号"></el-input>
              </el-form-item>
              <el-form-item>
                <el-input
                  prefix-icon="icon-name"
                  v-model="registFrom.nick"
                  placeholder="请输入昵称"></el-input>
              </el-form-item>
              <el-form-item>
                <el-input
                  type="password"
                  v-model="registFrom.password"
                  prefix-icon="icon-password"
                  placeholder="请输入密码"></el-input>
              </el-form-item>
              <el-form-item>
              <el-input
                type="password"
                v-model="registFrom.rePassword"
                prefix-icon="icon-password"
                placeholder="再次输入密码"></el-input>
              </el-form-item>
              <el-form-item>
                <el-col :span="12">
                  <el-input
                    type="text"
                    v-model="registFrom.captchacode"
                    prefix-icon="icon-password"
                    placeholder="输入验证码"></el-input>
                </el-col>
                <el-col :span="2"></el-col>
                <el-col :span="10">
                  <img :src="src" @click="getCode" style="height: 40px;margin-left: 50px"/>
                </el-col>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="register">注册</el-button>
              </el-form-item>
              <el-form-item>
                <el-button type="success" @click="showLogin">登录</el-button>
              </el-form-item>
            </el-form>

          </div>
        </transition>
    </div>
    <div class="footer">
      <ul>
        <li><i class="el-icon-success">在线判题</i></li>
        <li><i class="el-icon-document">线上学习</i></li>
        <li><i class="el-icon-star-on">关于我们</i></li>
      </ul>
      <span><a href="http://www.miitbeian.gov.cn" target="_blank">@copyright www.gpnuacm.com 粤ICP备18111867号</a></span>
    </div>
  </div>
</template>
<script>
import { mapMutations } from 'vuex'
import {host} from '@/api/config'
import api from '@/api/login'

export default {
  data () {
    return {
      src: '',
      maxNameLen: 20,
      loginFrom: {
        user: '',
        password: '',
        checkCode: ''
      },
      registFrom: {
        username: '',
        password: '',
        rePassword: '',
        nick: '',
        captchacode: ''
      },
      loginShow: true
    }
  },
  methods: {
    ...mapMutations([
      'TOGGLE_HEADER', 'TOGGLE_FOOTER', 'SET_USER'
    ]),
    showRegister () {
      this.loginShow = false
      this.getCode()
    },
    showLogin () {
      this.loginShow = true
    },
    keyDownLogin () {
      this.Login()
    },
    Login () {
      const _this = this
      if (!_this.loginFrom.user) {
        /*  _this.$message({
          message: '请输入账号',
          type: 'error'
        })  */
        _this.$message('请输入账号', '提示')
        return false
      } else if (!_this.loginFrom.password) {
        _this.$alert('请输入密码', '提示')
        return false
      }
      api.PostLoginData('/users/login/', _this.loginFrom.user, _this.loginFrom.password).then(function (res) {
        var data = res.data
        if (res.success === 'true' && res.status === 10002) {
          _this.$message({
            type: 'success',
            message: '登陆成功',
            showClose: true
          })
          setTimeout(() => {
            _this.$message.closeAll()
          }, 1000)
          // 缓存数据
          _this.getUserData(data.user_info)
          _this.SET_USER(data.user_info)
          // 是否存在重定向
          if (_this.$route.query.redirect) {
            _this.$router.push({path: _this.$route.query.redirect})
          } else {
            // 默认返回首页
            _this.$router.push({path: '/index', name: 'index', params: {dataObj: data}})
          }
        } else {
          _this.$message({
            type: 'error',
            message: res.msg,
            showClose: true
          })
        }
      })
    },
    register () {
      const _this = this
      let flag = _this.checkRegister()
      if (!flag.flag) {
        _this.$alert(flag.msg, '提示')
        return false
      }
      api.postRegistData('/users/register/', _this.registFrom.username, _this.registFrom.password, _this.registFrom.nick, _this.registFrom.captchacode).then(function (res) {
        if (res.data.success === 'false') {
          _this.$alert(res.msg, '提示')
          _this.getCode()
        } else {
          _this.$confirm(res.msg, '欢迎', {
            confirmButtonText: '确定'
          }).then(function () {
            _this.$router.push({path: '/login'})
          })
        }
      })
    },
    checkRegister () {
      if (!this.registFrom.username) {
        return {
          flag: false,
          msg: '请填写账号'
        }
      }
      if (!this.registFrom.nick) {
        return {
          flag: false,
          msg: '请填写用户昵称'
        }
      }
      // 判断是否是竞赛小组名，是的话允许20长度
      var reg = this.registFrom.nick.indexOf('GPNU_') === 0
        ? /^[-\w\u4e00-\u9fa5]{2,20}$/ : /^[-\w\u4e00-\u9fa5]{2,14}$/
      if (!reg.test(this.registFrom.nick)) {
        return {
          flag: false,
          msg: '昵称不符合规格'
        }
      }
      if (!this.registFrom.password) {
        return {
          flag: false,
          msg: '请填写密码'
        }
      }
      if (this.registFrom.rePassword !== this.registFrom.password) {
        return {
          flag: false,
          msg: '两次输入的密码不相同'
        }
      }
      if (!this.registFrom.captchacode) {
        return {
          flag: false,
          msg: '请输入验证码'
        }
      }
      return {
        flag: true
      }
    },
    getCode () {
      this.src = host + '/captcha' + '?_t=' + new Date().getTime()
    },
    getUserData (user) {
      // 将数据放在当前组件的数据内
      this.SET_USER(user || {})
      if (!window.sessionStorage) {
        alert('浏览器不支持localstorage')
        return false
      } else {
        var storage = window.sessionStorage
        var localStore = JSON.stringify(user)
        storage.setItem('userInfo', localStore)
      }
    }
  },
  mounted () {
    this.TOGGLE_HEADER(false)
    this.TOGGLE_FOOTER(false)
  },
  beforeDestroy () {
    this.TOGGLE_HEADER(true)
    this.TOGGLE_FOOTER(true)
  },
  created () {
    if (this.$route.query.show === -1) {
      this.showRegister()
    }
  }
}
</script>

<style scoped>
  .login {
    /*background-image: url("../../../static/image/other_bg.jpg");*/
  }
  .header {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    color: #646464;
    background-color:#e6e6e6;
    /*padding: 10px 0;
    box-shadow: 0px 10px 12px 0px rgba(54, 54, 54, 0.12);*/
  }
  .header img {
    display: inline-block;
    margin-top: 10px;
  }
  .back {
    position: absolute;
    right: -100px;
    top: 0;
    display: inline-block;
    width: 70px;
    height: 100px;
    line-height: 40px;
    font-size: 18px;
    color: white;
    background-color: #f48118;
    margin-top: 19px;
    border: 1px solid #f48118;
    text-align: center;
    border-radius: 5px;
    padding: 10px;
    font-weight: 700;
  }
  .form-pad {
    height: 660px;
    position: relative;
    width: 1000px;
    /*height: 500px;*/
    background-color: white;
    margin:50px auto;
    /*border-radius: 5px;*/
    box-shadow: 0px 3px 12px 0px
    rgba(54, 54, 54, 0.32);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .login-pad,.register-pad{
    display: inline-block;
    vertical-align: middle;
    /*margin-top: 100px;*/
    width: 485px;
  }
  .el-button {
    width: 100%;
    /*background-color: #f48118;*/
    /*border-color: #f48118;*/
  }
  .el-input {
    font-size: 16px;
  }
  .footer {
    text-align: center;
    width: 1170px;
    margin: 0 auto;
  }
  .footer ul {
    font-size: 20px;
    color: #646464;
    width: 1170px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 25px;
  }
  .footer ul li {
    display: inline-block;
  }
  .footer ul li>i {
    font-size: 24px;
  }
  .login-title {
    text-align: center;
    font-size: 24px;
    color: #323232;
    padding-top: 20px;
  }
  .login-from {
    margin-top: 20px;
    padding: 10px 20px;
  }
  .login-bg {
    display: inline-block;
    width: 495px;
    vertical-align: middle;
  }
</style>



