<template>
  <div class="choice-question"
       :class="{'max-height': maxHeight}"
       @mouseenter="handleMouseEnter"
       @mouseleave="handleMouseLeave">
    <p class="current-question" v-if="isExamPaper">{{question.belongTypeTitle}}{{question.questionNum}} ({{question.questionBonus}}分)</p>
    <p class="question-title" :class="{'preview-title': isBrowse}" v-html="question.questionTitle"></p>
    <div class="answer-area">
      <div class="option"
           v-for="(option, index) in question.answers"
           :key="index"
           @click="selectAnswer(index)"
           :class="{'active': typeof question.questionAnswer === 'undefined' && (activeIndex === index),
                    'pass': typeof question.questionAnswer !== 'undefined' && (toNumber(question.questionAnswer) - 1 === index),
                    'error': typeof question.questionAnswer !== 'undefined' && typeof question.userAnswer !== 'undefined' && (question.questionAnswer !== question.userAnswer) && (toNumber(question.userAnswer) - 1 === index)}">
        <span class="label">{{convert(index)}}:</span>
        <p class="data" v-html="option"></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    question: {
      type: Object
    },
    isExamPaper: {
      type: Boolean,
      default: false
    },
    isBrowse: {
      type: Boolean,
      default: false
    },
    maxHeight: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    getActiveIndex () {
      return this.activeIndex
    }
  },
  data () {
    return {
      activeIndex: -1
    }
  },
  methods: {
    handleMouseEnter () {
      this.$emit('enter')
    },
    handleMouseLeave () {
      this.$emit('leave')
    },
    convert (num) {
      return String.fromCharCode(num + 65)
    },
    toNumber (word) {
      return word.toUpperCase().charCodeAt(0) - 64
    },
    selectAnswer (index) {
      if (typeof this.question.questionAnswer !== 'undefined') {
        return
      }
      this.activeIndex = index
    },
    setUserAnswer (init) {
      if (init) {
        this.activeIndex = -1
        return
      }
      if (typeof this.question.userAnswer !== 'undefined') {
        this.activeIndex = this.question.userAnswer.toUpperCase().charCodeAt(0) - 65
      }
    }
  },
  mounted () {
    if (this.question.userAnswer) {
      this.activeIndex = this.question.userAnswer.toUpperCase().charCodeAt(0) - 65
    }
  }
}
</script>

<style scoped>
  .choice-question {
    width: 100%;
    font-size: 14px;
    /*padding-bottom: 20px;*/
  }
  .max-height {
    overflow-y: auto;
    max-height: 400px;
  }
  .choice-question .current-question {
    font-weight: 600;
    width: 100%;
    /*height: 30px;*/
    line-height: 30px;
    padding: 25px 15px 15px 15px;
    box-sizing: border-box;
    font-size: 16px;
  }
  .choice-question .question-title {
    width: 100%;
    min-height: 80px;
    line-height: 25px;
    padding: 10px 30px;
    box-sizing: border-box;
  }
  .choice-question .preview-title {
    min-height: auto;
  }
  .choice-question .answer-area {
    width: 100%;
    padding: 20px 30px;
    box-sizing: border-box;
  }
  .choice-question .answer-area .option {
    width: 100%;
    min-height: 45px;
    padding: 10px 15px;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-sizing: border-box;
    margin-top: 10px;
    font-size: 0;
    position: relative;
  }
  .choice-question .answer-area .option:first-child {
    margin-top: 0;
  }
  .choice-question .answer-area .option .label {
    position: absolute;
    left: 15px;
    top: 10px;
    width: 30px;
    height: 100%;
    line-height: 25px;
    font-size: 14px;
    font-weight: 600;
  }
  .choice-question .answer-area .option .data {
    display: inline-block;
    width: 100%;
    line-height: 25px;
    padding-left: 30px;
    box-sizing: border-box;
    font-size: 14px;
  }
  .choice-question .answer-area .active {
    border-color: #f48118;
    color: #f48118;
  }
  .choice-question .answer-area .pass {
    border-color: #00cc45;
    color: #00cc45;
  }
  .choice-question .answer-area .error {
    border-color: #ff6c3f;
    color: #ff6c3f;
  }
</style>



