<template>
  <div class="information" ref='information'>
    <div class="Info-head">
      <span class="Infotitle">{{userForm.nick}}的公开信息</span>
      <el-tag v-if="identify == 1">学生用户</el-tag>
      <el-tag v-if="identify == 2" type="danger">教师</el-tag>
      <el-tag v-if="identify == 0" type="warning">普通用户</el-tag>
      <!--<div class="Info-headR">-->
        <!--<el-tooltip class="item" effect="dark" content="ACM排名名次: 1" placement="top">-->
          <!--<span class="pass">通过</span>-->
        <!--</el-tooltip>-->
        <!--<el-tooltip class="item" effect="dark" content="ACM排名名次：1" placement="top">-->
          <!--<span class="submit">提交</span>-->
        <!--</el-tooltip>-->
      <!--</div>-->
    </div>
    <img v-if="showAvatar" class="user-avatar" :src="getAvatarUrl" alt="">
    <div class="Info-middle">
      <div class="line-class">
        <span class="Info">学校:</span>
        <span>{{userForm.school_name}}</span>
      </div>
      <div class="line-class">
        <span class="Info">电子邮箱:</span>
        <span>{{userForm.email}}</span>
      </div>
      <div class="line-class">
        <span class="Info">QQ:</span>
        <span>{{userForm.qq}}</span>
      </div>
      <div class="line-class">
        <span class="Info">签名:</span>
        <span>{{userForm.signature}}</span>
      </div>
      <div class="line-class">
        <span class="Info">上次登录:</span>
        <span>{{userForm.login_time}}</span>
      </div>
    </div>
    <div class="Info-bottom">
      <el-alert
        title="已完成题目"
        type="success"
        :closable=false>
      </el-alert>
      <div class="success-topic passList">
        <div v-for="(item, index) in done_list" :key="index" class="inline">
          <span class="topic" @click="toDetail(item.problem_id)">{{item.problem_id}}</span>
          <span class="Num">{{item.accepted}}/{{item.submit}}</span>
        </div>
      </div>
      <el-alert
        title="未完成的题目"
        type="error"
        :closable=false>
      </el-alert>
      <div class="success-topic">
        <div v-for="(item, index) in not_done_list" :key="index" class="inline">
          <span class="topic" @click="toDetail(item.problem_id)">{{item.problem_id}}</span>
          <span class="Num">{{item.accepted}}/{{item.submit}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api/information'
import { Loading } from 'element-ui'

let loading

export default{
  props: {
    information: {
      type: Number
    }
  },
  computed: {
    getAvatarUrl () {
      return this.userForm.avatarurl ? this.userForm.avatarurl : this.defaultImage
    }
  },
  data () {
    return {
      userForm: {
        avatarurl: '',
        email: '',
        identity: '',
        login_time: '',
        nick: '',
        qq: '',
        signature: '',
        sex: '',
        school_name: ''
      },
      showAvatar: false,
      done_list: [],
      not_done_list: [],
      identify: -1,
      defaultImage: require('../../../static/image/acmlogo.jpg')
    }
  },
  methods: {
    startLoading () {
      loading = Loading.service({
        lock: true,
        text: '加载中……',
        background: 'rgb(255, 255, 255, 0.7)',
        fullscreen: false,
        target: this.$refs.information
      })
    },
    endLoading () {
      loading.close()
    },
    getinfo (information) {
      const _this = this
      this.showAvatar = false
      _this.startLoading()
      api.getInfoList('/users/detail', information).then(function (res) {
        _this.userForm = res.data.user_info
        let arr = []
        arr = res.data.user_info.login_time.split('T')
        _this.userForm.login_time = arr[0] + ' ' + arr[1]
        _this.done_list = res.data.problem_info.done_list
        _this.not_done_list = res.data.problem_info.not_done_list
        _this.identify = res.data.user_info.identity
        _this.endLoading()
        setTimeout(function () {
          _this.showAvatar = true
        }, 400)
      })
    },
    toDetail (data) {
      // console.log(e)
      const {href} = this.$router.resolve({
        path: '/library/questionDetail',
        query: {PID: data}
      })
      window.open(href, '_blank')
      // this.$router.push({path: '/library/questionDetail', query: {PID: PID}})
    }
  },
  watch: {
    information: function (newVal, oldVal) {
      this.getinfo(newVal)
    }
  },
  created () {
    this.getinfo(this.information)
  }
}
</script>

<style scoped>
  .line-class{
    line-height: 35px;
  }
  .Infotitle{
    display: inline-block;
    font-size: 20px;
    line-height: 34px;
    padding: 2px 15px;
    border-bottom: 1px solid #eee;
    text-align: left;
  }
  .Info-headR{
    float: right;
  }
  .Info-headR .pass{
    display: inline-block;
    width: 100px;
    line-height: 34px;
    background-color: #8ecc45;
    color: #ffffff;
    text-align: center;
    border-radius: 30px;
    cursor: pointer;
  }
  .Info-headR .submit{
    display: inline-block;
    width: 100px;
    line-height: 34px;
    background-color: red;
    color: #ffffff;
    text-align: center;
    border-radius: 30px;
    cursor: pointer;
  }
  .Info{
    display: inline-block;
    width: 100px;
    text-align: center;
    font-size: 14px;
    margin-right: 5px;
    margin-left: 20px;
  }
  .user-avatar {
     width: 120px;
     height: 120px;
     border-radius: 50%;
     position: absolute;
     top: 50px;
     right: 45px;
     border: 1px solid #67c23a52;
     box-shadow: 1px 2px 21px #67c23a59;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -ms-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
   }
  .user-avatar:hover {
    box-shadow: 1px 2px 30px #67c23a59;
  }
  .passList {
    max-height: 250px;
    overflow: auto;
  }
  .success-topic {
    max-height: 200px;
    overflow: auto;
  }
  .inline {
    width: 110px;
    display: inline-block;
    vertical-align: top;
    margin: 5px 13px;
  }
  .topic{
    font-size: 14px;
    color: #006400;
    font-weight: 900;
    padding: 0 13px;
    cursor: pointer;
  }
  .topic:hover{
    color: #f48118;
  }
</style>



