<template>
  <div class="header">
    <div style="width: 1170px;margin:0 auto">
      <img src="../../../static/image/logo.png" alt="" class="logo">
      <ul class="nav-list">
        <li v-for="(item, index) in navs" :key="index" v-if="item.note !== '课程' || item.note === '课程' && getTitle !== ''">
          <template v-if="item.name !== null && item.name !== '' && typeof(item.name) !== 'undefined'">
            <!--我的课程和我的教学-->
            <router-link  v-if="item.note === '课程' && User.uid" tag="a" :to="{name: item.name,params: item.params}"><span>{{getTitle}}</span></router-link>
            <!--其他特别菜单项请设置在下面-->
          </template>
          <template v-else>
            <router-link tag="a" :to="item.path"><span>{{item.title}}</span></router-link>
          </template>
        </li>
      </ul>
      <div class="login" v-if='!User.uid'>
        <router-link class="UserName" to="/login" tag='a'>登录</router-link>
        <a class="header-quit-register" @click="toRegister" to="/login">注册</a>
      </div>
      <div class="login" v-if='User.uid'>
        <router-link tag='span' class="avatar" to='/personal-center/1'><img :src="getAvatarUrl"></router-link>
        <!--<a class="user-name header-quit-register" href="#">{{User.nick}}</a>-->
        <a href="#" class="header-quit-register" @click="exit">退出</a>
      </div>
    </div>
  </div>
</template>

<script>
import {host} from '@/api/config'
import api from '@/api/login'
import {mapGetters, mapMutations} from 'vuex'
export default {
  data () {
    return {
      User: {},
      navs: [
        {title: '首页', path: '/index'},
        {title: '题库', path: '/library'},
        {title: '竞赛', path: '/competition'},
        {title: '状态', path: '/status'},
        {note: '课程', name: 'PersonalCenter', params: {isShow: '2'}},
        {title: '排行榜', path: '/rank'},
        {title: '讨论区', path: '/forums'},
        {title: '荣誉墙', path: '/about'}
      ],
      defaultImage: require('../../../static/image/acmlogo.jpg')
    }
  },
  computed: {
    ...mapGetters({
      headerIndex: 'getHeaderIndex',
      user: 'user'
    }),
    getTitle () {
      return this.User.identity === '1' ? '我的课程' : this.User.identity === '2' ? '我的教学' : ''
    },
    getUserId () {
      return this.user.uid
    },
    getAvatarUrl () {
      return this.User.avatarurl ? host + this.User.avatarurl : this.defaultImage
    }
  },
  methods: {
    ...mapMutations([
      'SET_USER'
    ]),
    toRegister () {
      this.$router.push({path: '/login', query: {show: -1}})
    },
    exit () {
      const self = this
      api.logout('/users/logout').then(function (res) {
        if (res.success) {
          sessionStorage.clear()
          self.SET_USER({})
          self.User = {}
          self.$router.push({path: '/index'})
        } else {
          self.$message({
            message: '服务器错误',
            type: 'error',
            duration: 1000,
            showClose: true
          })
        }
      })
    }
  },
  watch: {
    getUserId: {
      handler () {
        if (this.user !== 'undefined' && typeof this.user === 'string') {
          this.User = JSON.parse(this.user)
        } else if (this.user.uid) {
          this.User = this.user
        }
      },
      immediate: true
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
  .header {
    background-image: url('../../../static/image/head_bg.png');
    background-size: 100% auto;
    /*padding-left: 30px;
    padding-right: 30px;*/
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    z-index: 5;
  }
  .logo {
    width: 45px;
    margin-right: 20px;
    display: inline-block;
    /*margin-right: 22px;*/
    vertical-align: middle;
  }
  .nav-list {
    color: #fff;
    display: inline-block;
  }
  .nav-list > li {
    display: inline-block;
    line-height: 60px;
    font-size: 15px;

  }
  .router-link-active {
    background: #f48118;
    transform: skewX(-25deg);
  }
  .login .router-link-active {
    background: none;
  }
  .router-link-active>span {
    display: block;
    transform: skewX(25deg);
  }
  .nav-list > li > a {
    color:white;
    padding: 0 30px;
    display: block;
  }
  .login {
    float: right;
    height: 60px;
    line-height: 60px;
  }
  .avatar {
    display: inline-block;
    transform: skewX(-25deg);
  }
  .avatar img{
    border-radius: 50%;
    width: 45px;
    height: 45px;
    vertical-align: middle;
    margin-right: 10px;
    cursor: pointer;
    transform: skewX(25deg);
  }
  a.user-name {
    color:white;
  }
  .UserName {
    margin-right: 24px!important;
    cursor: pointer;
    color: #f48118;
  }
  .header-quit-register {
    margin-right: 6px;
    cursor: pointer;
    color: orange;
  }
</style>



