<template>
  <div id="postPublish">
    <div class="postHead">
      <p class="postHead-left" @click="backToIndex">讨论区</p>
      <p class="postHead_block">></p>
      <p class="postHead-right">发布贴子</p>
      <div class="btn-save" @click="saveOrPublish(true)">编辑保存</div>
      <div class="btn-reset" v-if="post.save_date !== ''" @click="resetContent()">清空内容</div>
    </div>
    <div class="postBody" v-loading="ifPublish" element-loading-text="拼命执行中">
      <input class="title" type="text" placeholder="请输入帖子标题" v-model="post.title">
      <template v-if="info.identity !== '3'">
        <button :class="post.if_epic_post ? 'btn btn-applyForGood':'btn'" @click="post.if_epic_post=!post.if_epic_post">申精</button>
      </template>
      <template v-if="info.identity === '3'">
        <button :class="post.type === 3 ? 'btn btn-setType':'btn'" @click="setType(3)">通告</button>
        <button :class="post.type === 4 ? 'btn btn-setType':'btn'" @click="setType(4)">指南</button>
      </template>
      <span class="overTips" v-if="WidthCheck(post.title, 50)">超过字符限制</span>
      <div class="labelAndTime">
        <span class="labelAndTime-title">贴子关键词</span>
        <input id="label-input" type="text" v-model="keyWord" placeholder="请输入标签"  @keyup.enter="addLabel"><button @click="addLabel"><i class="el-icon-plus"></i></button>
        <ul>
          <li class="labels" v-for="(item,index) in post.labels" :key="index">{{item}}<button @click="delLabel(index)"><i class="el-icon-close"></i></button></li>
        </ul>
        <p class="time" v-if="post.save_date !== ''">上次保存时间:&nbsp;&nbsp;&nbsp;&nbsp;{{post.save_date.replace('T', ' ')}}</p>
      </div>
      <div class="editor">
        <wangEditor :catchData="catchData" :content="saveContent" :editorType="1"></wangEditor>
      </div>
      <div class="btn-publish" @click="saveOrPublish(false)">确定发布</div>
    </div>
  </div>
</template>

<script>
import wangEditor from '@/base/wangeditor/wangeditor'
import api from '@/api/forums'
export default {
  name: 'postPublish',
  components: {
    wangEditor
  },
  data () {
    return {
      keyWord: [],
      saveContent: '',
      post: {
        title: '',
        save_date: '',
        labels: [],
        content: '',
        type: 0,
        if_epic_post: false
      },
      postText: '',
      info: {},
      ifPublish: false
    }
  },
  methods: {
    backToIndex () {
      this.$router.push({path: '/forums'})
    },
    catchData (value, text) {
      this.post.content = value // 在这里接受子组件传过来的参数，赋值给data里的参数
      this.postText = text
      // console.log(this.postText)
    },
    setType (type) {
      this.post.type = (this.post.type === type) ? 0 : type
    },
    resetContent () {
      this.post = {
        title: '',
        save_date: '',
        labels: [],
        content: '',
        type: 0,
        if_epic_post: false
      }
      this.saveContent = ''
    },
    addLabel () {
      const _this = this
      var data = this.keyWord
      var warning = ''
      var warnFuc = function () {
        _this.$message.warning({
          message: warning,
          type: 'error',
          duration: 1000
        })
      }
      if (data === null || data === '' || data.match(/^\s*$/)) {
        warning = '标签不能为空或全空格！'
        warnFuc()
        return false
      }
      if (data.length > 10) {
        warning = '长度不能超过10个字符！'
        warnFuc()
        return false
      }
      for (var item in this.post.labels) {
        if (this.post.labels[item] === data) {
          warning = '标签重复！'
          warnFuc()
          return false
        }
      }
      var pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>《》/?~！@#￥……&*（）——|{}【】‘；：”“'。，、？ ]")
      if (pattern.test(data)) {
        warning = '不允许含有特殊字符！'
        warnFuc()
        return false
      }
      if (this.post.labels.length === 3) {
        warning = '最多设置3个自定义标签！'
        warnFuc()
        return false
      }
      this.keyWord = null
      this.$set(this.post.labels, this.post.labels.length, data)
    },
    delLabel (index) {
      this.$delete(this.post.labels, index)
    },
    WidthCheck (str, maxLen) {
      var w = 0
      // length 获取字数数，不区分汉子和英文
      for (var i = 0; i < str.length; i++) {
        // charCodeAt()获取字符串中某一个字符的编码
        var c = str.charCodeAt(i)
        // 单字节加1
        if ((c >= 0x0001 && c <= 0x007e) || (c >= 0xff60 && c <= 0xff9f)) {
          w++
        } else {
          w += 2
        }
        if (w > maxLen) {
          return true
        }
      }
      return false
    },
    saveOrPublish (ifSave) {
      if (this.post.content.length < 2000) {
        var clearInitContent = this.postText
        // 第一步：去除空格&nbsp;
        clearInitContent = clearInitContent.replace(/&nbsp;/g, '')
        // console.log(clearInitContent)
        // 第二步：去除换行
        clearInitContent = clearInitContent.replace(/<br>/g, '')
        // console.log(clearInitContent)
        if (this.post.title === '' || clearInitContent === '') {
          this.$message.warning({
            message: '标题和帖子内容皆不能为空！',
            type: 'error',
            duration: 1800
          })
          return false
        } else if (this.WidthCheck(this.post.title, 50)) {
          this.$message.warning({
            message: '标题过长，超过限制长度！',
            type: 'error',
            duration: 1800
          })
          return false
        } else if (!(/.*[\u4e00-\u9fa5]+.*$/.test(clearInitContent)) && !(/[0-9a-z]/.test(clearInitContent))) { // 内容是否没有一个有效内容
          this.$message.warning({
            message: '全空格或未输入有意义内容！',
            type: 'error',
            duration: 1800
          })
          return false
        }
      } else if (this.post.content.length > 32768) {
        this.$message.warning({
          message: '内容太长！若是直接复制桌面文档，请改为右键纯文本粘贴！',
          type: 'error',
          duration: 1800
        })
        return false
      } else if (this.post.content.length > 2000) {
        if (this.post.title === '') {
          this.$message.warning({
            message: '标题不能为空！',
            type: 'error',
            duration: 1800
          })
          return false
        } else if (this.WidthCheck(this.post.title, 50)) {
          this.$message.warning({
            message: '标题过长，超过限制长度！',
            type: 'error',
            duration: 1800
          })
          return false
        }
      }
      this.ifPublish = true
      const _this = this
      api.publishPost('/forum/posts/publish', {
        title: _this.post.title,
        content: _this.post.content,
        labels: _this.post.labels,
        type: _this.post.type,
        if_save: ifSave,
        if_epic_post: this.post.if_epic_post
      }).then(function (res) {
        if (!ifSave) {
          if (res.success === 'true') {
            _this.ifPublish = false
            _this.$router.push({path: '/forums'})
            _this.$message.success({
              message: '发布成功！',
              type: 'success',
              duration: 1800
            })
            _this.ifPublish = false
          } else {
            _this.ifPublish = false
            _this.$message.success({
              message: '网络信号差！请稍后再试！',
              type: 'warning',
              duration: 1200
            })
          }
          api.publishPost('/forum/posts/publish', {
            title: '',
            content: '',
            labels: [],
            type: 0,
            if_save: true,
            if_epic_post: false
          }).then(function (res) {})
        } else {
          if (res.success === 'true') {
            _this.ifPublish = false
            _this.$message.success({
              message: '帖子保存成功！',
              type: 'success',
              duration: 1200
            })
          } else {
            _this.ifPublish = false
            _this.$message.success({
              message: '网络信号差！请稍后再试！',
              type: 'warning',
              duration: 1200
            })
          }
        }
      })
    },
    getPostSave () {
      const _this = this
      api.getPostSave('/forum/posts/publish', {}).then(function (res) {
        if (res.data.title !== '') {
          _this.post = res.data
          _this.saveContent = res.data.content
        }
      })
    },
    getListInfo () {
      const _this = this
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json !== 'undefined' && json !== null && json !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
        api.getInfoList('/users/detail', uid).then(function (res) {
          _this.info = res.data.user_info
        })
      } else {
        this.$router.push({path: '/login'})
      }
    }
  },
  created () {
    this.getListInfo()
    this.getPostSave()
  }
}
</script>

<style scoped>
  #postPublish{
    margin: 0 auto;
  }
  .postHead{
    overflow: auto;
    line-height: 50px;
    width: 100%;
    min-width: 1170px;
    background-color: white;
    padding-left: 40px;
    font-size: 14px;
    border-bottom:2px solid rgba(142, 204, 69, 1);
    cursor: default;
  }
  .postHead p {
    font-weight: 600;
  }
  .postHead-left{
    color: #D7D7D7;
    display: inline-block;
    cursor: pointer;
    transition: all .3s;
  }
  .postHead-left:hover{
    color: rgba(255, 153, 0, 1);
  }
  .postHead-right{
    display: inline-block;
    color: rgba(255, 153, 0, 1);
  }
  .postHead_block{
    color: #D7D7D7;
    display: inline-block;
  }
  .btn-save{
    color: white;
    float: right;
    font-size: 18px;
    line-height: 33px;
    width:122px;
    background-color: rgba(142, 204, 69, 1);
    padding-left: 25px;
    border-radius:10px 0 0 10px;
    margin-top: 10px;
    cursor: pointer;
    transition: all .3s;
  }
  .btn-save:hover {
    background-color: rgba(133, 193, 71, 1);
  }
  .btn-reset{
    color: white;
    float: right;
    font-size: 18px;
    line-height: 33px;
    width:122px;
    background-color: rgba(245, 108, 108, 1);
    padding-left: 25px;
    border-radius:10px;
    margin-top: 10px;
    margin-right: 10px;
    cursor: pointer;
    transition: all .3s;
  }
  .btn-reset:hover {
    background-color: rgb(245, 90, 90);
  }
  .postBody{
    width:950px;
    background-color: white;
    margin: 30px auto;
    padding: 25px;
    padding-bottom: 60px;
    border-radius: 10px;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
  }
  .postBody>.title{
    width:580px;
    height: 35px;
    background-color: rgba(242, 242, 242, 1);
    border-radius:10px;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 10px;
    font-weight: 300;
    font-size: 16px;
    color: #333333;
    transition: all .5s;
  }
  .postBody>.overTips{
    font-size: 12px;
    color: rgba(255, 5, 14, 0.5)
  }
  .btn {
    width: 66px;
    height: 33px;
    background: inherit;
    background-color: rgba(242, 242, 242, 1);
    border-radius: 10px;
    font-size: 14px;
    color: #C9C9C9;
    margin-left: 5px;
    cursor: pointer;
    transition: all .5s;
  }
  .btn-applyForGood {
    color: #fff;
    background-color: rgba(255, 153, 0, 1);
  }
  .btn-setType {
    color: #fff;
    background-color: rgba(100, 149, 237, 1);
  }
  .btn:hover, .title:hover {
    box-shadow:1px 2px 15px rgba(0, 0, 0, 0.15);
  }
  .labelAndTime{
    margin-top: 10px;
    padding-bottom: 6px;
    border-bottom: 1px solid rgba(242, 242, 242, 1);
  }
  .labelAndTime-title{
    font-weight: 600;
    font-style: normal;
    font-size: 12px;
    display: inline-block;
  }
  .labelAndTime input{
    width: 120px;
    font-size: 12px;
    padding: 0 22px 0 15px;
    line-height: 1.8em;
    border-radius: 0.9em;
    background-color: rgba(242, 242, 242, 1);
  }
  .labelAndTime>button{
    width: 20px;
    margin-left: -20px;
    font-size: 12px;
    color: rgba(102, 102, 102, 0.3);
    line-height: 1.8em;
    border-radius:0 0.9em 0.9em 0;
    background-color: rgba(242, 242, 242, 1);
    cursor: pointer;
    transition: all .2s;
  }
  .labelAndTime>button:hover{
    color: #fff;
    background-color: rgba(133, 193, 71, 1);
  }
  .labelAndTime ul {
    display: inline-block;
  }
  .labelAndTime .labels{
    font-size: 12px;
    display: inline-block;
    padding: 0 5px 0 15px;
    margin-left: 10px;
    line-height: 1.8em;
    border-radius: 0.9em;
    background-color: rgba(102, 102, 102, 0.2);
    position: relative;
  }
  .labelAndTime .labels>button{
    width: 20px;
    font-size: 12px;
    color: rgba(102, 102, 102, 0.2);
    line-height: 1.8em;
    border-radius:0 0.9em 0.9em 0;
    background-color: transparent;
    cursor: pointer;
    position: relative;
    right: -5px;
    transition: all .2s;
  }
  .labelAndTime .labels>button:hover{
    background-color: rgba(255, 153, 0, 1);
  }
  .labelAndTime .labels>button:hover i{
    color: #fff;
  }
  .labelAndTime .time{
    line-height: 25px;
    margin-bottom: 8px;
    float: right;
    line-height: 25px;
    font-size: 12px;
    color: #AEAEAE;
    display: block;
  }
  .btn-publish{
    font-size: 18px;
    color: white;
    float: right;
    line-height: 33px;
    width:122px;
    background-color: rgba(255, 153, 0, 1);
    padding-left: 30px;
    border-radius:10px 0 0 10px;
    margin-top: 10px;
    margin-right: -25px;
    cursor: pointer;
    transition: all .3s;
  }
  .btn-publish:hover{
    background-color: rgba(234, 140, 0, 1);
  }
  .editor {
    margin: 15px 3px 0 3px;
  }
</style>



