<template>
    <div class="quiz-btn">
      <quiz-Panel :width="width" :height="height" :radius="radius" :topLine="false" :downLine="false">
        <div v-loading="isLoading" class="quiz-btn_main" :style="getMainStyle" @mouseover="ifHoverBtn = true" @mouseleave="ifHoverBtn = false">
          <template v-if="ifIconWord">
            <span :style="getIconStyle" v-html="this.icon.split('-')[1]"></span>
          </template>
          <template v-else>
            <i :class="icon" :style="getIconStyle"></i>
          </template>
        </div>
      </quiz-Panel>
    </div>
</template>

<script>
import quizPanel from '@/base/quiz/quiz-panel'
export default {
  name: 'quiz-btn',
  props: {
    isLoading: {
      type: Boolean,
      default: false
    },
    width: {
      type: String,
      default: '40px'
    },
    height: {
      type: String,
      default: '40px'
    },
    radius: {
      type: String,
      default: 'default'
    },
    icon: {
      type: String
    },
    isCenter: {
      type: Boolean,
      default: true
    },
    size: {
      type: String,
      default: '18px'
    },
    theme: {
      type: String,
      default: 'DEFAULT'
    },
    isDisabled: {
      type: Boolean,
      default: false
    }
  },
  components: {
    quizPanel
  },
  computed: {
    ifIconWord () {
      return this.icon.split('-')[0] === 'content'
    },
    getMainStyle () {
      // 取色，前者是未聚焦颜色，后者是聚焦时颜色
      var colorPicker = {
        'DEFAULT': ['#fff', '#f9f9f9'],
        'BACK': ['#ff3333', '#e03232'],
        'CHOOSE': ['#87c141', '#7ab441'],
        'SURE': ['#ff9900', '#ef8e00'],
        'BLACK': ['#2a2a2a', '#1a1a1a'],
        'FORBID': ['#fff', '#f9f9f9'],
        'TRANSLUCENCY': ['rgba(42, 42, 42, 0.6)', 'rgba(26, 26, 26, 1)']
      }
      var color = this.isDisabled
        ? '#292929'
        : !colorPicker[this.theme]
          ? this.theme
          : this.ifHoverBtn ? colorPicker[this.theme][1] : colorPicker[this.theme][0]
      return {
        fontSize: this.size,
        backgroundColor: this.isDisabled ? '#a2a2a2' : this.theme ? color : '#fff',
        textAlign: this.isCenter ? 'center' : 'left',
        cursor: this.isDisabled ? 'no-drop' : 'pointer',
        lineHeight: '1.3em',
        padding: `calc(${this.height} / 2 - ${1.3 * this.icon.split('-')[1].split('<br>').length / 2}em) 0`
      }
    },
    getIconStyle () {
      var fontColorPicker = {
        'DEFAULT': '#292929',
        'FORBID': '#979797'
      }
      return {
        color: fontColorPicker[this.theme] ? fontColorPicker[this.theme] : '#fff'
      }
    }
  },
  data () {
    return {
      ifHoverBtn: false
    }
  }
}
</script>

<style scoped>
  .quiz-btn_main {
    width: 100%;
    height: 100%;
    transition: background-color .3s;
  }
</style>



