<template>
  <div class="quizDetail">
    <div class="quizDetail_header">
      <h3 class="quizDetail_header_title">{{isInitData ? quizInfo.title : 'Loading...'}}</h3>
      <div class="quizDetail_header_rightFlame" v-if="isShowTimer">
        <span class="quizDetail_rightFlame_status">{{getTimeStatus}}</span>
        <quiz-timer
          class="quizDetail_rightFlame_time"
          :initTime="quizInfo.start_time"
          :duration="quizInfo.duration"
          :advance="quizInfo.advance"
          :isBeginTimer="isBeginTimer"
          @notBeginTimerHandler="quizInfo.status = 0"
          @enterTimerHandler="quizInfo.status = 1"
          @endTimerHander="quizInfo.status = 2"></quiz-timer>
      </div>
    </div>
    <div class="quizDetail_container">
      <ul>
        <li v-loading="!isInitData">
          <quiz-panel
            title="考试信息"
            width="300px" height="530px"
            :padding="getMainPanelPadding">
            <div class="quizDetail_container_baseInfo">
              <p class="infoItem">考试课程：<span>{{quizInfo.course_name}}</span></p>
              <p class="infoItem">考试班级：
                <span v-for="(item, index) in quizInfo.className_list" :key="index">{{item}}
                  <template v-if="index !==  quizInfo.className_list.length-1">、</template>
                </span>
              </p>
              <p class="infoItem">考试类型：<span>{{getQuizType(quizInfo.type)}}</span></p>
              <p class="infoItem">考试语言：<span>{{getQuizLang(quizInfo.language)}}</span></p>
              <p class="infoItem">开考时间：<span>{{getQuizStartTime(quizInfo.start_time)}}</span></p>
              <p class="infoItem">考试时长：<span>{{quizInfo.duration}}分钟</span></p>
              <p class="infoItem">允许提前交卷时间：<span>{{quizInfo.advance}}分钟</span></p>
              <template v-if="info.identity === '1'">
                <p class="infoItem">出卷人：<span>{{quizInfo.manager}}</span></p>
              </template>
              <template v-if="info.identity === '2'">
                <p class="infoItem">设置范围提醒：<span>{{getRemindType(quizInfo.remind_type)}}</span></p>
                <p class="infoItem" v-if="quizInfo.remind_type !== 1">提醒时间：<span>考前{{quizInfo.remind_time}}天</span></p>
              </template>
              <template v-if="quizInfo.status === 3">
                <p class="infoItem">参与考试人数：<span>{{quizResult.attendants}}人( 缺考{{quizResult.absentee}}人 )</span></p>
                <div class="quizDetail_baseInfo_resultStat">
                  <span class="label">分数段人数统计</span>
                  <ul class="quizDetail_resultStat_container">
                    <li v-for="(item, index) in quizResult.scoreLevels" :key="index">
                      <span>{{item.level}}</span>
                      <div class="progress-bar">
                        <span class="solid-bar" :style="getProgressBarLength(quizResult.attendants + quizResult.absentee, item.amount)"></span>
                      </div>
                      <span>{{item.amount}}人</span>
                    </li>
                  </ul>
                </div>
              </template>
              <template v-else>
                <div class="quizDetail_baseInfo_resultStat">
                  <h5 class="notBeginTips">考试尚未开始，无更多信息</h5>
                </div>
              </template>
            </div>
          </quiz-panel>
        </li>
        <li v-if="isShowRange">
          <quiz-panel
            title="考试范围"
            width="300px" height="530px"
            :padding="getMainPanelPadding">
            <div class="quizDetail_container_quizRange" v-if="isInitData">
              <quiz-table :dataObj="quizRangeList" :isSelected="true" :isOnlyRead="true"></quiz-table>
              <!--不做提醒-->
              <div class="quizDetail_quizRange_part" v-if="quizInfo.remind_type === 1">
                <h5 class="quizDetail_quizRange_partLabel" style="text-align: center;">该考试不提醒考试范围</h5>
              </div>
              <!--大致范围-->
              <div class="quizDetail_quizRange_part" v-if="quizInfo.remind_type === 2">
                <h5 class="quizDetail_quizRange_partLabel">重点关注以下知识要点</h5>
                <div class="quizDetail_quizRange_partContent">
                  <template v-if="partRange.length > 0">
                  <span v-for="(item, index) in partRange" :key="index">
                    {{item}}
                    <span v-if="item.length !== 0 && index !== partRange.length - 1">、</span>
                  </span>
                  </template>
                  <template v-else>
                    <span class="allRange">全部</span>
                  </template>
                </div>
              </div>
              <!--详细范围-->
              <div class="quizDetail_quizRange_detail" v-if="quizInfo.remind_type === 3">
                <div class="quizDetail_quizRange_detailItem" v-for="(item, index) in quizRangeList.question_type" :key="index">
                  <div class="quizDetail_quizRange_detailLabel">{{getProTypeName(item.type)}}</div>
                  <div class="quizDetail_quizRange_detailContent">
                    <template v-if="item.range.length > 0">
                    <span v-for="(sonItem, sonIndex) in item.range" :key="sonIndex">
                      {{sonItem}}
                      <span v-if="item.range.length !== 0 && sonIndex !== item.range.length - 1">、</span>
                    </span>
                    </template>
                    <template v-else>
                      <span class="allRange">全部</span>
                    </template>
                  </div>
                </div>
              </div>
            </div>
          </quiz-panel>
        </li>
        <li v-if="quizInfo.status === 3">
          <quiz-panel
            title="排行榜"
            width="300px" height="530px"
            :padding="getMainPanelPadding">
            <ul class="quizDetail_container_rankList">
              <li v-for="(item, index) in quizRank" :key="index">
                <div class="index">
                  <span v-if="index === 0" style="color: rgb(248, 208, 28);"><i class="icon-diyi"></i></span>
                  <span v-else-if="index === 1" style="color: rgb(190, 197, 204);"><i class="icon-dier"></i></span>
                  <span v-else-if="index === 2" style="color: rgb(208, 153, 106);"><i class="icon-disan"></i></span>
                  <span v-else>{{index + 1}}</span>
                </div>
                <div class="userInfo">
                  <img :src="item.avatarUrl ? item.avatarUrl : defaultImage">
                  <div class="middleHelper"></div>
                  <span class="nickAndScore">
                  <span class="nick" @click="getInfo(item.uid)">{{item.nick}}</span>
                  <span class="score">
                    <template v-if="item.score === -1">缺考</template>
                    <template v-else>{{item.score}}分</template>
                  </span>
                </span>
                </div>
              </li>
            </ul>
          </quiz-panel>
        </li>
      </ul>
      <div style="text-align: right" v-show="!isShowReportCard">
        <div style="position: absolute; display: inline-block">
          <div class="quizDetail_rightBtns">
            <quiz-btn
              v-if="info.identity === '1' && quizInfo.status === 3"
              icon="content-我<br>的<br>答<br>题"
              size="16px"
              radius="default"
              theme="FORBID"
              width="45px"
              height="100px"
              @click.native="toQuizPreview(true)"></quiz-btn>
            <quiz-btn
              v-if="info.identity === '2'"
              icon="content-查<br>看<br>试<br>卷"
              size="16px"
              radius="default"
              theme="FORBID"
              width="45px"
              height="100px"
              @click.native="toQuizPreview()"></quiz-btn>
            <quiz-btn
              v-if="info.identity === '2' && quizInfo.status === 3"
              icon="content-成绩<br>详情"
              size="12px"
              radius="default"
              theme="BLACK"
              width="45px"
              height="45px"
              @click.native="openReportCard()"></quiz-btn>
            <quiz-btn
              icon="icon-fanhui2"
              size="20px"
              radius="default"
              theme="BACK"
              width="45px"
              height="45px"
              @click.native="backQuizList()"></quiz-btn>
          </div>
        </div>
      </div>
    </div>
    <transition name="table-fade">
    <div class="quizDetail_reportCard" v-if="isShowReportCard">
      <div class="quizDetail_reportCard_container">
        <quiz-panel
          height="520px"
          :padding="getMainPanelPadding"
          :topLine="false">
          <div class="quizDetail_reportCard_header">
            <h3>详细成绩单
              <span class="classChoose">
                <el-select v-model="curClass" placeholder="请选择班级" @change="changeRankList()">
                  <el-option
                    v-for="item in classList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
              </span>
            </h3>
          </div>
          <div class="quizDetail_reportCard_table">
            <table id="reportCard_table" cellspacing="0">
              <tr>
                <th
                  v-for="(item, index) in tableHeadList"
                  :key="index"
                  @click="sortData(index)"
                  onselectstart="return false;"
                  :style="(index <= 3 || index === 9) ? 'cursor: pointer;' : 'cursor: default;'">{{item}}</th>
              </tr>
              <tr v-for="(item, index) in quizDetailRank" :key="item.code">
                <td>{{index + 1}}</td>
                <td>{{item.nick}}</td>
                <td>{{item.code}}</td>
                <td>{{item.className}}</td>
                <template v-if="item.mark !== -1">
                  <td>{{item.score_detail.problem_XZ}}</td>
                  <td>{{item.score_detail.problem_TK}}</td>
                  <td>{{item.score_detail.problem_BC}}</td>
                  <td>{{item.score_detail.problem_WD}}</td>
                  <td>{{item.score_detail.problem_PD}}</td>
                  <td>{{item.mark}}</td>
                </template>
                <template v-else>
                  <td v-for="i in 5" :key="i"> </td>
                  <td><span style="color: rgb(255, 0, 0)">缺考</span></td>
                </template>
              </tr>
              <template v-if="quizDetailRank.length < 15">
                <tr v-for="i in (15 - quizDetailRank.length)" :key="i">
                  <!--<td>{{quizDetailRank.length + index + 1}}</td>-->
                  <td v-for="j in 10" :key="j"> </td>
                </tr>
              </template>
            </table>
          </div>
        </quiz-panel>
        <div style="text-align: right">
          <div style="position: absolute; display: inline-block">
            <div class="quizDetail_rightBtns">
              <quiz-btn
                v-if="info.identity === '2' && quizInfo.status === 3"
                icon="icon-daochu"
                size="20px"
                radius="default"
                theme="BLACK"
                width="45px"
                height="45px"
                @click.native="export2Excel()"></quiz-btn>
              <quiz-btn
                icon="icon-fanhui2"
                size="20px"
                radius="default"
                theme="BACK"
                width="45px"
                height="45px"
                @click.native="isShowReportCard = false"></quiz-btn>
            </div>
          </div>
        </div>
      </div>
    </div>
    </transition>
    <el-dialog
      title="学生信息"
      :visible.sync="isShowUserInfo"
      width="60%">
      <Information :information="showerId"></Information>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isShowUserInfo = false">取 消</el-button>
        <el-button type="primary" @click="isShowUserInfo = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Information from '@/base/infomation/infomation'
import quizPanel from '@/base/quiz/quiz-panel'
import quizTable from '@/base/quiz/quiz-table'
import quizBtn from '@/base/quiz/quiz-btn'
import quizTimer from '@/base/quiz/quiz-timer'
import api from '@/api/quiz'
import { generateArray, exportJsonToExcel } from '@/utils/excel/Export2Excel'
export default {
  name: 'quizDetail',
  components: {
    Information,
    quizPanel,
    quizTable,
    quizBtn,
    quizTimer
  },
  computed: {
    getTimeStatus () {
      var statusList = {
        0: '离考试开始还剩：',
        1: '离考试结束还剩：',
        2: '考试已结束，等待教师批阅。',
        3: '考试已结束，成绩已公布。'
      }
      return this.quizInfo.start_time == null
        ? '等待教师手动开启'
        : statusList[this.quizInfo.status]
    },
    getMainPanelPadding () {
      return '10px 15px'
    }
  },
  data () {
    return {
      quizId: -1,
      info: {},
      isInitData: false,
      isShowRange: false,
      isShowUserInfo: false,
      isShowReportCard: false,
      isShowTimer: false,
      isBeginTimer: false,
      quizInfo: {},
      quizResult: {},
      quizRangeList: {},
      quizRank: [],
      quizDetailRank: [],
      partRange: [], // 简单提醒时合并的提醒范围
      curClass: '全部班级',
      classList: [{
        id: -1,
        name: '全部班级'
      }],
      showerId: '',
      defaultImage: require('../../../static/image/acmlogo.jpg'),
      tableHeadList: ['序号', '学生姓名', '学号', '所在班级', '选择题', '填空题', '编程题', '问答题', '判断题', '总成绩']
    }
  },
  methods: {
    sortData (index) {
      const params = {
        1: 'nick',
        2: 'code',
        3: 'className',
        9: 'mark'
      }[index]
      if (this.quizDetailRank.length < 2 || !params) return
      const first = this.quizDetailRank[0][params]
      const last = this.quizDetailRank[this.quizDetailRank.length - 1][params]
      this.quizDetailRank.sort((a, b) => first > last ? a[params] - b[params] : b[params] - a[params])
    },
    backQuizList () {
      this.$router.push({name: 'PersonalCenter', params: {isShow: 4}})
    },
    export2Excel () {
      require.ensure([], () => {
        exportJsonToExcel(this.tableHeadList, generateArray('#reportCard_table'), this.quizInfo.title)
      })
    },
    getInfo (uid) {
      this.showerId = null
      this.isShowUserInfo = true
      this.showerId = uid
    },
    // 打开详细成绩单
    openReportCard () {
      // 调用接口获取详细成绩单数据
      if (this.quizDetailRank.length === 0) { // 只请求一次，节省网络资源
        this.getDetailRankData()
      }
      this.isShowReportCard = true
    },
    // 成绩单改变班级
    changeRankList () {
      if (this.curClass === -1) {
        // 调用取得全部参加学生数据接口
        this.getDetailRankData()
      } else {
        // 取得id调用取得该班级参加学生数据接口
        this.getDetailRankData(this.curClass)
      }
    },
    toQuizPreview (isResult = false) {
      var params = {
        id: this.quizId
      }
      if (this.info.identity === '1' && isResult) params.uid = this.info.uid
      this.$router.push({
        name: 'quizPreview',
        params: params,
        query: {
          redirect: '/quizDetail/' + this.quizId
        }
      })
    },
    getProTypeName (type) {
      return {
        1: '填空题',
        2: '选择题',
        3: '编程题',
        4: '问答题',
        5: '判断题'
      }[type]
    },
    getProgressBarLength (all, part) {
      return {
        width: Math.round(part / all * 100) + '%'
      }
    },
    getQuizStartTime (time) {
      if (time == null) return '等待手动开启'
      return time.replace('T', ' ')
    },
    getQuizType (type) {
      return {
        1: '平时考试',
        2: '期中考试',
        3: '期末考试'
      }[type]
    },
    getQuizLang (lang) {
      return {
        0: 'C语言',
        1: 'C++',
        3: 'Java语言',
        6: 'Python语言'
      }[lang]
    },
    getRemindType (type) {
      return {
        1: '不提醒',
        2: '简洁提醒',
        3: '详细提醒'
      }[type]
    },
    getListInfo () {
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json !== 'undefined' && json !== null && json !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
        api.getInfoList('/users/detail', uid).then((res) => {
          this.info = res.data.user_info
          this.getQuizId()
          this.getQuizInfo()
        })
      }
    },
    async getRankData () {
      const result = await api.getResultRank('/quiz/rank', {
        quizId: this.quizId,
        size: 8
      })
      this.quizRank = result.data.rank_list
    },
    async getDetailRankData (classId = -1) {
      const result = await api.getResultRankDetail('/quiz/students', this.quizId, classId)
      this.quizDetailRank = result.data.score_list
    },
    getQuizId () {
      this.quizId = this.$route.params.id
    },
    async getQuizInfo () {
      const result = await api.getQuizDetailInfo('/quiz', this.quizId)
      if (result.success === 'false') {
        this.$router.push({path: '/personal-center/1'})
        return
      }
      const data = result.data
      this.quizInfo = data.quiz_info.quiz_info
      // 获取班级列表数据
      this.quizInfo.classId_list.forEach((item, index) => {
        this.classList.push({
          id: item,
          name: this.quizInfo.className_list[index]
        })
      })
      // 判断是否是手动开启的考试
      this.isBeginTimer = this.quizInfo.start_time != null
      this.isShowTimer = true
      this.quizResult = data.quiz_info.result.length === 0 ? null : data.quiz_info.result
      this.quizRangeList = {
        id: data.quiz_info.range_list.id,
        name: data.quiz_info.range_list.title
      }
      this.quizRangeList.question_type = data.quiz_info.range_list.detail.map(i => ({
        type: i.type,
        question_num: i.amount,
        type_bonus: i.value,
        range: i.range
      }))
      // 判断是否是简单提醒，合并提醒范围
      if (this.quizInfo.remind_type === 2) {
        this.quizRangeList.question_type.forEach(typeItem => {
          typeItem.range.forEach(item => {
            if (this.partRange.indexOf(item) < 0) {
              this.partRange.push(item)
            }
          })
        })
      }
      // 如果是老师直接展示
      if (this.info.identity === '2') {
        this.isShowRange = true
      } else {
        // 如果是学生且考试时间已经允许提醒
        if (this.quizInfo.start_time != null) {
          var now = new Date().getTime()
          var startTime = new Date(this.quizInfo.start_time).getTime()
          var advanceTime = this.quizInfo.remind_time * 24 * 60 * 60 * 1000
          if (this.quizInfo.result >= 2 || startTime - now <= advanceTime) {
            this.isShowRange = true
          }
        }
      }
      this.isInitData = true
      // 如果考试已经公布, 调用接口获取排行榜信息
      if (this.quizInfo.status === 3) {
        this.getRankData()
        this.getDetailRankData()
      }
    }
  },
  created () {
    this.getListInfo()
  }
}
</script>

