    padding: 0 10px 10px 10px;
  }
  .quizDetail_quizRange_partLabel {
    color: rgb(204, 204, 211);
    font-size: 13px;
    line-height: 2;
    transition: all .5s;
  }
  .quizDetail_quizRange_partContent {
    min-height: 45px;
    font-size: 12px;
    background-color: rgba(242, 242, 242, 1);
    padding: 5px 10px;
    transition: all .5s;
  }
  .quizDetail_quizRange_part:hover .quizDetail_quizRange_partLabel {
    color: rgb(161, 161, 168);;
  }
  .quizDetail_quizRange_part:hover .quizDetail_quizRange_partContent {
    background-color: rgba(232, 232, 232, 1);
  }
  /*详细范围提示的样式设置*/
  .quizDetail_quizRange_detail {
    max-height: 280px;
    overflow-y: auto;
    overflow-x: hidden;
  }
  /*定义滚动条轨道*/
  .quizDetail_quizRange_detail::-webkit-scrollbar-track
  {
    background-color: #f9f9f9;
    -webkit-box-shadow: inset 0 0 6px transparent;
  }
  /*定义滚动条高宽及背景*/
  .quizDetail_quizRange_detail::-webkit-scrollbar
  {
    width: 10px;
    background-color: #333;
  }
  /*定义滚动条*/
  .quizDetail_quizRange_detail::-webkit-scrollbar-thumb
  {
    background-color: #8b8b8b;
    border-radius: 10px;
  }
  .quizDetail_quizRange_detailItem {
    margin: 8px 5px 0 0;
    display: flex;
    cursor: default;
  }
  .quizDetail_quizRange_detailLabel {
    flex: 0 0 auto;
    width: 80px;
    line-height: 45px;
    text-align: center;
  }
  .quizDetail_quizRange_detailContent {
    min-height: 45px;
    font-size: 12px;
    flex: 1 1 auto;
    background-color: rgba(242, 242, 242, 1);
    padding: 3px 5px;
    transition: all .5s;
  }
  .quizDetail_quizRange_detailContent:hover {
    background-color: rgba(232, 232, 232, 1);
  }
  .allRange {
    color: rgb(204, 204, 204);
    display: block;
    line-height: 40px;
    text-align: center;
  }
  /*排行榜样式设置*/
  .quizDetail_container_rankList {
    padding: 5px 10px;
  }
  .quizDetail_container_rankList>li {
    overflow: hidden;
    height: 50px;
    margin: 8px 0;
  }
  .quizDetail_container_rankList>li .index {
    display: inline-block;
    width: 30px;
    line-height: 50px;
    float: left;
    text-align: center;
  }
  .quizDetail_container_rankList>li .userInfo {
    height: 50px;
    margin-left: 35px;
    padding: 0 10px;
    background-color: rgb(242, 242, 242);
    border-radius: 10px;
    position: relative;
    transition: all .3s;
  }
  .quizDetail_container_rankList>li .userInfo:hover {
    box-shadow: -2px 2px 15px rgba(0, 0, 0, 0.1);
    transform: translateY(-2px);
  }
  .quizDetail_container_rankList>li .userInfo img{
    width: 36px;
    vertical-align: middle;
    border-radius: 5px;
  }
  .quizDetail_container_rankList>li .middleHelper {
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }
  .quizDetail_container_rankList>li .nickAndScore {
    font-size: 13px;
    display: inline-block;
    cursor: default;
  }
  .quizDetail_container_rankList>li .nickAndScore>.nick {
    display: inline-block;
    max-width: 110px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: middle;
    cursor: pointer;
    transition: all .1s;
  }
  .quizDetail_container_rankList>li .nickAndScore>.nick:hover {
    color: #7ab441;
  }
  .quizDetail_container_rankList>li .nickAndScore>.score {
    position: absolute;
    line-height: 50px;
    right: 8px;
    top: 0;
  }
  /*右侧按钮样式设置*/
  .quizDetail_rightBtns {
    position: fixed;
    transform: translate(0%, -100%);
  }
  .quizDetail_rightBtns .quiz-btn {
    margin: 8px 0;
  }
  /*详细成绩单栏目样式设置*/
  .quizDetail_reportCard {
    position: fixed;
    width: 100vw;
    height: calc(100vh - 60px - 50px);
    top: 110px;
    left: 0px;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .quizDetail_reportCard_container {
    width: 984px;
    margin: 33px auto 0 auto;
  }
  .quizDetail_reportCard_container .quiz-panel {
    margin: 0 15px;
  }
  .quizDetail_reportCard_header h3 {
    text-align: center;
    font-size: 23px;
    line-height: 2;
  }
  .quizDetail_reportCard_header .classChoose {
    display: inline-block;
    position: absolute;
    right: 10px;
    top: 5px;
  }
  /*成绩单表格样式设置*/
  .quizDetail_reportCard_table {
    height: 430px;
    margin: 10px 15px 0 15px;
    overflow-y: auto;
    border-bottom: 3px solid rgb(242, 242, 242);
  }
  .table-fade-enter-to, .table-fade-leave-to {
    opacity: 1;
    transition: all .5s;
  }
  .table-fade-enter, .table-fade-leave-to{
    opacity: 0;
  }
  /*定义滚动条轨道*/
  .quizDetail_reportCard_table::-webkit-scrollbar-track
  {
    background-color: #f9f9f9;
    -webkit-box-shadow: inset 0 0 6px transparent;
  }
  /*定义滚动条高宽及背景*/
  .quizDetail_reportCard_table::-webkit-scrollbar
  {
    width: 10px;
    background-color: #333;
  }
  /*定义滚动条*/
  .quizDetail_reportCard_table::-webkit-scrollbar-thumb
  {
    background-color: #8b8b8b;
    border-radius: 10px;
  }
  .quizDetail_reportCard_table table{
    width: 100%;
  }
  .quizDetail_reportCard_table table>tr:nth-child(even) {
    background-color: rgb(242, 242, 242);
  }
  .quizDetail_reportCard_table table>tr th,
  .quizDetail_reportCard_table table>tr td {
    line-height: 2;
    text-align: center;
  }
  .quizDetail_reportCard_table table>tr th {
    color: rgb(102, 102, 102);
  }
  .quizDetail_reportCard_table table>tr td {
    font-size: 13px;
    width: 80px;
  }
  .quizDetail_reportCard_table table>tr td:first-child {
    height: 2em;
  }
</style>



