<template>
    <div class="myEssay">
      <div class="myEssay-header">
        <span>我的帖子</span>
        <button class="btn-publish" @click="toPublish">发布</button>
      </div>
      <div class="myEssay-body">
        <ul>
          <li v-for="(item , index) in post_list" :key="index" class="myEssay-item">
            <span class="index">{{pagesize*(page-1)+index+1}}</span>
            <div class="myEssay-item-top">
              <div class="myEssay-item-main">
                <h5 class="title">
                  <span  style="cursor: pointer" v-html="item.title" @click="toPostDetail(item.post_id)"></span>
                  <span class="tags tags-essence" v-if="item.type === 2">精华</span>
                  <span class="tags tags-notice" v-if="item.type === 3">系统公告</span>
                  <span class="tags tags-guide" v-if="item.type === 4">新手指南</span>
                  <span class="tags" v-if="item.title.length < 28" v-for="(subItem, index) in item.labels" :key="index">{{subItem}}</span>
                </h5>
                <h5 class="publishDate">{{item.publish_date.replace('T', ' ')}}</h5>
              </div>
              <div class="myEssay-item-statBox">
                <div class="statBox-item" style="color: rgba(142, 204, 69, 1)">
                  <h5 >{{item.likes}}</h5><span>点赞</span>
                </div>
                <div class="statBox-item" style="color: rgba(255, 153, 0, 1)">
                  <h5>{{item.replies}}</h5><span>评论</span>
                </div>
                <div class="statBox-item">
                  <h5>{{item.views}}</h5><span>浏览</span>
                </div>
              </div>
            </div>
            <div class="myEssay-item-bottom">
              <template v-if="item.new_rep && item.new_rep.is">
                <p class="reply btn-newMsg">
                  <span  style="color: rgba(234, 140, 0, 1)">有最新评论</span>
                  &nbsp;&nbsp;&nbsp;
                  <span class="repContent ud-editor-repContent" v-html="item.new_rep.content"></span>
                </p>
                <div class="myEssay-item-oper">
                  <span class="repTime">{{getDate(item.new_rep.date)}}</span>
                  <span class="btn-goRep" @click="toPostDetail(item.post_id)">前往回复</span>
                  <span class="separator">|</span>
                  <i class="el-icon-delete btn-del" @click="delPost(item.post_id)"></i>
                </div>
              </template>
              <template v-else>
                <p class="reply"><span>无最新评论</span></p>
                <div class="myEssay-item-oper">
                  <i class="el-icon-delete btn-del" @click="delPost(item.post_id)"></i>
                </div>
              </template>
            </div>
          </li>
        </ul>
        <span class="noList" v-if="total < 5">-&nbsp;无更多帖子，快<span class="refresh-btn" @click="toPublish">前往发布</span>&nbsp;-</span>
      </div>
      <div class="myEssay-footer MyEssayPage" v-if="total >= 5">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page"
          :page-sizes="[5, 10, 20]"
          :page-size="pagesize"
          background
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
</template>

<script>
import api from '@/api/personCenter'

export default {
  name: 'myEssay',
  props: {
    userId: {
      type: String
    }
  },
  data () {
    return {
      page: 1,
      pagesize: 10,
      total: 0,
      post: {},
      post_list: []
    }
  },
  methods: {
    toPublish () {
      this.$router.push({path: '/postPublish'})
    },
    toPostDetail (targetId) {
      this.$router.push({name: 'postDetail', params: {id: targetId}})
    },
    handleSizeChange (val) {
      this.page = 1
      this.pagesize = val
      this.getPosts()
    },
    handleCurrentChange (val) {
      this.page = val
      this.getPosts()
    },
    getPosts () {
      const _this = this
      api.getUserPosts('/forum/posts/user_post', {
        user_id: _this.userId,
        page: _this.page,
        pagesize: _this.pagesize
      }).then(function (res) {
        _this.post_list = res.data.post_list
        _this.total = res.total
      })
    },
    delPost (postId) {
      const _this = this
      this.$confirm('此操作将永久删除该帖子, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        api.deletePost('/forum/posts/publish', postId).then(function (res) {
          if (res.success === 'true') {
            _this.$message({
              type: 'success',
              message: '删除成功!'
            })
            _this.getPosts()
          }
        })
      }).catch(() => {

      })
    },
    getDate (date) {
      var postDate = new Date(date.replace('T', ' '))
      var now = new Date()
      var yearD = now.getFullYear() - postDate.getFullYear()
      var monthD = now.getMonth() - postDate.getMonth()
      var dayD = now.getDate() - postDate.getDate()
      var returnDate = (yearD > 0) ? yearD + '年前' : (yearD === 0 && monthD > 0) ? monthD + '月前' : (yearD === 0 && monthD === 0) ? dayD + '天前' : date.replace('T', ' ')
      var d = now.getTime() - postDate.getTime()
      if (d < 60 * 1000) {
        return Math.floor(d / 1000) + '秒前'
      } else if (d < 60 * 60 * 1000) {
        return Math.floor(d / (60 * 1000)) + '分钟前'
      } else if (d < 60 * 60 * 1000 * 24) {
        return Math.floor(d / (60 * 60 * 1000)) + '小时前'
      } else {
        return returnDate
      }
    }
  },
  created () {
    this.getPosts()
  }
}
</script>

<style scoped>
@import "../../../static/editorCss/style.css";
.myEssay{
  min-height: 700px;
  background-color: white;
  padding: 20px 20px;
}
.myEssay-header{
  padding-bottom: 5px;
  border-bottom: 1px solid gainsboro;
  line-height: 40px;
  font-size: 20px;
}
.btn-publish{
  border: none;
  padding: 10px 40px;
  background: #f48118;
  border-radius: 10px;
  color: white;
  float: right;
  cursor: pointer;
}
.btn-publish:hover{
  background-color: #d48118;
}
.myEssay-body {
  height: 560px;
  overflow-y: auto;
  padding: 10px 5px;
}
.myEssay-body .noList {
  line-height: 3em;
  display: block;
  font-weight: 300;
  font-size: 12px;
  color: #AEAEAE;
  text-align: center;
  cursor: default;
}
.myEssay-body .refresh-btn {
  cursor: pointer;
}
.myEssay-body .refresh-btn:hover {
  color: #FF9900;
}
.myEssay-body .myEssay-item {
  width: 100%;
  background-color: rgba(240, 240, 240, 0.2);
  border-radius: 10px;
  box-shadow: -2px 5px 5px rgba(0, 0, 0, 0.1);
  position: relative;
  top: 0px;
  margin: 10px 0;
  overflow: hidden;
  transition: all .3s;
}
.myEssay-body .myEssay-item:hover {
  background-color: rgba(240, 240, 240, 0.5);
  top: -3px;
}
.myEssay-item .index {
  font-size: 13px;
  font-weight: 700;
  color: #cccccc;
  position: absolute;
  top: 3px;
  left: 5px;
}
.myEssay-item-top {
  display: flex;
  padding: 10px 0;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap;
}
.myEssay-item .myEssay-item-main {
  width: 650px;
  padding-left: 20px;
  cursor: default;
}
.myEssay-item-main .title {
  width: 600px;
  font-size: 16px;
  font-weight: 600;
  line-height: 38px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.myEssay-item-main .title:first-child {
  cursor: pointer;
}
.myEssay-item-main .tags{
  height: 1.4em;
  font-weight: 300;
  font-size: 12px;
  text-align: center;
  line-height: 1.4em;
  color: #FFFFFF;
  background-color: rgba(204, 204, 204, 1);;
  display: inline-block;
  border-radius: 0.2em;
  padding: 0 0.4em;
  margin: 0 0.2em;
  transition: all .3s;
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0);
}
.myEssay-item-main .tags:hover{
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.1);
}
.myEssay-item-main .tags-essence {
  background-color: rgba(255, 5, 14, 0.8)
}
.myEssay-item-main .tags-notice {
  background-color: rgba(100, 149, 237, 0.8);
}
.myEssay-item-main .tags-guide {
  background-color: rgba(255, 153, 0, 0.8);
}
.myEssay-item-main .publishDate {
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #AEAEAE;
}
.myEssay-item-statBox {
  width: 180px;
  display: -webkit-flex; /* Safari */
  padding: 0 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.myEssay-item-statBox>.statBox-item{
  width: 60px;
  font-weight: 600;
  font-size: 16px;
  color: #868686;
  line-height: 25px;
  text-align: center;
  cursor: pointer;
}
.myEssay-item-statBox>.statBox-item>h5{
  margin: 0 10px;
  position: relative;
}
.myEssay-item-statBox>.statBox-item>span{
  font-size: 12px;
}
.myEssay-item-bottom {
  border-top: 1px solid rgba(230, 230, 230, 1);
  padding: 3px 20px 0 20px;
  font-size: 12px;
  color: #cccccc;
  cursor: default;
}
.myEssay-item-bottom p,
.myEssay-item-bottom span {
  line-height: 20px;
}
.myEssay-item-bottom .repContent {
  display: inline-block;
}
>>> .myEssay-item-bottom  .repContent img{
  height: 20px;
  vertical-align: top;
  padding: 0 2px;
}
.myEssay-item-bottom .reply {
  display: inline-block;
  width: 550px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.myEssay-item-bottom .btn-newMsg {
  cursor: pointer;
  transition: all .3s;
}
.myEssay-item-bottom .btn-newMsg:hover {
  color: #a0a0a0;
}
.myEssay-item-bottom .myEssay-item-oper {
  float: right;
}
.myEssay-item-oper .repTime {
  padding-right: 20px;
}
.myEssay-item-oper .separator {
  padding: 0 10px;
}
.myEssay-item-oper .btn-goRep,
.myEssay-item-oper .btn-goSign{
  cursor: pointer;
}
.myEssay-item-oper .btn-goRep:hover {
  color: rgba(234, 140, 0, 1);
}
.myEssay-item-oper .btn-goSign:hover {
  color: rgb(245, 108, 108);
}
.myEssay-item-oper .btn-del {
  cursor: pointer;
}
.myEssay-item-oper .btn-del:hover {
  color: rgb(245, 108, 108);
}
.MyEssayPage{
  padding-top: 20px;
}
.MyEssayPage /deep/ .el-pagination .el-pager .active{
  background-color: #f48118 !important;
}
.MyEssayPage /deep/ .el-pagination .el-pagination__total{
  color: inherit;
}
.MyEssayPage /deep/ .el-pagination .el-pagination__jump{
  color: inherit;
}
</style>



