import {
  getRequest,
  postFromData,
  deleteRequest,
  postFromDataImg,
  putFromData,
  postRequest,
  putRequest,
  putJsonData,
  postJsonData,
  delFromData
} from './config'

function getInfoList (link, data) {
  return getRequest(link, {
    uid: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getCourseList (link) {
  return getRequest(link).then((res) => {
    return Promise.resolve(res.data)
  })
}

function postUserImg (link, data) {
  return postFromDataImg(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getUserFeedback (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getCoursesTest (link, courseId) {
  return getRequest(link, {
    courses_id: courseId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getTestProblems (link, examId) {
  return getRequest(link, {
    exam_id: examId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getClassList (link, obj) {
  return getRequest(link, {
    page: obj.page,
    pagesize: obj.pagesize,
    academy_id: obj.academy_id,
    grade: obj.grade
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getClassStatsList (link, obj) {
  return getRequest(link, {
    class_id: obj.class_id,
    start_date: obj.start_date,
    end_date: obj.end_date
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getStudentsStatsList (link, obj) {
  return getRequest(link, {
    code: obj.code,
    start_date: obj.start_date,
    end_date: obj.end_date
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getExaminationList (link, data) {
  return getRequest(link, {
    uid: data.uid,
    courses_id: data.coursesId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getStudentTestList (link, obj) {
  return getRequest(link, {
    exam_id: obj.examId,
    class_id: obj.classId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function addCourses (link, data) {
  return postFromData(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function updateCourses (link, data) {
  return putRequest(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function updateUserInfo (link, data) {
  return putFromData(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function putUpdateTestInfo (link, data) {
  return putFromData(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function deleteCourse (link, id) {
  return deleteRequest(link, {
    courses_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function deleteTest (link, examID) {
  return deleteRequest(link, {
    exam_id: examID
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function addClassToCourse (link, data) {
  return postFromData(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function updatePassword (link, data) {
  return postRequest(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getClassByCoursesId (link, id) {
  return getRequest(link, {
    courses_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getQuizStudents (link, obj) {
  return getRequest(link, {
    class_id: obj.class_id,
    quiz_id: obj.quiz_id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function putPublicationResults (link, quizId) {
  return putRequest(link, {
    quiz_id: quizId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getUserPosts (link, data) {
  return getRequest(link, {
    user_id: data.user_id,
    page: data.page,
    pagesize: data.pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}
function getNoticeList (link, data) {
  return getRequest(link, {
    page: data.page,
    pagesize: data.pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getRepliesList (link, data) {
  return getRequest(link, {
    page: data.page,
    pagesize: data.pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function publishContribution (link, data) {
  return postJsonData(link, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getUserContribution (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

function sendReply (link, data) {
  return postJsonData(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function setHadRead (link, data) {
  return putJsonData(link, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function deletePost (link, postId) {
  return delFromData(link, {
    post_id: postId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getChildReplies (link, data) {
  return getRequest(link, {
    reply_id: data.reply_id,
    page: data.page,
    pagesize: data.pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getTopPostsList (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

const api = {
  getInfoList,
  getCourseList,
  postUserImg,
  getUserFeedback,
  getCoursesTest,
  getTestProblems,
  addCourses,
  updateCourses,
  deleteCourse,
  getClassList,
  getClassStatsList,
  getStudentsStatsList,
  deleteTest,
  putUpdateTestInfo,
  addClassToCourse,
  getExaminationList,
  getStudentTestList,
  updateUserInfo,
  updatePassword,
  getClassByCoursesId,
  getQuizStudents,
  putPublicationResults,
  getUserPosts,
  getNoticeList,
  getRepliesList,
  publishContribution,
  getUserContribution,
  sendReply,
  setHadRead,
  deletePost,
  getChildReplies,
  getTopPostsList
}

export default api



