<!--suppress ALL -->
<template>
  <div class="mycourse-module">
    <div v-if="identity == 2" class="add-pad">
      <el-button type='primary' @click="openAddCourse">添加课程</el-button>
    </div>
    <div class="courseDate-table">
    <el-table
      :data="courseData"
      stripe
      style="width: 100%;"
      row-key="courses_id"
      :expand-row-keys="expands"
      @row-click="rowClick"
      @row-mouse-leave="ifClick=false;"
      @cell-mouse-enter="rowMouseEnter"
      @row-dblclick="rowDoubleClick">
      <el-table-column type="expand" v-if="identity == 2">
        <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item>
                  <div class="title">
                    <h5>对应班级</h5>
                  </div>
                  <div class="labelBox">
                    <span class="class-item" v-for="(item, index) in props.row.class_name" :key="index" :title="item"
                          @mouseover="showDelClass.classId=props.row.class_id[index]; showDelClass.rowId=props.row.courses_id;"
                          @mouseleave="showDelClass.classId=-1; showDelClass.rowId=-1"
                          @click="getClassInfo(props.row.class_id[index])">{{item}}
                      <transition name="closeBtn">
                      <i  class="el-icon-close" v-if="showDelClass.classId===props.row.class_id[index] && showDelClass.rowId===props.row.courses_id"
                         @click.stop="delClass(props.row.courses_id, props.row, props.row.class_id[index])"></i>
                      </transition>
                    </span>
                    <span  class="class-item" @click="editClass(props.row.courses_id, props.row)"><i class="el-icon-plus"></i></span>
                  </div>
              </el-form-item>
            </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="grade"
        align="center"
        width="120"
        label="学年">
      </el-table-column>
      <el-table-column
        prop="term"
        align="center"
        width="120"
        label="学期">
        <template slot-scope="scope">
          <span>第{{scope.row.term}}学期</span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label="课程名">
        <template slot-scope="scope">
          <span  type="text" size="small" style="cursor: pointer">{{scope.row.courses_name}}</span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        width="280"
        label="操作">
        <template slot-scope="scope">
          <el-button @click="messageOpen(scope.row)" v-if="identity == 2" type="danger" icon="el-icon-delete" size="small"></el-button>
          <el-button type="primary" icon="el-icon-document" size="small" @click="detail(scope.row)">测</el-button>
          <el-button type="primary" icon="el-icon-tickets" size="small" @click="toQuiz(scope.row)">考</el-button>
          <el-button v-if="identity == 2" type="success" icon="el-icon-edit" size="small" @click="editQuiz(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    </div>
    <transition name="stats-fade">
      <classInformation :classInfoId="classInfoId" v-if="classInfoVisible" @closeMsg="getCloseMsg"></classInformation>
    </transition>
    <el-dialog
      title="请选择要绑定班级"
      :visible.sync="classDialog"
      width="40%">
      <div class="tree-box" v-loading="classPage.loading">
        <el-tree
          :data="classData"
          show-checkbox
          :check-on-click-node="true"
          node-key="id"
          ref="addClassTree"
          :default-checked-keys="checkedClass"
          :props="defaultProps"
          @check-change="handleCheckChange">
        </el-tree>
      </div>
      <classPagination
        :pageTotal="classPage.total"
        :page="classPage.page"
        @currentPage="handleCurrentPage">
      </classPagination>
      <div class="selected-class">
        <div v-for="(item, index) in addClassArr" :key="index" class="list-item">
          {{ item.label }}
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="classDialog = false">取 消</el-button>
        <el-button type="primary" @click="editClassToCourse">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      :title="(addOrEdit==='add')?'添加课程':'课程编辑'"
      :visible.sync="dialogVisible"
      width="30%">
      <el-form class="addFrom" :model="newCourseFrom">
        <el-form-item label="学年">
          <el-input
            placeholder="请输入学年,格式如:2015-2016"
            v-model="newCourseFrom.schoolYear"></el-input>
        </el-form-item>
        <el-form-item label="学期" class="semester">
          <el-select v-model="newCourseFrom.semester" placeholder="请选择">
            <el-option
              v-for="item in semesterOption"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item  label="课程名">
          <el-input
            placeholder="输入课程名"
            v-model="newCourseFrom.courseName"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible=false">取 消</el-button>
        <template v-if="addOrEdit==='add'">
          <el-button type="primary" @click="addCourse">添加课程</el-button>
        </template>
        <template v-else>
          <el-button type="primary" @click="editCourse">编辑完成</el-button>
        </template>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from '@/api/personCenter'
import classPagination from '@/base/common/pagination.vue'
import classInformation from '@/base/personal/classInformation'

export default {
  props: {
    identity: {
      type: String,
      default: ''
    },
    courseData: {
      type: Array
    }
  },
  components: {
    classPagination,
    classInformation
  },
  data () {
    return {
      classData: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      classDialog: false,
      dialogVisible: false,
      // 判断是添加还是编辑
      addOrEdit: 'add',
      newCourseFrom: {
        schoolYear: '',
        semester: '',
        courseName: ''
      },
      semesterOption: [{
        value: '1',
        label: '第一学期'
      }, {
        value: '2',
        label: '第二学期'
      }
      ],
      courseTable: [{
        year: '2015-2016',
        term: '第一学期',
        courseName: '如何变帅',
        className: '18软件',
        operating: 1,
        courseId: '001'
      }],
      addId: -1,
      classPage: {
        total: 1,
        page: 1,
        size: 10,
        loading: false
      },
      addClassArr: [],
      checkedClass: [],
      havedChoosedClass: [],
      expands: [],
      // 用户是否单击了
      ifClick: -1,
      // 用户是否双击了
      doubleClick: false,
      // 用于班级选中删除
      showDelClass: {
        classId: -1,
        rowId: -1
      },
      classInfoId: '',
      classInfoVisible: false
    }
  },
  methods: {
    getCloseMsg (res) {
      this.classInfoVisible = res
    },
    toQuiz (row) {
      // 将数据存储在缓存中，达到页面传值的作用
      var storage = window.sessionStorage
      storage.removeItem('courseInfo')
      storage.setItem('courseInfo', JSON.stringify(row))
      this.$router.push({name: 'PersonalCenter', params: {isShow: 4}, query: {courseId: row.courses_id}})
    },
    editQuiz (row) {
      // 选择编辑状态
      this.addOrEdit = 'edit'
      // 保存数据
      this.newCourseFrom.classList = row.class_id
      this.newCourseFrom.courseId = row.courses_id
      // 表单回显数据
      this.newCourseFrom.courseName = row.courses_name
      this.newCourseFrom.schoolYear = row.grade
      this.newCourseFrom.semester = row.term.toString()
      this.dialogVisible = true
    },
    handleCheckChange (data, checked, indeterminate) {
      let isInArray = function (arr, obj) {
        for (let i = 0; i < arr.length; i++) {
          if (obj.id === arr[i].id && obj.label === arr[i].label) {
            return true
          }
        }
        return false
      }
      if (checked) {
        let flag = true
        for (let i = 0; i < this.addClassArr.length; i++) {
          if (data.id === this.addClassArr[i].id) {
            flag = false
            break
          }
        }
        if (flag) {
          var obj = {
            id: data.id,
            label: data.label
          }
          if (!isInArray(this.addClassArr, obj)) {
            this.addClassArr.push(obj)
          }
        }
      } else {
        this.addClassArr.splice(this.addClassArr.findIndex(item => item.id === data.id), 1)
      }
    },
    handleCurrentPage (page) {
      this.classPage.page = page
      this.getClass()
    },
    editClass (id, row) {
      // 初始化
      this.addClassArr = []
      this.classPage.page = 1
      this.newCourseFrom.classList = []
      this.newCourseFrom.courseId = row.courses_id
      // 表单回显数据
      this.newCourseFrom.courseName = row.courses_name
      this.newCourseFrom.schoolYear = row.grade
      this.newCourseFrom.semester = row.term.toString()
      // 回显数据
      for (var i = 0; i < row.class_id.length; i++) {
        this.addClassArr.push({
          id: parseInt(row.class_id[i]),
          label: row.class_name[i]
        })
      }
      this.getClass(id)
    },
    delClass (id, row, delId) {
      const _this = this
      let data = {
        course_id: id,
        class_id: row.class_id,
        grade: row.grade,
        term: parseInt(row.term),
        course_name: row.courses_name
      }
      let index = data.class_id.indexOf(delId)
      if (index > -1) {
        data.class_id.splice(index, 1)
      }
      api.updateCourses('/courses', data).then(function (res) {
        if (res.success === 'true') {
          _this.$message({
            message: '班级删除成功',
            type: 'success'
          })
          _this.$emit('refreshCourse')
        } else {
          _this.$message({
            message: '班级删除成功，请稍候再试',
            type: 'warning'
          })
        }
      })
    },
    getClass (id) {
      const _this = this
      _this.classPage.loading = true
      this.classData = []
      if (id) {
        _this.addId = id
      }
      let obj = {
        page: _this.classPage.page,
        pagesize: _this.classPage.size
      }
      api.getClassList('/classes', obj).then(function (res) {
        for (var i = 0; i < res.data.length; i++) {
          let classObj = {
            id: parseInt(res.data[i].class_id),
            label: res.data[i].class_name
          }
          _this.classData.push(classObj)
        }
        _this.classPage.total = Math.ceil(res.total / _this.classPage.size)
        _this.classPage.loading = false
        if (_this.addClassArr.length) {
          _this.$nextTick(function () {
            let list = []
            let temp = _this.addClassArr
            for (let i = 0; i < temp.length; i++) {
              list.push(temp[i].id)
            }
            _this.$refs.addClassTree.setCheckedKeys(list)
          })
        }
      })
      this.classDialog = true
    },
    // 展开详细班级列表
    rowMouseEnter (row) {
      if (this.doubleClick || this.ifClick === row.courses_id) {
        return
      } else {
        this.ifClick = -1
      }
      this.expands = []
      if (this.expands.indexOf(row.courses_id) < 0) {
        this.expands.push(row.courses_id)
      } else {
        let index = this.expands.indexOf(row.courses_id)
        if (index > -1) {
          this.expands.splice(index, 1)
        }
      }
    },
    // 单击
    rowClick (row) {
      if (this.expands.length === this.courseData.length) {
        return
      }
      let index = this.expands.indexOf(row.courses_id)
      if (index > -1) {
        this.ifClick = row.courses_id
        this.expands.splice(index, 1)
      } else {
        this.expands.push(row.courses_id)
      }
    },
    // 展开所有班级列表
    rowDoubleClick (row) {
      // 判断是否是展开状态
      if (this.expands.length === this.courseData.length) {
        this.doubleClick = false
        this.expands = []
        this.expands.push(row.courses_id)
      } else {
        this.doubleClick = true
        this.expands = []
        for (var i = 0; i < this.courseData.length; i++) {
          this.expands.push(this.courseData[i].courses_id)
        }
      }
    },
    messageOpen (row) {
      const _this = this
      this.$confirm('此操作将会删除该课程, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!',
          callback: _this.deleteCourse(row)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    deleteCourse (row) {
      const _this = this
      var userObj
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var user = storage.getItem('userInfo')
      if (user !== 'undefined' && user !== null && user !== undefined) {
        userObj = JSON.parse(user)
      } else {
        console.log(userObj)
      }
      api.deleteCourse('/courses', row.courses_id).then(function (res) {
        _this.$emit('refreshCourse')
      })
    },
    // 打开添加课程面板
    openAddCourse () {
      this.addOrEdit = 'add'
      // 初始数据
      this.newCourseFrom.classList = []
      this.newCourseFrom.courseId = null
      this.newCourseFrom.courseName = null
      this.newCourseFrom.schoolYear = null
      this.newCourseFrom.semester = null
      this.dialogVisible = true
    },
    addCourse () {
      const _this = this
      var userObj
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var user = storage.getItem('userInfo')
      if (user !== 'undefined' && user !== null && user !== undefined) {
        userObj = JSON.parse(user)
      } else {
        console.log(userObj)
      }
      let data = {
        grade: this.newCourseFrom.schoolYear,
        term: this.newCourseFrom.semester,
        courses_name: this.newCourseFrom.courseName
      }
      api.addCourses('/courses', data).then(function (res) {
        _this.$emit('refreshCourse')
      })
      this.dialogVisible = !this.dialogVisible
    },
    editCourse () {
      const _this = this
      var userObj
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var user = storage.getItem('userInfo')
      if (user !== 'undefined' && user !== null && user !== undefined) {
        userObj = JSON.parse(user)
      } else {
        console.log(userObj)
      }
      let data = {
        course_id: this.newCourseFrom.courseId,
        class_id: this.newCourseFrom.classList,
        grade: this.newCourseFrom.schoolYear,
        term: parseInt(this.newCourseFrom.semester),
        course_name: this.newCourseFrom.courseName
      }
      api.updateCourses('/courses', data).then(function (res) {
        if (res.success === 'true') {
          _this.$message({
            message: '课程编辑成功',
            type: 'success'
          })
          _this.$emit('refreshCourse')
        } else {
          _this.$message({
            message: '课程编辑失败，请稍候再试',
            type: 'warning'
          })
        }
      })
      this.dialogVisible = !this.dialogVisible
    },
    editClassToCourse () {
      const _this = this
      let list = this.addClassArr
      for (var i = 0; i < list.length; i++) {
        this.newCourseFrom.classList.push(list[i].id.toString())
      }
      let data = {
        course_id: this.newCourseFrom.courseId,
        class_id: this.newCourseFrom.classList,
        grade: this.newCourseFrom.schoolYear,
        term: parseInt(this.newCourseFrom.semester),
        course_name: this.newCourseFrom.courseName
      }
      api.updateCourses('/courses', data).then(function (res) {
        if (res.success === 'true') {
          _this.$message({
            message: '修改班级列表成功',
            type: 'success'
          })
          _this.$emit('changeCoursesTable', true)
        } else {
          _this.$message({
            message: '修改班级列表失败，请稍候再试',
            type: 'warning'
          })
        }
        _this.classDialog = false
      })
    },
    detail (row) {
      let obj = {
        grade: row.grade,
        term: row.term,
        courses_name: row.courses_name,
        class_id: row.class_id,
        class_name: row.class_name,
        courses_id: row.courses_id,
        disable: false
      }
      this.$emit('disablePerson', obj)
    },
    getClassInfo (classInfoid) {
      this.classInfoId = null
      this.classInfoVisible = true
      this.classInfoId = classInfoid
    }
  }
}
</script>

<style scoped>
  .card /deep/ .el-card{
    margin-top: 20px;
  }
  .mycourse-module{
    min-height: 700px;
    background: white;
    padding-bottom: 30px;
    box-shadow:  0px 3px 12px 0px rgba(54, 54, 54, 0.12)
  }
  .module{
    height: 260px;
    background-color: pink;
    box-sizing: content-box;
  }
  .card-div{
    width: 100%;
    height: 220px;
    background-color: #8ecc45;
  }
  .course-name{
    text-align: center;
    height:40px;
    line-height: 40px;
    font-size: 20px;
  }
  .add-pad /deep/ .el-button{
    background: #f48118;
    border: none;
    color: white;
    padding: 10px 40px;
  }
  .semester /deep/ .el-select {
    display: block;
  }
  .semester /deep/ .el-select .el-input .el-input__suffix {
    top:20px
  }
  .add-pad{
    width: 100%;
    padding: 10px 20px;
    box-shadow: 1px 3px 10px -5px #323232;
  }
  .button-hover{
    width: 40px;
    transition: width 0.5s;
    -moz-transition: width 0.5s;
    -webkit-transition: width 0.5s;
    -o-transition: width 0.5s;
  }
  .delete:hover:after{
    content: '删除';
  }
  .edit:hover:after{
    content: '编辑';
  }
  .test:hover:after{
    content: '测试';
  }
  .quiz:hover:after{
    content: '考试';
  }
  .button-hover:hover{
    width: 80px;
  }
  .class-item-div {
    height: 24px;
    overflow: hidden;
  }
  .class-item {
    cursor: pointer;
    display: inline-block;
    width: 60px;
    margin-right: 5px;
    background-color: #fa8118;
    color: #ffffff;
    height: 24px;
    padding: 1px;
    border-radius: 3px;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  .tree-box {
    min-height: 260px;
  }
  .selected-class {
    padding: 10px;
  }
  .list-item {
    display: inline-block;
    text-align: center;
    margin-right: 5px;
    margin-bottom: 5px;
    width: 100px;
    line-height: 24px;
    border-radius: 3px;
    background-color: #fa8118;
    color: #ffffff;
  }
  /*表格展示班级面板*/
  .courseDate-table /deep/ .el-table .el-table__expanded-cell {
    background-color: #F2F2F2;
    padding: 0;
  }
  .courseDate-table /deep/ .el-table .el-table__body-wrapper .expanded + tr{
    background-color: #ebebeb;
    transition: all 0.5s;
  }
  .courseDate-table /deep/ .el-table .el-form-item {
    padding: 0;
    margin: 0;
  }
  .courseDate-table /deep/ .el-table .el-form-item__content {
    line-height: unset;
    display: flex;
    padding: 10px 60px;
  }
  .courseDate-table /deep/ .el-table .el-form-item__content .title{
    width: 80px;
    overflow: hidden;
  }
  .courseDate-table /deep/ .el-table .el-form-item__content h5 {
    font-size: 14px;
    font-weight: 700;
    color: #909399;

    position: relative;
    top: 50%;
    left: 50%;
    margin-top: -0.8em;
    margin-left: -2em;
  }
  .courseDate-table /deep/ .el-table .el-form-item .labelBox {
    width: 750px;
  }
  .courseDate-table /deep/ .el-table .el-form-item .class-item {
    line-height: 2em;
    width: unset;
    height: unset;
    padding: 0 15px;
    margin: 5px;

    transition: all 0.5s;
  }
  .courseDate-table /deep/ .el-table .el-form-item .class-item:hover,
  .courseDate-table /deep/ .el-table .el-form-item .class-item:focus{
