  }
  .courseDate-table /deep/ .el-table .el-form-item .el-icon-close:before {
    margin-left: 2px;
  }
  /*删除按钮的过渡动画*/
  .closeBtn-enter-active{
    transition: opacity .5s;
  }
  .closeBtn-leave-active{
    transition: opacity 0s;
  }
  .closeBtn-enter, .closeBtn-leave-to{
    opacity: 0;
  }
  .classLoginStats /deep/ .el-dialog {

  }
  .classLoginStats /deep/ .el-dialog .el-dialog__body {
    padding-top: 0;
  }
  .stats-fade-enter-to, .stats-fade-leave-to {
    opacity: 1;
    transition: all .5s;
  }
  .stats-fade-enter, .stats-fade-leave-to{
    opacity: 0;
  }
</style>



