<template>
  <div id="postSearch">
    <div class="container">
      <ul class="tab-items">
        <li :class="(currentTabType === item.type)?'currentTab':''" v-for="(item, index) in tabList" :key="index">
          <span @click="changeTab(item.type)">{{item.name}}</span>
          <template v-if="index < tabList.length-1">
            &nbsp;/ &nbsp;
          </template>
        </li>
      </ul>
      <div class="tab-content tab-content-loading" v-loading="loading">
        <ul>
          <li v-for="(item, index) in postsList" :key="index">
            <div class="post-body-middle">
              <h3 class="title">
                <span  style="cursor: pointer" v-html="item.title" @click="toPostDetail(item.post_id)"></span>
                <span class="tags tags-essence" v-if="item.type === 2">精华</span>
                <span class="tags tags-notice" v-if="item.type === 3">系统公告</span>
                <span class="tags tags-guide" v-if="item.type === 4">新手指南</span>
                <span class="tags" v-for="(subItem, index) in item.labels" :key="index">{{subItem}}</span>
              </h3>
              <h5 class="authorAndDate">{{(item.type === 3)? 'OJ平台管理员' : item.author.nick}}&nbsp;| &nbsp;{{item.publish_date.replace('T', ' ')}}</h5>
            </div>
            <div :class="!ifLogin ? 'post-body-right noLogin' : 'post-body-right'">
              <div class="good-tips" style="color: #8ECC45" @click="giveLike(item.post_id, item.is_liked, index)">
                <template v-if="ifLogin && item.is_liked">
                  <h5 class="hasGood">{{item.likes}}</h5><span>已点赞</span>
                </template>
                <template v-else>
                  <h5 >{{item.likes}}</h5><span>点赞</span>
                </template>
              </div>
              <div class="reply-tips" style="color: #FF9900">
                <h5>{{item.replies}}</h5><span>回复</span>
              </div>
              <div class="see-tips" style="color: #868686">
                <h5>{{item.views}}</h5><span>浏览</span>
              </div>
            </div>
          </li>
        </ul>
        <span class="noList" v-if="postsTotal <= pageSize">无更多帖子，请尝试<span class="refresh-btn" @click="refresh">刷新</span>页面</span>
      </div>
      <div class="public-pagination" v-if="postsTotal > pageSize">
        <el-pagination
          @current-change="handleCurrentChange"
          background
          layout="prev, pager, next"
          :page-size="pageSize"
          :pager-count="9"
          :total="postsTotal">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import {host} from '@/api/config'
import searchBar from '@/base/forums/search-bar'

import api from '@/api/forums'

export default {
  name: 'postSearch',
  data () {
    return {
      tabList: [
        { name: '全部帖子', type: 0 },
        { name: '精华帖子', type: 2 },
        { name: '系统公告', type: 3 },
        { name: '新手指南', type: 4 }
      ],
      loading: true,
      currentTabType: this.$route.params.type,
      postsList: [],
      postsTotal: 0,
      page: this.$route.params.page,
      pageSize: 20,
      searchKey: this.$route.query.search,
      info: {},
      ifLogin: false,
      host: host,
      defaultImage: require('../../../static/image/acmlogo.jpg')
    }
  },
  methods: {
    refresh () {
      location.reload()
    },
    toPostDetail (id) {
      this.$router.push({name: 'postDetail', params: {id: id}})
    },
    changeTab (type) {
      this.currentTabType = type
      // 此处调用api重新请求相应数据
      this.getPostsList()
    },
    handleCurrentChange (val) {
      this.page = val
      this.getPostsList()
    },
    // 点赞和取消赞
    giveLike (postId, ifGood, listNo) {
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json === 'undefined' || json === null || json === undefined) {
        return false
      }
      const _this = this
      this.postsList[listNo].is_liked = !this.postsList[listNo].is_liked
      if (ifGood) { // 取消
        api.delGiveLike('/forum/like', postId).then(function (res) {
          if (res.success === 'true') {
            _this.postsList[listNo].likes--
          }
        })
      } else { // 点赞
        api.postGiveLike('/forum/like', postId).then(function (res) {
          if (res.success === 'true') {
            _this.postsList[listNo].likes++
          }
        })
      }
    },
    getListInfo () {
      const _this = this
      var storage = window.sessionStorage
      // 将JSON字符串转换成为JSON对象输出
      var json = storage.getItem('userInfo')
      if (json !== 'undefined' && json !== null && json !== undefined) {
        var jsonObj = JSON.parse(json)
        var uid = jsonObj.uid
        api.getInfoList('/users/detail', uid).then(function (res) {
          _this.info = res.data.user_info
          _this.ifLogin = true
        })
      }
    },
    getPostsList () {
      this.loading = true
      const _this = this
      var obj = {
        type: _this.currentTabType,
        page: _this.page,
        pageSize: _this.pageSize
      }
      if (this.searchKey !== '' && typeof (this.searchKey) !== 'undefined') {
        obj.search = this.searchKey
      }
      api.getPostsList('/forum/posts', obj).then(function (res) {
        _this.postsList = res.data.post_list
        _this.postsTotal = res.total
        if (_this.searchKey !== '' && typeof (_this.searchKey) !== 'undefined') {
          _this.tipWord()
        }
        _this.loading = false
      })
    },
    // 标识搜索词
    tipWord () {
      // 列表添加标识
      var turnSpecial = function (str) {
        var re1 = new RegExp('[\\\\]', 'g')
        return str.replace(re1, '[\\\\\\\\]')
      }
      var getStrPart = function (str) { // 数组拆分，返回拆分后的字符串数组
        var list = [str]
        for (var i = str.length - 1; str.length > 2 && i >= 2; i--) {
          for (var j = 0; j <= str.length - i; j++) {
            list.push(str.slice(j, j + i))
          }
        }
        return list
      }
      var strongKey = function (str, obj, params, color) { // 内容添加标识
        var strPart = getStrPart(str)
        for (var j = 0; j < strPart.length; j++) {
          // 如果包含就替换相关字符且退出循环
          if (obj[params].indexOf(strPart[j]) !== -1) {
            var re = new RegExp(turnSpecial(strPart[j]), 'g')
            obj[params] = obj[params].replace(re, '<span style="color: ' + color + ';">' + strPart[j] + '</span>')
            // 成功匹配到的，更新搜索词
            str = str.replace(re, '')
            strPart = getStrPart(str)
            j = 0
          }
        }
      }
      for (var i = 0; i < this.postsList.length; i++) { // 为每个属性寻找相关匹配项
        // 匹配标题
        strongKey(this.searchKey, this.postsList[i], 'title', '#8ECC45')
      }
    }
  },
  created () {
    this.getListInfo()
    this.getPostsList()
  },
  watch: {
    $route () {
      if (this.$route.name === 'postSearch') {
        this.currentTabType = this.$route.params.type || 0
        this.searchKey = this.$route.query.search
        this.getPostsList()
      }
    }
  },
  components: {
    searchBar
  }
}
</script>

<style scoped>
  .container {
    max-width: 950px;
    margin: 18px auto;
  }
  .container > div {
    position: relative;
  }
  /*tab菜单*/
  .tab-items {
    text-align: center;
    margin-bottom: 15px;
  }
  .tab-items>li {
    font-weight: 600;
    font-size: 14px;
    color: #999;
    display: inline-block;
  }
  .tab-items>.currentTab span{
    color: #FF9900;
  }
  .tab-items>li span {
    transition: all .3s;
    cursor: pointer;
  }
  .left-module .tab-items>li span:hover {
    color: #FF9900;
  }
  /*帖子列表*/
  .tab-content {
    width: 100%;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
    padding: 5px;
    position: relative;
  }
  .tab-content-loading {
    min-height: 450px;
  }
  .tab-content /deep/ .el-loading-spinner .path {
    stroke: rgba(142, 204, 69, 1);
  }
  .noList {
    line-height: 3em;
    display: block;
    font-weight: 300;
    font-size: 12px;
    color: #AEAEAE;
    text-align: center;
    cursor: default;
  }
  .tab-content .refresh-btn {
    cursor: pointer;
  }
  .tab-content .refresh-btn:hover {
    color: #FF9900;
  }
  .tab-content ul {
    margin-bottom: 15px;
  }
  .tab-content ul>li {
    min-height: 74px;
    background-color: rgba(255, 255, 255, 0);
    border-bottom: 1px solid rgba(204, 204, 204, 0.3);
    padding: 10px 0 7px 0;
    display: -webkit-flex; /* Safari */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    transition: all .3s;
  }
  .tab-content ul>li:hover {
    background-color: rgba(174, 174, 174, 0.08);
  }
  /*列表中部内容*/
  .post-body-middle {
    flex: 1 0 auto;
    padding: 0 10px;
    line-height: 30px;
    cursor: default;
  }
  .post-body-middle .title {
    font-weight: 600;
    font-size: 18px;
  }
  .post-body-middle .tags{
    height: 1.4em;
    font-weight: 300;
    font-size: 12px;
    text-align: center;
    line-height: 1.4em;
    color: #FFFFFF;
    background-color: rgba(204, 204, 204, 1);;
    display: inline-block;
    border-radius: 0.2em;
    padding: 0 0.4em;
    margin: 0 0.2em;
    transition: all .3s;
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0);
  }
  .post-body-middle .tags:hover{
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.1);
  }
  .post-body-middle .tags-essence {
    background-color: rgba(255, 5, 14, 0.8)
  }
  .post-body-middle .tags-notice {
    background-color: rgba(100, 149, 237, 0.8);
  }
  .post-body-middle .tags-guide {
    background-color: rgba(255, 153, 0, 0.8);
  }
  .post-body-middle .authorAndDate {
    font-weight: 300;
    font-size: 12px;
    color: #AEAEAE;
  }
  /*列表点赞等情况*/
  .post-body-right {
    width: 180px;
    display: inline-block;
    display: -webkit-flex; /* Safari */
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .post-body-right>div{
    width: 60px;
    font-weight: 600;
    font-size: 16px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
  }
  .noLogin>div {
    cursor: default;
  }
  .post-body-right>div>h5{
    margin: 0 10px;
  }
  .post-body-right>div>span{
    font-size: 12px;
  }
  .post-body-right>div .hasGood {
    color: #fff;
    background-color: rgba(142, 204, 69, 0.8);
    border-radius: 5px;
  }
  /*分页*/
  #postSearch .public-pagination {
    margin-top: 20px;
  }
</style>



