import {getRequest} from './config'

function getStatusList (link, num, size, nick, id, result) {
  return getRequest(link, {
    page: num,
    pagesize: size,
    problem_id: id,
    nick: nick,
    result: result
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

// 获取用户自己的答题代码
function getPersonCode (link, id) {
  return getRequest(link, {
    solution_id: id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

const api = {
  getStatusList,
  getPersonCode
}

export default api



