<template>
    <div class="quizPreview">
      <template v-if="isGetResultData">
        <quiz-panel :title="mode === 1 ? '考试预览' : '考试结果页'" padding="10px 15px" height="[80vh, auto]">
          <h3 class="quizPreview_title">{{quizInfo.title}}<span v-if="mode === 2">&nbsp;·&nbsp;我的答题卷</span></h3>
          <table class="quizPreview_table" cellspacing="0" v-if="isShowTable">
            <tr>
              <th>题目类型</th>
              <th
                v-for="item in quizInfo.detail"
                :class="{'quizPreview_tr_selected': showTypes.indexOf(item.type) >= 0}"
                :key="item.type"
                @click="operShowType(item.type)">{{typeNameList[item.type]}}</th>
              <th>{{mode === 1 ? '总分值' : '我的总分'}}</th>
            </tr>
            <tr>
              <td>分值 / 题</td>
              <td v-for="item in quizInfo.detail" :key="item.type">{{item.value}}分</td>
              <td :rowspan="mode === 1 ? 2 : 3" style="font-size: 18px;">
                <span v-if="mode === 1">{{quizInfo.total}}</span>
                <span v-if="mode === 2">{{userTotal}}</span> 分
              </td>
            </tr>
            <tr>
              <td>题数</td>
              <td v-for="item in quizInfo.detail" :key="item.type">{{item.amount}}道</td>
            </tr>
            <tr v-if="mode === 2">
              <td>得分</td>
              <td v-for="item in userAnswer" :key="item.type">
                {{item.problem_info.reduce((total, i) => total + i.score, 0)}}分</td>
            </tr>
          </table>
          <div class="quizPreview_problemDetail">
            <div
              class="quizPreview_problemDetail_item"
              v-for="(item, index) in detailList"
              :key="item.type"
              v-show="showTypes.indexOf(item.type)>=0">
              <h5 class="quizPreview_problemDetail_title">{{getCNOrder(index)}}、{{typeNameList[item.type]}}：{{item.amount * item.value}}分
                （共{{item.amount}}题，每题{{item.value}}分）<span style="color: #e03232; font-size: 10px" v-if="item.type === 3 && isShowAnswer">* 答案略</span></h5>
              <div class="quizPreview_problemDetail_container" v-for="(sonItem, sonIdx) in item.problem_info" :key="sonIdx">
                <template v-if="mode === 1 || mode === 2 && userAnswer[item.type - 1]">
                  <preview-item
                    :type="item.type"
                    :No="sonIdx + 1"
                    :detail="sonItem"
                    :answer="mode === 2
                    ? (item.type === 1 || item.type === 2)
                      ? userAnswer[item.type - 1].problem_info[sonIdx].student_answer
                      : item.type === 5
                        ? userAnswer[item.type - 1].problem_info[sonIdx].student_answer.length === 0
                          ? ''
                          : userAnswer[item.type - 1].problem_info[sonIdx].student_answer
                        : userAnswer[item.type - 1].problem_info[sonIdx].student_answer instanceof Array
                          ? ''
                          : userAnswer[item.type - 1].problem_info[sonIdx].student_answer
                     : null"
                    :isShowAnswer="isShowAnswer"></preview-item>
                  <div class="quizPreview_score" v-if="mode === 2">
                <span
                  :class="{
                    'score-full': userAnswer[item.type - 1].problem_info[sonIdx].score === item.value,
                    'score-part': userAnswer[item.type - 1].problem_info[sonIdx].score < item.value,
                    'score-even': userAnswer[item.type - 1].problem_info[sonIdx].score === 0
                  }">
                  {{userAnswer[item.type - 1].problem_info[sonIdx].score}}分</span>
                  </div>
                </template>
              </div>
            </div>
          </div>
        </quiz-panel>
        <div style="text-align: right">
          <div class="quizPreview_fixedRight">
            <div class="quizPreview_btns" onselectstart="return false" >
              <quiz-btn
                icon="content-显示<br>答案"
                size="12px"
                radius="default"
                :theme="isShowAnswer ? 'DEFAULT' : 'FORBID'"
                width="45px"
                height="45px"
                @click.native="isShowAnswer = !isShowAnswer"></quiz-btn>
              <quiz-btn
                v-if="isShowToTopBtn"
                icon="icon-zuiding"
                size="30px"
                radius="default"
                theme="BLACK"
                width="45px"
                height="45px"
                @click.native="toTop()"></quiz-btn>
              <quiz-btn
                icon="icon-fanhui2"
                size="20px"
                radius="default"
                theme="BACK"
                width="45px"
                height="45px"
                @click.native="back()"></quiz-btn>
            </div>
          </div>
        </div>
      </template>
    </div>
</template>

<script>
import quizPanel from '@/base/quiz/quiz-panel'
import quizBtn from '@/base/quiz/quiz-btn'
import previewItem from '@/base/quiz/preview-item'
import api from '@/api/quiz'
const PREVIEW = 1
const RESULT = 2

export default {
  name: 'quizPreview',
  components: {
    quizPanel,
    quizBtn,
    previewItem
  },
  data () {
    return {
      mode: PREVIEW,
      isGetResultData: false,
      isShowTable: false,
      isShowToTopBtn: false,
      isShowAnswer: false,
      detailList: {},
      userAnswer: {},
      showTypes: [1, 2, 3, 4, 5],
      userTotal: 0,
      typeNameList: {
        0: '题目',
        1: '填空题',
        2: '选择题',
        3: '编程题',
        4: '问答题',
        5: '判断题'
      },
      quizInfo: {}
    }
  },
  methods: {
    toTop () {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      var speed = 1
      var timer = setInterval(function () {
        if (scrollTop > 20) {
          window.scrollTo(0, scrollTop -= 20)
        } else {
          window.scrollTo(0, 0)
          clearInterval(timer)
        }
        speed = speed < 20 ? speed += 2 : 20
      }, speed)
    },
    back () {
      this.$router.push({path: this.$route.query.redirect})
    },
    getCNOrder (idx) {
      var orderListStr = '一二三四五六七八'
      return orderListStr[idx - 1]
    },
    getQuizId () {
      this.quizId = this.$route.params.id
    },
    getQuizInfo () {
      this.isGetResultData = false
      // 拿到预览页数据
      api.getPreviewQuiz('/quiz/preview', this.quizId, true).then(res => {
        if (res.success === 'true') {
          res.data.detail.map(i => {
            if (i.type === 1) {
              i.problem_info.map(j => {
                j.description = j.description.replace(/<input.+?\/>/g, '{}')
                return j
              })
            }
            return i
          })
          this.quizInfo = res.data
          this.isShowTable = true
          this.setDetailList()
          if (this.$route.params.uid) {
            this.mode = RESULT
            this.initMode(this.$route.params.uid)
          }
        }
        this.isGetResultData = true
      })
    },
    async initMode (uid) {
      // 如果是PREVIEW模式直接返回，没有多余操作
      if (this.mode === PREVIEW) return
      // 调用接口拿到用户答题数据
      const result = await api.getOneStudentAnswer('/quiz/teacher_ckeck/student_answers', {
        quiz_id: this.quizId,
        student_id: uid
      })
      if (result.success === 'false') {
        return
      }
      const userAnswerData = []
      var total = 0
      this.quizInfo.detail.forEach(i => {
        var answerData = result.data.detail[i.type - 1].problem_info
        var typeObj = {
          type: i.type,
          problem_info: []
        }
        i.problem_info.forEach(problem => {
          var answerObj = {
            problem_id: problem.problem_id,
            student_answer: [],
            score: 0
          }
          answerData.forEach(answer => {
            if (answer.problem_id === answerObj.problem_id) {
              answerObj.student_answer = answer.student_answer
              answerObj.score = answer.score
              total += answerObj.score
            }
          })
          typeObj.problem_info.push(answerObj)
        })
        userAnswerData.push(typeObj)
      })
      this.userAnswer = userAnswerData
      this.userTotal = total
    },
    setDetailList () {
      var obj = {}
      if (this.quizInfo.detail) {
        this.quizInfo.detail.forEach(item => {
          obj[item.type] = item
        })
        this.detailList = obj
      }
    },
    operShowType (typeNo) {
      var idx = this.showTypes.indexOf(typeNo)
      if (idx >= 0) {
        this.showTypes.splice(idx, 1)
      } else {
        this.showTypes.push(typeNo)
      }
    },
    scrollToTop () {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      var browserHeight = window.outerHeight
      if (scrollTop > browserHeight / 2) {
        this.isShowToTopBtn = true
      } else {
        this.isShowToTopBtn = false
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.scrollToTop)
  },
  created () {
    if (!this.$route.query.redirect) this.$router.push({path: '/personal-center/1'})
    this.getQuizId()
    this.getQuizInfo()
  },
  destroyed () {
    window.removeEventListener('scroll', this.scrollToTop)
  }
}
</script>

<style scoped>
  .quizPreview {
    width: 1000px;
    margin: 0 auto;
  }
  .quizPreview_title {
    font-size: 18px;
    margin-top: 40px;
    text-align: center;
  }
  .quizPreview_table {
    margin: 30px auto 20px auto;
  }
  .quizPreview_table tr>th {
    font-size: 13px;
    padding: 5px 30px;
  }
  .quizPreview_table tr>td {
    font-size: 12px;
    padding-top: 5px;
    padding-bottom: 3px;
  }
  .quizPreview_table,
  .quizPreview_table th,
  .quizPreview_table td{
    text-align: center;
    border: 1px solid #787878;
    cursor: default;
    background-color: #fff;
  }
  .quizPreview_table th {
    color: #333;
    background-color: #b7b7b7;
    transition: all .3s;
    cursor: pointer;
  }
  .quizPreview_table th:first-child,
  .quizPreview_table th:last-child{
    background-color: #b7b7b7;
  }
  .quizPreview_table .quizPreview_tr_selected {
    background-color: rgba(232, 232, 232, 1);
  }
  .quizPreview_table th:first-child:hover,
  .quizPreview_table th:last-child:hover{
    background-color: #b7b7b7;
    cursor: default;
  }
  .quizPreview_problemDetail {
    padding: 20px 50px;
  }
  .quizPreview_problemDetail div {
    /*margin-bottom: 20px;*/
  }
  .quizPreview_problemDetail_title {
    font-size: 16px;
  }
  .quizPreview_problemDetail_container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .quizPreview_problemDetail_container .preview-item {
    width: 100%;
  }
  .quizPreview_score span {
    color: #fff;
    font-size: 13px;
    font-weight: 600;
    display: inline-block;
    width: 36px;
    height: 36px;
    text-align: center;
    line-height: 36px;
    border-radius: 50%;
    margin-left: 10px;
    margin-top: 20px;
  }
  .quizPreview_score .score-full {
    background-color: #87c141;
  }
  .quizPreview_score .score-part {
    background-color: #ff9900;
  }
  .quizPreview_score .score-even {
    background-color: #ff3333;
  }
  /*操作按钮*/
  .quizPreview_fixedRight {
    display: inline;
    position: fixed;
    bottom: 200px;
  }
  .quizPreview_btns {
    position: absolute;
  }
  .quizPreview_btns /deep/ .quiz-btn{
    margin: 8px 10px;
  }
</style>



