import {getRequest, postRequest} from './config'

function getQuestionList (link, data) {
  return getRequest(link, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getQuestionDetail (link, pid) {
  return getRequest(link, {
    problem_id: pid
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getProblemsList (link, num, size, obj, tagId, courseId) {
  if (obj !== undefined) {
    return getRequest(link, {
      page: num,
      pagesize: size,
      title: obj.title,
      problem_type: obj.type,
      problem_id: obj.problemId,
      accept_rate: obj.acceptRate,
      tagid: tagId,
      courses_id: courseId,
      defunct: 'N'
    }).then((res) => {
      return Promise.resolve(res.data)
    })
  } else {
    return getRequest(link, {
      page: num,
      pagesize: size,
      courses_id: courseId
    }).then((res) => {
      return Promise.resolve(res.data)
    })
  }
}

function postAnswer (link, data) {
  return postRequest(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

const api = {
  getQuestionList,
  getQuestionDetail,
  getProblemsList,
  postAnswer
}

export default api



