<template>
  <div class="burshResult">
    <div class="Info-bottom">
      <el-alert
        title="已完成题目"
        type="success"
        :closable=false>
      </el-alert>
      <div class="success-topic passList">
        <div v-for="(item, index) in done_list" :key="index" class="inline">
          <span class="topic" @click="toDetail(item.problem_id)">{{item.problem_id}}</span>
          <span class="Num">{{item.accepted}}/{{item.submit}}</span>
        </div>
      </div>
      <el-alert
        title="未完成的题目"
        type="error"
        :closable=false>
      </el-alert>
      <div class="success-topic">
        <div v-for="(item, index) in not_done_list" :key="index" class="inline">
          <span class="topic undone" @click="toDetail(item.problem_id)">{{item.problem_id}}</span>
          <span class="Num">{{item.accepted}}/{{item.submit}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    ProblemInfo: {
      type: Object
    }
  },
  data () {
    return {
      done_list: [],
      not_done_list: []
    }
  },
  methods: {
    getProblemList () {
      this.done_list = this.ProblemInfo.done_list
      this.not_done_list = this.ProblemInfo.not_done_list
    },
    toDetail (data) {
      // console.log(e)
      const {href} = this.$router.resolve({
        path: '/library/questionDetail',
        query: {PID: data}
      })
      window.open(href, '_blank')
      // this.$router.push({path: '/library/questionDetail', query: {PID: PID}})
    }
  },
  created () {
    this.getProblemList()
  }
}
</script>

<style scoped>
  .burshResult {
    min-height: 700px;
    background-color: white;
  }
  .passList {
    overflow: auto;
  }
  .Info-bottom /deep/ .el-alert--error {
    margin-top: 25px;
  }
  .inline {
    display: inline-block;
    vertical-align: top;
    margin-top: 5px;
    margin-bottom: 5px;
    width: 128.5px;
  }
  .topic{
    font-size: 14px;
    color: #006400;
    font-weight: 900;
    padding: 0 20px;
    cursor: pointer;
  }
  .topic:hover{
    color: #f48118;
  }
  .undone {
    color: #f48118;
  }
  .undone:hover {
    color: red;
  }
</style>



