<template>
  <div class="question-detail" ref="detail" v-if="question.PID">
    <div :class="(showSubmitBox !== 0)? 'submitBox submitBox-show': 'submitBox'" v-if="question.defunct === 'N'">
      <div class="container">
        <template v-if="showSubmitBox === 1">
          <submitData :problemId="question.PID" :dataType="1"></submitData>
        </template>
        <template v-else-if="showSubmitBox === 2">
          <submitData :problemId="question.PID" :dataType="2"></submitData>
        </template>
      </div>
      <div class="btn-Box">
        <button :class="(showSubmitBox === 1)? 'btn btn-submit-history btn-current':'btn btn-submit-history'" @click="sumHis(question.PID)">提交<br/>历史</button>
        <button :class="(showSubmitBox === 2)? 'btn btn-submit-rank btn-current':'btn btn-submit-rank'" @click="sumRank(question.PID)">提交<br/>排行</button>
        <button v-if="showSubmitBox !== 0" class="btn btn-back" @click="showSubmitBox = 0">返回<br/>题目</button>
      </div>
    </div>
    <div class="top">
      <p class="title">{{question.title}}</p>
      <div class="label-area">
        <span class="label-item pid">第{{question.PID}} 题</span>
        <span v-for="(label, index) in question.label" :key="index" class="label-item label">{{label}}</span>
        <span v-if="question.acceptedLanguages.length > 0" class="label-item success-tip">
          已完成[<span v-for="(acceptLang, index) in question.acceptedLanguages" :key="index">
            {{languages[langsList.indexOf(acceptLang)].title}}<span v-if="index !== question.acceptedLanguages.length - 1">,</span>
          </span>]
        </span>
      </div>
    </div>
    <transition name="bTtFade">
      <div class="fix-top" v-if="showFixTop">
        <div class="fix-top-container">
          <span class="title">{{question.title}}</span>
          <span class="label-item pid">第{{question.PID}} 题</span>
          <button class="button" @click.stop="openAnswerPad"><i class="icon-xiugai-copy"></i> 答题</button>
        </div>
      </div>
    </transition>
    <div class="content">
      <div class="limit-area">
        <div class="space-limit-area">
          <span class="limit-label"><i class="icon-project_space"></i> 空间限制：</span>
          <span class="space-limit limit-data">{{question.spaceLimit}}K</span>
        </div>
        <div class="time-limit-area">
          <span class="limit-label"><i class="icon-shijian"></i> 时间限制：</span>
          <span class="time-limit limit-data">{{question.timeLimit}}S</span>
        </div>
        <div class="other-info">
          <span class="submit-label">提交数:</span>
          <span class="submit-count">{{question.submit}}</span>
          <span class="submit-label">通过率:</span>
          <span class="submit-count">{{computePassRate(question.submit,question.accepted)}}</span>
          <span class="submit-label">题目来源:</span>
          <template v-if="question.source!=null && question.source!== '' && typeof (question.source) != 'undefined'">
            <span class="submit-count">{{question.source}}</span>
          </template>
          <template v-else>
            <span class="submit-count">本站</span>
          </template>
        </div>
        <div class="switch-area" onselectstart="return false">
          <span class="switch-item" @click="PgDn">&lt;</span>
          <span class="switch-item" @click="PgUp">&gt;</span>
        </div>
        <div class="button-area">
          <button  class="solve-button" v-if="question.defunct === 'N' && question.result===4" @click="toQueSolve" ><i class="icon-xiugai-copy"></i> 题解区</button>
          <button class="button" @click.stop="openAnswerPad"><i class="icon-xiugai-copy"></i> 答题</button>
        </div>
      </div>

      <div class="row">
        <p class="row-label">题目描述</p>
        <div class="container" v-html="question.desc"></div>
      </div>
      <div class="row">
        <p class="row-label">输入描述</p>
        <div class="container" v-html="question.input"></div>
      </div>
      <div class="row">
        <p class="row-label">输出描述</p>
        <div class="container" v-html="question.output"></div>
      </div>
      <div class="row">
        <p class="row-label">样本输入</p>
        <div class="container" v-html="question.inputDemo"></div>
      </div>
      <div class="row">
        <p class="row-label">样本输出</p>
        <div class="container" v-html="question.outputDemo"></div>
      </div>
    </div>
    <!--答题面板-->
    <transition name="bTtFade">
      <div class="do-plan-modal" v-show="showDoPlan" @click="showDoPlan = false;" @scroll.prevent>
        <div class="do-plan" @click.stop="showSelectMenu = false;">
          <div class="problem-header">
            <div class="row do-plan-row">
              <span class="title">{{question.title}}</span>
              <span class="label-item pid">第{{question.PID}}题</span>
              <div class="select-area">
                <span class="select" @click.stop="showSelectMenu = !showSelectMenu">{{getLanguage}}<i class="el-icon-arrow-down el-icon--right"></i></span>
                <ul class="select-menu" v-if="showSelectMenu">
                  <li v-for="(item, index) in languages" :key="index" :data-item="item" @click="selectLanguage(index)">{{item.title}}</li>
                </ul>
              </div>
            </div>
            <div class="row do-plan-row">
              <div class="limit-area">
                <div class="space-limit-area">
                  <span class="limit-label" style="color: #333;"><i class="icon-project_space"></i> 空间限制：</span>
                  <span class="space-limit limit-data" style="color: #666;">{{question.spaceLimit}}K</span>
                </div>
                <div class="time-limit-area">
                  <span class="limit-label" style="color: #333;"><i class="icon-shijian"></i> 时间限制：</span>
                  <span class="time-limit limit-data" style="color: #666;">{{question.timeLimit}}S</span>
                </div>
                <!--<div class="switch-area">-->
                <!--<span class="switch-item">&lt;</span>-->
                <!--<span class="switch-item">&gt;</span>-->
                <!--</div>-->
                <div class="button-area">
                  <button class="button solve" v-if="question.defunct === 'N' && question.result===4" @click="toQueSolve">题解区</button>
                  <button class="button reset" @click="reset">重置</button>
                  <button class="button submit" @click="postQuestionData" v-if="!isPostQuestionLoading">提交</button>
                  <button class="button disable" v-if="isPostQuestionLoading">{{loadingTime}}</button>
                </div>
              </div>
            </div>
          </div>
          <div class="do-plan-content">
            <textarea class="input-area" v-model="question.code" v-html="question.code" @mouseenter="cancelDisMouseWheel" @mouseleave="disabledMouseWheel"></textarea>
            <span class="result" v-if="!isCompile && (question.result || question.result === 0)" :style="{'color': getResultStyle}">{{status[question.result]}}</span>
            <span class="result" v-if="isCompile" :style="{'color': '#f48118'}">编译中...</span>
            <div v-if="question.defunct === 'N' && question.result===6 && question.errorMsg" :class="showErrorMsg? 'error-msg error-msg-open':'error-msg error-msg-close'">
              <span class="error-msg-label" @click="showErrorMsg =! showErrorMsg">错误信息 &nbsp;<i :class="showErrorMsg? 'el-icon-arrow-down' : 'el-icon-arrow-up'"></i></span>
              <p  class="error-msg-content" v-html="question.errorMsg.replace(/\n/g, '<br>')"></p>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { preventScrollEvent } from '@/utils/mixin'
import api from '@/api/questionDetail'
import submitData from '@/components/question-detail/submitData'

const LOADING_TIME = 30

export default {
  components: {
    submitData
  },
  mixins: [preventScrollEvent],
  data () {
    return {
      showSubmitBox: 0,
      submitData: '',
      showFixTop: false,
      showErrorMsg: true,
      PID: '',
      contest_id: -1,
      languageActive: 0,
      languages: [
        {title: 'C', value: 0},
        {title: 'C++', value: 1},
        {title: 'Java', value: 3},
        {title: 'Python3', value: 6}
      ],
      langsList: [0, 1, 3, 6],
      showSelectMenu: false,
      /*
         question: {
         PID: '1003',
         title: 'A+B for Input-Output Practice (I)',
         label: ['字符串'],
         spaceLimit: '32MB',
         timeLimit: '1S',
         desc: `<p>Your task is to Calculate a + b.</p>
         <p>Too easy?! Of course! I specially designed the problem for acm beginners.</p>
         <p>You must have found that some problems have the same titles with this one, yes, all these problems were designed for the same aim. </p>`,
         input: 'The input will consist of a series of pairs of integers a and b, separated by a space, one pair of integers per line.',
         output: 'For each pair of input integers a and b you should output the sum of a and b in one line, and with one line of output for each line in input.',
         inputDemo: `<p>1 5</p>
         <p>10 20</p>`,
         outputDemo: `<p>6</p>
         <p>30</p>`,
         result: 4,
         resultInfo: 'Accept'
         },
         */
      question: {},
      status: [
        'Waiting...',
        'Waiting...',
        'Compiling...',
        'Running',
        'Accept',
        'Presenting Error',
        'Wrong Answer',
        'Time Limit Exceed',
        'Memory Limit Exceed',
        'Output Limit Exceed',
        'Runtime Error',
        'Compile Error'
      ],
      onlineUser: {},
      isCompile: false,
      isPostQuestionLoading: false,
      loadingTime: LOADING_TIME,
      timer: null
    }
  },
  computed: {
    getResultStyle () {
      return (this.question.result === 0 || this.question.result === 1)
        ? '#8ecc45' : this.question.result === 2
          ? '#888' : this.question.result === 3
            ? '#888' : this.question.result === 4
              ? '#8ecc45' : this.question.result === 5
                ? '#f577d0' : this.question.result === 6
                  ? '#d43039' : this.question.result === 7
                    ? '#6495ed' : this.question.result === 8
                      ? '#6495ed' : this.question.result === 9
                        ? '#284cad' : this.question.result === 10
                          ? '#ff6c3f' : '#843f10'
    },
    getLanguage () {
      return this.languages[this.languageActive].title
    }
  },
  methods: {
    computePassRate (submit, accepted) {
      if (submit) {
        return ((accepted / submit).toFixed(2) * 100).toFixed(0) + '%'
      } else {
        return 0
      }
    },
    handleScroll () {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      if (scrollTop > 140) {
        this.showFixTop = true
      } else {
        this.showFixTop = false
      }
    },
    reset () {
      this.question.code = ''
      this.question.result = ''
    },
    selectLanguage (index) {
      this.languageActive = index
      this.showSelectMenu = false
      this.question.result = ''
      this.getQuestionData()
    },
    PgDn () {
      const _this = this
      var obj = {
        problem_id: this.PID,
        direction: 'last'
      }
      if (this.contest_id !== -1) {
        obj['contest_id'] = this.contest_id
      }
      api.getOtherQuestion('/problems/get_problem', obj).then(function (res) {
        if (res.data === -1) {
          _this.$message({
            message: '已经是第一道题了',
            type: 'warning'
          })
          return
        }
        _this.PID = res.data
        _this.getQuestionData()
      })
    },
    PgUp () {
      const _this = this
      let obj = {
        problem_id: this.PID,
        direction: 'next'
      }
      if (this.contest_id !== -1) {
        obj['contest_id'] = this.contest_id
      }
      api.getOtherQuestion('/problems/get_problem', obj).then(function (res) {
        if (res.data === -1) {
          _this.$message({
            message: '已经是最后一题了',
            type: 'warning'
          })
          return
        }
        _this.PID = res.data
        _this.getQuestionData()
      })
    },
    toQueSolve () {
      // console.log(this.question.PID)
      this.$router.push({
        path: '/questionSolve',
        name: 'questionSolve',
        query: {
          PID: this.question.PID,
          Lang: this.languages[this.languageActive].value
        }
      })
    },
    openAnswerPad () {
      let self = this
      if (!self.onlineUser.uid) {
        this.$alert('请先登录再答题', '温馨提示', {
          confirmButtonText: '去登陆',
          type: 'warning'
        }).then(function () {
          self.$router.push({path: '/login'})
        })
        return
      }
      this.showDoPlan = true
    },
    getQuestionData () {
      const _this = this
      api.getQuestionDetail('/problems/detail', {
        problem_id: _this.PID,
        language: _this.languages[_this.languageActive].value
      }).then(function (res) {
        _this.filterQuestionData(res.data)
      })
    },
    postQuestionData () {
      const _this = this
      // 如果代码为空或者在编译中或者在30秒内
      if (!this.question.code || this.isCompile || this.isPostQuestionLoading) {
        if (!this.question.code) {
          this.$message.error('答案不能为空！')
        }
        return
      }
      this.isPostQuestionLoading = true
      this.timer = setInterval(function () {
        if (--_this.loadingTime < 1) {
          _this.isPostQuestionLoading = false
          _this.loadingTime = LOADING_TIME
          clearInterval(_this.timer)
          _this.timer = null
        }
      }, 1000)
      this.isCompile = true
      let obj
      if (this.$route.query.contest_id) {
        obj = {
          problem_id: _this.question.PID,
          protype: 3,
          source: _this.question.code,
          language: this.languages[this.languageActive].value,
          contest_id: this.$route.query.contest_id
        }
      } else {
        obj = {
          problem_id: _this.question.PID,
          protype: 3,
          source: _this.question.code,
          language: this.languages[this.languageActive].value
        }
      }
      api.postAnswer('/problems/submit', obj).then(() => {
        api.getQuestionResult('/problems/submit', _this.question.PID).then(function (res) {
          _this.isCompile = false
          _this.$set(_this.question, 'code', res.data.code)
          _this.$set(_this.question, 'errorMsg', res.data.error_msg)
          _this.$set(_this.question, 'language', res.data.language)
          _this.$set(_this.question, 'result', res.data.result)
          _this.$set(_this.question, 'solution_id', res.data.solution_id)
        })
      })
    },
    filterQuestionData (data) {
      let question = {}
      question.PID = data.problem_id
      question.title = data.info.title ? data.info.title : ''
      question.label = data.tagnames
      question.defunct = data.defunct
      question.errorMsg = data.error_msg
      question.spaceLimit = data.info.memory_limit
      question.timeLimit = data.info.time_limit
      question.desc = data.info.description
      question.input = data.info.input
      question.output = data.info.output
      question.accepted = data.accepted
      question.source = data.source
      question.submit = data.submit || 0
      question.inputDemo = data.info.sample_input
      question.outputDemo = data.info.sample_output
      question.acceptedLanguages = data.accepted_languages || []
      if (data.code) {
        question.code = data.code
        question.result = data.result
        question.resultInfo = this.status[data.result]
      }
      this.question = question
    },
    sumHis () {
      this.showSubmitBox = 1
    },
    sumRank () {
      this.showSubmitBox = 2
    }
  },
  created () {
    /** 从缓存中获取用户信息 开始 **/
    let storage = window.sessionStorage
    let userInfo = storage.getItem('userInfo')
    if (userInfo !== 'undefined' && typeof userInfo === 'string') {
      this.onlineUser = JSON.parse(userInfo)
    } else {
      this.onlineUser = {}
    }
    /** 从缓存中获取用户信息 结束 **/
    // 从路由中获取PID
    let PID = this.$route.query.PID
    this.PID = PID
    // 从路由中获取竞赛id
    if (this.$route.query.contest_id) {
      this.contest_id = this.$route.query.contest_id
    }
    this.getQuestionData()
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeDestroy () {
    if (!this.timer) {
      clearInterval(this.timer)
      this.timer = null
    }
  },
  watch: {
    'question.code': function (newV) {
    }
  }
}
</script>

