  .select-menu li {
    padding: 0px 20px;
  }
  .select-menu li:hover {
    background-color: #f8f8f8;
  }
  .do-plan .do-plan-content {
    position: relative;
  }
  .do-plan .do-plan-content .input-area {
    display: block;
    width: 100%;
    height: 480px;
    line-height: 22px;
    border: none;
    padding: 15px;
    box-sizing: border-box;
    font-size: 16px;
    resize: none;
  }
  .do-plan .do-plan-content .result {
    position: absolute;
    right: 30px;
    top: 10px;
    font-size: 30px;
    font-weight: 900;
  }
  .do-plan .do-plan-content .accept {
    color: #8ecc45;
  }
  .do-plan .error-msg {
    width: 100%;
    background-color: #fff;
    position: absolute;
    bottom: -23px;
    border-top: 1px solid #f0f0f0;
  }
  .do-plan .error-msg-label {
    display: inline-block;
    font-size: 13px;
    /*font-weight: 600;*/
    color: #6b6969;
    padding: 5px 15px;
    border-bottom: 1px solid #f0f0f0;
    cursor: pointer;
  }
  .do-plan .error-msg-label:hover {
    background-color: #f0f0f0;
  }
  .do-plan .error-msg-content {
    padding: 0 15px;
    font-size: 12px;
    background-color: #f0f0f0;
    overflow-y: scroll;
    transition: max-height 0.5s;
  }
  .error-msg-open .error-msg-content {
    max-height: 200px;
  }
  .error-msg-close .error-msg-content {
    max-height: 0px;
  }
  .other-info{
    margin-left: 25px;
    display: inline-block;
    color: #999;
  }
  .submit-count {
    margin-right: 5px;
    color: #333;
  }
  .btn-Box{
    position:absolute;
    left: -40px;
    top: 50%;
    box-shadow: -2px 2px 6px rgba(0, 0, 0, 0.1);
    border-radius: 5px 0 0 5px;
  }
  .btn-Box>.btn {
    font-size: 12px;
    color: rgba(130, 130, 130, 1);
    line-height: 20px;
    display: block;
    width: 40px;
    cursor: pointer;
    background-color: #fff;
    border-bottom: 1px solid rgba(210, 210, 210, 1);
    position: relative;
    transition: all .2s;
    padding: 2px;
  }
  .btn-Box>.btn:first-child {
    border-top-left-radius: 5px;
  }
  .btn-Box>.btn:last-child {
    border-bottom-left-radius: 5px;
    border-bottom: none;
  }
  .btn-Box>.btn:hover,
  .btn-Box>.btn-current{
    color: #fff;
    background-color: #8ecc45;
  }
  .btn-Box>.btn-back:hover {
    color: #fff;
    background-color: #f48118;
  }
  .submitBox {
    height: calc(100vh - 60px);
    width: 0;
    background-color: #fff;
    border-radius: 10px 0 0 10px;
    box-shadow: -2px 0px 10px rgba(0, 0, 0, 0.1);
    position: fixed;
    top: 60px;
    right: 0;
    z-index: 20;
    transition: all 1s;
  }
  .submitBox .container {
    position: relative;
    min-width: 1370px;
    height: 100%;
    overflow: auto;
  }
  .submitBox-show {
    width: calc(100vw - 70px);
  }
</style>



