<template>
  <div class="classInformation" ref="classInformation">
    <div class="container">
      <div class="backClassStats-btn"  v-if="ifStudentShow" @click="hideStudentMsg"><i class="icon-left-arrow"></i>&nbsp;返回班级统计</div>
      <div class="flewBox">
        <div class="echart-container">
          <div class="closeStats-btn" @click="closeStats"><i class="icon-guanbi"></i></div>
          <span class="cName">{{title}}</span>
          <div class="selectTime">
            <span>请选择时间</span>
            <el-date-picker
              v-model="sdate"
              type="date"
              placeholder="起始日期"
              format="yyyy - MM - dd "
              value-format="yyyy-MM-dd" class="sDate">
            </el-date-picker>
            <span>至</span>
            <el-date-picker
              v-model="edate"
              type="date"
              placeholder="结束日期"
              format="yyyy - MM - dd "
              value-format="yyyy-MM-dd" class="eDate">
            </el-date-picker>
          </div>
          <div v-loading="dataLoading">
            <div id="main" class="echart_top"></div>
            <div id="onlineCount" class="echart_bottom"></div>
          </div>
        </div>
        <transition name="table-fade">
          <div id="excel__list-data" class="data-container" v-if="!dataLoading">
            <el-table
              :data="classForm.student_list"
              height="100%"
              border
              style="width: 100%"
              :default-sort = "{prop: 'code', order: 'ascending'}"
              @row-click="openStudentMsg">
              <el-table-column
                width="140" property="code" label="学号" align="center" sortable>
              </el-table-column>
              <el-table-column
                width="120" property="student_name" label="姓名" align="center">
              </el-table-column>
              <el-table-column
                width="120" property="submit" label="提交数" align="center" sortable>
              </el-table-column>
              <el-table-column
                width="120" property="accept" label="通过数" align="center" sortable>
              </el-table-column>
              <el-table-column
                property="rate" label="通过率" align="center" sortable>
              </el-table-column>
            </el-table>
          </div>
        </transition>
        <transition name="button-fade">
          <div class="btn-to-excel" v-if="!dataLoading"> <span class="button-excel" @click="export2Excel()"><i class="el-icon-download"></i></span></div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import api from '@/api/personCenter'
import { generateArray, exportJsonToExcel } from '@/utils/excel/Export2Excel'

export default {
  name: 'classInformation',
  props: {
    classInfoId: {
      type: String // 子组件接收父组件Mycourse传递过来的数据
    }
  },
  data () {
    return {
      studentId: '',
      classForm: {},
      studentsForm: {},
      // x轴时间数组
      xDate: [],
      // 日期选择时间表数据
      pickerOptions2: {
        shortcuts: [{
          text: '最近一周',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      sdate: '',
      edate: '',
      startDate: '',
      endDate: '',
      // 折线图
      echart_top: null,
      echart_bottom: null,
      title: '班级信息统计面板',
      ifStudentShow: false,
      ifOpenStudentList: false,
      dataLoading: true
    }
  },
  methods: {
    export2Excel () {
      require.ensure([], () => {
        const tHeader = ['学号', '姓名', '提交数', '通过数', '通过率']
        exportJsonToExcel(tHeader, generateArray('#excel__list-data'), this.title)
      })
    },
    closeStats () {
      this.$emit('closeMsg', false)
    },
    openStudentMsg (row) {
      this.studentId = row.code
      this.studentName = row.student_name
      this.ifStudentShow = true
      this.getClassStats(this.classInfoId, this.studentId)
    },
    hideStudentMsg () {
      this.ifStudentShow = false
      this.studentId = ''
      this.updateData()
      this.title = this.classForm.class_name
    },
    getClassStats (cid, studentId) {
      const _this = this
      // 设置默认时间
      if (typeof (_this.sdate) !== 'undefined' && _this.sdate !== '' && _this.sdate != null && typeof (_this.edate) !== 'undefined' && _this.edate !== '' && _this.edate != null) {
        if (_this.sdate > _this.edate) {
          _this.startDate = _this.edate
          _this.endDate = _this.sdate
        } else {
          _this.startDate = _this.sdate
          _this.endDate = _this.edate
        }
      } else {
        var now = new Date()
        // 这个月一号
        _this.startDate = now.getFullYear() + '-' + (now.getMonth() + 1) + '-1'
        // 如果是这个月的前六天，从上个月的25号开始算
        if (now.getDate() < 7) {
          _this.startDate = now.getMonth() === 0 ? (now.getFullYear() - 1) + '-12-25' : now.getFullYear() + '-' + now.getMonth() + '-25'
        }
        // 下个月的一号
        _this.endDate = now.getFullYear() + '-' + (now.getMonth() + 1) + '-' + now.getDate()
        // 默认请求这个月一号到下一个月一号的数据
      }
      // 就算startDate与endDate 有多少天
      var dateSpan
      var iDays
      var sDate1
      var sDate2
      sDate1 = Date.parse(_this.startDate)
      sDate2 = Date.parse(_this.endDate)
      dateSpan = sDate2 - sDate1
      dateSpan = Math.abs(dateSpan)
      // 得到天数
      iDays = Math.floor(dateSpan / (24 * 3600 * 1000))
      // 每次使用接口加载的时候初始化x轴
      _this.xDate = []
      _this.xDate.push(_this.startDate) // 把起始日期加进去进去横坐标xDate数组里边
      var countStartDate = _this.startDate
      for (var i = 0; i < iDays; i++) {
        // 再指定日期上加上一天
        countStartDate = new Date(countStartDate)
        countStartDate = +countStartDate + 1000 * 60 * 60 * 24
        countStartDate = new Date(countStartDate)
        var nextStartDate = countStartDate.getFullYear() + '-' + (countStartDate.getMonth() + 1) + '-' + countStartDate.getDate() // 加上一天
        countStartDate = nextStartDate
        // console.log(countStartDate)
        _this.xDate.push(countStartDate) // 把增加一天的日期加进去进去横坐标xDate数组里边
        // console.log(_this.xDate)
      }
      // console.log(_this.xDate)
      if (arguments.length > 1) {
        // 请求学生信息接口
        api.getStudentsStatsList('/students/stats', {
          code: studentId,
          start_date: _this.startDate,
          end_date: _this.endDate
        }).then(function (res) {
          _this.studentsForm = res.data
          _this.updateData('student')
          _this.title = '学生' + _this.studentName + '情况统计'
        })
      } else {
        // 请求班级信息接口 没有收到数据的时候转圈圈...
        _this.dataLoading = true
        api.getClassStatsList('/classes/stats', {
          class_id: cid,
          start_date: _this.startDate,
          end_date: _this.endDate
        }).then(function (res) {
          if (res.success === 'true') {
            _this.classForm = res.data
            _this.updateData()
            _this.title = _this.classForm.class_name
            // console.log(_this.classForm)
            _this.dataLoading = false
            // 成功拿到数据之后 不要转圈圈...
          } else {
            _this.$message({
              message: '服务器中无此班数据！',
              type: 'error',
              showClose: true,
              duration: 1500
            })
            setTimeout(function () {
              _this.closeStats()
            }, 800)
          }
        })
      }
    },
    drawPie (id) {
      // console.log(this.classForm.per_accepted)
      this.echart_top = echarts.init(document.getElementById(id))
      this.echart_top.setOption({
        title: {
          text: '班级情况统计'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['通过数', '提交数']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: 'none'
            },
            magicType: {type: ['line', 'bar']},
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.xDate
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: '通过数',
            type: 'line',
            data: this.classForm.per_accepted
          },
          {
            name: '提交数',
            type: 'line',
            data: this.classForm.per_submitted
          }
        ]
      })
    },
    drawOnline (id) {
      this.echart_bottom = echarts.init(document.getElementById(id))
      this.echart_bottom.setOption({
        title: {
          text: '班级上线人数情况统计'
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.xDate
        },
        yAxis: {
          type: 'value'
        },
        toolbox: {
          feature: {
            dataZoom: {
              yAxisIndex: 'none'
            },
            magicType: {type: ['line', 'bar']},
            restore: {},
            saveAsImage: {}
          }
        },
        series: [{
          data: this.classForm.per_online,
          type: 'line',
          areaStyle: {}
        }
        ]
      })
    },
    clearData () {
      this.echart_top.setOption({
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: []
        },
        series: [
          {
            name: '通过数',
            type: 'line',
            data: []
          },
          {
            name: '提交数',
            type: 'line',
            data: []
          }
        ]
      })
      this.echart_bottom.setOption({
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: []
        },
        series: [{
          data: [],
          type: 'line',
          areaStyle: {}
        }
        ]
      })
    },
    updateData () {
      const _this = this
      if (arguments.length === 1) {
        this.echart_top.setOption({
          title: {
            text: '数据统计'
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: this.xDate
          },
          series: [
            {
              name: '通过数',
              type: 'line',
              data: _this.studentsForm.per_stu_accepted
            },
            {
              name: '提交数',
              type: 'line',
              data: _this.studentsForm.per_stu_submitted
            }
          ]
        })
        this.echart_bottom.setOption({
          title: {
            text: 'Ta的在线时长统计<尚未完成，敬请期待>'
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: this.xDate
          },
          series: [{
            data: [],
            type: 'line',
            areaStyle: {}
          }
          ]
        })
      } else {
        this.title = this.classForm.class_name
        this.echart_top.setOption({
          title: {
            text: '班级情况统计'
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: this.xDate
          },
          series: [
            {
              name: '通过数',
              type: 'line',
              data: _this.classForm.per_accepted
            },
            {
              name: '提交数',
              type: 'line',
              data: _this.classForm.per_submitted
            }
          ]
        })
        this.echart_bottom.setOption({
          title: {
            text: '班级上线人数情况统计'
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: this.xDate
          },
          series: [{
            data: _this.classForm.per_online,
            type: 'line',
            areaStyle: {}
          }
          ]
        })
      }
    }
  },
  // 挂载
  mounted () {
    this.$nextTick(function () {
      this.drawPie('main')
      this.drawOnline('onlineCount')
    })
  },
  created () {
    this.getClassStats(this.classInfoId)
  },
  watch: {
    classInfoId () {
      this.sdate = ''
      this.edate = ''
      this.studentId = ''
      this.title = '班级信息统计面板'
      this.classForm.student_list = []
      this.ifOpenStudentList = false
      this.ifStudentShow = false
      this.clearData()
      this.getClassStats(this.classInfoId)
    },
    sdate () {
      if (typeof (this.edate) !== 'undefined' && this.edate !== '' && this.edate != null) {
        if (this.ifStudentShow) {
          this.xDate = []
          this.title = '班级信息统计面板'
          this.clearData()
          // console.log(this.studentId)
          this.getClassStats(this.classInfoId, this.studentId)
        } else {
          this.xDate = []
          this.title = '班级信息统计面板'
          this.clearData()
          // console.log(this.studentId)
          this.getClassStats(this.classInfoId)
        }
      }
    },
    edate () {
      if (typeof (this.sdate) !== 'undefined' && this.sdate !== '' && this.sdate != null) {
        if (this.ifStudentShow) {
          this.xDate = []
          this.title = '班级信息统计面板'
          this.clearData()
          // console.log(this.studentId)
          this.getClassStats(this.classInfoId, this.studentId)
        } else {
          this.xDate = []
          this.title = '班级信息统计面板'
          this.clearData()
          // console.log(this.studentId)
          this.getClassStats(this.classInfoId)
        }
      }
    }
  }
}
</script>

<style scoped>
  .classInformation {
    position: fixed;
    top: 60px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .container {
    min-width: 900px;
    max-width: 1170px;
    height: 600px;
    /*background-color: #fff;*/
    margin: 60px auto;
    border-radius: 10px;
    /*box-shadow: -2px 2px 15px rgba(0, 0, 0, 0.2);*/
    /*padding: 5px 10px;*/
    position: relative;
  }
  .cName{
    display: block;
    width: 100%;
    font-size: 20px;
    color: #fff;
    line-height: 2.5em;
    text-align: center;
    border-radius: 10px 10px 0 0;
    background-color: rgba(133, 193, 71, 1);
  }
  .selectTime{
    text-align: center;
    padding: 10px;
    background-color: rgba(153, 153, 153, 0.16);
  }
  .selectTime span{
    font-size: 14px;
    color: #999;
    padding: 0 5px;
  }
  .flewBox {
    min-width: 1210px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 100%;
    overflow: hidden;
    padding: 25px 0
  }
  .echart-container {
    min-width: 490px;
    width: 530px;
    flex: 0 1 auto;
    position: relative;
    border-radius: 10px;
    /*overflow: hidden;*/
    box-shadow: -2px 2px 15px rgba(0, 0, 0, 0.2);
    background-color: #fff;
    z-index: 101;
  }
  .data-container {
    max-width: 640px;
    flex: 1 1 auto;
    margin: 8px 0;
    position: relative;
    border-radius: 0 10px 10px 0;
    overflow: hidden;
    /*border-left: 3px solid rgba(87, 87, 87, .1);*/
    background-color: #fff;
    z-index: 100;
  }
  .echart_top,
  .echart_bottom{
    width: 500px;
    height: 200px;
    margin: 0 10px 10px 10px
  }
  .echart_top {
    margin-top: 10px;
  }
  .sDate{
    width: 182px;
    position: relative;
  }
   .eDate{
      width: 182px;
      position: relative;
   }
  /*学生列表*/
  .student-table {
    width: 281px;
    height: 100%;
    position: absolute;
    top: 0;
    right: -290px;
    background-color: #fff;
    z-index: -1;
  }
  .backClassStats-btn{
    color: #fff;
    font-size: 16px;
    padding: 0 8px 0 15px;
    line-height: 2.5em;
    display: inline-block;
    /*background-color: #f48118;*/
    position: absolute;
    top: 30px;
    left: 0;
    cursor: pointer;
    z-index: 502;
  }
  .closeStats-btn {
     color: #fff;
     width: 30px;
     height: 30px;
    line-height: 30px;
     background-color: rgba(255, 153, 0, 1);
     border-radius: 50%;
     position: absolute;
     right: -12px;
     top: -12px;
     cursor: pointer;
    transition: all .3s;
    padding: 0 7px;
   }
  .closeStats-btn:hover {
    background-color: rgba(234, 140, 0, 1);
  }
  .student-table /deep/ .el-table .cell {
    cursor: pointer;
  }
  .table-fade-enter-to, .table-fade-leave-to {
    opacity: 1;
    transform: translateX(0%);
    transition: all 1s;
  }
  .table-fade-enter, .table-fade-leave-to{
    transform: translateX(-100%);
    opacity: 0;
  }
  .button-fade-enter-to, .button-fade-leave-to {
    opacity: 1;
    transition: all 1s;
  }
  .button-fade-enter, .button-fade-leave-to{
    opacity: 0;
  }
  .btn-to-excel{
    margin-top: 495px;
    margin-left: 10px;
  }
  .button-excel{
    /* margin: 5px;*/
    /* border: none; */
    padding: 10px 13px;
    cursor: pointer;
    border-radius: 50%;
    color: white;
    float: right;
    background: rgb(255, 153, 0);
    box-shadow: -4px 4px 21px 5px rgba(150, 119, 73, 0.17);
    transition: all .3s;
  }
  .button-excel:hover {
    background-color: rgba(234, 140, 0, 1);
    box-shadow: -4px 4px 21px 5px rgba(150, 119, 73, 0.4);
  }
</style>



