import * as types from './mutation-types'

const mutations = {
  [types.SET_USER] (state, user) {
    state.user = user
  },
  [types.TOGGLE_HEADER] (state, status) {
    state.header = status
  },
  [types.TOGGLE_FOOTER] (state, status) {
    state.footer = status
  }
}

export default mutations



