import {getRequest, postFromData, putFromData} from './config'

function getRankList (link, data) {
  return getRequest(link, {
    count: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getBannerList (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getNewsList (link, data) {
  return getRequest(link, {
    page: data.page,
    pagesize: data.pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getProList (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getUserInfo (link, data) {
  return getRequest(link, {
    uid: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getPostsList (link, data) {
  return getRequest(link, {
    type: data.type,
    search: data.search,
    page: data.page,
    pagesize: data.pageSize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function postFeedback (link, data) {
  return postFromData(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getSystemStats (link, data) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getPeriodList (link, data) {
  return getRequest(link, {
    type: data.type,
    date: data.date
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function updateUserInfo (link, data) {
  return putFromData(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}

function checkQuizStatus (link) {
  return getRequest(link, {}).then((res) => {
    return Promise.resolve(res.data)
  })
}

const api = {
  getRankList,
  getBannerList,
  getNewsList,
  getProList,
  getUserInfo,
  getPostsList,
  postFeedback,
  getSystemStats,
  getPeriodList,
  updateUserInfo,
  // 检测是否有考试正在进行或即将开始
  checkQuizStatus
}

export default api



