import {getRequest, postRequest, postFromData, delFromData} from './config'

function getInfoList (link, data) {
  return getRequest(link, {
    uid: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getQuestionDetail (link, obj) {
  return getRequest(link, {
    problem_id: obj.problem_id,
    language: obj.language
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getQuestionResult (link, pid) {
  return getRequest(link, {
    problem_id: pid
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function postAnswer (link, data) {
  return postRequest(link, data).then((res) => {
    return Promise.resolve(res.data)
  })
}
function getOtherQuestion (link, data) {
  return getRequest(link, data).then(function (res) {
    return Promise.resolve(res.data)
  })
}

function getStatusHistoryList (link, obj) {
  return getRequest(link, {
    page: obj.page,
    pagesize: obj.pagesize,
    problem_id: obj.problem_id,
    status: obj.status,
    language: obj.language
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getStatusRankList (link, obj) {
  return getRequest(link, {
    page: obj.page,
    pagesize: obj.pagesize,
    problem_id: obj.problem_id,
    status: obj.status,
    language: obj.language,
    rank: obj.rank
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getSolutionsList (link, obj) {
  return getRequest(link, {
    page: obj.page,
    pagesize: obj.pagesize,
    problem_id: obj.problem_id
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function shareMyCode (link, obj) {
  return postFromData(link, {
    solution_id: obj.solution_id,
    content: obj.content
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function cancelShareMyCode (link, forumSolutionId) {
  return delFromData(link, {
    forum_solution_id: forumSolutionId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function setLike (link, forumSolutionId) {
  return postFromData(link, {
    forum_solution_id: forumSolutionId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function cancelLike (link, forumSolutionId) {
  return delFromData(link, {
    forum_solution_id: forumSolutionId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getMySolutions (link, obj) {
  return getRequest(link, {
    problem_id: obj.problem_id,
    page: obj.page,
    pagesize: obj.pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}
const api = {
  getInfoList,
  getQuestionDetail,
  postAnswer,
  getQuestionResult,
  getOtherQuestion,
  getStatusHistoryList,
  getStatusRankList,
  getSolutionsList,
  shareMyCode,
  cancelShareMyCode,
  setLike,
  cancelLike,
  getMySolutions
}

export default api



