<template>
  <div :key="key" :class="getClass">
    <div class="quiz-table_header">
      <div class="quiz-table_title">
        <quiz-input
          v-model="myDataObj.name"
          :fontSize="15"
          :next="'(' + myDataObj.total + '分)'"
          :hideUnderline="true"
          contentWidth="100%"
          :disabled="status===0 || status===3"
          :openAnimation="false"></quiz-input>
      </div>
      <div class="quiz-table_icon" v-if="!this.isOnlyRead && this.isSelected">
        <template v-if="status === 0">
          <quiz-icon class="icon" name="icon-xiugai-copy" tips="编辑模板" @click.native="toEdit"></quiz-icon>
        </template>
        <template v-else>
          <quiz-icon class="icon" name="icon-guanbi" tips="删除模板" v-if="canBeDeled && status !== 3" @click.native="deleteTemplate(myDataObj.template_id)"></quiz-icon>
          <quiz-icon class="icon" name="icon-queding" tips="确定更改" v-if="status === 2" @click.native="updateTemplate(myDataObj.template_id)"></quiz-icon>
          <quiz-icon class="icon" name="icon-chehui" tips="退出编辑"  v-if="status !== 3" @click.native="endEdit"></quiz-icon>
        </template>
      </div>
    </div>
    <table>
      <tr v-for="(item, index) in myDataObj.question_type" :key="index">
        <td>{{getTypeName(item.type)}}</td>
        <td :class="{'tipEdit': status === 3}">
          <quiz-input
            v-model="item.question_num"
            type="number"
            :range="[0, 20]"
            :fontSize="13"
            next="道"
            :hideUnderline="true"
            contentWidth="35px"
            :disabled="status===0 || status===3"
            :openAnimation="false"></quiz-input>
        </td>
        <td :class="{'tipEdit': status === 3}">
          <quiz-input
            v-model="item.type_bonus"
            type="number"
            :range="[1, 30]"
            :fontSize="13"
            pre="各"
            next="分"
            :hideUnderline="true"
            contentWidth="45px"
            :disabled="status===0 || status===3"
            :openAnimation="false"></quiz-input>
        </td>
        <td>{{item.question_num * item.type_bonus}}&nbsp;分</td>
      </tr>
    </table>
  </div>
</template>

<script>
import quizInput from '@/base/quiz/quiz-input'
import quizIcon from '@/base/quiz/quiz-icon'

export default {
  name: 'quiz-table',
  components: {
    quizInput,
    quizIcon
  },
  computed: {
    getClass () {
      if (this.status !== 0) {
        return 'quiz-table quiz-table_isEditing'
      } else {
        return this.isSelected ? 'quiz-table quiz-table_isSelected' : 'quiz-table'
      }
    }
  },
  props: {
    isSelected: {
      type: Boolean,
      default: false
    },
    canBeDeled: {
      type: Boolean,
      default: true
    },
    dataObj: {
      type: Object,
      default: function () {
        return {}
      }
    },
    isOnlyRead: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      key: new Date().getTime(),
      myDataObj: {}, // 保存父亲给的列表数据，防止违反单向数据流原则
      status: 0 // 面板状态：0-无状态 1-编辑状态，但没有更改数据 2-存在更改数据 3-短暂提示可以修改
    }
  },
  methods: {
    deleteTemplate (id) {
      this.$emit('deleteTemplate', id)
    },
    updateTemplate () {
      this.$emit('updateTemplate', this.myDataObj, this.revertObj)
    },
    getTotal () {
      if (!this.myDataObj) {
        return 0
      }
      if (!this.myDataObj.question_type || this.myDataObj.question_type.length === 0) {
        return 0
      }
      var total = 0
      this.myDataObj.question_type.forEach(i => {
        total += i.question_num * i.type_bonus
      })
      this.myDataObj.total = total
    },
    getTypeName (typeNo) {
      var typeList = {
        '填空题': 1,
        '选择题': 2,
        '编程题': 3,
        '问答题': 4,
        '判断题': 5
      }
      for (let typeListKey in typeList) {
        if (typeList[typeListKey] === typeNo) {
          return typeListKey
        }
      }
      return 'ERROR'
    },
    toEdit () {
      this.status = 3
      var timer = setTimeout(() => {
        this.status = 1
        clearTimeout(timer)
      }, 800)
    },
    endEdit () {
      this.revertObj()
      this.status = 0
    },
    revertObj (isSuccess = true) {
      if (isSuccess) {
        this.myDataObj = this.copyObject(this.dataObj)
        this.key = new Date().getTime()
      } else {
        this.endEdit()
      }
    },
    copyObject (obj) {
      return JSON.parse(JSON.stringify(obj)) // 还原
    },
    diffWithFather () {
      return JSON.stringify(this.myDataObj) !== JSON.stringify(this.dataObj)
    }
  },
  created () {
    this.myDataObj = this.copyObject(this.dataObj)
  },
  watch: {
    isSelected () {
      if (this.isSelected === false) {
        this.revertObj()
        this.status = 0
      }
    },
    myDataObj: {
      handler () {
        this.status = this.diffWithFather() ? 2 : 0
        this.getTotal()
      },
      deep: true,
      immediate: true
    },
    dataObj () {
      this.revertObj()
    }
  }
}
</script>

<style scoped>
  .quiz-table {
    margin: 10px 5px;
    position: relative;
  }

  .quiz-table_header {
    width: 100%;
    display: flex;
    text-align: center;
    font-size: 15px;
    line-height: 26px;
    position: absolute;
    padding: 0 5px;
  }

  .quiz-table_title {
    flex: 1 1 auto;
    padding: 0 10px;
  }

  .quiz-table_header .icon {
    float: right;
    margin-right: 5px;
    opacity: 0.6;
    cursor: pointer;
  }

  .quiz-table_header .icon:hover {
    opacity: 1;
  }

  .quiz-table_isSelected .quiz-table_header,
  .quiz-table_isEditing .quiz-table_header{
    color: #fff;
  }

  table {
    color: #333;
    padding: 0;
    width: 100%;
    border: 1px solid rgba(242, 242, 242, 1);
    border-bottom: 5px solid rgba(242, 242, 242, 1);
    border-top: 26px solid rgba(242, 242, 242, 1);
    border-radius: 3px;
    transition: all .5s;
  }

  .quiz-table:hover table {
    border: 1px solid rgba(232, 232, 232, 1);
    border-bottom: 5px solid rgba(232, 232, 232, 1);
    border-top: 26px solid rgba(232, 232, 232, 1);
  }

  .quiz-table_isSelected table {
    border: 1px solid #87c141;
    border-bottom: 5px solid #87c141;
    border-top: 26px solid #87c141;
  }

  .quiz-table_isSelected:hover table {
    border: 1px solid #7ab441;
    border-bottom: 5px solid #7ab441;
    border-top: 26px solid #7ab441;
  }

  .quiz-table_isEditing table {
    border: 1px solid #ff9900;
    border-bottom: 5px solid #ff9900;
    border-top: 26px solid #ff9900;
  }

  .quiz-table_isEditing:hover table {
    border: 1px solid #ef8e00;
    border-bottom: 5px solid #ef8e00;
    border-top: 26px solid #ef8e00;
  }

  table, tr, td {
    font-size: 13px;
    text-align: center;
  }

  td {
    padding: 2px 5px;
  }

  td:last-child {
    border-left: 2px solid rgba(242, 242, 242, 1);
  }

  >>> .quiz-table_title input[disabled],
  >>> .quiz-table_title input:disabled,
  >>> td input[disabled],
  >>> td input:disabled {
    color: #333;
    cursor: default;
  }
  .quiz-table_isSelected  /deep/ .quiz-table_title input,
  .quiz-table_isSelected  /deep/ .quiz-table_title input
  .quiz-table_isEditing  /deep/ .quiz-table_title input,
  .quiz-table_isEditing  /deep/ .quiz-table_title input{
    color: #fff !important;
  }

  >>> .tipEdit input[disabled],
  >>> .tipEdit input:disabled {
    color: #ef8e00 !important;
  }
</style>



