<template>
  <div class="quizAdd">
    <div class="leftFrame">
      <div class="leftFrame_top" v-loading="!isShowQuizInfo">
        <quiz-panel title="添加考试" :padding="getMainPanelPadding" height="522px">
          <div class="leftFrame_form" v-if="isShowQuizInfo">
            <quiz-input v-model="quizInfo.title" :fontSize="20" :hideUnderline="true" tip="点击此处添加考试名"></quiz-input><br>
            <quiz-input label="当前课程" v-model="quizInfo.course" :disabled="true"></quiz-input>
            <quiz-input label="考试班级" v-model="quizInfo.classes" :disabled="true"></quiz-input>
            <quiz-choose label="考试类型" name="type" :dataList="typeSelectList" :updateFuc="updateSelectList"></quiz-choose>
            <quiz-choose label="考试语言" name="lang" :dataList="langSelectList" :updateFuc="updateSelectList"></quiz-choose>
            <quiz-choose label="考试时间" name="time" :dataList="timeSelectList" :updateFuc="updateSelectList"></quiz-choose>
            <quiz-input v-if="getSelectedItem('time') === 1" label="设置自动开考时间" type="datetime-local" v-model="quizInfo.startTime"></quiz-input>
            <quiz-input label="考试时长" type="number" next="分钟" :range="[10, 500]" v-model="quizInfo.duration"></quiz-input>
            <quiz-choose label="设置提醒" name="tips" :dataList="tipsSelectList" :updateFuc="updateSelectList"></quiz-choose>
            <quiz-input v-if="getSelectedItem('tips') !== 1" label="提醒时间" type="number" pre="考前" next="天" :range="[0]" v-model="quizInfo.remindTime"></quiz-input>
            <quiz-input label="运行提前交卷时间" type="number" next="分钟" :range="[0, quizInfo.duration - 5]" v-model="quizInfo.advance"></quiz-input>
            <quiz-dialog title="成功提示" closeTips="返回个人中心" showProperty="isShowMessage" @clickHandler="backToList">
              <span>您设置的试卷已生成，感谢使用。</span>
            </quiz-dialog>
          </div>
        </quiz-panel>
      </div>
      <div class="leftFrame_bottom">
        <quiz-btn
          icon="icon-fanhui2"
          size="40px"
          theme="BACK"
          width="70px"
          height="70px"
          @click.native="requireToBack()"></quiz-btn>
        <div class="leftFrame_bottom_btn">
          <quiz-btn
            :isLoading="!isCanClickPreviewBtn"
            icon="content-保存并预览"
            size="20px"
            radius="default 0 0 default"
            theme="DEFAULT"
            width="360px"
            height="70px"
            @click.native="toQuizPreview(true)"></quiz-btn>
          <quiz-btn
            :isLoading="!isCanClickAddBtn"
            icon="content-生成试卷"
            size="20px"
            radius="0 default default 0"
            theme="SURE"
            width="360px"
            height="70px"
            :isDisabled="isBanToAddQuiz"
            @click.native="addQuiz"></quiz-btn>
        </div>
      </div>
    </div>
    <div :class="step === 1 ? 'rightFrame' : 'rightFrame rightFrame_second'">
      <quiz-panel :title="step === 1 ? '模板选择' : '范围选择'" :padding="getMainPanelPadding" height="610px" :isHidden="false">
        <div class="rightFrame_tables" :key="tablesKey">
          <div class="rightFrame_table" v-for="item in templateList" :key="item.template_id" @click="changeSelectedRange(item.template_id)">
            <quiz-table
              :dataObj="item"
              :isSelected="selectedTemplateId === item.template_id"
              @deleteTemplate="deleteTemplate"
              @updateTemplate="updateTemplate"></quiz-table>
          </div>
          <div class="rightFrame_addTable_btn" @click="addTemplate()" v-if="templateList.length < MAXTEMPLATE">
            <i class="el-icon-plus"></i>
          </div>
        </div>
        <div v-if="selectedTemplateId !== -1" class="rightFrame_nextStep">
          <div class="rightFrame_nextStep_main">
            <i v-show="step === 1" class="icon-shangyiti"></i>
            <div class="rightFrame_nextStep_content">
              <quiz-table
                :key="selectedTemplateId"
                :dataObj="choosedTemplate"
                :isSelected="true"
                :canBeDeled="false"
                @updateTemplate="updateTemplate"></quiz-table>
              <div class="rightFrame_setRange" v-if="isShowQuizInfo">
                <div class="rightFrame_setRange_item" v-for="(item, index) in problemRange" :key="index">
                  <div class="rightFrame_setRange_label">
                    <i class="icon-success" style="color: #7ab441"
                       v-if="problemRange[item.type - 1].problemList.length === choosedTemplate.question_type[item.type - 1].question_num"></i>
                    {{getTypeName(item.type)}}</div>
                  <div class="rightFrame_setRange_content" onselectstart="return false" @dblclick="showRangeChoose = item.type">
                    <span v-for="(sonItem, sonIndex) in item.rangeLabelList" :key="sonIndex">
                      {{sonItem}}
                      <span v-if="item.rangeLabelList.length !== 0 && sonIndex !== item.rangeLabelList.length - 1">、</span>
                    </span>
                    <span class="rightFrame_setRange_tips" v-if="item.rangeLabelList.length === 0">双击进入考题范围设置，默认全部</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <span class="rightFrame_nextStep_btn" @click="changeStep()">
            {{step === 1 ? '继续设置考试范围，默认全部' : '返回模板选择'}}
          </span>
        </div>
      </quiz-panel>
    </div>
    <rangeChoose
      v-if="showRangeChoose!=-1"
      :problemType="showRangeChoose"
      :needAmount="parseInt(choosedTemplate['question_type'][showRangeChoose-1].question_num)"
      :rangeData="problemRange[showRangeChoose-1]"
      @back="backToMain"></rangeChoose>
    <previewChoose
      v-if="isShowQuizChoose"
      :quizList="quizPreviewList"
      @chooseHandler="initPreviewData"></previewChoose>
  </div>
</template>

<script>
import quizPanel from '@/base/quiz/quiz-panel'
import quizBtn from '@/base/quiz/quiz-btn'
import quizInput from '@/base/quiz/quiz-input'
import quizChoose from '@/base/quiz/quiz-choose'
import quizTable from '@/base/quiz/quiz-table'
import rangeChoose from '@/base/quiz/rangeChoose'
import previewChoose from '@/base/quiz/previewQuizChoose'
import quizDialog from '../../base/quiz/quiz-dialog'
import api from '@/api/quiz'

export default {
  name: 'quizAdd',
  components: {
    quizPanel,
    quizBtn,
    quizInput,
    quizChoose,
    previewChoose,
    quizTable,
    quizDialog,
    rangeChoose
  },
  computed: {
    getMainPanelPadding () {
      return '10px 15px'
    },
    isBanToAddQuiz () {
      // 判断标题是否填写
      if (this.quizInfo.title.trim() === '') return true
      // 判断开考时间是否合理
      var timeType = this.timeSelectList.filter(i => i.selected)[0].value
      if (timeType === 1 && this.quizInfo.startTime === '') return true
      // 判断时间数据是否合理
      if (timeType === 1) {
        var now = new Date().getTime()
        var start = new Date(this.quizInfo.startTime).getTime()
        if (start - now < 10 * 60 * 1000) {
          return true
        }
      }
      // 判断提前时间是否合理
      if (parseInt(this.quizInfo.duration) < parseInt(this.quizInfo.advance)) return true
      // 判断模板是否选择
      if (!this.choosedTemplate.question_type) return true
      // 判断数量是否没达到
      if (this.choosedTemplate.question_type.some(i => this.problemRange[i.type - 1].problemList.length !== i.question_num)) return true
      return false
    }
  },
  data () {
    return {
      isShowQuizInfo: false,
      isShowMessage: false,
      isShowQuizChoose: false,
      isCanClickPreviewBtn: true,
      isCanClickAddBtn: true,
      showRangeChoose: -1,
      step: 1,
      courseId: -1,
      quizId: -1,
      courseInfo: {},
      quizPreviewList: [],
      quizInfo: {
        title: '',
        course: '',
        classes: '',
        startTime: '',
        duration: 120,
        advance: 30,
        remindTime: 5
      },
      typeSelectList: [
        {name: '平时考试', value: 1, selected: false},
        {name: '期中考试', value: 2, selected: true},
        {name: '期末考试', value: 3, selected: false}],
      langSelectList: [
        {name: 'C语言', value: 0, selected: false},
        {name: 'C++', value: 1, selected: true},
        {name: 'JAVA', value: 3, selected: false},
        {name: 'Python', value: 6, selected: false}],
      timeSelectList: [
        {name: '定时开启', value: 1, selected: true},
        {name: '手动开启', value: 2, selected: false}],
      tipsSelectList: [
        {name: '不提醒', value: 1, selected: false},
        {name: '简单提醒', value: 2, selected: false},
        {name: '详细提醒', value: 3, selected: true}],
      templateList: [],
      tablesKey: new Date().getTime(), // 用于一次性更新模板数据
      choosedTemplate: {},
      MAXTEMPLATE: 8,
      selectedTemplateId: -1,
      problemRange: []
    }
  },
  methods: {
    backToList () {
      this.$router.push({name: 'PersonalCenter', params: {isShow: 4}, query: {courseId: this.courseId}})
    },
    requireToBack () {
      this.$confirm('确定不保存直接返回列表页?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.backToList()
      })
    },
    backToPersonCenter () {
      this.$router.push({path: '/personal-center/1'})
    },
    updateChoosedTemplate () {
      if (this.selectedTemplateId === -1) return
      for (let i = 0; i < this.templateList.length; i++) {
        if (this.problemRange.length === 0) return
        if (this.templateList[i].template_id === this.selectedTemplateId) {
          var newTemplate = JSON.parse(JSON.stringify(this.templateList[i]))
          this.choosedTemplate = newTemplate
          for (var params in this.choosedTemplate) {
            if (this.choosedTemplate[params] instanceof Array) {
              this.choosedTemplate[params].forEach((i, index) => {
                for (var p in i) {
                  i[p] = parseInt(newTemplate[params][index][p])
                }
              })
            } else {
              this.choosedTemplate[params] = newTemplate[params]
            }
          }
        }
      }
      this.choosedTemplate.question_type.forEach(i => {
        var obj = this.problemRange[i.type - 1]
        while (obj.problemList.length > i.question_num) {
          obj.problemList.pop()
          obj.problemDetail_list.pop()
        }
      })
    },
    getTypeName (typeNo) {
      var typeList = {
        '填空题': 1,
        '选择题': 2,
        '编程题': 3,
        '问答题': 4,
        '判断题': 5
      }
      for (let typeListKey in typeList) {
        if (typeList[typeListKey] === typeNo) {
          return typeListKey
        }
      }
      return 'ERROR'
    },
    // 判断是否填好必要内容
    testFulled () {
      var isFull = true
      // 判断是否生成足量的题目
      if (this.choosedTemplate.question_type.some(i => this.problemRange[i.type - 1].problemList.length !== i.question_num)) {
        //  自动补全所有题目
        var obj = {
          problemSet_list: []
        }
        this.choosedTemplate.question_type.forEach(i => {
          obj.problemSet_list.push({
            need_amount: i.question_num - this.problemRange[i.type - 1].problemList.length,
            type: i.type,
            range: this.problemRange[i.type - 1].range,
            ques_list: this.problemRange[i.type - 1].problemList
          })
        })
        return api.autoAddProblem('/quiz/auto_add', obj).then(res => {
          res.data.detail.forEach((i, idx) => {
            i.ques_list.forEach(j => {
              this.problemRange[i.type - 1].problemList.push(j.problem_id)
              this.problemRange[i.type - 1].problemDetail_list.push(j)
            })
            if (i.ques_list.length < obj.problemSet_list[idx].need_amount) {
              isFull = false
            }
          })
          if (!isFull) {
            this.$message({
              message: `某些题目的范围设置不足以补足题目，请手动设置！`,
              type: 'warning'
            })
          }
          return Promise.resolve({isFull: isFull})
        })
      } else {
        return Promise.resolve({isFull: isFull})
      }
    },
    setApiQuizInfo (obj) {
      // 取得基本可默认获得的数据
      obj.quizInfo = {
        type: this.typeSelectList.filter(i => i.selected)[0].value,
        language: this.langSelectList.filter(i => i.selected)[0].value,
        duration: parseInt(this.quizInfo.duration),
        advance: parseInt(this.quizInfo.advance),
        remind_type: this.tipsSelectList.filter(i => i.selected)[0].value,
        remind_time: parseInt(this.quizInfo.remindTime),
        quiz_class: this.courseInfo.class_id.map(i => +i)
      }
      // 标题是否填写检测
      if (this.quizInfo.title.trim() === '') {
        this.$message({
          message: '没有填写考试标题！',
          type: 'warning',
          duration: 1500
        })
        return false
      }
      obj.quizInfo.title = this.quizInfo.title
      // 开考时间检测
      var timeType = this.timeSelectList.filter(i => i.selected)[0].value
      if (timeType === 1 && this.quizInfo.startTime === '') {
        this.$message({
          message: '没有填写开考时间！你可设置为手动开启。',
          type: 'warning',
          duration: 1500
        })
        return false
      }
      // 判断时间数据是否合理
      if (timeType === 1) {
        var now = new Date().getTime()
        var start = new Date(this.quizInfo.startTime).getTime()
        if (start - now < 10 * 60 * 1000) {
          this.$message({
            message: '开考时间不合理，最早可设置为现在时刻的十分钟后',
            type: 'warning',
            duration: 1500
          })
          return false
        }
      }
      // 判断提前时间是否合理
      if (parseInt(this.quizInfo.duration) < parseInt(this.quizInfo.advance)) {
        this.$message({
          message: '提前交卷时间的设置不合理，请修改。',
          type: 'warning',
          duration: 1500
        })
        return false
      }
      obj.quizInfo.start_time = timeType === 1
        ? this.quizInfo.startTime.length >= 19
          ? this.quizInfo.startTime.replace('T', ' ')
          : this.quizInfo.startTime.replace('T', ' ') + ':00'
        : -1
      return true
    },
    toQuizPreview (isPreview) {
      if (!this.isCanClickPreviewBtn || !this.isCanClickAddBtn) return
      this.isCanClickPreviewBtn = !isPreview
      this.isCanClickAddBtn = isPreview
      const _this = this
      var obj = {
        quiz_id: this.quizId,
        courses_id: parseInt(_this.courseId),
        quizInfo: null,
        template_id: _this.selectedTemplateId,
        problemRange: [],
        problemList: []
      }
      if (!this.setApiQuizInfo(obj)) {
        this.isCanClickPreviewBtn = true
        return false
      }
      this.testFulled().then(res => {
        if (!res.isFull) return
        this.problemRange.forEach(i => {
          obj.problemRange.push({
            type: i.type,
            range: i.range
          })
          obj.problemList.push({
            type: i.type,
            ques_list: i.problemList
          })
        })
        // 发送生成预览试卷的接口请求
        api.postOrPreviewQuiz('/quiz', obj, isPreview).then(res => {
          if (res.success === 'true') {
            if (!isPreview) {
              this.isShowMessage = !this.isShowMessage
              return
            }
            // 跳转页面
            this.$router.push({
              name: 'quizPreview',
              params: {
                id: res.data.quiz_id
              },
              query: {
                redirect: `/quizAdd/${this.courseId}/${res.data.quiz_id}`
              }
            })
          } else {
            this.$message({
              message: '没有成功生成考试数据，请稍后再试！',
              type: 'error',
              duration: 1500
            })
          }
        })
      }).finally(() => {
        this.isCanClickPreviewBtn = true
        this.isCanClickAddBtn = true
      })
    },
    addQuiz () {
      if (this.isBanToAddQuiz) return
      this.toQuizPreview(false)
    },
    backToMain (obj) {
      var {type, tagsValue, tagsLabel, problemValue, problemDetail} = obj
      this.showRangeChoose = -1
      this.problemRange.map(i => {
        if (i.type === type) {
          i.range = tagsValue
          i.rangeLabelList = tagsLabel
          i.problemList = problemValue
          i.problemDetail_list = problemDetail
        }
        return i
      })
    },
    getSelectedItem (formName) {
      var result = this[formName + 'SelectList'][0].value
      this[formName + 'SelectList'].forEach(i => {
        if (i.selected) {
          result = i.value
        }
      })
      return result
    },
    updateSelectList (formName, newData) {
      this[formName + 'SelectList'] = newData
    },
    clearProblemData () {
      this.problemRange.map(i => {
        i.range = []
        i.rangeLabelList = []
        i.problemList = []
        i.problemDetail_list = []
        return i
      })
    },
    changeSelectedRange (id) {
      this.selectedTemplateId = id
      this.updateChoosedTemplate()
      this.clearProblemData() // 清空已经选了的模板题目
    },
    changeStep () {
      this.step = this.step === 1 ? 2 : 1
    },
    // 一进来添加时先判断该课程有没有未生成的考试，有则回显继续修改
    checkIsHasPerviewQuiz () {
      api.checkOnlyPreviewQuiz('/quiz/find_priview', this.courseId).then(res => {
        if (res.success === 'true' && res.data.quiz_id.length > 0) {
          this.quizPreviewList = res.data.quiz_id
          if (this.quizPreviewList.length === 1) {
            this.quizId = this.quizPreviewList[0].quiz_id
            // 取回数据
            this.initData(this.quizId)
          } else {
            this.isShowQuizChoose = true
          }
        } else {
          var storage = window.sessionStorage
          var jsonData = JSON.parse(storage.getItem('courseInfo'))
          if (jsonData === 'undefined' || jsonData == null) {
            this.backToPersonCenter()
            return
          }
          this.courseInfo = jsonData
          this.quizInfo.course = this.courseInfo.courses_name
          this.quizInfo.classes = this.courseInfo.class_name.toString()
          this.isShowQuizInfo = true
        }
      })
    },
    initPreviewData (val) {
      this.quizId = val
      // 取回数据
      this.initData(this.quizId)
      // 隐藏选择框
      this.isShowQuizChoose = false
    },
    getCourseInfo () {
      // 取得课程id
      if (!this.$route.params.courseId) this.backToPersonCenter()
      this.courseId = this.$route.params.courseId
      // 初始范围列表
      this.initProblemRange()
      // 获取模板列表
      this.getTemplateList()
    },
    initProblemRange () {
      this.problemRange = [
        {type: 1, range: [], rangeLabelList: [], problemList: [], problemDetail_list: []},
        {type: 2, range: [], rangeLabelList: [], problemList: [], problemDetail_list: []},
        {type: 3, range: [], rangeLabelList: [], problemList: [], problemDetail_list: []},
        {type: 4, range: [], rangeLabelList: [], problemList: [], problemDetail_list: []},
        {type: 5, range: [], rangeLabelList: [], problemList: [], problemDetail_list: []}]
    },
    // 回显数据初始，可能是预览后的数据也可能是要回来重新编辑的数据
    initData (quizId) {
      api.getPreviewInitData('/quiz/find_detail_by_id', quizId).then(res => {
        if (res.success === 'true') {
          // 初始基本数据
          this.courseInfo = {
            courses_name: res.data.quiz_info.course_name,
            class_name: res.data.quiz_info.className_list,
            class_id: res.data.quiz_info.classId_list
          }
          this.quizInfo.title = res.data.quiz_info.title
          this.quizInfo.course = this.courseInfo.courses_name
          this.quizInfo.classes = this.courseInfo.class_name.toString()
          this.quizInfo.duration = res.data.quiz_info.duration
          this.quizInfo.advance = res.data.quiz_info.advance
          this.quizInfo.remindTime = res.data.quiz_info.remind_time
          // 处理时间
          if (new Date(res.data.quiz_info.start_time).getTime() - (new Date()).getTime() < new Date(this.courseInfo.duration).getTime() + 10 * 60 * 1000) {
            this.quizInfo.startTime = ''
          } else {
            this.quizInfo.startTime = res.data.quiz_info.start_time
          }
          // 处理多选
          [
            {label: 'type', value: res.data.quiz_info.type},
            {label: 'lang', value: res.data.quiz_info.language},
            {label: 'time', value: !res.data.quiz_info.start_time || res.data.quiz_info.start_time === -1 ? 2 : 1},
            {label: 'tips', value: res.data.quiz_info.remind_type}
          ].forEach(i => {
            this[i.label + 'SelectList'].forEach(j => {
              j.selected = j.value === i.value
            })
          })
          // 处理数据范围
          this.changeSelectedRange(res.data.template_id)
          this.problemRange = this.problemRange.map((i, idx) => {
            var temp = res.data.detail[idx]
            i = {
              range: temp.tags_value,
              rangeLabelList: temp.tags_label,
              problemList: temp.ques_list.filter((j, index) => index < this.choosedTemplate.question_type[i.type - 1].question_num),
              problemDetail_list: temp.ques_detail_list.filter((j, index) => index < this.choosedTemplate.question_type[i.type - 1].question_num),
              type: temp.type
            }
            return i
          })
          this.isShowQuizInfo = true
        }
      })
    },
    getTemplateList () {
      api.getTemplateList('/quiz/templates', {}).then((res) => {
        this.templateList = res.data.template_list
        this.selectedTemplateId = this.templateList.length === 0 ? -1 : this.templateList[0].template_id
        this.updateChoosedTemplate()
        if (this.$route.params.id) {
          this.quizId = parseInt(this.$route.params.id)
          // 情况一：存在preview_id，是预览后返回修改，需要回显数据
          this.initData(this.quizId)
        } else {
          // 情况二：一进来添加时先判断该课程有没有未生成的考试，有则回显继续修改
          this.checkIsHasPerviewQuiz()
        }
      })
    },
    deleteTemplate (id) {
      const _this = this
      api.deleteTemplate('/quiz/templates', id).then((res) => {
        if (res.success === 'true') {
          // 视图上清除模板
          _this.templateList = _this.templateList.filter(i => i.template_id !== id)
          // 改变模板的选择
          if (_this.selectedTemplateId === id) {
            _this.selectedTemplateId = _this.templateList.length === 0 ? -1 : _this.templateList[0].template_id
            _this.updateChoosedTemplate()
          }
          _this.$message({
            message: '删除成功！',
            type: 'success',
            duration: 1500
          })
        } else {
          _this.$message({
            message: res.msg,
            type: 'warning',
            duration: 1500
          })
        }
      })
    },
    updateTemplate (newTemp, revertTable) {
      const _this = this
      api.updateTemplate('/quiz/templates', newTemp).then((res) => {
        if (res.success === 'true') {
          _this.templateList.map(i => {
            if (i.template_id !== newTemp.template_id) return i
            i.name = newTemp.name
            i.question_type.map((j, idx) => {
              j.type = newTemp.question_type[idx].type
              j.question_num = newTemp.question_type[idx].question_num
              j.type_bonus = newTemp.question_type[idx].type_bonus
              return j
            })
            i.total = newTemp.total
            return i
          })
          // 更新模板选择处的数据
          _this.updateChoosedTemplate()
          if (_this.step !== 1) _this.tablesKey = new Date().getTime()
          // 调用回调关闭模板表格编辑状态
          revertTable()
          _this.$message({
            message: '编辑成功！',
            type: 'success',
            duration: 1500
          })
        } else {
          revertTable(false)
          _this.$message({
            message: res.msg,
            type: 'warning',
            duration: 1500
          })
        }
      })
    },
    addTemplate () {
      const _this = this
      const newTemplate = {
        name: '模板名',
        question_type: [
          {type: 1, question_num: 4, type_bonus: 5},
          {type: 2, question_num: 4, type_bonus: 5},
          {type: 3, question_num: 4, type_bonus: 5},
          {type: 4, question_num: 4, type_bonus: 5},
          {type: 5, question_num: 4, type_bonus: 5}
        ]
      }
      // 调用接口添加新的模板
      api.addTemplate('/quiz/templates', newTemplate).then((res) => {
        if (res.success === 'true') {
          newTemplate.template_id = res.data.template_id
          newTemplate.total = 100
          _this.templateList.push(newTemplate)
          if (_this.templateList.length === 1) {
            _this.selectedTemplateId = _this.templateList[0].template_id
            _this.updateChoosedTemplate()
          }
          _this.$message({
            message: '添加成功！',
            type: 'success',
            duration: 1500
          })
        } else {
          _this.$message({
            message: '添加失败！可能网络链接不稳定。',
            type: 'error',
            duration: 1500
          })
        }
      })
    }
  },
  created () {
    this.getCourseInfo()
  }
}
</script>

<style scoped>
  .quizAdd {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .leftFrame {
    display: inline-block;
    width: 800px;
  }
  .leftFrame_form {
    padding: 20px 60px;
    /*background-color: pink;*/
  }
  .leftFrame_form .quiz-input,
  .leftFrame_form .quiz-choose {
    margin-bottom: 10px;
  }

  .leftFrame_bottom {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .leftFrame_bottom_btn {
    display: flex;
  }
  .rightFrame {
    display: inline-block;
    width: 350px;
  }
  .rightFrame_tables {
    height: 525px;
    margin-top: 15px;
    overflow-y: scroll;
    overflow-x: hidden;
    /*border-top: 1px solid rgba(139, 139, 139, 0.1);*/
    /*border-bottom: 1px solid rgba(139, 139, 139, 0.1);*/
    transition: height .8s;
  }
  .rightFrame_second .rightFrame_tables {
    height: 0px;
  }
  .rightFrame_table {
    margin-top: 10px;
    cursor: pointer;
  }
  .rightFrame_addTable_btn {
    color: #8b8b8b;
    text-align: center;
    line-height: 50px;
    margin: 0 5px 5px 5px;
    background-color: rgba(242, 242, 242, 1);
    cursor: pointer;
    transition: all .5s;
  }
  .rightFrame_addTable_btn:hover {
    background-color: rgba(232, 232, 232, 1);
  }
  /*定义滚动条轨道*/
  .rightFrame_setRange::-webkit-scrollbar-track,
  .rightFrame_tables::-webkit-scrollbar-track
  {
    background-color: #f9f9f9;
    -webkit-box-shadow: inset 0 0 6px transparent;
  }
  /*定义滚动条高宽及背景*/
  .rightFrame_setRange::-webkit-scrollbar,
  .rightFrame_tables::-webkit-scrollbar
  {
    width: 10px;
    background-color: #333;
  }
  /*定义滚动条*/
  .rightFrame_setRange::-webkit-scrollbar-thumb,
  .rightFrame_tables::-webkit-scrollbar-thumb
  {
    background-color: #8b8b8b;
    border-radius: 10px;
  }
  .rightFrame_nextStep {
    text-align: center;
  }
  .rightFrame_nextStep i,
  .rightFrame_nextStep_btn {
    color: #ccc;
  }
  .rightFrame_nextStep_btn {
    transition: all .5s;
    cursor: pointer;
  }
  .rightFrame_nextStep_btn:hover {
    color: rgb(138, 138, 138);
  }
  .rightFrame_nextStep_main {
    position: relative;
    height: 15px;
    top: -3px;
    transition: all .2s;
    transition: height .8s;
    overflow: hidden;
  }
  .rightFrame_second .rightFrame_nextStep_main {
    height: 550px;
    border-bottom: 1px solid rgba(139, 139, 139, 0.1);
  }
  .rightFrame_setRange {
    max-height: 350px;
    overflow-y: auto;
    overflow-x: hidden;
  }
  .rightFrame_setRange_item {
    margin: 8px 5px 0 0;
    display: flex;
  }
  .rightFrame_setRange_label {
    flex: 0 0 auto;
    width: 80px;
    line-height: 45px;
  }
  .rightFrame_setRange_content {
    min-height: 45px;
    font-size: 12px;
    flex: 1 1 auto;
    background-color: rgba(242, 242, 242, 1);
    padding: 3px 5px;
    cursor: pointer;
    transition: all .5s;
  }
  .rightFrame_setRange_content:hover {
    background-color: rgba(232, 232, 232, 1);
  }
  .rightFrame_setRange_tips {
    color: #a4a4a4;
    line-height: 45px;
  }
</style>



