<template>
  <div class="analyze-question">
    <p class="current-question" v-if="isExamPaper">{{question.belongTypeTitle}}{{question.questionNum}}</p>
    <p class="question-title" :class="{'preview-title': isBrowse}" v-html="question.questionTitle"></p>
    <div class="answer-area">
      <el-input
        v-if="!isBrowse"
        type="textarea"
        :autosize="{ minRows: 15}"
        placeholder="请输入内容"
        v-model="answer">
      </el-input>
      <p class="answer pass"
         v-html="answer"
         v-if="question.questionAnswer !== undefined &&
          ((question.userAnswer !== undefined && question.questionAnswer === question.userAnswer) ||
           (question.userAnswer === undefined))"></p>
      <p class="answer error"
         v-html="question.userAnswer"
         v-if="question.questionAnswer !== undefined &&
          question.userAnswer !== undefined &&
          question.questionAnswer !== question.userAnswer"></p>
    </div>
    <div class="answer-area-error"
         v-if="question.questionAnswer !== undefined &&
            question.userAnswer !== undefined &&
            question.questionAnswer !== question.userAnswer">
      <span v-if="!isBrowse">很抱歉，您做错了！</span>
      <span class="question-answer">正确答案是：{{getQuestionAnswer}}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    question: {
      type: Object
    },
    isExamPaper: {
      type: Boolean,
      default: false
    },
    isBrowse: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    getQuestionAnswer () {
      return this.question.questionAnswer
    },
    getUserAnswer () {
      return this.answer
    }
  },
  data () {
    return {
      answer: ''
    }
  },
  methods: {
    setUserAnswer (init) {
      if (init) {
        this.answer = ''
        return
      }
      if (typeof this.question.userAnswer !== 'undefined') {
        this.answer = this.question.userAnswer
      }
    }
  },
  mounted () {
    this.answer = this.question.userAnswer ? this.question.userAnswer : this.question.questionAnswer ? this.question.questionAnswer : ''
  }
}
</script>

<style scoped>
  .analyze-question {
    width: 100%;
    font-size: 14px;
  }
  .analyze-question .current-question {
    font-weight: 600;
    width: 100%;
    /*height: 30px;*/
    line-height: 30px;
    padding: 25px 15px 15px 15px;
    box-sizing: border-box;
    font-size: 16px;
  }
  .analyze-question .question-title {
    width: 100%;
    min-height: 80px;
    line-height: 25px;
    padding: 10px 30px;
    box-sizing: border-box;
  }
  .analyze-question .preview-title {
    min-height: auto;
  }
  .analyze-question .answer-area {
    width: 100%;
    padding: 20px 30px;
    box-sizing: border-box;
  }
  .analyze-question .answer-area .answer {
    width: 100%;
    height: auto;
    border-radius: 8px;
    padding: 10px;
    font-size: 14px;
    line-height: 20px;
    background-color: #fafafa;
  }

  .analyze-question .answer-area .active {
    border-color: #f48118;
    background-color: #f0f0f0;
  }
  .analyze-question .answer-area .pass {
    color: #8ecc45;
    border: 1px solid #8ecc45;
  }
  .analyze-question .answer-area .error {
    color: #d43039;
    border: 1px solid red;
  }
  .analyze-question .answer-area-error {
    width: 100%;
    min-height: 50px;
    line-height: 24px;
    padding: 10px;
    background-color: #f99090;
    font-size: 14px;
    color: #d43039;
  }
  .analyze-question .answer-area-error .question-answer {
    font-size: 18px;
  }</style>



