<template>
  <div class="user-card">
    <img class="img"
         :src="user.avatarurl"
         alt=""
         :style="{width: imgSize, height: imgSize}">
    <div class="badge-area">
      <span v-for="(badge, index) in user.job" :key="index" class="item blue" :class="{'green': index === 0}">{{badge}}</span>
    </div>
    <div class="desc-area" :style="{marginTop: marginTop}">
      <span class="name">{{user.name}}</span>
      <span class="desc">{{user.description}}</span>
    </div>
  </div>
</template>

<script>
import {host} from '@/api/config'
export default {
  props: {
    imgSize: {
      type: String,
      default: '150px'
    },
    marginTop: {
      type: String,
      default: '10px'
    },
    user: {
      type: Object
    }
  },
  data () {
    return {}
  },
  mounted () {
    this.user.avatarurl = host + '/' + this.user.avatarurl
  }
}
</script>

<style scoped>
  .user-card {
    margin: 0 auto;
    /*border: 1px solid skyblue;*/
    text-align: center;
    padding: 20px 15px;
    box-sizing: border-box;
    /*background-color: #f8f8f8;*/
    background-color: inherit;
  }
  .user-card .img {
    border-radius: 50%;
    transition: all .4s;
    -webkit-box-shadow: 0 5px 15px #888;
  }
  .user-card .img:hover {
    cursor: pointer;
    transform: scale(1.1);
    -ms-transform:scale(1.1);     /* IE 9 */
    -moz-transform:scale(1.1);     /* Firefox */
    -webkit-transform:scale(1.1); /* Safari 和 Chrome */
    -o-transform:scale(1.1);
  }
  .badge-area {
    margin-top: 5px;
    color: #f2f2f2;
  }
  .badge-area .item {
    display: inline-block;
    border-radius: 12px;
    min-width: 24px;
    height: 24px;
    line-height: 24px;
    color: #f2f2f2;
    padding: 0 10px;
    font-size: 14px;
    box-sizing: border-box;
    margin-left: 5px;
  }
  .badge-area .item:first-child {
    margin-left: 0;
  }
  .badge-area .blue {
    background-color: #6495ed;
  }
  .badge-area .green {
    background-color: #8ecc45;
  }
  .desc-area {
    font-size: 16px;
    color: #676767;
  }
  .desc-area .name {
    color: #006;
    font-weight: bold;
    margin-right: 6px;
  }
</style>



