import {getRequest} from './config'
// import getRequest from './config'
// import postRequest from './config'

function getInfoList (link, data) {
  return getRequest(link, {
    uid: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getRankList (link, data) {
  return getRequest(link, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getAllRankList (link, page, pagesize) {
  return getRequest(link, {
    page: page,
    pagesize: pagesize
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

function getSomeoneRank (link, page, pagesize, nick, type, rangeId) {
  return getRequest(link, {
    page: page,
    pagesize: pagesize,
    nick: nick,
    type: type,
    class_id: rangeId
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}

const api = {
  getInfoList,
  getRankList,
  getAllRankList,
  getSomeoneRank
}

export default api



