<template>
  <div class="quiz-panel" :style="getStyle">
    <span class="quiz-panel_title" v-if="title">{{title}}&nbsp;</span>
    <template v-if="topLine"><hr></template>
    <slot></slot>
    <template v-if="downLine"><hr></template>
  </div>
</template>

<script>
export default {
  name: 'quiz-panel',
  props: {
    title: {
      type: String
    },
    topLine: {
      type: Boolean,
      default: true
    },
    downLine: {
      type: Boolean,
      default: false
    },
    width: {
      type: String,
      default: '[0%, auto, unset]'
    },
    height: {
      type: String,
      default: '[0%, auto, unset]'
    },
    padding: {
      type: String,
      default: '0px'
    },
    radius: {
      type: String,
      default: 'default'
    },
    isHidden: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    getStyle () {
      var setWidth = this.width.replace('[', '').replace(']', '').split(',')
      var setHeight = this.height.replace('[', '').replace(']', '').split(',')
      var width = setWidth.length > 1 ? setWidth[1] : setWidth[0]
      var height = setHeight.length > 1 ? setHeight[1] : setHeight[0]
      var style = {
        maxWidth: setWidth.length === 3 ? setWidth[2] : 'unset',
        minWidth: setWidth.length > 1 ? setWidth[0] : '0%',
        width: width,
        maxHeight: setHeight.length === 3 ? setHeight[2] : 'unset',
        minHeight: setHeight.length > 1 ? setHeight[0] : '0%',
        height: height,
        padding: this.padding,
        borderRadius: this.radius.replace(/default/g, '10px'),
        overflow: this.isHidden ? 'hidden' : 'unset'
      }
      return style
    }
  },
  data () {
    return {
      ifHoverBtn: false
    }
  }
}
</script>

<style scoped>
  .quiz-panel {
    background-color: #fff;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
    position: relative;
  }
  .quiz-panel_title {
    color: rgba(139, 139, 139, 0.4);
    background-color: #fff;
    position: absolute;
    z-index: 0;
    margin-left: 10px;
    white-space: nowrap;
    cursor: default;
  }
  hr {
    height: 1px;
    background-color: rgba(139, 139, 139, 0.2);
    border: none;
    margin: 10px;
  }
</style>



