<template>
  <div class="news-template">
    <h5>{{title}}</h5>
    <ul>
      <li v-for="(item, index) in newsData" @click="toPost(item.post_id)" :key="index">
        <a href="javascript:void(0)">{{item.title}}</a>
        <span>{{item.author.nick}}</span>
        <span class="time">{{item.publish_date.split('T')[0]}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default{
  props: {
    newsData: {
      type: Array
    },
    title: {
      type: String
    }
  },
  methods: {
    toPost (id) {
      this.$router.push({name: 'postDetail', params: {id: id}})
    }
  }
}
</script>

<style scoped>
  .news-template {
    width: 600px;
    padding-left: 20px;
    border-left: 4px solid rgb(254, 202, 62);
    margin: 30px 20px;
    position: relative;
  }
  .news-template h5{
    line-height: 16px;
    font-size: 15px;
    color: #2b303e;
  }
  .news-template ul {
    /*background-color: pink;*/
    padding-top: 10px;
  }
  .news-template ul>li {
    line-height: 2;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    cursor: default;
  }
  .news-template ul>li a {
    width: 300px;
    white-space: nowrap;
    text-overflow:ellipsis;
    overflow: hidden;
    cursor: pointer;
    transition: all .3s;
  }
  .news-template ul>li a:hover {
    color: #f48118;
  }
</style>



