<template>
  <div class="change-uname">
    <div class="container">
      <h5 class="change-uname_title">公告</h5>
      <div class="change-uname_notice">
        <p>由于平台内个别用户的昵称过于随意，导致页面某些功能无法完整使用，所以从今日起(2019/3/31)启用强制改名措施，让平台更加规范。恭喜您中奖啦，快给自己改个更好听的昵称吧~</p>
      </div>
      <div class="change-uname_changeName">
        <label for="username" :style="getLabelStyle">修改昵称</label>
        <input id="username" v-model="username" type="text" placeholder="请输入符合规格的用户名">
        <span class="tip">*&nbsp;&nbsp;当“修改昵称”标签变为绿色时即表示符合规范</span>
        <span class="tip">*&nbsp;&nbsp;昵称可以由2-14个中文/字母/数字/减号/下划线组成</span>
      </div>
      <div style="text-align: right" v-if="isAllow">
        <div class="change-uname_submit">
          <span class="tips">确定修改且进入愉快玩耍</span>
          <span class="btn" @click="updateInfo"><i class="icon-tijiao1"></i></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'change-uname',
  computed: {
    getLabelStyle () {
      return {
        backgroundColor: this.isAllow ? '#7ab441' : '#f48118'
      }
    }
  },
  props: {
    info: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      isAllow: false,
      username: '',
      copyInfo: {}
    }
  },
  methods: {
    updateInfo () {
      this.copyInfo.nick = this.username
      this.$emit('changeUname', this.copyInfo)
    }
  },
  watch: {
    username () {
      var reg = this.username.indexOf('GPNU_') === 0
        ? /^[-\w\u4e00-\u9fa5]{2,20}$/ : /^[-\w\u4e00-\u9fa5]{2,14}$/
      this.isAllow = reg.test(this.username)
    }
  },
  created () {
    this.copyInfo = JSON.parse(JSON.stringify(this.info))
  }

}
</script>

<style scoped>
  .change-uname {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 2;
  }
  .container {
    display: inline-block;
    width: 360px;
    height: 220px;
    text-align: left;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.1);
    vertical-align: middle;
    padding: 5px 0;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
  }
  .change-uname_title {
    font-size: 14px;
    line-height: 2;
    cursor: default;
  }
  .change-uname_title::before {
    content: '';
    width: 16px;
    height: 16px;
    background-color: #f48118;
    border-radius: 50%;
    display: inline-block;
    margin: 0 5px 0 10px;
    vertical-align: middle;
    position: relative;
    top: -2px;
  }
  .change-uname_notice {
    font-size: 12px;
    margin: 5px 20px;
    line-height: 1.8;
    text-indent: 2em;
    padding-bottom: 5px;
    /*border-bottom: 1px solid #eee;*/
    cursor: default;
  }
  .change-uname_changeName {
    margin: 5px 0;
    line-height: 2;
    text-align: center;
  }
  .change-uname_changeName>label {
    display: inline-block;
    font-size: 14px;
    color: #fff;
    padding: 0 5px;
    border-radius: 5px 0 0 5px;
    margin-right: -5px;
  }
  .change-uname_changeName>input {
    font-size: 14px;
    line-height: 2;
    border: 1px solid #eee;
    padding: 0 10px;
    border-radius: 0 5px 5px 0;
    border-left: none;
  }
  .change-uname_changeName .tip{
    font-size: 10px;
    display: block;
    color: #ff3333;
    cursor: default;
  }
  .change-uname_submit {
    display: inline-block;
    line-height: 30px;
    margin-top: 20px;
  }
  .change-uname_submit .btn {
    display: inline-block;
    width: 30px;
    text-align: center;
    border-radius: 50%;
    background-color: #fff;
    cursor: pointer;
    transition: all .3s;
  }
  .change-uname_submit .btn:hover {
    color: #fff;
    background-color: #7ab441;
  }
  .change-uname_submit .tips {
    color: #fff;
    display: none;
  }
  .change-uname_submit:hover .tips {
    display: inline;
  }
</style>



