<template>
  <div class="left-frame">
    <div class="frame-head"
         @click.stop="handleClickTag(-1, -1, null)"
         :class="{'tag-active': activeTitleIndex === -1}">全部题目</div>
    <!--<el-collapse accordion>-->
      <!--<ul>-->
        <!--<li v-for="(tree, index) in tagTree"-->
            <!--:key="index">-->
          <!--<el-collapse-item :title="tree.label">-->
            <!--<span v-for="(item, k) in tree.children"-->
                  <!--:key="k"-->
                  <!--:class="{'tag-active': index === activeTitleIndex && k === activeItemIndex}"-->
                  <!--@click.stop="handleClickTag(index, k, item.id)">{{item.label}}</span>-->
          <!--</el-collapse-item>-->
        <!--</li>-->
      <!--</ul>-->
    <!--</el-collapse>-->
    <el-tree :data="treeData" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
  </div>
</template>

<script>
import api from '@/api/questionBank'

export default {
  data () {
    return {
      activeName: '1',
      tagTree: [],
      activeTitleIndex: -1,
      activeItemIndex: -1,
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      treeData: []
    }
  },
  methods: {
    handleClickTag (index, k, id) {
      this.activeTitleIndex = index
      this.activeItemIndex = k
      // 第二个参数this.childValue是需要传的值
      this.$emit('changeTagId', id)
    },
    // 递归处理成一个带有value和label和指定父节点的数组
    recursiveData (Object, arr, ObjectArr, index) {
      for (let i in Object) {
        let obj = {
          value: i,
          label: ObjectArr[i],
          isFather: index,
          children: []
        }
        arr.push(obj)
        if (Object[i] !== {}) {
          this.recursiveData(Object[i], arr, ObjectArr, i)
        } else {
          break
        }
      }
      return arr
    },
    initTagTree1 () {
      const _this = this
      let tagDict = []
      let tagTree = []
      let arr = []
      let ObjArr = []
      api.getQuestionList('/tags').then(function (res) {
        tagDict = res.data.tag_dict
        tagTree = res.data.tag_tree
        _this.recursiveData(tagTree, arr, tagDict, '0')
        for (let i = 0; i < arr.length; i++) {
          for (let j = 0; j < arr.length; j++) {
            if (arr[i].value === arr[j].isFather) {
              arr[i].children.push(arr[j])
            }
          }
        }
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].isFather === '0') {
            ObjArr.push(arr[i])
          }
        }
        _this.treeData = ObjArr
      })
    },
    handleNodeClick (data) {
      // 第二个参数this.childValue是需要传的值
      if (data.children.length === 0) {
        this.$emit('changeTagId', parseInt(data.value))
      }
    },
    initTagTree () {
      let self = this
      let tagDict = []
      let tagTree = []
      api.getQuestionList('/tags').then(function (res) {
        // 把得到的数据转成数组
        tagDict = res.data.tag_dict
        tagTree = res.data.tag_tree
        for (let key in tagTree) {
          let obj = {
            key: key,
            label: tagDict[key],
            children: self.initTree(tagDict, tagTree[key])
          }
          self.tagTree.push(obj)
        }
        // console.log(self.tagTree)
      })
    },
    objEmpty (obj) {
      for (let key in obj) {
        return false
      }
      return true
    },
    initTree (data, tree) {
      if (this.objEmpty(tree)) {
        return []
      } else {
        let List = []
        for (let tag in tree) {
          let obj = {
            id: tag,
            label: data[tag]
          }
          List.push(obj)
        }
        return List
      }
    },
    getSubscript () {
    }
  },
  created () {
    this.initTagTree()
    this.initTagTree1()
    var el = window.document.body// 声明一个变量，默认值为body
    window.document.body.onmouseover = function (event) {
      el = event.target// 鼠标每经过一个元素，就把该元素赋值给变量el
      if (el.className === 'el-tree-node__label') {
        if (!el.classList.contains('label-over-height') && el.clientWidth > 120) {
          el.classList.add('label-over-height')
        }
      }
    }
  },
  watch: {
  }
}
</script>

<style scoped>
  .left-frame {
    width: 100%;
    height: 100%;
    background-image: url("../../../static/image/questionBank.jpg");
    background-size: 100%;
    overflow: auto;
  }
  .left-frame .tag-active {
    color: #f48118 !important;
  }
  .frame-head {
    width: 62px;
    padding-top: 19px;
    font-size: 15px;
    margin-left: 35px;
    color: white;
    padding-bottom: 10px;
    border-bottom: 1px solid white;
    cursor: pointer;
  }
  .frame-head:hover {
    color: #f48118;
  }
  .left-frame /deep/ .el-collapse-item__header {
    background: none;
    border: 0;
    color: white;
    padding-left: 10px;
  }
  .left-frame /deep/ .el-collapse-item__header .el-collapse-item__arrow{
    float: left;
    margin-right: 14px;
  }
  .left-frame /deep/ .el-collapse-item__wrap {
    padding-left: 40px;
    background-color: transparent;
    border: none;
  }
  .left-frame /deep/ .el-collapse-item__content {
    padding-bottom: 0px;
  }
  .left-frame /deep/ .el-collapse-item__wrap span{
    display: block;
    color: #ebebeb;
    padding-top: 10px;
  }
  .left-frame /deep/ .el-collapse-item__wrap span:first-child {
    padding-top: 0;
  }
  .left-frame /deep/ .el-collapse-item__wrap span:hover{
    cursor: pointer;
    color: #f48118;
  }
  .left-frame /deep/ .el-collapse {
    border: none;
  }
  .el-collapse-item /deep/ .el-collapse-item__header {
    height: 50px;
    line-height: 50px;
  }
  .left-frame /deep/ .el-tree{
    background-color: transparent !important;
    color: white;
  }
  .left-frame  .el-tree /deep/ .el-tree-node{
    margin-top: 15px !important;
  }
  .left-frame  .el-tree /deep/ .el-tree-node__content{
    background-color: transparent;
  }
  .left-frame  .el-tree /deep/ .el-tree-node__content:hover{
    cursor: pointer;
    color: #f48118;
  }
  .left-frame  .el-tree /deep/ .el-tree-node__label {
    display: inline-block;
    position: relative;
    max-width: 121px;
    transition: color .3s;
    transition: text-indent 1.2s;
    overflow: hidden;
    white-space: nowrap;
    /*text-overflow: ellipsis;*/
    text-indent: 0em;
  }
  .left-frame  .el-tree /deep/ .label-over-height:hover {
    text-indent: -5em;
  }
  ::-webkit-scrollbar {
    width: 8px;
    background-color: #F5F5F5;
  }
  ::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    background: #000;
  }
  ::-webkit-scrollbar-thumb {
    border-radius: 3px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color:rgba(175, 175, 175, 0.7);
  }
  ::-webkit-scrollbar-thumb:hover {
    border-radius: 3px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color:rgba(175, 175, 175, 0.85);
  }
</style>



