<template>
  <div class="personal-head clearfix">
    <div class="head-img">
      <el-upload
        class="avatar-uploader"
        action="http://localhost:8080"
        :http-request="uploadImg"
        :show-file-list="false"
        :before-upload="beforeAvatarUpload">
        <img v-if="info.avatarurl" :src="avatarUrl" class="avatar">
      </el-upload>
    </div>
    <div class="information">
      <span class="nick">{{info.nick}}</span>
      <span class="identity" v-if="info.identity === '2'">(教师)</span>
      <span class="identity" v-if="info.identity === '1'">(学生)</span>
      <span class="identity" v-if="info.identity === '3'">(管理员)</span>
      <div class="bottom">
        <div class="line">
          <i class="icon-class"></i>
          <span v-if="info.identity == '1'">学号:</span>
          <span v-if="info.identity == '2'">教工号:</span>
          <span>{{info.code}}</span>
        </div>
        <div class="line">
          <i class="icon-academy"></i>
          <span v-if="info.identity == 1">班级:</span>
          <span v-if="info.identity == 1">{{info.class_name}}</span>
          <span v-if="info.identity == 2">学院:</span>
          <span v-if="info.identity == 2">{{info.academy}}</span>
          <span v-if="info.identity == 0">QQ:</span>
          <span v-if="info.identity == 0">{{info.qq}}</span>
        </div>
        <div class="line">
          <i v-if="info.identity != 1" class="el-icon-message"></i>
          <span v-if="info.identity != 1">邮箱:</span>
          <span v-if="info.identity != 1">{{info.email}}</span>
        </div>
        <div class="line">
          <i class="icon-time"></i>
          <span>最近登录时间</span>
          <span>{{info.login_time.replace('T',' ')}}</span>
          <!--<span>结课程</span>
          <span>1</span>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {host} from '@/api/config'
import api from '@/api/personCenter'
import { mapMutations } from 'vuex'

export default{
  props: {
    info: {
      type: Object
    }
  },
  data () {
    return {
      avatarUrl: '',
      postData: new FormData(),
      headers: {
        'Content-Type': 'application/json'
      },
      defaultImage: require('../../../static/image/acmlogo.jpg')
    }
  },
  methods: {
    ...mapMutations([
      'SET_USER'
    ]),
    /** 上传头像前校验图片是否符合规定 **/
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isPNG = file.type === 'image/png'
      const isGIF = file.type === 'image/gif'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG && !isPNG && !isGIF) {
        this.$message.error('请上传JPG、PNG、GIF格式头像图片!')
        file.abort() // 取消上传
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
        file.abort() // 取消上传
      }
      for (let o in this.info) {
        this.postData.append(o, this.info[o])
      }
    },
    // 上传头像
    uploadImg (content) {
      const _this = this
      let data = {
        file: content.file
      }
      api.postUserImg('/users/uploadImage', data).then(function (res) {
        if (res.success === 'true') {
          _this.$message({
            message: '上传成功!',
            type: 'success',
            duration: 1500
          })
          _this.info.avatarurl = res.data + '?_t=' + new Date().getTime()
          _this.avatarUrl = host + _this.info.avatarurl
          _this.setSessionStorage()
          _this.SET_USER(_this.info || {})
        } else {
          _this.$message({
            message: '上传失败!',
            type: 'error',
            duration: 1500
          })
        }
      })
    },
    setInfoAvatarUrl () {
      this.avatarUrl = this.info.avatarurl ? host + this.info.avatarurl : this.defaultImage
    },
    setSessionStorage () {
      // 将JSON字符串转换成为JSON对象输出
      localStorage.setItem('userInfo', JSON.stringify(this.info))
    }
  },
  created () {
    this.setInfoAvatarUrl()
  }
}
</script>

<style>
  .head-img .avatar-uploader .el-upload {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    cursor: pointer;
  }
</style>

<style scoped>
  .personal-head{
    margin: 0 auto;
    width: 100%;
    height: 100px;
    background-color: white;
    padding: 10px 30px;
    box-shadow:  0px 3px 12px 0px rgba(54, 54, 54, 0.12);
    border-radius: 5px;
  }
  .head-img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    float: left;
    margin-right: 20px;
    background-color: beige;
  }
  .head-img .avatar {
    width: 80px;
    height: 80px;
    border-radius: 50%;
  }
  .information{
    height: 100%;
    padding: 15px 0px;
  }
  .bottom{
    margin-top: 10px;
  }
  .nick{
    display: inline-block;
    vertical-align: bottom;
    font-size: 18px;
    color: #f48118;
  }
  .identity{
    display: inline-block;
    margin-left: 10px;
    font-size: 12px;
    color: #333333;
  }
  .line{
    margin-left: 15px;
    display: inline-block;
  }
  .line:first-child{
    margin-left: 0px;
  }
</style>



