export function getRequest (url, data) {
  return new Promise((resolve, reject) => {
    axios.get(host + url, {
      params: data,
      xhrFields: {
        withCredentials: true
      },
      headers: {
        'Content-Type': 'application/json'
      },
      emulateJSON: true
    }).then(function (res) {
      return resolve(res)
    })
  })
}

