export function postFromData (link, data) {
  return new Promise((resolve, reject) => {
    axios({
      method: 'post',
      url: host + link,
      data: data,
      xhrFields: {
        withCredentials: true
      },
      transformRequest: [
        function (data) {
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          ret = ret.slice(0, -1)
          return ret
        }
      ]
    }).then(function (res) {
      return resolve(res)
    })
  })
}

export function delFromData (link, data) {
  return new Promise((resolve, reject) => {
    axios({
      method: 'delete',
      url: host + link,
      data: data,
      xhrFields: {
        withCredentials: true
      },
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      transformRequest: [
        function (data) {
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          ret = ret.slice(0, -1)
          return ret
        }
      ]
    }).then(function (res) {
      return resolve(res)
    })
  })
}

